import { camelCaseReshape, getItems } from "utilities";

export const toMapsItemModel = (data = {}) =>
  camelCaseReshape(data, {
    id: "id",
    createdAt: "createdAt",
    lastModifiedAt: "lastModifiedAt",
    type: "type",
    source: "source",
    farmId: "farmId",
    jobId: "jobId",
    date: "date",
    sequence: "sequence",
    name: "name",
    description: "description",
    properties: "properties"
  });

export const toSceneFileItemModel = (data = {}) =>
  camelCaseReshape(data, {
    id: "id",
    createdAt: "createdAt",
    lastModifiedAt: "lastModifiedAt",
    blobUrl: "blobUrl",
    sceneId: "sceneId",
    format: "format",
    type: "type",
    name: "name",
    downloadSasUrl: "downloadSASUrl",
    description: "description",
    properties: "properties",
    contentType: "contentType",
    imageUrlMaps: "imageUrlMaps"
  });

export const toSceneFileItemsModel = (response = {}) =>
  getItems(response).map(toSceneFileItemModel);

export const toMapsItemsModel = (response = {}) =>
  getItems(response).map(toMapsItemModel);
