import {
  toJobsItemsModel,
  toJobTypesItemsModel,
  toJobsItemModel
} from "./models";

import { HttpClient } from "utilities/httpClient";
import Config from "app.config";

const JOB_ENDPOINT = Config.serviceUrls.job;
const JOBTYPE_ENDPOINT = Config.serviceUrls.jobType;
export class JobsService {
  static getJobsItem(id) {
    return HttpClient.get(`${JOB_ENDPOINT}/${id}`).map(toJobsItemsModel);
  }
  static getJobTypesItems() {
    return HttpClient.get(
      `${JOBTYPE_ENDPOINT}?includes=PipelineDetails&includes=Properties&maxItems=${Config.apiGetAllMaxItems}`
    ).map(toJobTypesItemsModel);
  }
  static getJobsItems() {
    return HttpClient.get(
      `${JOB_ENDPOINT}?includes=Arguments&includes=Properties&maxItems=${Config.apiGetAllMaxItems}`
    ).map(toJobsItemsModel);
  }

  static submitJob(body) {
    return HttpClient.post(`${JOB_ENDPOINT}`, body).map(toJobsItemModel);
  }

  static abortJobs(id) {
    return HttpClient.patch(`${JOB_ENDPOINT}/${id}/stop`);
  }
}
