import { Component } from "react";
import update from "immutability-helper";

import { isFunc, isDef, isNull } from "utilities";

/** Contains logic for validating a value */
export class Validator {
  static notEmpty = x => {
    if (Array.isArray(x)) return x.length > 0;
    if (typeof x === "number") return true;
    return !!x;
  };

  static latValidation = x => {
    if (x.toString().includes("Syncing ")) return true;
    const isLatitude = /^(\+|-)?(?:90(?:(?:\.0{1,6})?)|(?:[0-9]|[1-8][0-9])(?:(?:\.[0-9]{1,6})?))$/;
    const pattern = new RegExp(isLatitude);
    return pattern.test(x);
  };

  static longValidation = x => {
    if (x.toString().includes("Syncing ")) return true;
    const isLongitude = /^(\+|-)?(?:180(?:(?:\.0{1,6})?)|(?:[0-9]|[1-9][0-9]|1[0-7][0-9])(?:(?:\.[0-9]{1,6})?))$/;
    const pattern = new RegExp(isLongitude);
    return pattern.test(x);
  };

  static reportingIntervalValidation = x => {
    if (x.toString().includes("Syncing ")) return true;
    const isReportingInterval = /^(1[2-8][0-9]|19[0-9]|[2-9][0-9]{2}|[1-8][0-9]{3}|9[0-8][0-9]{2}|99[0-8][0-9]|999[0-9]|[1-7][0-9]{4}|8[0-5][0-9]{3}|86[0-3][0-9]{2}|86400)$/;
    const pattern = new RegExp(isReportingInterval);
    return pattern.test(x);
  };

  static nameValidation = x => {
    const isNameRegex = /^[A-Za-z0-9_]+[A-Za-z0-9_\s]*$/;
    const pattern = new RegExp(isNameRegex);
    return pattern.test(x);
  };

  static notDuplicate = (value, valuesToCompare, form, editItemName) => {
    let match = true;
    // have to check enterted value is exists to avoid duplicate value
    if (value !== editItemName) {
      // while edit, it should have to check for other then the editing object

      if (form === "devices") {
        const deviceObj = valuesToCompare.find(
          item =>
            item.displayName
              .trim()
              .toString()
              .toLowerCase() ===
            value
              .trim()
              .toString()
              .toLowerCase()
        );

        if (deviceObj !== undefined) match = false;
      } else if (form === "templates") {
        const tempObj = valuesToCompare.find(
          item =>
            item.templateName
              .trim()
              .toString()
              .toLowerCase() ===
            value
              .trim()
              .toString()
              .toLowerCase()
        );

        if (tempObj !== undefined) match = false;
      } else if (form === "rules") {
        const tempObj = valuesToCompare.find(
          item =>
            item.name
              .trim()
              .toString()
              .toLowerCase() ===
            value
              .trim()
              .toString()
              .toLowerCase()
        );

        if (tempObj !== undefined) match = false;
      } else if (form === "farms") {
        const farmObj = valuesToCompare.find(
          item =>
            item.name
              .trim()
              .toString()
              .toLowerCase() ===
            value
              .trim()
              .toString()
              .toLowerCase()
        );

        if (farmObj !== undefined) match = false;
      }
    }
    return match;
  };

  static noSpaces = x => {
    if (x.trim().length === 0 && x.match(/\s/g))
      return (x = x.replace(/\s/g, ""));
    return !!x;
  };

  static maxlength = (value, maxLength) => {
    if (!isNull(value) && value.length > maxLength) return false;
    return true;
  };

  static charlength = x => {
    if (!isNull(x) && x.length > 25) return false;
    return true;
  };

  static minlength = x => {
    if (!isNull(x) && x.length < 3) return false;
    return true;
  };

  constructor(validator = {}) {
    this.validators = validator.validators || [];
    this.rejectors = validator.rejectors || [];
  }

  /**
   * Adds a checking function to the validator list
   * If the function returns false, validation fails
   */
  check(checker, msg = true) {
    this.validators = [
      ...this.validators,
      value => (checker(value) ? "" : isFunc(msg) ? msg(value) : msg)
    ];
    return this;
  }

  checkDuplicate(checkerArray, msg = true) {
    this.validators = [
      ...this.validators,
      value => (checkerArray(value, []) ? "" : isFunc(msg) ? msg(value) : msg)
    ];
    return this;
  }
  /**
   * Adds a rejection function to the rejector list
   * If the function returns false, the value is rejected
   */
  reject(rejector) {
    this.rejectors = [...this.rejectors, rejector];
    return this;
  }

  shouldReject(value) {
    return this.rejectors.some(rejector => rejector(value));
  }

  hasErrors(value) {
    let error = "";
    for (let i = 0; i < this.validators.length; i++) {
      error = this.validators[i](value);
      if (error) return error;
    }
    return error;
  }
}

/** Links to a part of a component state */
export class Link extends Validator {
  /** Accepts either an existing link or a component and state key name */
  constructor(...args) {
    super();

    if (args.length === 1 && args[0] instanceof Link) {
      const [link] = args;
      this.component = link.component;
      this.setter = link.setter;
      this.selector = link.selector;
    } else if (args.length >= 2) {
      const [component, name] = args;
      this.component = component;
      this.setter = value => ({ [name]: value });
      this.selector = state => state[name];
    } else {
      throw new Error("Link class: Invalid constructor signature.");
    }

    this.mappers = [];
  }

  onChange = ({ target: { value, type, checked } }) => {
    if (this.shouldReject(value)) return;
    const finalVal = this.mappers.reduce(
      (val, mapFunc) => mapFunc(val),
      type === "checkbox" ? checked : value
    );
    this.set(finalVal);
  };

  /** Links to property in the component state */
  to = name => {
    const { selector, setter } = this;
    this.setter = value => setter({ [name]: value });
    this.selector = state => selector(state)[name];
    return this;
  };

  /** Adds a function for mapping an input value to a new value */
  map = func => {
    this.mappers = [...this.mappers, func];
    return this;
  };

  /**
   * Updates the value of the component state property the Link is
   * connected to.
   */
  set(value) {
    this.component.setState(
      update(this.component.state, this.setter({ $set: value }))
    );
    return this;
  }

  get value() {
    // In case the user defaults a value to be undefined, always default to the empty string
    // to prevent switching between a controlled and uncontrolled input
    const val = this.selector(this.component.state);
    return isDef(val) ? val : "";
  }

  get error() {
    return this.hasErrors(this.value);
  }

  /** Allows the link to use predefined validator logic */
  withValidator(validator) {
    this.validators = validator.validators;
    this.rejectors = validator.rejectors;
    return this;
  }

  /** A helper method for array state properties */
  getLinkedChildren = mapFunc => {
    return this.value.map((_, idx) => mapFunc(this.forkTo(idx), idx));
  };

  /**
   * Used to create a new Link instance using the state property hierarchy of an
   * existing instance
   */
  forkTo = name => new Link(this).to(name);
}

/** A helper for creating components with links into the component state */
export class LinkedComponent extends Component {
  linkTo = name => new Link(this, name);
}
