import React, { Component } from "react";
import { PcsGrid, ComponentArray, Btn, Protected } from "components/shared";
import { sensorsColumnDefs, defaultSensorGridProps } from "./sensorsGridConfig";
import { isFunc, translateColumnDefs, svgs } from "utilities";
import { checkboxColumn } from "components/shared/pcsGrid/pcsGridConfig";
import { DeleteModal } from "components/shared/deleteModal/deleteModal";
import { SensorDetailsContainer } from "../flyouts/sensorDetails.container";
import { permissions /* toDiagnosticsModel*/ } from "services/models";

const closedFlyoutState = {
  openFlyoutName: undefined,
  softSelectedSensorId: undefined,
  softSelectedSensorData: undefined
};

const initialState = {
  openFlyoutName: undefined,
  softSelectedId: undefined,
  selectedSensorList: undefined
};

export class SensorsGrid extends Component {
  constructor(props) {
    super(props);

    // Set the initial state
    this.state = initialState;
    // Default device grid columns
    this.columnDefs = [
      checkboxColumn,
      sensorsColumnDefs.name,
      sensorsColumnDefs.sensorType,
      sensorsColumnDefs.farm,
      sensorsColumnDefs.portType,
      sensorsColumnDefs.parentDevice,
      sensorsColumnDefs.portName,
      sensorsColumnDefs.lastUpdated
    ];
    this.contextBtns = (
      <ComponentArray>
        <Protected permissions={permissions.deleteSensors}>
          <Btn svg={svgs.trash} onClick={this.openModal("delete")}>
            {props.t("sensors.modal.delete")}
          </Btn>
        </Protected>
      </ComponentArray>
    );
  }

  /**
   * Get the grid api options
   *
   * @param {Object} gridReadyEvent An object containing access to the grid APIs
   */
  onGridReady = gridReadyEvent => {
    this.sensorGridApi = gridReadyEvent.api;
    // Call the onReady props if it exists
    if (isFunc(this.props.onGridReady)) {
      this.props.onGridReady(gridReadyEvent);
    }
  };

  openModal = ModelName => () =>
    this.setState({
      openModalName: ModelName
    });

  closeModal = () =>
    this.setState({
      openModalName: undefined
    });

  clickContextBtn = input => () => {
    // Just for demo purposes. Don't console log in a real grid.
  };

  openFlyout = flyoutName => () =>
    this.setState({
      openFlyoutName: flyoutName,
      softSelectedSensorId: undefined,
      softSelectedSensorData: undefined
    });

  getOpenFlyout = () => {
    const constDetails = "details";
    switch (this.state.openFlyoutName) {
      case constDetails:
        return (
          <SensorDetailsContainer
            key="sensor-device-key"
            onClose={this.closeFlyout}
            sensorId={this.state.softSelectedSensorId}
            sensorData={this.state.softSelectedSensorData}
          />
        );
      default:
        return null;
    }
  };
  /**
   * Handles soft select props method.
   * Soft selection happens when the user clicks on the row.
   *
   * @param rowId The id of the currently soft selected item
   * @param rowData The rowData from the underlying grid. MAY BE OUT OF DATE.
   */

  closeFlyout = () => this.setState(closedFlyoutState);

  onSoftSelectChange = (rowId, rowData) => {
    // Note: only the Id is reliable, rowData may be out of date
    const { onSoftSelectChange } = this.props;
    const DETAILS = "details";
    if (rowId) {
      this.setState({
        openFlyoutName: DETAILS,
        softSelectedSensorId: rowId,
        softSelectedSensorData: this.props.rowData.find(obj => obj.id === rowId)
      });
    } else {
      this.closeFlyout();
    }
    if (isFunc(onSoftSelectChange)) {
      onSoftSelectChange(rowId, rowData);
    }
  };

  /**
   * Handles context filter changes and calls any hard select props method.
   * Hard selection happens when the user checks the box for the row.
   *
   * @param {Array} selectedObjs A list of currently selected objects in the grid
   */
  onHardSelectChange = selectedObjs => {
    const { onContextMenuChange, onHardSelectChange } = this.props;
    // Show the context buttons when there are rows checked.
    if (isFunc(onContextMenuChange)) {
      onContextMenuChange(selectedObjs.length === 1 ? this.contextBtns : null);
      this.setState({ selectedSensorList: selectedObjs[0] });
    }
    if (isFunc(onHardSelectChange)) {
      onHardSelectChange(selectedObjs);
    }
  };

  deleteDevices = () => {
    this.props.sensorListUpdate(this.state.selectedSensorList);
  };
  getModel = () => {
    const constdelete = "delete";
    switch (this.state.openModalName) {
      case constdelete:
        return (
          <DeleteModal
            key="delete-modal"
            type="deleteDevice"
            t={this.props.t}
            deleteConfirmation={this.props.t(
              "sensors.deleteModal.deleteConfirmation",
              { returnObjects: true }
            )}
            title={this.props.t("sensors.deleteModal.title")}
            deleteItem={this.deleteDevices}
            onClose={this.closeModal}
            deleteBtn={this.props.t("modal.delete")}
            icon={svgs.trash}
            isPending={this.props.deleteSensorPending}
            deleteModalError={this.props.deleteModalError}
            deleteModalSuccess={this.props.deleteModalSuccess}
            successMsg={this.props.t("sensors.deleteSuccess")}
          />
        );
      default:
        return null;
    }
  };
  getSoftSelectId = ({ id } = "") => id;

  render() {
    const gridProps = {
      /* Grid Properties */
      ...defaultSensorGridProps,
      columnDefs: translateColumnDefs(this.props.t, this.columnDefs),
      sizeColumnsToFit: true,

      getSoftSelectId: this.getSoftSelectId,
      softSelectId: (this.state.softSelectedSensorId || {}).id,
      ...this.props, // Allow default property overrides
      deltaRowDataMode: true,
      enableSorting: true,
      unSortIcon: true,
      getRowNodeId: ({ id }) => id,
      context: {
        t: this.props.t
      },
      /* Grid Events */
      onRowClicked: ({ node }) => node.setSelected(!node.isSelected()),
      onGridReady: this.onGridReady,
      onSoftSelectChange: this.onSoftSelectChange,
      onHardSelectChange: this.onHardSelectChange,
      onColumnMoved: this.onColumnMoved,
      onSortChanged: this.onSortChanged
    };

    return [
      <PcsGrid key="sensor-grid-key" {...gridProps} />,
      this.getOpenFlyout(),
      this.getModel()
    ];
  }
}
