import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";
import { FarmEditor } from "./farmEditor";
import { withRouter } from "react-router-dom";
import {
  redux as farmRedux,
  getFarms,
  getFarmById
} from "store/reducers/farmsReducer";
import { getAzureMapsKey } from "store/reducers/appReducer";

const mapStatetoProps = (state, props) => ({
  farms: getFarms(state),
  farm: props.farmId ? getFarmById(state, props.farmId) : undefined,
  azureMapsKey: getAzureMapsKey(state)
});

const mapDispatchToProps = dispatch => ({
  insertFarm: farm => dispatch(farmRedux.actions.insertFarm(farm)),
  modifyFarm: farm => dispatch(farmRedux.actions.modifyFarm(farm)),
  deleteFarm: farm => dispatch(farmRedux.actions.deleteFarm(farm))
});

export const FarmEditorContainer = withRouter(
  withNamespaces()(
    connect(
      mapStatetoProps,
      mapDispatchToProps
    )(FarmEditor)
  )
);
