const AzureMaps = window.atlas;

export function zoomToGeometry(map, geometry) {
  const boundingBox = AzureMaps.data.BoundingBox.fromData(geometry);

  map.setCamera({ bounds: boundingBox, padding: 50 });
}

// Use with caution. Might not work as expected.
export function zoomToPins(map, pins) {
  const lons = [];
  const lats = [];
  pins.forEach(({ geometry: { coordinates: [longitude, latitude] } }) => {
    lons.push(longitude);
    lats.push(latitude);
  });

  const swLon = Math.min.apply(null, lons);
  const swLat = Math.min.apply(null, lats);
  const neLon = Math.max.apply(null, lons);
  const neLat = Math.max.apply(null, lats);

  // Zoom the map to the bounding box of the devices
  map.setCameraBounds({
    bounds: [swLon, swLat, neLon, neLat],
    padding: 50
  });
}
