import React, { Component } from "react";

import { Svg } from "components/shared/svg/svg";
import { ErrorMsg } from "components/shared/forms";
import { svgs } from "utilities";
import {
  Panel,
  PanelHeader,
  PanelHeaderLabel,
  PanelContent,
  Radio
  // Hyperlink
} from "components/shared";

import "./descriptionPanel.scss";

export class DescriptionPanel extends Component {
  constructor(props) {
    super(props);

    this.state = { selectedPanel: this.props.geoSelect };
    this.toggleGeoJSONInput = this.toggleGeoJSONInput.bind(this);
  }

  toggleGeoJSONInput(event) {
    const geoSelectValue = event.target.value;
    const newState = { ...this.state, selectedPanel: geoSelectValue };
    this.setState(newState);
    this.props.toggleGeoJSONInput(geoSelectValue);
  }
  render() {
    const { t, isGeometryEmpty } = this.props;
    const { selectedPanel } = this.state;
    return (
      <Panel className="description-panel-content">
        <PanelHeader className="description-panel-header-container">
          <PanelHeaderLabel>
            <div className="description-panel-header">
              {t("farms.panels.descriptionPanel.header")}
              <div className="tooltip">
                <Svg className="summary-icon tooltip" path={svgs.infoBubble} />
                <span className="tooltip tooltiptext">
                  {t("farms.panels.descriptionPanel.panelBody")}
                </span>
              </div>
              {isGeometryEmpty && (
                <ErrorMsg className="create-farm-error-panel">
                  <div className="farm-error-message">
                    {t("farms.errorMsg.required")}
                  </div>
                </ErrorMsg>
              )}
            </div>
          </PanelHeaderLabel>
        </PanelHeader>
        <PanelContent className="description-panel-content">
          {this.props.disableMarkOnMap ? (
            <div className="markOnMapDisabled">
              {t("farms.panels.descriptionPanel.markOnMapDisabled")}
            </div>
          ) : (
            <Radio
              checked={selectedPanel === "Azure-Maps"}
              value="Azure-Maps"
              onChange={this.toggleGeoJSONInput}
            >
              {t("farms.panels.descriptionPanel.azuremaps")}
            </Radio>
          )}
          <Radio
            checked={selectedPanel === "GeoJSON"}
            value="GeoJSON"
            onChange={this.toggleGeoJSONInput}
          >
            <div className="form-group-label">
              {t("farms.panels.descriptionPanel.geoJSON")}
              <div className="description-panel-header tooltip">
                <Svg className="summary-icon tooltip" path={svgs.infoBubble} />
                <span className="tooltip tooltiptext">
                  <div>
                    {t("farms.panels.descriptionPanel.geoJSONInstructions")}
                  </div>
                </span>
              </div>
            </div>
            {selectedPanel === "GeoJSON" && (
              <p className="geo-JSON-panel-header">
                {" "}
                {t("farms.panels.geoJSON.panelBody")}
              </p>
            )}
          </Radio>
        </PanelContent>
      </Panel>
    );
  }
}
