import React, { Component } from "react";
import { Route, Redirect, Switch } from "react-router-dom";

import { FarmDetailsContainer as FarmDetails } from "components/pages";
import "./farms.scss";
import FarmsSummary from "./farmsSummary";

const closedFlyoutState = { openFlyoutName: undefined };

export class Farms extends Component {
  constructor(props) {
    super(props);
    this.state = {
      openFlyoutName: undefined,
      farmsLength: undefined
    };
    const {
      fetchFarms,
      farmsLastUpdated,
      farmsError,
      devicesLastUpdated,
      devicesError,
      fetchDevices
    } = this.props;

    if (!farmsLastUpdated && !farmsError) {
      fetchFarms();
    }
    if (!devicesLastUpdated && !devicesError) {
      fetchDevices();
    }
  }

  onGridReady = gridReadyEvent => (this.farmGridApi = gridReadyEvent.api);

  searchOnChange = ({ target: { value } }) => {
    if (this.farmGridApi) this.farmGridApi.setQuickFilter(value);
  };

  closeFlyout = () => this.setState(closedFlyoutState);
  openFlyout = name => () => this.setState({ openFlyoutName: name });

  render() {
    const {
      t,
      farms,
      farmsError,
      isFarmsPending,
      farmsLastUpdated,
      fetchFarms,
      devices,
      history
    } = this.props;

    const generalProps = {
      t,
      farms,
      farmsError,
      isFarmsPending,
      farmsLastUpdated,
      fetchFarms,
      devices,
      history,
      openFlyoutName: this.state.openFlyoutName,
      onCloseFlyout: this.closeFlyout,
      openFlyout: this.openFlyout,
      onGridReady: this.onGridReady,
      rowData: isFarmsPending ? undefined : farms || [],
      onContextMenuChange: this.onContextMenuChange,
      searchOnChange: this.searchOnChange,
      refresh: fetchFarms
    };
    return (
      <Switch>
        <Route
          exact
          path={"/farms"}
          render={() => <FarmsSummary {...generalProps} />}
        />
        <Route
          exact
          path={"/farms/:name"}
          render={() => <FarmDetails {...generalProps} />}
        />
        <Redirect to="/farms" />
      </Switch>
    );
  }
}
