import React, { Component } from "react";

import "./emptyFarm.scss";
import { Btn, Protected } from "components/shared";
import { permissions } from "services/models";
export class EmptyFarm extends Component {
  render() {
    const imageUrl = "./image.png";
    const { t, openFlyout } = this.props;
    return (
      <div>
        <center>
          <img
            className=" image-empty"
            src={imageUrl}
            alt={t("farms.emptyFarm")}
          />
        </center>
        <div className="center-align">{t("farms.emptyFarm")}</div>
        <center>
          <Protected permissions={permissions.createFarms}>
            <Btn
              className=" button-empty"
              primary={true}
              type="submit"
              onClick={openFlyout}
              style={{ backgroundColor: "lightblue" }}
            >
              {t("farms.createFarm")}
            </Btn>
          </Protected>
        </center>
      </div>
    );
  }
}
