import { camelCaseReshape, getItems, reshape } from "utilities";
import update from "immutability-helper";

export const ruleCalculations = ["Average", "Instant"];

// Represented in milliSeconds (label is minutes, value is milliseconds)
export const ruleTimePeriods = [
  { label: "1", value: "60000" },
  { label: "5", value: "300000" },
  { label: "10", value: "600000" }
];

export const getRuleTimePeriodLabel = value => {
  const timePeriod = ruleTimePeriods.find(item => item.value === value);
  return (timePeriod || {}).label || value;
};

export const ruleOperators = [
  "Equals",
  "GreaterThan",
  "LessThan",
  "GreaterThanOrEqual",
  "LessThanOrEqual"
];

export const ruleAPIOperators = ["EQ", "GT", "LT", "GE", "LE"];

export const toRulesModel = (response = {}) =>
  getItems(response).map(toRuleModel);

export const toRuleModel = (response = {}) => {
  const model = camelCaseReshape(response, {
    id: "id",
    createdAt: "createdAt",
    lastModifiedAt: "lastModifiedAt",
    farmId: "farmId",
    conditions: "conditions",
    severityLevel: "severityLevel",
    status: "status",
    name: "name",
    severity: "severity",
    description: "description",
    properties: "properties",
    "action.type": "type",
    deleted: "deleted",
    actions: "actions"
  });
  let updatedConditions;
  if (model.conditions !== undefined) {
    updatedConditions = model.conditions.map(condition => ({
      field: condition.field,
      operator: ruleOperators[ruleAPIOperators.indexOf(condition.operator)],
      value: condition.value
    }));
  }
  return update(model, {
    severityLevel: { $set: (model.severityLevel || "").toLowerCase() },
    status: {
      $set: model.deleted ? "Deleted" : model.status
    },
    conditions: { $set: updatedConditions }
  });
};
