export const toEditRuleRequestModel = ({
  name,
  farmId,
  description,
  conditions,
  Calculations,
  actions,
  severityLevel,
  status
}) => {
  let updatedConditions;
  if (conditions !== undefined) {
    updatedConditions = conditions.map(condition => ({
      field: condition.field,
      operator: ruleAPIOperators[ruleOperators.indexOf(condition.operator)],
      value: condition.value
    }));
  }
  const requiredDescriptionLength = 3;
  let Actions;
  if (actions !== undefined) {
    Actions = (actions || []).map(act => ({
      type: act.type,
      parameters: {
        notes: act.parameters.notes,
        recipients: act.parameters.recipients,
        subject: act.parameters.subject
      }
    }));
  }

  const isDescriptionRequired =
    description && description.length >= requiredDescriptionLength;

  return {
    name: name,
    farmId: farmId,
    description: isDescriptionRequired ? description : undefined,
    severityLevel: severityLevel,
    status: status,
    Conditions: updatedConditions,
    Calculations: Calculations,
    actions: Actions
  };
};

