import Config from "app.config";
import { HttpClient } from "utilities/httpClient";
import {
  toMessagesModel,
  toRuleModel,
  toRulesModel,
  toStatusModel,
  toTelemetryRequestModel,
  toTelemetryQueryModel,
  toTelemetryModel
} from "./models";
import { Observable } from "rxjs";
import moment from "moment";

const ENDPOINT = Config.serviceUrls.telemetry;
const RULES_ENDPOINT = Config.serviceUrls.rules;
/** Contains methods for calling the telemetry service */
export class TelemetryService {
  /** Returns the status properties for the telemetry service */
  static getStatus() {
    return HttpClient.get(`${ENDPOINT}status`).map(toStatusModel);
  }

  /** Returns a list of rules */
  static getRules() {
    return HttpClient.get(
      `${RULES_ENDPOINT}?includes=Actions&includes=Conditions&includes=Properties&maxItems=${Config.apiGetAllMaxItems}`
    ).map(toRulesModel);
  }

  /** creates a new rule */
  static createRule(rule) {
    return HttpClient.post(`${RULES_ENDPOINT}`, rule).map(toRuleModel);
  }

  /** updates an existing rule */
  static updateRule(id, rule) {
    return HttpClient.put(`${RULES_ENDPOINT}/${id}`, rule).map(toRuleModel);
  }

  /** Returns a telemetry events */

  /**
   *
   * Url Hard coded for telemetry.
   * SensorId hardcoded for data results and checks
   */
  static getTelemetry(params = {}) {
    const queryFilter = toTelemetryQueryModel(params);
    return HttpClient.post(`${ENDPOINT}`, queryFilter).map(toTelemetryModel);
  }

  static getTelemetryBySensorIdP1D(sensorId) {
    return TelemetryService.getTelemetry({
      searchSpan: {
        from: moment()
          .subtract(24, "hours")
          .toISOString(),
        to: moment().toISOString()
      },
      sensorId: sensorId
    });
  }

  static getTelemetryBySensorIdForDateRange(sensorId, start, end) {
    // TODO: Add Valid Date Checks
    return TelemetryService.getTelemetry({
      sensorId: sensorId,
      searchSpan: {
        from: start,
        to: end
      }
    });
  }

  static getTelemetryByMessages(params = {}) {
    if (params.devices.length === 0) return Observable.never();
    const body = toTelemetryRequestModel(params);
    return HttpClient.post(`${ENDPOINT}messages`, body).map(toMessagesModel);
  }

  static getTelemetryByDeviceIdP2M(devices = []) {
    return TelemetryService.getTelemetryByMessages({
      from: "NOW-PT2M",
      to: "NOW",
      order: "desc",
      devices,
      limit: "10000"
    });
  }

  static getTelemetryByDeviceIdP15M(devices = []) {
    return TelemetryService.getTelemetryByMessages({
      from: "NOW-P1D", // Last One Day
      to: "NOW",
      order: "desc",
      devices,
      limit: "10000"
    });
  }

  static getTelemetryByDeviceIdForDateRange(devices = [], startDate, endDate) {
    // TODO: Add Valid Date Checks

    return TelemetryService.getTelemetryByMessages({
      from: startDate,
      to: endDate,
      order: "desc",
      devices,
      limit: "10000"
    });
  }

  static deleteRule(id) {
    return HttpClient.delete(`${RULES_ENDPOINT}/${id}`).map(() => ({
      deletedRuleId: id
    }));
  }
}
