import React, { Component } from "react";

import { svgs } from "utilities";
import Shell from "components/shell/shell";
import { SettingsContainer, ProfileContainer } from "components/shell/flyouts";
import {
  DevicesContainer,
  RulesContainer,
  SensorContainer,
  FarmsContainer,
  FarmDetailsContainer,
  MapContainer,
  JobsContainer,
  AlertsContainer
} from "./pages";
import { initialize as initAppInsights } from "applicationInsights";

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      openFlyout: ""
    };
    // Init is here and not in index.js because we need history object
    initAppInsights(
      global.DeploymentConfig.appInsightsInstrumentationKey,
      props.history
    );
  }

  closeFlyout = () => this.setState({ openFlyout: "" });

  openSystemSettings = () => this.setState({ openFlyout: "settings" });

  openUserProfile = () => this.setState({ openFlyout: "profile" });

  render() {
    const { openFlyout } = this.state;
    const tabsConfig = [
      {
        to: "/farms",
        exact: true,
        svg: svgs.tabs.farms,
        labelId: "tabs.farms",
        component: FarmsContainer
      },
      {
        to: "/devices",
        exact: true,
        svg: svgs.tabs.devices,
        labelId: "tabs.devices",
        component: DevicesContainer
      },
      {
        to: "/sensors",
        exact: true,
        svg: svgs.tabs.sensors,
        labelId: "tabs.sensors",
        component: SensorContainer
      },
      {
        to: "/maps",
        exact: true,
        svg: svgs.tabs.maps,
        labelId: "tabs.maps",
        component: MapContainer
      },
      {
        to: "/rules",
        exact: true,
        svg: svgs.tabs.rules,
        labelId: "tabs.rules",
        component: RulesContainer
      },
      {
        to: "/alerts",
        exact: true,
        svg: svgs.tabs.alerts,
        labelId: "tabs.alerts",
        component: AlertsContainer
      },
      {
        to: "/jobs",
        exact: false,
        svg: svgs.tabs.jobs,
        labelId: "tabs.jobs",
        component: JobsContainer
      }
    ];
    const pagesConfig = [
      {
        to: "/farms",
        exact: true,
        component: FarmsContainer
      },
      {
        to: "/sensors",
        exact: true,
        component: SensorContainer
      },
      {
        to: "/devices",
        exact: true,
        component: DevicesContainer
      },
      {
        to: "/maps",
        exact: true,
        component: MapContainer
      },
      {
        to: "/farms/:name",
        exact: true,
        component: FarmDetailsContainer
      },
      {
        to: "/jobs",
        exact: true,
        component: JobsContainer
      },
      {
        to: "/rules",
        exact: true,
        component: RulesContainer
      },
      {
        to: "/alerts",
        exact: true,
        component: AlertsContainer
      }
    ];

    const crumbsConfig = [
      {
        path: "/devices",
        crumbs: [{ to: "/devices", labelId: "tabs.devices" }]
      },
      {
        path: "/sensors",
        crumbs: [{ to: "/sensors", labelId: "tabs.sensors" }]
      },
      {
        path: "/maps",
        crumbs: [{ to: "/maps", labelId: "tabs.maps" }]
      },

      {
        path: "/farms",
        crumbs: [{ to: "/farms", labelId: "tabs.farms" }]
      },
      {
        path: "/farms/:name",
        crumbs: [
          { to: "/farms", labelId: "tabs.farms" },
          {
            to: "/farms/:name",
            matchParam: "name",
            state: this.props.location.state
          }
        ]
      },
      {
        path: "/rules",
        crumbs: [{ to: "/rules", labelId: "tabs.rules" }]
      },
      {
        path: "/alerts",
        crumbs: [{ to: "/alerts", labelId: "tabs.alerts" }]
      },
      {
        path: "/jobs",
        crumbs: [{ to: "/jobs", labelId: "tabs.jobs" }]
      }
    ];

    const shellProps = {
      tabsConfig,
      pagesConfig,
      crumbsConfig,
      openSystemSettings: this.openSystemSettings,
      openUserProfile: this.openUserProfile,
      ...this.props
    };

    return (
      <Shell denyAccess={false} {...shellProps}>
        {openFlyout === "settings" && (
          <SettingsContainer onClose={this.closeFlyout} />
        )}
        {openFlyout === "profile" && (
          <ProfileContainer onClose={this.closeFlyout} />
        )}
      </Shell>
    );
  }
}

export default App;
