import React from "react";

import { joinClasses } from "utilities";

import "./modalFooter.scss";

/* A presentational component containing the footer of the modal */
export const ModalFooter = ({
  children,
  className,
  onClickDisagree,
  onClickAgree
}) => (
  <footer className={joinClasses("modal-footer", className)}>
    <button onClick={onClickDisagree} className="action">
      Disagree
    </button>
    <button onClick={onClickAgree} className="action">
      I Agree
    </button>
  </footer>
);
