import React, { Component } from "react";
import { PcsGrid } from "components/shared";
import { mapsColumnDefs, defaultmapsGridProps } from "./mapsGridConfig";
import {
  isFunc,
  translateColumnDefs
  //  contentTypeLabelsMap
} from "utilities";
import { Modal } from "components/shared/mapsModal/modal/modal";
import moment from "moment";
import { appInsights } from "applicationInsights";
import {
  dateFormat,
  mapModalType,
  // pngImageType,
  // contentTypeInitials,
  // getSceneData,
  getPNGFilefromSceneFiles,
  getSceneFile
} from "utilities";
import { MapService } from "services";

const initialState = {
  softSelectedId: undefined,
  isModalOpen: false,
  rowData: undefined,
  blobUrlError: undefined,
  isMapFilesPending: false,
  noMaps: false,
  mapFileDownloadSelectObject: [],
  sensorPlacementLatLong: null
};

export class MapsGrid extends Component {
  constructor(props) {
    super(props);

    // Set the initial state
    this.state = initialState;

    // Default device grid columns
    this.columnDefs = [
      mapsColumnDefs.name,
      mapsColumnDefs.farm,
      mapsColumnDefs.date,
      mapsColumnDefs.createdOn,
      mapsColumnDefs.type
      // mapsColumnDefs.jobid,
      // mapsColumnDefs.id
    ];

    this.onModalClose = this.onModalClose.bind(this);
  }

  onGridReady = gridReadyEvent => {
    this.mapsGridApi = gridReadyEvent.api;
    if (isFunc(this.props.onGridReady)) {
      this.props.onGridReady(gridReadyEvent);
    }
  };

  onModalClose() {
    appInsights.trackEvent({
      name: "UiEvent:ModalClose: Map Modal " + this.state.eachRowData.type
    });
    this.setState({ isModalOpen: false });
  }

  getFarmByID = farmid => {
    return this.props.farms.filter(farm => farm.id === farmid);
  };

  onSoftSelectChange = (rowId, eachRowData) => {
    const { onSoftSelectChange } = this.props;

    appInsights.trackEvent({
      name: "UiEvent:ModalOpen: Map Modal " + eachRowData.type
    });

    this.setState({
      softSelectedId: eachRowData.id,
      eachRowData: eachRowData,
      isModalOpen: true,
      isMapFilesPending: true
    });

    if (eachRowData.type === "sensor-placement") {
      MapService.getSceneById(eachRowData.id).subscribe(scene =>
        this.setState({
          sensorPlacementLatLong: scene.properties.sensor_placements,
          isMapFilesPending: false
        })
      );
    }

    getSceneFile(eachRowData.id, null, this.sceneDataCallback);

    if (isFunc(onSoftSelectChange)) {
      onSoftSelectChange(rowId, eachRowData);
    }
  };

  sceneDataCallback = (selectedObject, mapData, sceneError) => {
    if (sceneError) {
      this.setState({
        isMapFilesPending: false,
        noPngMaps: true,
        noMaps: true
      });
    } else if (mapData.mapFound) {
      const pngUrl = getPNGFilefromSceneFiles(selectedObject);
      this.setState({
        mapFound: mapData.mapFound,
        isMapFilesPending: mapData.isMapFilesPending,
        blobUrl: pngUrl,
        noPngMaps: mapData.noPngMaps,
        noMaps: mapData.noMaps,
        mapFileDownloadSelectObject: selectedObject.mapFileDownloadSelectObject
      });
    } else {
      this.setState({
        url: mapData.url,
        mapFound: mapData.mapFound
      });
    }
  };

  onHardSelectChange = selectedObjs => {
    const { onContextMenuChange, onHardSelectChange } = this.props;
    // Show the context buttons when there are rows checked.
    if (isFunc(onContextMenuChange)) {
      onContextMenuChange(selectedObjs.length > 0 ? this.contextBtns : null);
    }
    if (isFunc(onHardSelectChange)) {
      onHardSelectChange(selectedObjs);
    }
  };

  getSoftSelectId = ({ id } = "") => id;

  onRowClicked = ({ data }) => {};
  render() {
    const gridProps = {
      /* Grid Properties */
      ...defaultmapsGridProps,
      columnDefs: translateColumnDefs(this.props.t, this.columnDefs),
      sizeColumnsToFit: true,
      getSoftSelectId: this.getSoftSelectId,
      softSelectId: (this.state.softSelectedDevice || {}).id,
      ...this.props, // Allow default property overrides
      deltaRowDataMode: true,
      enableSorting: true,
      unSortIcon: true,
      getRowNodeId: ({ id }) => id,
      context: {
        t: this.props.t
      },

      onGridReady: this.onGridReady,
      onRowClicked: this.onRowClicked,
      onSoftSelectChange: this.onSoftSelectChange,
      onHardSelectChange: this.onHardSelectChange
    };

    const {
      eachRowData,
      isModalOpen,
      blobUrlError,
      blobUrl,
      isMapFilesPending,
      noMaps,
      noPngMaps,
      mapFileDownloadSelectObject,
      sensorPlacementLatLong
    } = this.state;
    const { t } = this.props;
    let farm = undefined;

    if (eachRowData && eachRowData.farmId) {
      farm = this.getFarmByID(eachRowData.farmId)[0];
    }

    return [
      <PcsGrid key="farm-grid-key" {...gridProps} />,
      isModalOpen && (
        <Modal
          key="modal"
          type={mapModalType}
          name={eachRowData.type}
          t={t}
          blobUrlError={blobUrlError}
          blobUrl={blobUrl}
          farm={farm}
          farmName={eachRowData.farmName}
          mapName={eachRowData.mapName}
          modalClose={this.onModalClose}
          mapFileDownloadSelectObject={mapFileDownloadSelectObject}
          sensorPlacementLatLong={sensorPlacementLatLong}
          mapDate={moment(eachRowData.date).format(dateFormat)}
          latestCreatedAt={moment(eachRowData.createdAt).format(dateFormat)}
          isPending={isMapFilesPending}
          noMaps={noMaps}
          noPngMaps={noPngMaps}
        />
      )
    ];
  }
}
