import "rxjs";
import { Observable } from "rxjs";
import moment from "moment";
import { schema, normalize } from "normalizr";
import update from "immutability-helper";
import { createSelector } from "reselect";
import { JobsService } from "services";
import {
  createReducerScenario,
  createEpicScenario,
  errorPendingInitialState,
  pendingReducer,
  errorReducer,
  setPending,
  toActionCreator,
  getPending,
  getError
} from "store/utilities";
import { name, jobsDateFormat, renderUndefined, isDef } from "utilities";
import Config from "app.config";
import { getFarmsMap } from "store/reducers/farmsReducer";

// ========================= Epics - START
const handleError = fromAction => error =>
  Observable.of(
    redux.actions.registerError(fromAction.type, { error, fromAction })
  );

export const epics = createEpicScenario({
  /** Loads the jobs jobItems */
  fetchJobs: {
    type: "JOBS_FETCH",
    epic: fromAction =>
      JobsService.getJobsItems()
        .map(toActionCreator(redux.actions.updateJobs, fromAction))
        .catch(handleError(fromAction))
  },
  fetchJobTypes: {
    type: "JOBS_TYPES_FETCH",
    epic: fromAction =>
      JobsService.getJobTypesItems()
        .map(toActionCreator(redux.actions.updateJobTypes, fromAction))
        .catch(handleError(fromAction))
  },
  abortJob: {
    type: "ABORT_JOB",
    epic: fromAction =>
      JobsService.abortJobs(fromAction.payload)
        .map(toActionCreator(redux.actions.abortJob, fromAction))
        .catch(handleError(fromAction))
  }
});
// ========================= Epics - END

// ========================= Schemas - START
const itemSchema = new schema.Entity("jobs");
const itemListSchema = new schema.Array(itemSchema);

const jobTypesSchema = new schema.Entity("jobTypes");
const jobTypesListSchema = new schema.Array(jobTypesSchema);
// ========================= Schemas - END

// ========================= Reducers - START
const initialState = {
  ...errorPendingInitialState,
  jobEntities: {},
  jobTypesEntities: {},
  jobItems: [],
  jobTypesItems: [],
  jobLastUpdated: "",
  jobtypeLastUpdated: ""
};

const insertJobReducer = (state, { payload }) => {
  const {
    entities: { jobs },
    result
  } = normalize(payload, itemListSchema);

  if (state.jobEntities) {
    return update(state, {
      jobEntities: { $merge: jobs },
      jobItems: { $splice: [[0, 0, ...result]] }
    });
  }
  return update(state, {
    jobEntities: { $set: jobs },
    jobItems: { $set: [result] }
  });
};

const updateJobsReducer = (state, { payload, fromAction, store }) => {
  const {
    entities: { jobs },
    result
  } = normalize(payload, itemListSchema);

  return update(state, {
    jobEntities: { $set: jobs },
    jobItems: { $set: result },
    jobLastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

const abortJobReducer = (state, { payload, fromAction }) => {
  const {
    entities: { jobs = {} }
  } = normalize(payload, itemListSchema);
  return update(state, {
    jobEntities: { $merge: jobs }
  });
};

const updateJobTypesReducer = (state, { payload, fromAction, store }) => {
  const {
    entities: { jobTypes },
    result
  } = normalize(payload, jobTypesListSchema);

  return update(state, {
    jobTypesEntities: { $set: jobTypes },
    jobTypesItems: { $set: result },
    jobTypeLastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

/* Action types that cause a pending flag */
const fetchableTypes = [
  epics.actionTypes.fetchJobs,
  epics.actionTypes.fetchJobTypes
];

export const redux = createReducerScenario({
  updateJobs: { type: "JOBS_UPDATE", reducer: updateJobsReducer },
  insertJob: { type: "JOBS_INSERT", reducer: insertJobReducer },
  abortJob: { type: "ABORT_JOB", reducer: abortJobReducer },
  updateJobTypes: { type: "JOB_TYPES_UPDATE", reducer: updateJobTypesReducer },
  registerError: { type: "JOBS_REDUCER_ERROR", reducer: errorReducer },
  isFetching: { multiType: fetchableTypes, reducer: pendingReducer }
});

export const reducer = { jobs: redux.getReducer(initialState) };
// ========================= Reducers - END

// ========================= Selectors - START
export const getJobsReducer = state => state.jobs;
export const getJobEntities = state => getJobsReducer(state).jobEntities || {};
export const getJobItems = state => getJobsReducer(state).jobItems || [];
export const getJobsLastUpdated = state => getJobsReducer(state).jobLastUpdated;
export const getJobsError = state =>
  getError(getJobsReducer(state), epics.actionTypes.fetchJobs);
export const getJobsPendingStatus = state =>
  getPending(getJobsReducer(state), epics.actionTypes.fetchJobs);

export const getJobTypesEntities = state =>
  getJobsReducer(state).jobTypesEntities || {};
export const getJobTypesItems = state =>
  getJobsReducer(state).jobTypesItems || [];
export const getJobTypesLastUpdated = state =>
  getJobsReducer(state).jobTypeLastUpdated;
export const getJobTypesError = state =>
  getError(getJobsReducer(state), epics.actionTypes.fetchJobTypes);
export const getJobTypesPendingStatus = state =>
  getPending(getJobsReducer(state), epics.actionTypes.fetchJobTypes);

export const getJobTypesMap = createSelector(
  getJobTypesEntities,
  getJobTypesItems,
  (jobTypeEntities, jobTypeItems) =>
    jobTypeItems.reduce((JobTypeMap, id) => {
      return JobTypeMap.set(id, jobTypeEntities[id][name]);
    }, new Map())
);
export const getJobs = createSelector(
  getJobEntities,
  getJobItems,
  getFarmsMap,
  getJobTypesMap,
  (jobEntities, jobItems, farmMap, jobTypesMap) => {
    const days = "days";
    return jobItems.map(id => {
      const FarmIds = jobEntities[id].arguments.farm_ids
        ? jobEntities[id].arguments.farm_ids
        : jobEntities[id].arguments.farm_id
          ? jobEntities[id].arguments.farm_id
          : undefined;
      const FarmNamesArray = [];
      let FarmsNames = undefined;
      if (FarmIds && FarmIds.indexOf(",") !== -1) {
        FarmIds.split(", ").forEach(id => {
          FarmNamesArray.push(farmMap.get(id));
        });
        FarmsNames =
          FarmNamesArray.length > 0 ? FarmNamesArray.join(", ") : undefined;
      } else {
        FarmsNames = farmMap.get(FarmIds);
      }

      return {
        ...jobEntities[id],
        farmName: FarmsNames,
        jobTypeName: jobTypesMap.get(jobEntities[id].typeId),
        startDate: isDef(jobEntities[id].arguments.from)
          ? jobEntities[id].arguments.from
          : isDef(jobEntities[id].arguments.start_date) ? jobEntities[id].arguments.start_date : isDef(jobEntities[id].arguments.date)
            ? moment(jobEntities[id].arguments.date, jobsDateFormat)
              .subtract(days, jobEntities[id].arguments.history)
              .format(jobsDateFormat)
            : Config.emptyValue,
        endDate: isDef(jobEntities[id].arguments.to)
          ? jobEntities[id].arguments.to
          : isDef(jobEntities[id].arguments.start_date) ? jobEntities[id].arguments.start_date : renderUndefined(jobEntities[id].arguments.date)
      };
    });
  }
);

export const getJobTypes = createSelector(
  getJobTypesEntities,
  getJobTypesItems,
  (jobTypeEntities, jobTypeItems) => {
    return jobTypeItems.map(id => jobTypeEntities[id]);
  }
);

// ========================= Selectors - END
