import { getFormattedTelemtery } from "utilities";
import { Observable } from "rxjs";

/**
 *  A helper function containing the logic to convert a getTelemetry response
 *  object into the chart object structure.
 *
 * @param getCurrentTelemetry A function that returns an object of formatted telemetry messages
 * @param response An object of telemetry messages from the getTelemetry response
 */
export const transformTelemetryResponse = getCurrentTelemetry => items => {
  return Observable.from(items)
    .flatMap(({ properties, timestamps }) => {
      const id =
        properties.length !== 0
          ? properties.find(property => property.name === "Id").values[0]
          : undefined;

      const dataset =
        properties.length !== 0
          ? properties.filter(
              property =>
                property.name !== "Id" && property.name !== "timestamp"
              // && property.name !== "Device"
              // && property.name !== "Farm"
              // && property.name !== "Measure"
              // && property.name !== "Timestamp"
              // && property.name !== "Value"
            )
          : undefined;

      const telemetry = dataset
        ? dataset.reduce((acc, measure) => {
            const metric = measure.name;
            const measureMap = measure.values.map((value, index) => ({
              metric,
              deviceId: id,
              data: value,
              time: timestamps[index]
            }));
            return acc.concat(measureMap);
          }, [])
        : undefined;
      return telemetry ? telemetry : [];
    })
    .reduce((acc, { metric, deviceId, time, data }) => {
      return getFormattedTelemtery(acc, { metric, deviceId, time, data });
    }, getCurrentTelemetry());
};
