import React, { Component } from "react";
import { svgs } from "utilities";
import { permissions /* toDiagnosticsModel*/ } from "services/models";
import {
  Btn,
  ComponentArray,
  Protected,
  ProtectedError
} from "components/shared";
import { RuleEditorContainer } from "./ruleEditor";
import { RuleViewerContainer } from "./ruleViewer";
import Flyout from "components/shared/flyout";

import "./ruleDetailsFlyout.scss";
import { withAITracking } from "@microsoft/applicationinsights-react-js";
import { reactPlugin, appInsights } from "applicationInsights";

class RuleDetailsFlyout extends Component {
  constructor(props) {
    super(props);

    // Set the initial state
    this.state = {
      isEditable: false
    };
  }

  componentDidMount() {
    appInsights.trackEvent({ name: "UiEvent:FlyOpen: Rule Details" });
  }

  componentWillUnmount() {
    appInsights.trackEvent({ name: "UiEvent:FlyClose: Rule Details" });
  }

  componentDidUpdate(prevProps) {
    if (prevProps.ruleId !== this.props.ruleId) {
      appInsights.trackEvent({ name: "UiEvent:FlyChange: Rule Details" });
      this.setState({ isEditable: false });
    }
  }

  goToEditMode = () => {
    this.setState({ isEditable: true });
  };

  render() {
    const { t, onClose, ruleId } = this.props;
    const { isEditable } = this.state;

    return (
      <div className={this.props.className ? this.props.className : ""}>
        <Flyout.Container>
          <Flyout.Header>
            <Flyout.Title>
              {isEditable
                ? t("rules.flyouts.editRule")
                : t("rules.flyouts.viewRule")}
            </Flyout.Title>
            <Flyout.CloseBtn onClick={() => this.props.onClose()} />
          </Flyout.Header>
          <Flyout.Content className="rule-details">
            {!isEditable ? (
              <ComponentArray>
                <RuleViewerContainer onClose={onClose} ruleId={ruleId} />

                <Protected permissions={permissions.updateRules}>
                  <Btn
                    className="edit-mode-btn"
                    svg={svgs.edit}
                    onClick={this.goToEditMode}
                  >
                    {t("rules.flyouts.edit")}
                  </Btn>
                </Protected>
              </ComponentArray>
            ) : (
              <Protected
                id="rule-details-edit"
                permission={permissions.updateRules}
              >
                {(hasPermission, permission) =>
                  hasPermission ? (
                    <RuleEditorContainer onClose={onClose} ruleId={ruleId} />
                  ) : (
                    <ProtectedError t={t} permissions={permission} />
                  )
                }
              </Protected>
            )}
          </Flyout.Content>
        </Flyout.Container>
      </div>
    );
  }
}

export default withAITracking(
  reactPlugin,
  RuleDetailsFlyout,
  "Rule Details Flyout"
);
