import React, { Component } from "react";
import { permissions /* toDiagnosticsModel*/ } from "services/models";
import { Btn, PcsGrid, Protected } from "components/shared";
import { DeleteModal } from "components/shared/deleteModal/deleteModal";
import { deviceColumnDefs, defaultDeviceGridProps } from "./devicesGridConfig";
import { DeviceDetailsContainer } from "../flyouts/deviceDetails";
import { isFunc, svgs, translateColumnDefs } from "utilities";
import { checkboxColumn } from "components/shared/pcsGrid/pcsGridConfig";
import { AssociateDevicesContainer } from "../flyouts/associateDevices";
import { appInsights } from "applicationInsights";
const closedFlyoutState = {
  openFlyoutName: undefined,
  softSelectedDeviceId: undefined
};

const initialState = {
  softSelectedId: undefined,
  closedFlyoutState
  // openPopup: undefined
};

/**
 * A grid for displaying devices
 *
 * Encapsulates the PcsGrid props
 */
export class DevicesGrid extends Component {
  constructor(props) {
    super(props);
    // Set the initial state
    // this.state = initialState;
    // this.state = { selectedDeviceList: undefined, softSelectedId: undefined, closedFlyoutState };
    this.state = initialState;
    this.state = { selectedDeviceList: [] };
    // Default device grid columns
    this.columnDefs = [
      { ...checkboxColumn, headerCheckboxSelection: true },
      deviceColumnDefs.name,
      deviceColumnDefs.deviceModelType,
      deviceColumnDefs.status,
      // deviceColumnDefs.criticalAlert,
      // deviceColumnDefs.warnings,
      deviceColumnDefs.farmId,
      deviceColumnDefs.parentDevice,
      deviceColumnDefs.lastUpdated
    ];

    this.contextBtns = {
      /* to do later
         <Protected permissions={permissions.createJobs}>
          <Btn svg={svgs.reconfigure} onClick={this.openFlyout("jobs")}>
            {props.t("devices.flyouts.jobs.title")}
          </Btn>
        </Protected> */
      associateDevices: (
        <Protected
          key="associateDevices"
          permissions={permissions.updateDevices}
        >
          <Btn
            svg={svgs.links}
            onClick={event => {
              this.openAssociateDevicesFlyout(event);
              appInsights.trackEvent({
                name: "UiEvent:Click: Open Associate Devices Flyout Btn"
              });
            }}
          >
            {props.t("devices.flyouts.associateDevices.title")}
          </Btn>
        </Protected>
      ),
      delete: (
        <Protected key="delete" permissions={permissions.deleteDevices}>
          <Btn svg={svgs.trash} onClick={this.openModel("delete")}>
            {props.t("devices.flyouts.delete.title")}
          </Btn>
        </Protected>
      )
    };
  }

  /**
   * Get the grid api options
   *
   * @param {Object} gridReadyEvent An object containing access to the grid APIs
   */
  onGridReady = gridReadyEvent => {
    this.deviceGridApi = gridReadyEvent.api;
    // Call the onReady props if it exists
    if (isFunc(this.props.onGridReady)) {
      this.props.onGridReady(gridReadyEvent);
    }
  };

  openAssociateDevicesFlyout = () => {
    this.setState({ openFlyoutName: "associateDevices" });
  };

  openModel = ModelName => () =>
    this.setState({
      openModelName: ModelName
    });

  closeModel = () =>
    this.setState({
      openModelName: undefined
    });

  deleteDevices = () => {
    this.props.deleteDevicesById(this.state.selectedDeviceList);
  };

  openFlyout = flyoutName => () =>
    this.setState({
      openFlyoutName: flyoutName,
      softSelectedDeviceId: undefined
    });

  openDelePopup = () => {
    this.setState({ openPopup: "deleteDevicePopup" });
  };

  getOpenFlyout = () => {
    const {
      selectedDeviceList,
      softSelectedDeviceId,
      openFlyoutName
    } = this.state;

    switch (openFlyoutName) {
      case "details":
        return (
          <DeviceDetailsContainer
            key="details-device-key"
            onClose={this.closeFlyout}
            deviceId={softSelectedDeviceId}
          />
        );
      case "associateDevices":
        return (
          <AssociateDevicesContainer
            key="associate-device-key"
            onClose={this.closeFlyout}
            rowData={selectedDeviceList}
          />
        );
      default:
        return null;
    }
  };

  getDeleteModel = () => {
    switch (this.state.openModelName) {
      case "delete":
        return (
          <DeleteModal
            type="deleteDevice"
            icon={svgs.trash}
            deleteConfirmation={this.props.t(
              "devices.deleteModal.deleteConfirmation",
              { returnObjects: true }
            )}
            title={this.props.t("devices.deleteModal.title")}
            deleteItem={this.deleteDevices}
            onClose={this.closeModel}
            deleteBtn={this.props.t("modal.delete")}
            t={this.props.t}
            isPending={this.props.deleteDevicePending}
            deleteModalError={this.props.deleteModalError}
            deleteModalSuccess={this.props.deleteModalSuccess}
            successMsg={this.props.t("devices.deleteSuccess")}
          />
        );
      default:
        return null;
    }
  };

  closeFlyout = () => this.setState(closedFlyoutState);
  /**
   * Handles soft select props method.
   * Soft selection happens when the user clicks on the row.
   *
   * @param deviceId The id of the currently soft selected item
   * @param rowData The rowData from the underlying grid. MAY BE OUT OF DATE.
   */
  onSoftSelectChange = (deviceId, rowData) => {
    const { onSoftSelectChange } = this.props;
    if (deviceId) {
      this.setState({
        openFlyoutName: "details",
        softSelectedDeviceId: deviceId
      });
    } else {
      this.closeFlyout();
    }
    if (isFunc(onSoftSelectChange)) {
      onSoftSelectChange(deviceId, rowData);
    }
  };

  /**
   * Handles context filter changes and calls any hard select props method
   *
   * @param {Array} selectedDevices A list of currently selected devices
   */

  setSelectedDevices = selectedDevices =>
    this.setState({ selectedDeviceList: selectedDevices });

  onHardSelectChange = selectedDevices => {
    const { onContextMenuChange, onHardSelectChange } = this.props;
    if (isFunc(onContextMenuChange)) {
      if (selectedDevices.length > 1) {
        this.setSelectedDevices(selectedDevices);
        onContextMenuChange(this.contextBtns.associateDevices);
      } else if (selectedDevices.length === 1) {
        this.setSelectedDevices(selectedDevices);
        onContextMenuChange([
          this.contextBtns.associateDevices,
          this.contextBtns.delete
        ]);
      } else {
        this.setSelectedDevices(selectedDevices);
        onContextMenuChange(null);
      }
    }
    if (isFunc(onHardSelectChange)) {
      onHardSelectChange(selectedDevices);
    }
  };

  componentDidMount() {
    if (this.props.devices) {
      this.populateDevicesState(this.props.devices);
    }
  }

  getSoftSelectId = ({ id } = "") => id;

  render() {
    const gridProps = {
      /* Grid Properties */
      ...defaultDeviceGridProps,
      columnDefs: translateColumnDefs(this.props.t, this.columnDefs),
      sizeColumnsToFit: true,
      getSoftSelectId: this.getSoftSelectId,
      softSelectId: this.state.softSelectedDeviceId || {},
      ...this.props, // Allow default property overrides
      deltaRowDataMode: true,
      enableSorting: true,
      unSortIcon: true,
      getRowNodeId: ({ id }) => id,
      context: {
        t: this.props.t
      },
      onRowClicked: ({ node }) => node.setSelected(!node.isSelected()),
      onGridReady: this.onGridReady,
      onSoftSelectChange: this.onSoftSelectChange,
      onHardSelectChange: this.onHardSelectChange
    };
    return [
      <PcsGrid key="device-grid-key" {...gridProps} />,
      this.getOpenFlyout(),
      this.getDeleteModel()
    ];
  }
}
