import React, { Component } from "react";
import "util";
import {
  AjaxError,
  ComponentArray,
  ContextMenu,
  ContextMenuAlign,
  PageContent,
  PageTitle,
  // RefreshBarContainer as RefreshBar,
  SearchInput
} from "components/shared";
import { AlertsGrid } from "./alertsGrid";
import "./alerts.scss";
import { reactPlugin } from "applicationInsights";
import { withAITracking } from "@microsoft/applicationinsights-react-js";

class Alerts extends Component {
  constructor(props) {
    super(props);
    this.state = {
      contextBtns: null
    };

    const {
      rulesLastUpdated,
      rulesError,
      fetchRules,
      alertsLastUpdated,
      alertsError,
      fetchAlerts
    } = this.props;

    if (!rulesLastUpdated && !rulesError) fetchRules();
    if (!alertsLastUpdated && !alertsError) fetchAlerts();
  }

  onContextMenuChange = contextBtns => this.setState({ contextBtns });

  onGridReady = gridReadyEvent => (this.alertsGridApi = gridReadyEvent.api);

  searchOnChange = ({ target: { value } }) => {
    if (this.alertsGridApi) this.alertsGridApi.setQuickFilter(value);
  };

  onRefresh() {
    const { fetchAlerts, fetchRules } = this.props;
    fetchAlerts();
    fetchRules();
  }

  render() {
    const {
      t,
      alertsError,
      isAlertsPending,
      // alertsLastUpdated,
      // fetchAlerts,
      alertsByRule
    } = this.props;

    const gridProps = {
      onGridReady: this.onGridReady,
      rowData: isAlertsPending ? undefined : alertsByRule || [],
      onContextMenuChange: this.onContextMenuChange,
      t: t,
      refresh: this.onRefresh
    };

    return (
      <ComponentArray>
        <ContextMenu>
          <ContextMenuAlign left={true}>
            <SearchInput
              onChange={this.searchOnChange}
              placeholder={t("alerts.searchPlaceholder")}
            />
          </ContextMenuAlign>
        </ContextMenu>

        <PageContent className="alerts-container" key="page-content">
          {/* <RefreshBar
            refresh={fetchAlerts}
            time={alertsLastUpdated}
            isPending={isAlertsPending}
            t={t}
          /> */}
          <PageTitle
            titleValue={t("alerts.title").concat(
              "(",
              String(alertsByRule.length),
              ")"
            )}
          />
          {!!alertsError && <AjaxError t={t} error={alertsError} />}
          {!alertsError && <AlertsGrid {...gridProps} />}
        </PageContent>
      </ComponentArray>
    );
  }
}

export default withAITracking(reactPlugin, Alerts, "Alerts Grid");
