import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";

import {
  epics as jobsEpics,
  redux as jobsRedux,
  getJobs,
  getJobsError,
  getJobsLastUpdated,
  getJobsPendingStatus,
  getJobTypes,
  getJobTypesError,
  getJobTypesPendingStatus,
  getJobTypesLastUpdated
} from "store/reducers/jobsReducer";

import {
  epics as farmEpics,
  getFarms,
  getFarmsMap,
  getFarmsError,
  getFarmsPendingStatus,
  getFarmsLastUpdated
} from "store/reducers/farmsReducer";
import Jobs from "./jobs";

// Pass the data
const mapStateToProps = state => ({
  farms: getFarms(state),
  farmsError: getFarmsError(state),
  isFarmsPending: getFarmsPendingStatus(state),
  farmsLastUpdated: getFarmsLastUpdated(state),
  jobs: getJobs(state),
  jobTypes: getJobTypes(state),
  jobTypesError: getJobTypesError(state),
  isJobTypesPending: getJobTypesPendingStatus(state),
  jobTypeslastUpdated: getJobTypesLastUpdated(state),
  jobsError: getJobsError(state),
  isJobsPending: getJobsPendingStatus(state),
  jobsLastUpdated: getJobsLastUpdated(state),
  farmsMap: getFarmsMap(state)
});

// Wrap the dispatch method
const mapDispatchToProps = dispatch => ({
  fetchJobs: () => dispatch(jobsEpics.actions.fetchJobs()),
  abortJob: job => dispatch(jobsRedux.actions.abortJob(job)),
  fetchJobTypes: () => dispatch(jobsEpics.actions.fetchJobTypes()),
  fetchFarms: () => dispatch(farmEpics.actions.fetchFarms())
});

export const JobsContainer = withNamespaces()(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Jobs)
);
