import React, { Component } from "react";
import {
  svgs,
  isNull,
  isEmptyCheck,
  isJSON,
  getCentroid,
  geoJSONValidation,
  KeyCodes,
  handleKeyPressInput
} from "utilities";
import {
  AjaxError,
  Indicator,
  Btn,
  Svg,
  Panel,
  PanelHeader,
  PanelContent,
  PanelOverlay,
  PanelError
} from "components/shared";
import { AzureMap } from "./azureMap";
import { PolygonDrawingTool } from "./PolygonDrawingTool";
// import {mapStyles} from "utilities";
import "./mapPanel.scss";
// import { toAzureMapSearchResultModel } from "services/models"; //TODO

const AzureMaps = window.atlas;

let datasource;
let drawingTools;

export class MapPanel extends Component {
  constructor(props) {
    super(props);
    this.state = {
      searchQuery: "",
      showList: false,
      address: "",
      searchUnsuccessful: false,
      centerSet: false,
      currentGeoJSON: props.geometry
    };
    this.searchAddress = this.searchAddress.bind(this);
    this.handleSearchInputChange = this.handleSearchInputChange.bind(this);
    this.onMapReady = this.onMapReady.bind(this);
  }

  onMapReady = map => {
    // Create the map popup
    this.popup = new AzureMaps.Popup();
    const {
      getGeoJSON,
      geometry,
      address,
      azureMapsKey,
      setGeoJSONInvalidStatus
    } = this.props;
    let center = this.state.centerSet;
    const { currentGeoJSON } = this.state;
    map.addEventListener("load", function(e) {
      datasource = new AzureMaps.source.DataSource();

      map.sources.add(datasource);
      // var pinLayer= new AzureMaps.layer.SymbolLayer(datasource);
      // map.layers.add(pinLayer);

      if (
        !isNull(geometry) &&
        !isEmptyCheck(geometry) &&
        isJSON(geometry) &&
        geoJSONValidation.isPolygon(JSON.parse(geometry))
      ) {
        const GeoJSON = JSON.parse(geometry);
        if (!center) {
          const c = getCentroid(GeoJSON.coordinates);
          map.setCamera({
            center: c,
            zoom: 14
          });
          center = true;
        }
        const polygon = new AzureMaps.data.Polygon(GeoJSON.coordinates);
        datasource.add(polygon);
        const polygonLayer = new AzureMaps.layer.PolygonLayer(datasource);
        map.layers.add(polygonLayer, "abcde");
      }

      if (!center && !isNull(address) && !isEmptyCheck(address)) {
        fetch(
          `https://atlas.microsoft.com/search/fuzzy/json?typeahead=true&api-version=1.0&subscription-key=${azureMapsKey}&query=${address}`
        )
          .then(results => {
            return results.json();
          })
          .then(searchResults => {
            if (searchResults.results.length !== 0) {
              const results = searchResults.results;
              const latLon = results[0].position;
              map.setCamera({
                center: [latLon.lon, latLon.lat],
                zoom: 14
              });
              center = true;
            }
          });
      }

      drawingTools = new PolygonDrawingTool(map, /* pinLayer =*/ null, function(
        polygonShape
      ) {
        if (polygonShape) {
          const result = JSON.stringify(polygonShape.data.geometry);
          getGeoJSON(result);
          setGeoJSONInvalidStatus(false);
        } else {
          getGeoJSON(currentGeoJSON);
        }
      });
      document
        .getElementById("drawPolygon")
        .addEventListener("click", function() {
          drawingTools.startDrawing();
        });

      document
        .getElementById("clearPolygon")
        .addEventListener("click", function() {
          drawingTools.clear();
        });
    });
    this.setState({ centerSet: center });
    this.map = map;
  };

  style = styleOption => {
    if (this.map) {
      this.map.setStyle({ style: styleOption });
    }
  };

  /**
 * TODO provide search suggestions to the user
inputQuery(event) {
  this.setState({ ...this.state, searchQuery: event.target.value });
  const target = event.target;
  const name = target.name;
  const value = target.value;
  if (name === "queryBox" && value.length >= 3) {
    this.getSearchResults(value);
  }
  console.log(mapSearch);
}
*/

  searchAddress = address => {
    const searchQuery = isEmptyCheck(this.state.searchQuery)
      ? address
      : this.state.searchQuery;

    fetch(
      `https://atlas.microsoft.com/search/fuzzy/json?typeahead=true&api-version=1.0&subscription-key=${this.props.azureMapsKey}&query=${searchQuery}`
    )
      .then(results => {
        return results.json();
      })
      .then(searchResults => {
        if (searchResults.results) {
          const results = searchResults.results;
          if (results.length === 0) {
            this.setState({ ...this.state, searchUnsuccessful: true });
          } else {
            const latLon = results[0].position;
            this.map.setCamera({
              center: [latLon.lon, latLon.lat],
              zoom: 13
            });
            this.setState({ ...this.state, searchUnsuccessful: false });
          }
        }
      });
  };

  handleSearchInputChange(event) {
    const searchBox = event.target;
    const value = searchBox.value;
    this.setState({ ...this.state, searchQuery: value });
  }

  render() {
    const { t, isPending, mapKeyIsPending, azureMapsKey, error } = this.props;
    const { searchQuery, searchUnsuccessful } = this.state;
    const showOverlay = !error && isPending && mapKeyIsPending;
    return (
      <Panel className="map-panel-container">
        <PanelHeader>
          <div className="address-container">
            <input
              type="textarea"
              name="queryBox"
              className="address"
              value={searchQuery}
              onChange={this.handleSearchInputChange}
              onKeyPress={event =>
                handleKeyPressInput(event, this.searchAddress, KeyCodes.Enter)
              }
            />
            <Btn onClick={this.searchAddress} className="address-button">
              {t("farms.panels.map.submit")}
            </Btn>
          </div>
        </PanelHeader>
        <PanelContent className="map-panel-container">
          <div className="map-panel-container">
            <AzureMap
              azureMapsKey={azureMapsKey}
              onMapReady={this.onMapReady}
              drawingTools={this.drawingTools}
            />
            {searchUnsuccessful && (
              <PanelError className="search-results">
                location not found
              </PanelError>
            )}
            <button
              type="button"
              className="control-btn draw-polygon"
              id="drawPolygon"
            >
              <Svg path={svgs.edit} className="icon-btn" />
            </button>
            <button
              type="button"
              className="control-btn delete-polygon"
              id="clearPolygon"
            >
              <Svg path={svgs.cancelX} className="icon-btn" />
            </button>
          </div>
        </PanelContent>
        {showOverlay && (
          <PanelOverlay>
            <Indicator />
          </PanelOverlay>
        )}
        {!mapKeyIsPending && !this.props.azureMapsKey && (
          <PanelError>{t("farms.panels.map.notSupportedError")}</PanelError>
        )}
        {error && (
          <PanelError>
            <AjaxError t={t} error={error} />
          </PanelError>
        )}
      </Panel>
    );
  }
}
