import "rxjs";
import { Observable } from "rxjs";

import moment from "moment";
import { schema, normalize } from "normalizr";
import update from "immutability-helper";

import { createSelector } from "reselect";
import { sensorService } from "services";

import {
  createReducerScenario,
  createEpicScenario,
  resetPendingAndErrorReducer,
  errorPendingInitialState,
  pendingReducer,
  errorReducer,
  getPending,
  setPending,
  toActionCreator,
  getError
} from "store/utilities";

// ========================= Epics - START
const handleError = fromAction => error =>
  Observable.of(
    redux.actions.registerError(fromAction.type, { error, fromAction })
  );

export const epics = createEpicScenario({
  /** Loads the Sensor */
  fetchSensors: {
    type: "SENSORS_FETCH",
    epic: fromAction =>
      sensorService
        .getSensors()
        .map(toActionCreator(redux.actions.updateSensors, fromAction))
        .catch(handleError(fromAction))
  },

  /** Loads the SensorModels */
  fetchSensorModels: {
    type: "SENSOR_MODELS_FETCH",
    epic: fromAction =>
      sensorService
        .getSensorModels()
        .map(toActionCreator(redux.actions.updateSensorModels, fromAction))
        .catch(handleError(fromAction))
  },

  deleteSensorsById: {
    type: "DELETE_SENSORS",
    epic: fromAction => {
      Observable.from(fromAction.payload)
        .flatMap(({ id }) => sensorService.deleteSensor(id).map(() => id))
        .map(toActionCreator(redux.actions.deleteSensorsbyId, fromAction))
        .catch(handleError(fromAction));
    }
  }
});
const sensorSchema = new schema.Entity("sensors");
const sensorListSchema = new schema.Array(sensorSchema);

const sensorModelSchema = new schema.Entity("sensorModel");
const sensorModelsListSchema = new schema.Array(sensorModelSchema);

// ========================= Schemas - END

// ========================= Reducers - START
const initialState = {
  ...errorPendingInitialState,
  sensorEntities: {},
  sensorItems: [],
  sensorlastUpdated: undefined,
  sensorModelEntities: [],
  sensorModelItems: []
};

const updateSensorReducer = (state, { payload, fromAction }) => {
  const {
    entities: { sensors },
    result
  } = normalize(payload, sensorListSchema);
  return update(state, {
    sensorEntities: { $set: sensors },
    sensorItems: { $set: result },
    sensorlastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

const deleteSensorReducer = (state, { payload, fromAction }) => {
  const spliceArr = payload.reduce((idxAcc, payloadItem) => {
    const idx = state.sensorItems.indexOf(payloadItem);
    if (idx !== -1) {
      idxAcc.push([idx, 1]);
    }
    return idxAcc;
  }, []);
  return update(state, {
    sensorEntities: { $unset: payload },
    sensorItems: { $splice: spliceArr }
  });
};

const updateSensorModelReducer = (state, { payload, fromAction }) => {
  const {
    entities: { sensorModel },
    result
  } = normalize(payload, sensorModelsListSchema);
  return update(state, {
    sensorModelEntities: { $set: sensorModel },
    sensorModelItems: { $set: result },
    sensorModelsLastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

/* Action types that cause a pending flag */
const fetchableTypes = [
  epics.actionTypes.fetchSensors,
  epics.actionTypes.fetchSensorModels
];

export const redux = createReducerScenario({
  updateSensors: { type: "SENSOR_UPDATE", reducer: updateSensorReducer },
  updateSensorModels: {
    type: "SENSOR_MODEL_UPDATE",
    reducer: updateSensorModelReducer
  },
  deleteSensorsbyId: {
    type: "SENSOR_DELETE_BY_ID",
    reducer: deleteSensorReducer
  },
  registerError: { type: "SENSOR_REDUCER_ERROR", reducer: errorReducer },
  isFetching: { multiType: fetchableTypes, reducer: pendingReducer },
  resetPendingAndError: {
    type: "SENSOR_REDUCER_RESET_ERROR_PENDING",
    reducer: resetPendingAndErrorReducer
  }
});

export const reducer = {
  sensors: redux.getReducer(initialState)
};

// ========================= Reducers - END

// ========================= Selectors - START
export const getSensorsReducer = state => state.sensors;

export const getSensorEntities = state =>
  getSensorsReducer(state).sensorEntities || {};
export const getSensorItems = state =>
  getSensorsReducer(state).sensorItems || [];
export const getSensorLastUpdated = state =>
  getSensorsReducer(state).sensorlastUpdated;

export const getSensorError = state =>
  getError(getSensorsReducer(state), epics.actionTypes.fetchSensors);
export const getSensorModelEntities = state =>
  getSensorsReducer(state).sensorModelEntities || {};
export const getSensorModelsError = state =>
  getError(getSensorsReducer(state), epics.actionTypes.fetchSensorModels);
export const getSensorModelItems = state =>
  getSensorsReducer(state).sensorModelItems || [];

export const getSensorPendingStatus = state =>
  getPending(getSensorsReducer(state), epics.actionTypes.fetchSensors);

export const getSensorModelPendingStatus = state =>
  getPending(getSensorsReducer(state), epics.actionTypes.fetchSensorModels);

export const getSensorModelLastUpdated = state =>
  getSensorsReducer(state).sensorModelsLastUpdated;

export const getSensorModels = createSelector(
  getSensorModelEntities,
  getSensorModelItems,
  (sensorModelEntities, sensorModelItems) =>
    sensorModelItems.map(id => sensorModelEntities[id])
);

export const getSensors = createSelector(
  getSensorEntities,
  getSensorItems,
  (sensorEntities, sensorItems) => sensorItems.map(id => sensorEntities[id])
);
