import { stringify } from "query-string";
import {
  toMapsItemModel,
  toMapsItemsModel,
  toSceneFileItemsModel
} from "./models";
import { HttpClient } from "utilities/httpClient";
import Config from "app.config";

const SCENE_ENDPOINT = Config.serviceUrls.scene;
const SCENE_FILE_ENDPOINT = Config.serviceUrls.sceneFile;
const mapTypes = [
  "sensor-placement",
  "evi",
  "ndvi",
  "soil-moisture",
  "ndwi",
  "drone"
];
export class MapService {
  static getMapsItems() {
    const types = stringify({ types: mapTypes });
    return HttpClient.get(
      `${SCENE_ENDPOINT}?maxItems=${Config.apiGetAllMaxItems}&${types}`
    ).map(toMapsItemsModel);
  }

  static getLatestSensorPlacementMapByFarmId(farmId) {
    return HttpClient.get(
      `${SCENE_ENDPOINT}?includes=Properties&maxItems=1&types=sensor-placement&farmId=${farmId}`
    ).map(toMapsItemsModel);
  }

  static getLatestFarmMapFileById(type, farmId) {
    return HttpClient.get(
      `${SCENE_ENDPOINT}?maxItems=${Config.apiGetAllMaxItems}&types=${type}&farmId=${farmId}`
    ).map(toMapsItemsModel);
  }

  static getSceneById(id) {
    return HttpClient.get(`${SCENE_ENDPOINT}/${id}`).map(toMapsItemModel);
  }

  static getMapFileByIdAndType(
    sceneId,
    fileType = undefined,
    sceneFileType = undefined
  ) {
    const mapFileParams = {
      contentType: fileType,
      sceneFileTypes: sceneFileType,
      sceneId: sceneId,
      generateDownloadSASUrl: true
    };

    const query = stringify(mapFileParams);
    return HttpClient.get(
      `${SCENE_FILE_ENDPOINT}?maxItems=${Config.apiGetAllMaxItems}&${query}`
    ).map(toSceneFileItemsModel);
  }

  static updateMapsItems() {
    return this.getMapsItems().delay(2000);
  }
}
