import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";
import {
  epics as appEpics,
  getTheme,
  getCurrentUserEmail
} from "store/reducers/appReducer";
import App from "./app";

const mapStateToProps = state => ({
  userEmail: getCurrentUserEmail(state),
  theme: getTheme(state)
});

// Wrap with the router and wrap the dispatch method
const mapDispatchToProps = dispatch => ({
  fetchUser: () => dispatch(appEpics.actions.fetchUser()),
  registerRouteEvent: pathname =>
    dispatch(appEpics.actions.detectRouteChange(pathname))
  // logout: () => AuthService.logout()
});

export const AppContainer = withRouter(
  withNamespaces()(
    connect(
      mapStateToProps,
      mapDispatchToProps
    )(App)
  )
);
