import React, { Component } from "react";

import { getEnumTranslation, isDef, dateFormat } from "utilities";
import { Link } from "react-router-dom";
import Config from "app.config";
import {
  PropertyGrid as Grid,
  PropertyGridHeader as GridHeader,
  PropertyRow as Row,
  PropertyCell as Cell
} from "components/shared";
import { RuleSummaryAutoContainer as RuleSummaryAuto } from "../shared/ruleSummaryAuto.container";
import Flyout from "components/shared/flyout";
import "./ruleViewer.scss";
import moment from "moment";

const Section = Flyout.Section;

export class RuleViewer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      devicesAffected: null
    };
  }

  componentWillUnmount() {
    if (this.subscription) this.subscription.unsubscribe();
  }
  fetchApiData = () => {
    const {
      fetchSensors,
      fetchSensorModels,
      isSensorsPending,
      sensorLastUpdated,
      sensorModelsLastUpdated,
      sensorModelsIsPending
    } = this.props;
    if (!sensorLastUpdated && !isSensorsPending) fetchSensors();
    if (!sensorModelsLastUpdated && !sensorModelsIsPending) fetchSensorModels();
  };

  componentDidMount() {
    this.fetchApiData();
  }

  render() {
    const { rule, t } = this.props;
    const WITH_COLON_AND_SPACE = ": ";
    const fillData = checkData => {
      return checkData === undefined
        ? WITH_COLON_AND_SPACE + Config.emptyValue
        : WITH_COLON_AND_SPACE + checkData;
    };

    return (
      <div className="view-rule-flyout-container">
        <Section.Container>
          <Section.Content className="ruleviewer-table">
            <table className="rule-description">
              <tbody>
                <tr>
                  <th>{t("rules.flyouts.ruleViewer.Description")}</th>
                  <td>{fillData(rule.description)}</td>
                </tr>
              </tbody>
            </table>
            <Grid>
              <Row className="ruledetails-table">
                <Cell className="col-5">
                  <table>
                    <tbody>
                      <tr>
                        <th>{t("rules.flyouts.ruleViewer.RuleName")}</th>
                        <td>{fillData(rule.name)}</td>
                      </tr>
                      <tr>
                        <th>{t("rules.flyouts.ruleViewer.CreatedAt")}</th>
                        <td>
                          {fillData(moment(rule.createdAt).format(dateFormat))}
                        </td>
                      </tr>
                      <tr>
                        <th>{t("rules.flyouts.ruleViewer.SeverityLevel")}</th>
                        <td>{fillData(rule.severityLevel)}</td>
                      </tr>
                    </tbody>
                  </table>
                </Cell>
                <Cell className="col-5">
                  <table>
                    <tbody>
                      <tr>
                        <th>{t("rules.flyouts.ruleViewer.Farm")}</th>
                        <td>
                          {isDef(rule.farmId) ? (
                            <Link
                              className="farmLink"
                              to={{
                                pathname: `/farms/${rule.farmId}`,
                                state: {
                                  id: rule.farmId,
                                  farmName: this.props.farm.name
                                }
                              }}
                            >
                              {fillData(this.props.farm.name)}
                            </Link>
                          ) : (
                            <div>
                              {fillData(
                                t("rules.flyouts.ruleViewer.farmDoesNotExist")
                              )}
                            </div>
                          )}
                        </td>
                      </tr>
                      <tr>
                        <th>{t("rules.flyouts.ruleViewer.LastModifiedAt")}</th>
                        <td>
                          {fillData(
                            moment(rule.lastModifiedAt).format(dateFormat)
                          )}
                        </td>
                      </tr>
                      <tr>
                        <th>{t("rules.flyouts.ruleViewer.Status")}</th>
                        <td>{fillData(rule.status)}</td>
                      </tr>
                    </tbody>
                  </table>
                </Cell>
              </Row>
            </Grid>
          </Section.Content>
        </Section.Container>
        {rule.conditions.length > 0 && (
          <Section.Container collapsable={false}>
            <Section.Header className="ruleflyout-section-header">
              {t("rules.flyouts.ruleEditor.conditions")}
            </Section.Header>
            <Section.Content>
              <Grid className="row-grid">
                <GridHeader>
                  <Row>
                    <Cell className="col-4">
                      {t("rules.flyouts.ruleEditor.condition.field")}
                    </Cell>
                    <Cell className="col-3">
                      {t("rules.flyouts.ruleEditor.condition.operator")}
                    </Cell>
                    <Cell className="col-3">
                      {t("rules.flyouts.ruleEditor.condition.value")}
                    </Cell>
                  </Row>
                </GridHeader>
                {rule.conditions.map((condition, idx) => (
                  <Row key={idx}>
                    <Cell className="col-4">{condition.field}</Cell>
                    <Cell className="col-3">
                      {getEnumTranslation(
                        t,
                        "rules.flyouts.ruleEditor.condition.operatorOptions",
                        condition.operator
                      )}
                    </Cell>
                    <Cell className="col-3">{condition.value}</Cell>
                  </Row>
                ))}
              </Grid>
            </Section.Content>
          </Section.Container>
        )}
        {rule.actions && rule.actions.length > 0 && (
          <Section.Container collapsable={false}>
            <Section.Header className="ruleflyout-section-header">
              {t("rules.flyouts.ruleEditor.actions.action")}
            </Section.Header>
            <Section.Content className="rule-action">
              <div className="rule-prop-label">
                {t("rules.flyouts.ruleEditor.actions.emailAddresses")}
              </div>
              <div className="rule-prop-value">
                {rule.actions[0].parameters.recipients.join(", ")}
              </div>
              <div className="rule-prop-label">
                {t("rules.flyouts.ruleEditor.actions.emailSubject")}
              </div>
              <div className="rule-prop-value">
                {rule.actions[0].parameters.subject}
              </div>
              <div className="rule-prop-label">
                {t("rules.flyouts.ruleEditor.actions.emailComments")}
              </div>
              <div className="rule-prop-value">
                {rule.actions[0].parameters.notes}
              </div>
            </Section.Content>
          </Section.Container>
        )}

        <RuleSummaryAuto rule={rule} t={t} />
      </div>
    );
  }
}
