import React, { Component } from "react";
import Flyout from "components/shared/flyout";
import { TelemetryPanelContainer as TelemetryPanel } from "components/shared/telemetryPanel";
import { svgs } from "utilities";
import { Svg } from "components/shared/svg/svg";
import { SensorProperties } from "components/pages/sensors/sensorProperties/sensorProperties";
import { appInsights, reactPlugin } from "applicationInsights";
import { withAITracking } from "@microsoft/applicationinsights-react-js";

const Section = Flyout.Section;

class SensorDetails extends Component {
  componentDidMount() {
    appInsights.trackEvent({ name: "UiEvent:FlyOpen: Sensor Details" });
  }
  componentDidUpdate(prevProps) {
    if (prevProps.sensorData.id !== this.props.sensorData.id) {
      appInsights.trackEvent({ name: "UiEvent:FlyChange: Sensor Details" });
    }
  }
  componentWillUnmount() {
    appInsights.trackEvent({ name: "UiEvent:FlyClose: Sensor Details" });
  }
  render() {
    const { t, onClose, sensorData, theme } = this.props;
    return (
      <Flyout.Container>
        <Flyout.Header>
          <Flyout.Title />
          <Flyout.CloseBtn onClick={onClose} />
        </Flyout.Header>
        <Flyout.Content className="sensor-details-container">
          {
            <div className="sensor-details-container">
              <div>
                <table className="header-table">
                  <tbody>
                    <tr>
                      <td className="sensorType">
                        <Svg
                          path={
                            sensorData.deviceModelType === "Node"
                              ? svgs.nodeBig
                              : svgs.gatewayBig
                          }
                        />
                      </td>
                      <td className="sensorName">
                        <div className="sensor-title"> {sensorData.name}</div>
                      </td>

                      <td className="callspan-td" />
                    </tr>
                  </tbody>
                </table>
              </div>
              <Section.Container className="sensor-property-container">
                <Section.Header>
                  {t("sensors.flyouts.details.property.title")}
                </Section.Header>
                <Section.Content>
                  <div>
                    <SensorProperties sensorData={sensorData} t={t} />
                  </div>
                </Section.Content>
              </Section.Container>
              {/**
              <Section.Container>
                <Section.Header>
                  {t("sensors.flyouts.details.alert.title")}
                </Section.Header>
                <Section.Content />
              </Section.Container>
              */}
              <Section.Container
                className="sensor-telemetry-panel"
                collapsable={false}
              >
                <Section.Header>
                  {t("sensors.flyouts.details.telemetry.title")}
                </Section.Header>
                <Section.Content>
                  <TelemetryPanel
                    theme={theme}
                    t={t}
                    showHeader={false}
                    sensorIds={[this.props.sensorData.id]}
                  />
                </Section.Content>
              </Section.Container>
            </div>
          }
        </Flyout.Content>
      </Flyout.Container>
    );
  }
}

export default withAITracking(
  reactPlugin,
  SensorDetails,
  "Sensor Details Flyout"
);
