import React, { Component } from "react";
import {
  Flyout,
  FlyoutHeader,
  FlyoutTitle,
  FlyoutCloseBtn,
  FlyoutContent,
  Btn,
  BtnToolbar,
  FormControl,
  FormSection,
  ErrorMsg
} from "components/shared";

import PropTypes from "prop-types";

import "./filterControl.scss";

import { DatePickerElement } from "../../shared/generateMapsFlyout/datePickerElement";

import { FormElementType, jobsDateFormat } from "utilities";
import moment from "moment";

export default class FilterControl extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  handleOnChange = (e, key, calendar) => {
    /** check for date picker Jquery Object and Event for startDate */
    if (calendar) {
      const startDate = key.startDate.format(jobsDateFormat);
      const endDate = key.endDate.format(jobsDateFormat);
      this.setState({
        [calendar]: {
          from: startDate,
          to: endDate
        }
      });
    } else {
      /** Below case is for Select element type */
      if (e.target.value) {
        this.setState({ [key]: e.target.value });
      } else {
        this.setState({ [key]: undefined });
      }
    }
  };

  // Get all the elements based on the Config provided
  getElementByFilterContent = (filter, t, key) => {
    switch (filter.type) {
      case FormElementType.select:
        return (
          <React.Fragment key={filter.filterKey}>
            {filter.label && <div className="heading">{filter.label}</div>}

            {filter.List && filter.typeProps && (
              <FormControl
                type="select"
                options={filter.List}
                className={`flyouts-select-list  ${
                  filter.className ? filter.className : ""
                }`}
                placeholder={filter.typeProps.placeholder}
                onChange={e => this.handleOnChange(e, key)}
                matchProp={"label"}
                clearable={
                  filter.typeProps.clearable
                    ? filter.typeProps.clearable
                    : this.props.clearable
                }
                searchable={
                  filter.typeProps.searchable
                    ? filter.typeProps.searchable
                    : this.props.searchable
                }
                value={this.state[filter.filterKey]}
              />
            )}
          </React.Fragment>
        );

      case FormElementType.datePicker:
        return (
          <React.Fragment key={filter.filterKey}>
            {filter.label && <div className="heading">{filter.label}</div>}
            <div className="date-picker-container">
              <DatePickerElement
                startDate={
                  filter.typeProps.selectedValue &&
                  filter.typeProps.selectedValue.from
                    ? moment(filter.typeProps.selectedValue.from)
                    : undefined
                }
                endDate={
                  filter.typeProps.selectedValue &&
                  filter.typeProps.selectedValue.to
                    ? moment(filter.typeProps.selectedValue.to)
                    : undefined
                }
                singleDatePicker={filter.typeProps.singleDatePicker}
                className={filter.typeProps.className}
                inputValue={filter.typeProps.inputValue}
                IconClass={filter.typeProps.IconClass}
                onChange={(e, index) => this.handleOnChange(e, index)}
                containerType={filter.typeProps.containerType}
                containerIcon={filter.typeProps.containerIcon}
                containerPlaceHolder={filter.typeProps.containerPlaceHolder}
                containerValue={
                  this.state[filter.filterKey] &&
                  this.state[filter.filterKey].from &&
                  this.state[filter.filterKey].to
                    ? `${this.state[filter.filterKey].from} to ${this.state[filter.filterKey].to} `
                    : undefined
                }
                onApplyEvent={(e, picker) =>
                  this.handleOnChange(e, picker, filter.filterKey)
                }
                t={t}
              />
            </div>
          </React.Fragment>
        );

      default:
        return (
          <React.Fragment>
            {filter.label && <div className="heading">{filter.label}</div>}
            <ErrorMsg>{t("maps.filterFlyout.invalidFilter")}</ErrorMsg>
          </React.Fragment>
        );
    }
  };

  componentDidMount() {
    const stateObjfromProps = {};

    // If there are state values already set, reset all the values here

    this.props.filterConfig.FilterList.map(item => {
      if (item.typeProps && item.typeProps.selectedValue) {
        stateObjfromProps[item.filterKey] = item.typeProps.selectedValue;
      }

      return item;
    });

    this.setState(stateObjfromProps);
  }

  /** Gives you the count of filters applied */
  getFilterCount = () => {
    let count = 0;

    Object.keys(this.state).map(stateKey => {
      /** Undefined check when we use select box clearable button */
      if (this.state[stateKey] !== undefined) {
        if (!(Object.getOwnPropertyNames(this.state[stateKey]).length === 0)) {
          count = count + 1;
        }
      }

      return null;
    });

    return count;
  };

  // Gives all the state values to the parent component
  onFilterClick = () => {
    // let filterCount = Object.entries(this.state).filter((item) => item[1] !== undefined)
    const filterCount = this.getFilterCount();

    const obj = {
      ...this.state,
      isFilterOn: true,
      appliedFilters: filterCount
    };
    this.props.filterConfig.applyCallback(obj);
  };

  // Resets the current state values and return an empty object to the parent component
  onResetClick = () => {
    const obj = {};
    Object.keys(this.state).map(ele => (obj[ele] = undefined));
    this.setState(obj);
    this.props.filterConfig.applyCallback({});
  };

  render() {
    const { t, onCloseFlyout, filterConfig } = this.props;

    return (
      <Flyout>
        <FlyoutHeader>
          <FlyoutTitle>
            {
              <div
                className={`title ${
                  filterConfig.header && filterConfig.header.className
                    ? filterConfig.header.className
                    : ""
                }`}
              >
                {filterConfig.header && filterConfig.header.content
                  ? filterConfig.header.content
                  : ""}
              </div>
            }
          </FlyoutTitle>
          <FlyoutCloseBtn onClick={onCloseFlyout} />
        </FlyoutHeader>
        <FlyoutContent>
          <form>
            <FormSection>
              {// Loop through all the elements(FilterList) of the config object
              filterConfig.FilterList.map(element =>
                this.getElementByFilterContent(element, t, element.filterKey)
              )}
            </FormSection>
          </form>

          <BtnToolbar className={"wrapper-button-container"}>
            <Btn
              primary={true}
              onClick={this.onResetClick}
              className="reset-btn"
            >
              {t("jobs.flyouts.reset")}
            </Btn>
            <Btn
              primary={true}
              type="submit"
              onClick={this.onFilterClick}
              className="apply-btn"
            >
              {t("jobs.flyouts.apply")}
            </Btn>
          </BtnToolbar>
        </FlyoutContent>
      </Flyout>
    );
  }
}

FilterControl.propTypes = {
  t: PropTypes.func,
  onCloseFlyout: PropTypes.func,
  filterConfig: PropTypes.object
};

FilterControl.defaultProps = {
  t: null,
  onCloseFlyout: undefined,
  filterConfig: [],
  clearable: true,
  searchable: true
};
