import React, { Component } from "react";
import { Btn, PcsGrid, Protected } from "components/shared";
import { permissions /* toDiagnosticsModel*/ } from "services/models";
import { jobsColumnDefs, defaultjobsGridProps } from "./jobsGridConfig";
import { DeleteModal } from "components/shared/deleteModal/deleteModal";
import { toJobsItemModel } from "services/models";
import {
  isFunc,
  translateColumnDefs,
  svgs,
  succeeded,
  failed,
  stopped,
  stopRequested
} from "utilities";
import { checkboxColumn } from "components/shared/pcsGrid/pcsGridConfig";
import { JobsService } from "services";
const initialState = {
  softSelectedId: undefined,
  rowData: undefined,
  selectedJobs: undefined,
  deleteModalSuccess: false,
  deleteModalError: undefined,
  abortJobPending: false
};

export class JobsGrid extends Component {
  constructor(props) {
    super(props);

    // Set the initial state
    this.state = initialState;

    // Default device grid columns
    this.columnDefs = [
      checkboxColumn,
      jobsColumnDefs.id,
      jobsColumnDefs.name,
      jobsColumnDefs.typeOfJob,
      jobsColumnDefs.farmName,
      jobsColumnDefs.status,
      jobsColumnDefs.startTime,
      jobsColumnDefs.endTime,
      jobsColumnDefs.createdAt
    ];

    this.abortJob = this.abortJob.bind(this);

    this.contextBtns = {
      abort: (
        <Protected key="abort" permissions={permissions.deleteRules}>
          <Btn
            svg={svgs.abort}
            className="abort-job"
            onClick={this.openModal("abort")}
          >
            {this.props.t("jobs.abort")}
          </Btn>
        </Protected>
      )
    };
  }

  openModal = ModalName => () =>
    this.setState({
      openModalName: ModalName
    });

  closeModal = () =>
    this.setState({
      openModalName: undefined
    });

  abortJobsClick = () => {
    this.abortJob();
  };

  onGridReady = gridReadyEvent => {
    this.jobsGridApi = gridReadyEvent.api;
    // Call the onReady props if it exists
    if (isFunc(this.props.onGridReady)) {
      this.props.onGridReady(gridReadyEvent);
    }
  };

  setselectedJobs = selectedJobs => this.setState({ selectedJobs });

  abortJob() {
    this.setState({
      deleteModalSuccess: false,
      deleteModalError: undefined,
      abortJobPending: true
    });
    const { selectedJobs } = this.state;
    for (let i = 0; i < selectedJobs.length; i++) {
      this.subscription = JobsService.abortJobs(selectedJobs[i].id).subscribe(
        job => {
          this.props.abortJob([toJobsItemModel(job)]);
          this.props.refresh();
          this.setState({
            deleteModalError: undefined,
            deleteModalSuccess: true,
            abortJobPending: false,
            changesApplied: true
          });
        },
        error =>
          this.setState({
            deleteModalError: error,
            deleteModalSuccess: false,
            changesApplied: true,
            abortJobPending: false
          })
      );
    }
  }
  onHardSelectChange = selectedJobs => {
    const { onContextMenuChange, onHardSelectChange } = this.props;

    if (isFunc(onContextMenuChange)) {
      this.setselectedJobs(selectedJobs);

      let allowAbortJob = true;
      for (let i = 0; i < selectedJobs.length; i++) {
        if (
          selectedJobs[i].currentState === failed ||
          selectedJobs[i].currentState === succeeded ||
          selectedJobs[i].currentState === stopped ||
          selectedJobs[i].currentState === stopRequested
        )
          allowAbortJob = false;
      }

      if (allowAbortJob && selectedJobs.length >= 1)
        onContextMenuChange(this.contextBtns.abort);
      else {
        onContextMenuChange(null);
      }
    }
    if (isFunc(onHardSelectChange)) {
      onContextMenuChange(this.contextBtns.abort);
      onHardSelectChange(selectedJobs);
    }
  };

  getSoftSelectId = ({ id } = "") => id;

  render() {
    const gridProps = {
      /* Grid Properties */
      ...defaultjobsGridProps,
      columnDefs: translateColumnDefs(this.props.t, this.columnDefs),
      sizeColumnsToFit: true,
      getSoftSelectId: this.getSoftSelectId,
      softSelectId: (this.state.softSelectedDevice || {}).id,
      ...this.props, // Allow default property overrides
      deltaRowDataMode: true,
      enableSorting: true,
      unSortIcon: true,
      getRowNodeId: ({ id }) => id,
      context: {
        t: this.props.t
      },
      onGridReady: this.onGridReady,
      onHardSelectChange: this.onHardSelectChange
    };
    const modal = this.state.openModalName === "abort";
    return [
      <PcsGrid key="farm-grid-key" {...gridProps} />,
      modal && (
        <DeleteModal
          key="abort-job"
          type="abortJob"
          deleteItem={this.abortJobsClick}
          title={this.props.t("jobs.abortJobModal.title")}
          deleteConfirmation={this.props.t("jobs.abortJobModal.deleteInfo")}
          onClose={this.closeModal}
          deleteBtn={this.props.t("jobs.abort")}
          t={this.props.t}
          icon={svgs.abort}
          isPending={this.state.abortJobPending}
          deleteModalError={this.state.deleteModalError}
          deleteModalSuccess={this.state.deleteModalSuccess}
          successMsg={this.props.t("jobs.abortJobModal.success")}
        />
      )
    ];
  }
}
