import { camelCaseReshape, getItems } from "utilities";

export const deviceModelsModel = (response = []) =>
  getItems(response).map(deviceModelModel);

export const deviceModelModel = (response = {}) =>
  camelCaseReshape(response, {
    id: "id",
    createdAt: "createdAt",
    lastModifiedAt: "lastModifiedAt",
    type: "type",
    manufacturer: "manufacturer",
    "ports.name": "name",
    "ports.type": "type",
    name: "name",
    description: "description"
  });

export const toDeviceModel = (device = {}) =>
  camelCaseReshape(device, {
    status: "status",
    id: "id",
    createdAt: "createdAt",
    lastModifiedAt: "lastModifiedAt",
    deviceModelId: "deviceModelId",
    hardwareId: "hardwareId",
    createIoTHubDevice: "createIoTHubDevice",
    farmId: "farmId",
    reportingInterval: "reportingInterval",
    "location.latitude": "latitude",
    "location.longitude": "longitude",
    "location.elevation": "elevation",
    parentDeviceId: "parentDeviceId",
    name: "name",
    description: "description",
    properties: "properties"
  });

export const toDevicesModel = (response = []) =>
  getItems(response).map(toDeviceModel);

export const toDevicePortModel = (response = []) =>
  response.map(deviceType =>
    camelCaseReshape(deviceType, {
      id: "id",
      name: "name",
      type: "type"
    })
  );

export const toDevicePortsModel = (response = []) =>
  getItems(response).map(toDevicePortModel);

export const toNewDeviceRequestModel = (device = {}) => {
  return {
    deviceModelId: device.deviceModelId,
    hardwareId: device.hardwareId,
    createIoTHubDevice: device.createIoTHubDevice,
    farmId: device.farmId,
    reportingInterval: device.reportingInterval,
    location: {
      latitude: device.latitude,
      longitude: device.longitude,
      elevation: device.elevation
    },
    parentDeviceId: device.parentDeviceId,
    name: device.name,
    description: device.description,
    properties: device.properties
  };
};
