import {
  camelCaseReshape,
  getItems,
  sensorPlacementMap,
  jobsDateFormat,
  satelliteIndicesMap,
  soilMoistureMap
} from "utilities";
import update from "immutability-helper";
import moment from "moment";
export const toJobsItemModel = (data = {}) => {
  const model = camelCaseReshape(data, {
    id: "id",
    createdAt: "createdAt",
    lastModifiedAt: "lastModifiedAt",
    stopRequested: "stopRequested",
    currentState: "currentState",
    error: "error",
    output: "output",
    type: "type",
    farmIds: "farmIds",
    arguments: "arguments",
    name: "name",
    description: "description",
    properties: "properties",
    typeId: "typeId"
  });
  return update(model, {
    currentState: {
      $set: model.stopRequested ? "StopRequested" : model.currentState
    }
  });
};

export const toJobTypesItemModel = (data = {}) =>
  camelCaseReshape(data, {
    id: "id",
    name: "name"
  });

export const toJobTypesItemsModel = (response = {}) =>
  getItems(response).map(toJobTypesItemModel);

export const toJobsItemsModel = (response = {}) =>
  getItems(response).map(toJobsItemModel);

export const toSensorPlacementRequestModel = ({
  startDate,
  endDate,
  farmId,
  mapType,
  typeId
}) => {
  return {
    typeId: typeId,
    arguments: {
      startDate: startDate,
      endDate: endDate,
      farm_id: farmId
    },
    name: mapType
  };
};

export const toNewJobsRequestModel = ({
  startDate,
  endDate,
  farmId,
  sensorMeasureName,
  mapType,
  jobTypeId
}) => {
  startDate = moment(new Date(startDate));
  endDate = moment(new Date(endDate));
  let argumentsObject;
  mapType === sensorPlacementMap
    ? (argumentsObject = {
        date: endDate.format(jobsDateFormat),
        history: moment.duration(endDate.diff(startDate)).asDays(),
        farm_id: farmId
      })
    : mapType === satelliteIndicesMap
    ? (argumentsObject = {
        from: startDate.format(jobsDateFormat),
        to: endDate.format(jobsDateFormat),
        farm_ids: farmId,
        force: false
      })
    : mapType === soilMoistureMap
    ? (argumentsObject = {
        from: startDate.format(jobsDateFormat),
        to: endDate.format(jobsDateFormat),
        farm_id: farmId,
        measure_name: sensorMeasureName
      })
    : (argumentsObject = {
        from: startDate.format(jobsDateFormat),
        to: endDate.format(jobsDateFormat),
        farm_id: farmId
      });

  return {
    typeId: jobTypeId,
    arguments: argumentsObject,
    name: mapType
  };
};
