import React, { Component } from "react";
import { Svg } from "components/shared";
import { svgs, renderUndefined, isDef } from "utilities";
import { ModalHeader } from "..";
import {
  Select,
  Indicator,
  PropertyGrid as Grid,
  PropertyRow as Row,
  PropertyCell as Cell
} from "components/shared";
import { MapPanelContainer as MapPanel } from "components/pages/farmDetails/panels/sensorPlacementMap";

import {
  joinClasses,
  mapModalType,
  getDownloadRequest,
  contentTypeExtensionsMap
} from "utilities";
import "./modal.scss";

const downloadOptionRenderer = option => {
  return (
    <div className="inline">
      <div className="fileName">{option.label}</div>
      <Svg className="right-icon" path={svgs.downloadButton} />
    </div>
  );
};

export class Modal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      disagree: false
    };
  }

  componentDidMount() {
    if (this.props.onClose) {
      window.addEventListener("keydown", this.listenKeyboard, true);
    }
  }

  componentWillUnmount() {
    if (this.props.onClose) {
      window.removeEventListener("keydown", this.listenKeyboard, true);
    }
  }

  listenKeyboard = event => {
    if (event.key === "Escape" || event.keyCode === 27) {
      this.props.onClose();
    }
  };

  onOverlayClick = () => {
    this.props.modalClose();
  };

  onClickDisagree = () => {
    this.setState({
      disagree: true
    });
  };

  downloadMapFile = event => {
    const mapName = this.props.mapName;
    const responseType = "blob";
    const aTag = "a";
    const extension = event.target.value.contentType;
    const xhr = new XMLHttpRequest();
    xhr.open(getDownloadRequest, event.target.value.value, true);
    xhr.responseType = responseType;
    xhr.onload = function() {
      const urlCreator = window.URL || window.webkitURL;
      const imageUrl = urlCreator.createObjectURL(this.response);
      const tag = document.createElement(aTag);
      tag.href = imageUrl;
      tag.download = mapName + (contentTypeExtensionsMap.get(extension) || "");
      document.body.appendChild(tag);
      tag.click();
      document.body.removeChild(tag);
    };
    xhr.send();
  };

  render() {
    const {
      farmName,
      name,
      type,
      blobUrl,
      t,
      mapDate,
      className,
      latestCreatedAt,
      mapFileDownloadSelectObject,
      isPending,
      noMapImage,
      noMaps,
      isMapFound,
      noPngMaps,
      sensorPlacementLatLong,
      farm
    } = this.props;

    if (type === mapModalType) {
      return (
        <div className={joinClasses("modal-map", "open-map", className)}>
          <article className="content-wrapper-map">
            <ModalHeader modalClose={this.props.modalClose}>{name}</ModalHeader>
            <Grid>
              <Row className="properties">
                <Cell className="col-3">
                  <Row className="name-section">
                    <Cell className="col-2 text">
                      <span>{t("maps.modal.farm")}</span>
                    </Cell>
                    <Cell className="col-7 modalfarm-name">
                      {renderUndefined(farmName)}
                    </Cell>
                  </Row>
                </Cell>
                <Cell className="col-3">
                  <span>
                    {t("maps.modal.mapDate") + renderUndefined(mapDate)}{" "}
                  </span>
                </Cell>
                <Cell className="col-3">
                  <span>
                    {t("maps.modal.generatedDate") +
                      renderUndefined(latestCreatedAt)}
                  </span>
                </Cell>
                <Cell className="col-2 download-map">
                  <Select
                    className="download-dropdown"
                    closeMenuOnSelect={false}
                    options={mapFileDownloadSelectObject}
                    clearable={false}
                    searchable={false}
                    onChange={this.downloadMapFile}
                    placeholder={
                      <div className="Select-placeholder">
                        <Svg path={svgs.downloadButton} />
                        {t("maps.modal.downloadMap")}
                      </div>
                    }
                    defaultValue={mapFileDownloadSelectObject[0]}
                    optionRenderer={downloadOptionRenderer}
                  />
                </Cell>
              </Row>
            </Grid>
            <div className="line" />
            {isPending && <Indicator className="no-map-loader" />}
            {!isPending &&
              ((noMaps || !blobUrl) &&
                !(
                  name === "sensor-placement" && isDef(sensorPlacementLatLong)
                ) && (
                  <div>
                    <center>
                      <div className="no-preview" id="no-preview">
                        <img src={svgs.NoMapImage} alt="" />

                        <div className="no-maps">
                          {/* <p>
                              <b>{t("maps.modal.badUrl")}</b>
                            </p> */}
                          <p className="nomap-text" id="nomap-text">
                            {t("maps.modal.noMapYet")}
                          </p>
                          {/* <center>
                              <Link className="nomap-Url" to={"#"}>
                                <span>{t("maps.modal.reportIt")}</span>
                              </Link>
                            </center> */}
                        </div>
                      </div>
                    </center>
                  </div>
                ))}
            {name === "sensor-placement" && sensorPlacementLatLong ? (
              <MapPanel
                farm={farm}
                sensorPlacementLatLong={sensorPlacementLatLong}
                page={"Maps"}
                t={t}
              />
            ) : (
              !isPending &&
              !noMaps &&
              !noPngMaps && (
                <div>
                  <center>
                    {isMapFound ? (
                      <div className="modal-image">
                        <img src={blobUrl} alt="Map" />
                        <p>{t("maps.modal.Disclaimer")}</p>
                      </div>
                    ) : (
                      ""
                    )}
                    <div
                      className={
                        noMapImage ? "no-preview modal-image" : "mapModal-image"
                      }
                    >
                      {blobUrl && !isMapFound && (
                        <img src={blobUrl} alt={t("maps.modal.noMap")} />
                      )}
                      {!noMapImage && <p>{t("maps.modal.Disclaimer")}</p>}
                      {blobUrl === noMapImage && (
                        <div className="no-maps">
                          <p>
                            <b>{t("maps.modal.noMapYet")}</b>
                          </p>
                          <p className="nomap-text">
                            {t("maps.modal.noGeneratedYet")}
                          </p>
                        </div>
                      )}
                    </div>
                  </center>
                </div>
              )
            )}
          </article>
        </div>
      );
    }
  }
}
