import { camelCaseReshape, getItems } from "utilities";

export const SensorModelsModel = (response = []) =>
  getItems(response).map(SensorModelModel);

export const SensorModelModel = (response = {}) =>
  camelCaseReshape(response, {
    id: "id",
    createdAt: "createdAt",
    lastModifiedAt: "lastModifiedAt",
    type: "type",
    manufacturer: "manufacturer",
    productCode: "productCode",
    sensorMeasures: "sensorMeasures",
    name: "name",
    description: "description",
    properties: "properties"
  });

export const SensorsModel = (response = []) =>
  getItems(response).map(SensorModel);

export const SensorModel = (response = {}) =>
  camelCaseReshape(response, {
    id: "id",
    createdAt: "createdAt",
    lastModifiedAt: "lastModifiedAt",
    hardwareId: "hardwareId",
    sensorModelId: "sensorModelId",
    location: "location",
    port: "port",
    deviceId: "deviceId",
    name: "name",
    description: "description",
    properties: "properties"
  });
