import React from "react";
import {
  ComponentArray,
  PageContent,
  ContextMenuAlign,
  FormControl,
  Btn
} from "components/shared";
import { AssociateDeviceGrid } from "./associateDeviceGrid";
import "./associateDevices.scss";
import { LinkedComponent, isFunc } from "utilities";

const initialState = {
  farmApplyToAll: undefined,
  deviceMapping: undefined
};

export class ManualAssociate extends LinkedComponent {
  constructor(props) {
    super(props);
    const { rowData } = props;
    const deviceMapping = new Map();
    rowData.forEach(device => {
      deviceMapping.set(device.id, undefined);
    });
    this.state = { ...initialState, deviceMapping };
  }

  onGridReady = gridReadyEvent =>
    (this.associateDeviceGridApi = gridReadyEvent.api);

  toSelectOptions = ({ id, name }) => {
    return {
      label: name,
      value: id
    };
  };

  componentWillReceiveProps(nextProps) {
    if (this.props.rowData !== nextProps.rowData) {
      const { rowData, onDeviceFarmMappingChange } = nextProps;
      const { deviceMapping } = this.state;
      const newDeviceMapping = new Map();
      rowData.forEach(device => {
        if (deviceMapping.get(device.id))
          newDeviceMapping.set(device.id, deviceMapping.get(device.id));
        else newDeviceMapping.set(device.id, undefined);
      });
      if (isFunc(onDeviceFarmMappingChange)) {
        onDeviceFarmMappingChange(newDeviceMapping);
      }
      this.setState({ ...initialState, deviceMapping: newDeviceMapping });
    }
  }

  mapDevicesToFarm = () => {
    const { rowData } = this.props;
    const { deviceMapping } = this.state;

    return rowData.map((device, id) => {
      return {
        farmId: device.farmId,
        id: id,
        deviceId: device.id,
        farmName: device.farmName ? device.farmName : undefined,
        deviceName: device.name,
        assignToFarm: deviceMapping ? deviceMapping.get(device.id) : undefined
      };
    });
  };

  onApplyFarmToAll = () => {
    const { farmApplyToAll } = this.state;
    const { rowData, onDeviceFarmMappingChange } = this.props;
    const deviceMapping = new Map();
    rowData.forEach(device => {
      deviceMapping.set(device.id, farmApplyToAll);
    });
    if (isFunc(onDeviceFarmMappingChange)) {
      onDeviceFarmMappingChange(deviceMapping);
    }
    this.setState({ ...this.state, deviceMapping });
  };

  onFarmSelectChange = (data, farmId) => {
    if (farmId) {
      const { deviceMapping } = this.state;
      const { onDeviceFarmMappingChange } = this.props;
      const rowData = this.mapDevicesToFarm();
      const row = rowData.find(row => row.id === data.id);

      if (data.deviceId === row.deviceId)
        deviceMapping.set(data.deviceId, farmId);
      else deviceMapping.set(row.deviceId, farmId);
      if (isFunc(onDeviceFarmMappingChange)) {
        onDeviceFarmMappingChange(deviceMapping);
      }
      this.setState({
        ...this.state,
        farmApplyToAll: undefined,
        deviceMapping
      });
    }
  };

  render() {
    const { t, farms } = this.props;
    const rowData = this.mapDevicesToFarm();
    const gridProps = {
      t,
      rowData,
      farms,
      onGridReady: this.onGridReady,
      onFarmSelectChange: this.onFarmSelectChange
    };
    const farmOptions = farms.map(this.toSelectOptions);

    this.applyToAllLink = this.linkTo("farmApplyToAll").map(
      ({ value }) => value
    );
    return (
      <ComponentArray>
        <ContextMenuAlign left={true}>
          <div className="farm-applytoall-group">
            <FormControl
              type="select"
              options={farmOptions}
              className="farm-apply-to-all-select"
              clearable={false}
              searchable={true}
              placeholder={t("devices.flyouts.associateDevices.selectFarm")}
              link={this.applyToAllLink}
            />
            <Btn
              className="farm-apply-to-all-btn"
              onClick={() => this.onApplyFarmToAll()}
            >
              {t("devices.flyouts.associateDevices.applyToAll")}
            </Btn>
          </div>
        </ContextMenuAlign>
        <PageContent>
          <AssociateDeviceGrid {...gridProps} />
        </PageContent>
      </ComponentArray>
    );
  }
}
