import React, { Component } from "react";
import { Link } from "react-router-dom";
import { FarmService } from "services";
import { permissions } from "services/models";
import { DeleteModal } from "components/shared/deleteModal/deleteModal";
import { OverviewPanel } from "../../pages/farmDetails/panels/overview";
import {
  svgs,
  noMapImage,
  satelliteIndicesJobTypeName,
  sensorPlacementJobTypeName,
  soilMoistureJobTypeName
} from "utilities";
import { PanelErrorBoundary } from "components/shared";
import { SatelliteIndicesMap } from "./panels/satelliteIndicesMap/satelliteIndicesMap";
import { SoilMoistureMap } from "./panels/soilMoistureMap";
import { MapPanelContainer as MapPanel } from "./panels";
import { TelemetryPanelContainer as TelemetryPanel } from "components/shared/telemetryPanel";
import {
  ComponentArray,
  ContextMenu,
  ContextMenuAlign,
  PageContent,
  Protected,
  // RefreshBarContainer as RefreshBar,
  Btn,
  Grid,
  Cell,
  Select,
  GenerateMapsFlyoutContainer
} from "components/shared";
import { SensorPlacementMap } from "./panels/sensorPlacementMap";
import {
  EditFarmFlyout,
  AddDevicesContainer
} from "components/pages/farms/flyouts";
import { Drone } from "./panels/drone";

import "./farmDetails.scss";
import { appInsights, reactPlugin } from "applicationInsights";
import { withAITracking } from "@microsoft/applicationinsights-react-js";

const satelliteIndicesMap = "Satellite Indices";
const soilMoistureMap = "Soil Moisture";
const sensorPlacementMap = "Sensor Placement";

const AzureMaps = window.atlas;

const initialState = {
  deleteModalSuccess: false,
  deleteModalError: undefined,
  deleteFarmPending: false,

  // edit Farms Flyout
  // openFlyoutName: undefined,

  // delete Farm Modal
  openModalName: undefined,

  // Analytics data
  currentActiveAlerts: [],
  topAlerts: [],
  alertsPerDeviceId: {},
  eviBlobUrl: undefined,
  soilMoistureUrl: undefined,
  openWarningCount: undefined,
  openCriticalCount: undefined,
  countMaxAlerts: 0,

  // Map data
  devicesInAlert: {},

  lastRefreshed: undefined,
  devices: []
};

const farmsPathName = "/farms/";
class FarmDetails extends Component {
  constructor(props) {
    super(props);

    const farmIdFromPathNameFirstIndex =
      this.props.location.pathname.indexOf(farmsPathName) +
      farmsPathName.length;
    const farmIdFromPathNameLastIndex = this.props.location.pathname.length;
    const farmIdFromPathName = this.props.location.pathname.substring(
      farmIdFromPathNameFirstIndex,
      farmIdFromPathNameLastIndex
    );

    this.state = {
      DeviceCount: [],
      ...initialState,
      farmId:
        this.props && this.props.location && this.props.location.state
          ? this.props.location.state.id
          : farmIdFromPathName,
      farmName:
        this.props &&
        this.props.location &&
        this.props.location.state &&
        this.props.location.state.farmName,
      farmNameLoading:
        this.props &&
        this.props.location &&
        this.props.location.state &&
        this.props.location.state.farmName
          ? false
          : true
    };

    !(this.props && this.props.location && this.props.location.state) &&
      FarmService.getFarmById(this.state.farmId).subscribe(
        farm => {
          this.setState({ farmNameLoading: false, farmName: farm.name });
        },
        error =>
          this.setState({ farmNameError: error, farmNameLoading: false }),
        () => {}
      );

    this.subscriptions = [];

    this.createMaps = this.createMaps.bind(this);
    this.openDeleteFarm = this.openDeleteFarm.bind(this);
    // TOBEADDED
    // this.getOpenModal = this.getOpenModal.bind(this);
    this.onDelete = this.onDelete.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.getSensorIds = this.getSensorIds.bind(this);

    const {
      fetchFarms,
      farmsLastUpdated,
      farmsError,

      fetchDevices,
      devicesLastUpdated,
      devicesError,

      deviceModelsLastUpdated,
      deviceModelsError,
      fetchDeviceModel,

      fetchSensors,
      sensorsLastUpdated,
      sensorsError,

      mapsError,
      mapsLastUpdated,
      fetchMaps
    } = this.props;

    if (!farmsLastUpdated && !farmsError) fetchFarms();
    if (!devicesLastUpdated && !devicesError) fetchDevices();
    if (!sensorsLastUpdated && !sensorsError) fetchSensors();
    if (!mapsLastUpdated && !mapsError) fetchMaps();
    if (!deviceModelsLastUpdated && !deviceModelsError) fetchDeviceModel();
  }

  getSensorIds() {
    const { farmId } = this.state;
    const { devices, sensors } = this.props;

    return devices
      .filter(device => device.farmId === farmId)
      .reduce(
        (acc, device) =>
          acc.concat(sensors.filter(sensor => sensor.deviceId === device.id)),
        []
      )
      .map(sensor => sensor.id);
  }

  componentDidMount() {
    this.getDeviceStatus(this.props.devices);
  }

  componentDidUpdate(prevProps) {
    if (prevProps.devices !== this.props.devices) {
      this.getDeviceStatus(this.props.devices);
    }
  }

  componentWillUnmount() {
    this.subscriptions.forEach(sub => sub.unsubscribe());
  }

  extractfarmNames() {
    const { farms } = this.props;
    const farmsSelectObj = [];

    for (let i = 0; i < farms.length; i++) {
      farmsSelectObj.push({ label: farms[i].name, value: farms[i].id });
    }

    // farmsNameObject= farmsSelectObj;
  }

  createMaps(event) {
    this.extractfarmNames();
    const typeOfSelectedMap = event.target.value.value;
    switch (typeOfSelectedMap) {
      case satelliteIndicesMap:
        this.setState({
          openFlyoutName: satelliteIndicesMap,
          jobTypeName: satelliteIndicesJobTypeName
        });
        break;
      case soilMoistureMap:
        this.setState({
          openFlyoutName: soilMoistureMap,
          jobTypeName: soilMoistureJobTypeName
        });
        break;
      case sensorPlacementMap:
        this.setState({
          openFlyoutName: sensorPlacementMap,
          jobTypeName: sensorPlacementJobTypeName
        });
        break;
      default:
    }
  }

  refreshRules = () => {
    if (!this.props.rulesError && !this.props.isRulesPending)
      this.props.fetchRules();
  };

  openFlyout = name => this.setState({ openFlyoutName: name });

  onCloseFlyout = () => this.setState({ openFlyoutName: undefined });

  deleteFarm = () => {
    this.setState({
      deleteModalSuccess: false,
      deleteModalError: undefined,
      deleteFarmPending: true
    });

    this.subscription = FarmService.deleteFarm(this.state.farmId).subscribe(
      () => {
        this.props.deleteFarm([this.state.farmId]);
        this.setState({
          deleteModalError: undefined,
          deleteModalSuccess: true,
          deleteFarmPending: false
        });
      },
      error => {
        this.setState({
          deleteModalError: error,
          deleteModalSuccess: false,
          isPending: false,
          changesApplied: true,
          deleteFarmPending: false
        });
      },
      () =>
        this.setState({
          isPending: false,
          changesApplied: true,
          ruleDeleted: true
        })
    );
  };

  getOpenModal = () => {
    if (this.state.openModalName === "delete-farm") {
      return (
        <DeleteModal
          type="deleteFarm"
          itemId={this.state.farmId}
          onClose={this.closeModal}
          onDelete={this.onDelete}
          deleteItem={this.deleteFarm}
          title={this.props.t("farms.modals.delete.title")}
          deleteConfirmation={this.props.t(
            "farms.modals.delete.deleteConfirmation",
            { returnObjects: true }
          )}
          deleteBtn={this.props.t("modal.delete")}
          icon={svgs.trash}
          t={this.props.t}
          isPending={this.state.deleteFarmPending}
          deleteModalError={this.state.deleteModalError}
          deleteModalSuccess={this.state.deleteModalSuccess}
          successMsg={this.props.t("farms.modals.delete.deleteSuccess")}
        />
      );
    }
    return null;
  };
  openDeleteFarm = () => {
    this.setState({ ...this.state, openModalName: "delete-farm" });
  };
  closeModal = () => {
    const { history } = this.props;
    if (this.state.deleteModalSuccess) history.replace("/farms");
    this.setState({ ...this.state, openModalName: undefined });
  };

  onDelete = () => {
    const { history, fetchFarms } = this.props;
    fetchFarms();
    history.goBack();
    this.setState({ ...this.state, openModalName: undefined });
  };

  getDeviceStatus = devices => {
    let onlineCount = 0;
    let offlineCount = 0;
    let gatewayCount = 0;
    let nodesCount = 0;
    if (devices.length > 0) {
      devices
        .filter(item => item.farmId === this.state.farmId)
        .map(item => {
          if (item.status === "Offline") {
            offlineCount = offlineCount + 1;
          } else if (item.status === "Online") {
            onlineCount = onlineCount + 1;
          }
          if (item.deviceModelType === "Node") {
            nodesCount = nodesCount + 1;
          } else if (item.deviceModelType === "Gateway") {
            gatewayCount = gatewayCount + 1;
          }
          return null;
        });
      this.setState({
        DeviceCount: [onlineCount, offlineCount, nodesCount, gatewayCount]
      });
    } else {
      this.setState({ DeviceCount: [] });
    }
  };

  render() {
    const {
      theme,
      azureMapsKey,
      // azureMapsKeyError,
      // isAzureMapsKeyPending,

      devices,
      devicesError,
      isDevicesPending,

      isDeviceModelsPending,
      deviceModelsError,

      rules,
      // isRulesPending,
      t,
      maps,

      farms,
      farmsError
    } = this.props;

    const {
      openFlyoutName,
      farmId,
      currentActiveAlerts,
      topAlerts,
      farmName,
      jobTypeName,
      farmNameLoading,
      DeviceCount
    } = this.state;

    // const currentActiveAlertsWithName = currentActiveAlerts.map(alert => ({
    //   ...alert,
    //   name: (rules[alert.ruleId] || {}).name || alert.ruleId,
    //   // limit the number shown in the UI to 1000 active
    //   countMaxAlerts: Math.min(alert.count, Config.maxAlertsCount)
    // }));

    // Determine if the rules for all of the alerts are actually loaded.
    const unloadedRules =
      topAlerts.filter(alert => !rules[alert.ruleId]).length +
      currentActiveAlerts.filter(alert => !rules[alert.ruleId]).length;
    if (unloadedRules > 0) {
      // Fetch the rules since at least one alert doesn't know the name for its rule
      this.refreshRules();
    }

    // if(alerts){
    //   this.getAlertsForRules( alerts ,ruleIdsByFarmId)
    // }

    const selectObject = [
      { label: t("maps.satelliteIndices"), value: satelliteIndicesMap },
      { label: t("maps.soilMoisture"), value: soilMoistureMap },
      { label: t("maps.sensorPlacement"), value: sensorPlacementMap }
    ];

    const currentFarm = farms.find(farm => farm.id === farmId);
    // TOBEADDED : MAPS-FLYOUT
    const isFlyoutOpen =
      openFlyoutName === satelliteIndicesMap ||
      openFlyoutName === soilMoistureMap ||
      openFlyoutName === sensorPlacementMap;

    const AzureMapError = AzureMaps ? false : true;
    return (
      <ComponentArray>
        <ContextMenu>
          <ContextMenuAlign left={true} className="farmdetail-menus">
            {/* <DeviceGroupDropdown /> */}
            <Protected permissions={permissions.editFarms}>
              <Btn
                svg={svgs.edit}
                className="farm-details-menu-buttons"
                onClick={() => {
                  this.openFlyout("edit");
                  appInsights.trackEvent({
                    name: "UiEvent:Click: Edit Farm Btn"
                  });
                }}
              >
                {t("farms.flyouts.edit.open")}
              </Btn>
            </Protected>
            <Protected permissions={permissions.deleteFarms}>
              <Btn
                svg={svgs.trash}
                className="farm-details-menu-buttons"
                onClick={() => {
                  this.openDeleteFarm();
                  appInsights.trackEvent({
                    name: "UiEvent:Click: Delete Farm Btn"
                  });
                }}
              >
                {t("farms.delete")}
              </Btn>
            </Protected>
            <Protected permissions={permissions.addDevices}>
              <Btn
                svg={svgs.addDevice}
                className="farm-details-menu-buttons"
                onClick={() => {
                  this.openFlyout("addDevices");
                  appInsights.trackEvent({
                    name: "UiEvent:Click: Add Devices Fly Open Btn"
                  });
                }}
              >
                {t("farms.addDevices")}
              </Btn>
            </Protected>
            <Protected permissions={permissions.createMaps}>
              <Select
                className="precision-maps-group-dropdown"
                placeholder="Generate Precision Maps"
                options={selectObject}
                value={"CREATE MAPS"}
                clearable={false}
                searchable={false}
                onChange={event => {
                  this.createMaps(event);
                  appInsights.trackEvent(
                    {
                      name:
                        "UiEvent:Click: Generate " +
                        event.target.value.value +
                        " Fly Open"
                    },
                    {
                      codeSource: {
                        component: "FarmDetails"
                      }
                    }
                  );
                }}
                icon={svgs.plus}
              />
            </Protected>
          </ContextMenuAlign>

          <ContextMenuAlign>
            {/* <RefreshBar
              refresh={this.refreshfarmDetail}
              time={lastRefreshed}
              isPending={isDevicesPending}
              t={t}
            /> */}
          </ContextMenuAlign>
        </ContextMenu>
        <PageContent className="farmDetails-container">
          {openFlyoutName === "edit" && (
            <EditFarmFlyout
              farmId={farmId}
              onClose={this.onCloseFlyout}
              t={t}
            />
          )}
          {openFlyoutName === "addDevices" && (
            <AddDevicesContainer
              farmId={farmId}
              onClose={this.onCloseFlyout}
              t={t}
            />
          )}
          <div>
            <Grid className="grid-farm-container">
              <Cell className="grid-farm-cell col-1">
                <OverviewPanel
                  onlineDeviceCount={
                    DeviceCount && DeviceCount.length > 0
                      ? DeviceCount[0]
                      : undefined
                  }
                  offlineDeviceCount={
                    DeviceCount && DeviceCount.length > 0
                      ? DeviceCount[1]
                      : undefined
                  }
                  nodesCount={
                    DeviceCount && DeviceCount.length > 0
                      ? DeviceCount[2]
                      : undefined
                  }
                  gatewaysCount={
                    DeviceCount && DeviceCount.length > 0
                      ? DeviceCount[3]
                      : undefined
                  }
                  isPending={isDevicesPending || isDeviceModelsPending}
                  error={farmsError || devicesError || deviceModelsError}
                  t={t}
                />
              </Cell>
              <Cell className="grid-farm-cell col-5">
                <PanelErrorBoundary
                  msg={t("farms.farmDetails.panels.map.runtimeError")}
                >
                  <MapPanel
                    azureMapsKey={!AzureMapError && azureMapsKey}
                    farmId={farmId}
                    farm={currentFarm}
                    devices={devices}
                    // devicesInAlert={[]}
                    mapKeyIsPending={false}
                    isPending={isDevicesPending}
                    error={farmsError || devicesError}
                    t={t}
                  />
                </PanelErrorBoundary>
              </Cell>

              <Cell className="grid-farm-cell col-9">
                <TelemetryPanel
                  theme={theme}
                  sensorIds={this.getSensorIds()}
                  showHeader={true}
                  t={t}
                />
              </Cell>
              <div className="grid-farm-maps">
                {t("maps.flyouts.latestPrecisionMaps")}
              </div>

              <Cell className="grid-farm-cell col-3">
                <SoilMoistureMap
                  t={t}
                  farmName={farmName}
                  farmId={farmId}
                  maps={maps}
                  noMapImage={t("dashboard.noData")}
                  farmsError={farmsError}
                  farmNameLoading={farmNameLoading}
                />
              </Cell>
              <Cell className="grid-farm-cell col-7">
                <PanelErrorBoundary
                  msg={t("farms.farmDetails.panels.map.runtimeError")}
                >
                  <SensorPlacementMap
                    t={t}
                    farmName={farmName}
                    farmId={farmId}
                    maps={maps}
                    noMapImage={noMapImage}
                    azureMapsKey={!AzureMapError && azureMapsKey}
                    farm={currentFarm}
                    mapKeyIsPending={false}
                    farmNameLoading={farmNameLoading}
                    farmsError={farmsError}
                  />
                </PanelErrorBoundary>
              </Cell>
              <Cell className="col-5">
                <SatelliteIndicesMap
                  t={t}
                  farmName={farmName}
                  farmId={farmId}
                  maps={maps}
                  noMapImage={t("dashboard.noData")}
                  farmsError={farmsError}
                  farmNameLoading={farmNameLoading}
                />
              </Cell>
              <Cell className="grid-farm-cell col-5">
                <Drone
                  farmName={farmName}
                  farmId={farmId}
                  maps={maps}
                  noMapImage={t("dashboard.noData")}
                  farmsError={farmsError}
                  farmNameLoading={farmNameLoading}
                  t={t}
                />
              </Cell>
            </Grid>

            <div className="farm-details-link">
              <Link
                to={{
                  pathname: "/maps",
                  state: { farmId: farmId, farmName: farmName }
                }}
              >
                {t("maps.flyouts.viewAllMaps")}
              </Link>
            </div>
            {/** Wraped the grid into a div to fix the text alignment bug for view all maps link */}
          </div>

          {/* TOBEADDED */}
          {isFlyoutOpen && (
            <GenerateMapsFlyoutContainer
              farmId={farmId}
              flyoutName={openFlyoutName}
              onClose={this.onCloseFlyout}
              jobTypeName={jobTypeName}
              farmsError={farmsError}
              t={t}
            />
          )}
        </PageContent>
        {this.getOpenModal()}
      </ComponentArray>
    );
  }
}

export default withAITracking(reactPlugin, FarmDetails, "Farm Details");
