import React from "react";
import PropTypes from "prop-types";

import { Svg } from "components/shared/svg/svg";
import { joinClasses, svgs } from "utilities";

import "./styles/successMsg.scss";

export const SuccessMsg = props => {
  const { children, className } = props;
  return (
    <div className={joinClasses("success-message", className)}>
      <Svg path={svgs.online} className="success-icon" />
      {children}
    </div>
  );
};

SuccessMsg.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string
};
