import "polyfills";
import { camelCaseReshape } from "utilities";

const methods = ["GET", "POST", "PUT", "PATCH", "DELETE"];
const resources = [
  "Device",
  "DeviceModel",
  "Rule",
  "Sensor",
  "SensorModel",
  "Job",
  "JobType",
  "Scene",
  "SceneFile",
  "Farm",
  "RoleAssignment",
  "RoleDefinition",
  "Alert",
  "ExtendedType",
  "Telemetry",
  "Partner"
];

export const permissions = {
  readAll: resources.map(resource =>
    resource === "Telemetry" ? resource + "_POST" : resource + "_GET"
  ),
  createFarms: ["Farm_POST"],
  editFarms: ["Farm_PUT"],
  deleteFarms: ["Farm_DELETE"],
  addDevices: ["Device_POST"],
  // createDevices: ['CreateDevices'],
  deleteDevices: ["Device_DELETE"],
  updateDevices: ["Device_PUT"],
  deleteSensors: ["Sensor_DELETE"],
  createMaps: ["Job_POST"],
  createRules: ["Rule_POST"],
  deleteRules: ["Rule_DELETE"],
  updateRules: ["Rule_PUT"],
  addUser: ["RoleAssignment_POST"],
  deleteUser: ["RoleAssignment_DELETE"],
  readRoleAssignments: ["RoleAssignment_GET"]
};

export const toUserModel = (user = {}) =>
  camelCaseReshape(user, {
    id: "id",
    email: "email",
    name: "name",
    roles: "roles",
    permissions: Object.values(permissions) // / 'allowedActions': 'permissions'
  });

// When authentication is disabled, this user has all permissions.
export const authDisabledUser = {
  id: "AuthIsDisabled",
  email: "authdisabled@iot.auth",
  name: "Disabled Auth",
  roles: ["Admin"],
  permissions: Object.values(permissions)
};

export function getAtomicPermissions(permissions) {
  const ALL = "All";
  const atomicPermissions = [];
  permissions.forEach(permission => {
    const expandedActions = permission.actions.includes(ALL)
      ? methods
      : permission.actions;
    const expandedNotActions = permission.notActions.includes(ALL)
      ? methods
      : permission.notActions;
    const expandedResources = permission.resourceTypes.includes(ALL)
      ? resources
      : permission.resourceTypes;

    const allowedActions = expandedActions.filter(
      action => !expandedNotActions.includes(action)
    );

    expandedResources.forEach(resource => {
      allowedActions.forEach(action => {
        atomicPermissions.push(resource + "_" + action);
      });
    });
  });
  return atomicPermissions;
}
