import React, { Component } from "react";

import { PcsGrid } from "components/shared";
import {
  translateColumnDefs,
  caseInsensitiveStringComparator
} from "utilities";
import Flyout from "components/shared/flyout";

import "./accessControl.scss";
import { AddUserContainer as AddUser } from "./assignRole.container";
import { SuccessMessage, AjaxError } from "components/shared";
import { DeleteRoleAssignmentRenderer } from "./deleteRoleAssignmentRenderer";
import { appInsights } from "applicationInsights";

export class AccessControl extends Component {
  constructor(props) {
    super(props);

    this.state = { requestedUserIds: [], error: null, success: null };

    this.setSuccess = this.setSuccess.bind(this);
    this.setError = this.setError.bind(this);
    this.getGridData = this.getGridData.bind(this);
  }

  componentDidMount() {
    const {
      roleAssignmentLastUpdated,
      roleAssignmentFetchPending,
      fetchRoleAssignments,

      roleDefinitionLastUpdated,
      roleDefinitionPending,
      fetchRoleDefinitions
    } = this.props;

    if (!roleAssignmentLastUpdated && !roleAssignmentFetchPending) {
      fetchRoleAssignments();
    }
    if (!roleDefinitionLastUpdated && !roleDefinitionPending) {
      fetchRoleDefinitions();
    }

    appInsights.trackEvent({ name: "UiEvent:FlyOpen: Access Control" });
  }

  componentWillUnmount() {
    appInsights.trackEvent({ name: "UiEvent:FlyClose: Access Control" });
  }

  componentDidUpdate(prevProps) {
    const { userPending, roleAssignments, fetchUsers, users } = this.props;

    // If users are not being fetched and we have non-empty role assignments list
    if (!userPending && roleAssignments.length > 0) {
      // Find out the ids for the users that we don't have resolutions for,
      // and we have not requested resolutions for
      // Resolution => get name and other details from graph endpoint
      const reqdUserIds = roleAssignments
        .filter(roleAssignment => roleAssignment.objectIdType === "UserId")
        .filter(
          roleAssignment =>
            roleAssignment.tenantId === global.DeploymentConfig.aad.tenant
        )
        .map(roleAssignment => roleAssignment.objectId)
        .filter(
          objectId =>
            !users.map(user => user.id).includes(objectId) &&
            !this.state.requestedUserIds.includes(objectId)
        );

      // If there are ids we need to resolve, dispatch the action
      // and append to the list of requested user ids
      if (reqdUserIds.length > 0) {
        fetchUsers(reqdUserIds);
        this.setState({
          requestedUserIds: this.state.requestedUserIds.concat(reqdUserIds)
        });
      }
    }
  }

  setSuccess(success) {
    this.setState({ success, error: null });
  }

  setError(error) {
    this.setState({ error, success: null });
  }

  onFlyoutClose = eventName => {
    this.props.clearRolesError();
    this.props.onClose();
  };

  getSoftSelectId = ({ id } = "") => id;

  getGridData() {
    const {
      users,
      userError,
      userPending,
      roleAssignments,
      roleAssignmentFetchError,
      roleAssignmentFetchPending,
      roleDefinitions,
      roleDefinitionError,
      roleDefinitionPending
    } = this.props;
    if (
      !userError &&
      !roleDefinitionError &&
      !roleAssignmentFetchError &&
      !userPending &&
      !roleAssignmentFetchPending &&
      !roleDefinitionPending &&
      users &&
      users.length > 0 &&
      roleAssignments &&
      roleAssignments.length > 0 &&
      roleDefinitions &&
      roleDefinitions.length > 0
    ) {
      // Keeping only UserId RoleAssingments form the current tenant
      const userRoleAssignments = roleAssignments
        .filter(roleAssignment => roleAssignment.objectIdType === "UserId")
        .filter(
          roleAssignment =>
            roleAssignment.tenantId === global.DeploymentConfig.aad.tenant
        );

      const gridData = userRoleAssignments
        .map(roleAssignment => {
          const user = users.find(user => user.id === roleAssignment.objectId);
          if (!user) {
            return null;
          }
          const roleDefinition = roleDefinitions.find(
            roleDefinition =>
              roleDefinition.id === roleAssignment.roleDefinitionId
          );
          const returnObject = {
            emailOrUpn: user.mail ? user.mail : user.userPrincipalName,
            role: roleDefinition.name,

            roleAssignment: roleAssignment
          };
          return returnObject;
        })
        .filter(data => data !== null);

      const skippedRoleAssignments =
        userRoleAssignments.length - gridData.length;
      if (skippedRoleAssignments > 0) {
        console.warn("User role assignments skipped: ", skippedRoleAssignments); // eslint-disable-line no-console
      }

      return gridData;
    }
  }

  render() {
    const {
      t,
      deleteRoleAssignmentsById,
      userError,
      roleAssignmentFetchError,
      roleAssignmentAddError,
      roleAssignmentDeleteError,
      roleDefinitionError,
      clearRolesError,
      roleAssignmentAddPending,
      roleAssignmentDeletePending
    } = this.props;
    const { success, error } = this.state;

    this.columnDefs = [
      {
        headerName: "users.manageUsersFlyout.rolesGridSection.colHeaders.email",
        field: "emailOrUpn",
        width: 150,
        sort: "asc",
        comparator: caseInsensitiveStringComparator
      },
      {
        field: "role",
        headerName: "users.manageUsersFlyout.rolesGridSection.colHeaders.role",
        width: 50
      },
      {
        cellRendererFramework: DeleteRoleAssignmentRenderer,
        width: 45,
        cellRendererParams: {
          deleteRoleAssignmentsById,
          clearRolesError,
          successCallback: this.setSuccess,
          errorCallback: this.setError
        },
        suppressSorting: true
      }
    ];
    const rowData = this.getGridData();
    const gridProps = {
      ...this.defaulGridProps,
      columnDefs: translateColumnDefs(t, this.columnDefs),
      sizeColumnsToFit: true,
      getSoftSelectId: this.getSoftSelectId,
      rowData: rowData,
      ...this.props,
      deltaRowDataMode: true,
      enableSorting: true,
      unSortIcon: true,
      getRowNodeId: rowData => rowData.roleAssignment.id,
      context: {
        t: t
      }
    };

    const dataFetchError =
      userError || roleDefinitionError || roleAssignmentFetchError;

    const onActionError =
      error || roleAssignmentAddError || roleAssignmentDeleteError;

    const actionPending =
      roleAssignmentAddPending || roleAssignmentDeletePending;

    return (
      <Flyout.Container>
        <Flyout.Header>
          <Flyout.Title>{t("users.manageUsersFlyout.title")}</Flyout.Title>
          <Flyout.CloseBtn
            onClick={this.onFlyoutClose.bind(this, "Users_TopXClose_Click")}
          />
        </Flyout.Header>
        {!dataFetchError && (
          <div className="manage-users">
            <AddUser
              successCallback={this.setSuccess}
              errorCallback={this.setError}
            />

            <Flyout.Section.Container collapsable={false}>
              <Flyout.Section.Header>
                {t("users.manageUsersFlyout.rolesGridSection.header")}
              </Flyout.Section.Header>
            </Flyout.Section.Container>

            {/* Don't try to move the grid into a Flyout.Section.Content. The grid disappears and I don't know why. */}
            <div className="add-device-grid">
              <PcsGrid key="farm-grid-key" {...gridProps} />
            </div>
            {!actionPending && (success || onActionError) && (
              <Flyout.Section.Container
                collapsable={false}
                className="error-section"
              >
                <Flyout.Section.Content>
                  {!onActionError && (
                    <SuccessMessage
                      className="add-role-assignment-success"
                      t={t}
                      successMsg={success}
                    />
                  )}
                  {onActionError && <AjaxError t={t} error={onActionError} />}
                </Flyout.Section.Content>
              </Flyout.Section.Container>
            )}
          </div>
        )}

        {!!dataFetchError && (
          <div className="manage-error-section">
            <AjaxError error={dataFetchError} t={t} />
          </div>
        )}
      </Flyout.Container>
    );
  }
}
