import { connect } from "react-redux";
import PropTypes from "prop-types";
import { withNamespaces } from "react-i18next";
import TelemetryPanel from "./telemetryPanel";
import { getTheme } from "store/reducers/appReducer";
import {
  epics as deviceEpics,
  getDevices,
  getDevicesError,
  getDevicesLastUpdated,
  getDevicesPendingStatus
} from "store/reducers/devicesReducer";
import {
  epics as sensorEpics,
  getSensors,
  getSensorError,
  getSensorPendingStatus,
  getSensorLastUpdated,
  getSensorModels,
  getSensorModelsError,
  getSensorModelLastUpdated,
  getSensorModelPendingStatus
} from "store/reducers/sensorsReducer";

const mapStateToProps = (state, props) => ({
  theme: getTheme(state),

  devices: getDevices(state),
  devicesError: getDevicesError(state),
  devicesLastUpdated: getDevicesLastUpdated(state),
  isDevicesPending: getDevicesPendingStatus(state),

  sensors: getSensors(state),
  sensorsError: getSensorError(state),
  sensorsLastUpdated: getSensorLastUpdated(state),
  isSensorsPending: getSensorPendingStatus(state),

  sensorModels: getSensorModels(state),
  sensorModelsError: getSensorModelsError(state),
  sensorModelsLastUpdated: getSensorModelLastUpdated(state),
  isSensorModelsPending: getSensorModelPendingStatus(state)
});

// Wrap the dispatch method
const mapDispatchToProps = dispatch => ({
  fetchDevices: () => dispatch(deviceEpics.actions.fetchDevices()),
  fetchSensors: () => dispatch(sensorEpics.actions.fetchSensors()),
  fetchSensorModels: () => dispatch(sensorEpics.actions.fetchSensorModels())
});

export const TelemetryPanelContainer = withNamespaces()(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(TelemetryPanel)
);

TelemetryPanelContainer.propTypes = {
  showHeader: PropTypes.bool.isRequired
};
