import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";

import {
  epics as roleEpics,
  redux as rolesRedux,
  getRoleAssignments,
  getRoleDefinitions,
  getAddRoleAssignmentPendingStatus
} from "store/reducers/rolesReducer";

import { AddUser } from "./assignRole";

const mapStateToProps = state => ({
  roleAssignments: getRoleAssignments(state),
  roleDefinitions: getRoleDefinitions(state),
  roleAssignmentAddPending: getAddRoleAssignmentPendingStatus(state)
});

// Wrap the dispatch method
const mapDispatchToProps = dispatch => ({
  addRoleAssignment: roleAssignment =>
    dispatch(roleEpics.actions.addRoleAssignment(roleAssignment)),
  clearError: () => dispatch(rolesRedux.actions.clearError())
});

export const AddUserContainer = withNamespaces()(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(AddUser)
);
