// import Config from 'app.config';
import { Observable } from "rxjs";
import { authDisabledUser } from "./models";

import { UserAgentApplication } from "msal";

export class AuthService {
  static authContext; // Created on AuthService.initialize()
  static authEnabled = false;

  static initialize() {
    if (typeof global.DeploymentConfig === "undefined") {
      alert(
        "The dashboard configuration is missing.\n\nVerify the content of webui-config.js."
      );
      throw new Error(
        "The global configuration is missing. Verify the content of webui-config.js."
      );
    }

    if (typeof global.DeploymentConfig.authEnabled !== "undefined") {
      AuthService.authEnabled = global.DeploymentConfig.authEnabled;
      if (!AuthService.authEnabled) {
        console.warn("Auth is disabled! (see webui-config.js)"); // eslint-disable-line no-console
      }
    }

    const msalConfig = {
      auth: {
        clientId: global.DeploymentConfig.aad.appId,
        authority:
          global.DeploymentConfig.aad.instance +
          global.DeploymentConfig.aad.tenant,
        redirectUri: window.location.origin
      }
    };

    AuthService.msalInstance = new UserAgentApplication(msalConfig);
    AuthService.msalInstance.handleRedirectCallback((error, response) => {
      // console.log("I don't know what to do here (handleRedirectCallback)");
      // console.log(error);
      // console.log(response);
    });

    AuthService.getCurrentUser();
  }

  static isDisabled() {
    return AuthService.authEnabled === false;
  }

  static isEnabled() {
    return !AuthService.isDisabled();
  }

  static onLoad(successCallback) {
    AuthService.initialize();
    if (AuthService.isDisabled()) {
      console.debug("Skipping Auth onLoad because Auth is disabled"); // eslint-disable-line no-console
      if (successCallback) successCallback();
      return;
    }

    AuthService.doLoginIfNotLoggedInOrLoggingIn();
    if (AuthService.getCurrentUser()) {
      successCallback();
    }
  }

  static doLoginIfNotLoggedInOrLoggingIn() {
    // eslint-disable-next-line no-console
    console.log(
      "Was told to login... Fould already logged in user ",
      AuthService.msalInstance.getAccount(),
      " and login_in_progress is ",
      AuthService.msalInstance.getLoginInProgress()
    );
    if (
      !AuthService.msalInstance.getAccount() &&
      !AuthService.msalInstance.getLoginInProgress()
    ) {
      console.log("Hence, initiating login with redirect"); // eslint-disable-line no-console
      AuthService.msalInstance.loginRedirect({
        scopes: [
          `${global.DeploymentConfig.backendEndpoint}/user_impersonation`,
          "https://graph.microsoft.com/User.ReadBasic.All"
        ]
      });
    }
  }

  static getCurrentUser() {
    if (AuthService.isDisabled()) {
      return Observable.of(authDisabledUser);
    }

    return AuthService.msalInstance.getAccount();
  }

  static logout() {
    if (AuthService.isDisabled()) return;

    AuthService.msalInstance.logout();
  }

  static getAccessTokenApi() {
    return Observable.create(observer => {
      const tokenRequest = {
        scopes: [
          `${global.DeploymentConfig.backendEndpoint}/user_impersonation`
        ]
      };
      return AuthService.msalInstance
        .acquireTokenSilent(tokenRequest)
        .then(response => {
          // console.log("(Acquire Token Silent) Received response: ", response);
          observer.next(response.accessToken);
          observer.complete();
        })
        .catch(error => {
          // eslint-disable-next-line no-console
          console.log(
            `acquireTokenSilent failed with Authentication Error: ${error}`
          );
          console.log("Trying acquireTokenRedirect..."); // eslint-disable-line no-console
          AuthService.msalInstance.acquireTokenRedirect(tokenRequest);
          observer.error(error);
        });
    });
  }

  static getAccessTokenGraph() {
    return Observable.create(observer => {
      const tokenRequest = {
        scopes: ["User.ReadBasic.All"]
      };
      return AuthService.msalInstance
        .acquireTokenSilent(tokenRequest)
        .then(response => {
          // console.log("(Acquire Token Silent) Received response: ", response);
          observer.next(response.accessToken);
          observer.complete();
        })
        .catch(error => {
          // eslint-disable-next-line no-console
          console.log(
            `acquireTokenSilent failed with Authentication Error: ${error}`
          );
          // console.log("Trying acquireTokenRedirect...");
          AuthService.msalInstance.acquireTokenRedirect(tokenRequest);
          observer.error(error);
        });
    });
  }
}
