import React, { Component } from "react";
import {
  Btn,
  ComponentArray,
  ContextMenu,
  PageContent,
  AjaxError,
  ContextMenuAlign,
  PageTitle,
  Protected,
  // RefreshBarContainer as RefreshBar,
  SearchInput
} from "components/shared";
import { permissions } from "services/models";
import { svgs } from "utilities";
import { FarmGrid } from "./farmsGrid";
import { EmptyFarm } from "./emptyFarm/emptyFarm";
import "./farms.scss";
import { NewFarmFlyout } from "./flyouts";

import { reactPlugin } from "applicationInsights";
import { withAITracking } from "@microsoft/applicationinsights-react-js";

class FarmsSummary extends Component {
  render() {
    const {
      t,
      farms,
      farmsError,
      isFarmsPending,
      // farmsLastUpdated,
      // fetchFarms,
      history,
      openFlyoutName,
      onCloseFlyout,
      openFlyout,
      onGridReady,
      rowData,
      onContextMenuChange,
      searchOnChange
    } = this.props;
    const gridProps = {
      onGridReady,
      rowData,
      onContextMenuChange,
      t,
      history
    };

    return (
      <ComponentArray>
        <ContextMenu>
          <ContextMenuAlign left={true}>
            <SearchInput
              onChange={searchOnChange}
              placeholder={t("farms.searchPlaceholder")}
            />
            <Protected permissions={permissions.createFarms}>
              <Btn
                svg={svgs.plus}
                className="create-farm"
                onClick={openFlyout("create")}
              >
                {t("farms.flyouts.create.open")}
              </Btn>
            </Protected>
          </ContextMenuAlign>
        </ContextMenu>
        <PageContent className="farms-container">
          {/* <RefreshBar
            refresh={fetchFarms}
            time={farmsLastUpdated}
            isPending={isFarmsPending}
            t={t}
          /> */}
          <PageTitle
            titleValue={t("farms.title").concat("(", String(farms.length), ")")}
          />

          {!isFarmsPending && farms.length === 0 && !farmsError && (
            <EmptyFarm openFlyout={openFlyout("create")} t={t} />
          )}
          {!!farmsError && <AjaxError t={t} error={farmsError} />}
          {(isFarmsPending || farms.length !== 0) && !farmsError && (
            <FarmGrid {...gridProps} />
          )}
          {openFlyoutName === "create" && (
            <NewFarmFlyout onClose={onCloseFlyout} t={t} />
          )}
        </PageContent>
      </ComponentArray>
    );
  }
}

export default withAITracking(reactPlugin, FarmsSummary, "Farms Grid");
