import { camelCaseReshape, getItems } from "utilities";

export const toAzureMapSearchResultModel = (response = {}) =>
  camelCaseReshape(response, {
    summary: "summary",
    results: "results"
  });

export const toFarmsModel = (response = {}) =>
  getItems(response).map(toFarmModel);

export const toFarmModel = (response = {}) =>
  camelCaseReshape(response, {
    id: "id",
    createdAt: "createdAt",
    lastModifiedAt: "lastModifiedAt",
    geometry: "geometry",
    name: "name",
    properties: "properties",
    description: "description"
  });

export const toNewFarmRequestModel = ({
  name,
  geometry,
  crops,
  address,
  description
}) => {
  const properties = {
    crops: crops && crops.value ? crops.value : crops,
    address: address
  };
  return {
    geometry: geometry,
    name: name,
    properties: properties,
    description: description
  };
};
