import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";
import { DeleteRule } from "./deleteRule";
import {
  rulesRedux,
  epics as rulesAlertsEpics
} from "store/reducers/rulesAlertsReducer";

// Wrap the dispatch method
const mapDispatchToProps = dispatch => ({
  modifyRules: rule => dispatch(rulesRedux.actions.modifyRules(rule)),
  deleteRule: rule => dispatch(rulesRedux.actions.deleteRule(rule)),
  fetchRules: () => dispatch(rulesAlertsEpics.actions.fetchRules())
});

export const DeleteRuleContainer = withNamespaces()(
  connect(
    null,
    mapDispatchToProps
  )(DeleteRule)
);
