import React from "react";
import update from "immutability-helper";
import Config from "app.config";
import { getAffectedDevicesCount } from "utilities";
import memoizeOne from "memoize-one";
import {
  AjaxError,
  SuccessMessage,
  Btn,
  BtnToolbar,
  FormControl,
  FormGroup,
  FormLabel,
  PillGroup,
  Radio,
  ToggleBtn
} from "components/shared";
import { ActionEmailSetupContainer } from "./actionEmailSetup.container";
import { SeverityRenderer } from "components/shared/cellRenderers";
import {
  Validator,
  svgs,
  isValidEmail,
  LinkedComponent,
  enabled,
  disabled,
  ruleStatus
} from "utilities";
import Flyout from "components/shared/flyout";
import { TelemetryService } from "services";
import {
  toEditRuleRequestModel,
  ruleCalculations,
  ruleOperators
} from "services/models";
import "./ruleEditor.scss";
import { RuleSummary } from "../shared";
import { appInsights } from "applicationInsights";
const Section = Flyout.Section;
// A counter for creating unique keys per new condition
let conditionKey = 0;
// A counter for creating unique keys per new action
let actionKey = 0;

// Creates a state object for a condition
const newCondition = () => ({
  field: undefined,
  operator: undefined,
  value: undefined,
  key: conditionKey++ // Used by react to track the rendered elements
});

const newAction = () => ({
  type: "Email",
  parameters: {
    recipients: [],
    notes: "",
    subject: ""
  },
  key: actionKey++
});

const healthMonitorRuleId = "default_Health_Monitor";
let isHealthRule = false;
const isAlphaNumericRegex = /^[a-zA-Z0-9_]*$/;
const nonAlphaNumeric = x => !x.match(isAlphaNumericRegex);

// A state object for a new rule
const newRule = () => ({
  name: "",
  description: "",
  groupId: "",
  calculation: "",
  timePeriod: "",
  farmId: "",
  conditions: [newCondition()], // Start with one condition
  actions: [newAction()], // Start with one action
  severityLevel: Config.ruleSeverity.critical,
  status: ruleStatus.enabled,
  actionEnabled: false
});

const newInitialState = () => ({
  error: undefined,
  fieldOptions: [],
  sensorMeasuresForCurrentFarm: [],
  fieldQueryPending: true,
  formData: newRule(),
  newEmail: "",
  isPending: false,
  changesApplied: false,
  isNewRuleSucceeded: undefined,
  isEditedRuleSucceeded: undefined,
  editableRule: undefined
});

export class RuleEditor extends LinkedComponent {
  constructor(props) {
    super(props);

    if (!this.props.sensorLastUpdated && !this.props.sensorError)
      this.props.fetchSensorModels();

    this.state = newInitialState();
    const { fetchFarms, farmsLastUpdated, farmsError } = this.props;
    if (!farmsLastUpdated && !farmsError) {
      fetchFarms();
    }

    this.newEmailLink = this.linkTo("newEmail") // Matches email address pattern
      .check(
        val => isValidEmail(val),
        () => this.props.t("rules.flyouts.ruleEditor.actions.syntaxError")
      );
    this.affectedDevicesCount = memoizeOne((farmId, conditions) =>
      getAffectedDevicesCount.call(this, farmId, conditions)
    );
  }
  fetchApiData = () => {
    const {
      fetchSensors,
      fetchSensorModels,
      fetchDevices,
      isSensorsPending,
      sensorLastUpdated,
      sensorModelsLastUpdated,
      sensorModelsIsPending,
      devicesLastUpdated,
      isDevicesPending
    } = this.props;
    if (!sensorLastUpdated && !isSensorsPending) fetchSensors();
    if (!sensorModelsLastUpdated && !sensorModelsIsPending) fetchSensorModels();
    if (!isDevicesPending && !devicesLastUpdated) fetchDevices();
  };
  componentWillMount() {
    this.fetchApiData();
  }
  componentDidMount() {
    const { rule } = this.props;

    if (rule) {
      this.getDeviceCountAndFields(rule.farmId);
      this.setFormState(rule);
    }
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      changesApplied: false,
      isNewRuleSucceeded: undefined,
      isEditedRuleSucceeded: undefined,
      editableRule: undefined
    });
    const { rule } = nextProps;
    if (rule) {
      this.getDeviceCountAndFields(rule.farmId);
      this.setFormState(rule);
    }
  }
  setFormState = rule =>
    this.setState({
      formData: {
        ...rule,
        conditions: (rule.conditions || []).map(condition => ({
          ...condition,
          key: conditionKey++
        })),
        actions:
          !rule.actions || rule.actions.length === 0
            ? [newAction()]
            : rule.actions.map(action => ({
                ...action,
                key: actionKey++
              })),
        actionEnabled:
          rule.actions === undefined
            ? false
            : (rule.actions || []).length > 0
            ? true
            : false,
        enabled: rule.status === ruleStatus.enabled ? true : false
      },
      editableRule: rule
    });

  componentWillUnmount() {
    if (this.subscription) this.subscription.unsubscribe();
  }

  toSelectOption = ({ id, name }) => {
    return {
      label: name,
      value: id
    };
  };

  addCondition = () => {
    this.conditionsLink.set([...this.conditionsLink.value, newCondition()]);
  };

  filteredModelMeasures = ({ name }) => {
    return {
      label: name,
      value: name
    };
  };
  deleteCondition = index => () =>
    this.conditionsLink.set(
      this.conditionsLink.value.filter((_, idx) => index !== idx)
    );

  formIsValid() {
    const check = [this.ruleNameLink, this.deviceGroupLink, this.actionsLink];

    if (!isHealthRule) check.push(this.conditionsLink);

    return check.every(link => !link.error);
  }

  apply = event => {
    event.preventDefault();
    const { insertRules, modifyRules, t } = this.props;
    const requestProps = { ...this.state.formData };
    const { newEmail } = this.state;

    // Deafult instant
    requestProps.calculation = ruleCalculations[1];
    if (requestProps.calculation === ruleCalculations[1])
      requestProps.timePeriod = "";

    if (this.state.formData.actionEnabled === false) requestProps.actions = [];
    if (this.formIsValid()) {
      this.setState({ isPending: true, error: null });
      if (this.subscription) this.subscription.unsubscribe();
      if (this.state.formData.actionEnabled) {
        const action = requestProps.actions[0];
        if (newEmail !== "") {
          action.parameters.recipients.push(newEmail);
          this.setState({ newEmail: "" });
        }

        if (action.parameters.subject === "") {
          const ruleName = requestProps.name;
          action.parameters.subject = t(
            "rules.flyouts.ruleEditor.actions.defaultEmailSubject",
            { ruleName }
          );
        }
      }

      if (this.props.rule) {
        // If rule object exist then update the existing rule
        this.subscription = TelemetryService.updateRule(
          this.props.rule.id,
          toEditRuleRequestModel(requestProps)
        ).subscribe(
          updatedRule => {
            const editableRule = {
              ...this.state.editableRule,
              ...requestProps
            };
            modifyRules([{ ...editableRule }]);
            this.setState({
              isPending: false,
              changesApplied: true,
              isEditedRuleSucceeded: true
            });
          },
          error => {
            this.setState({
              error,
              isPending: false,
              changesApplied: true,
              isEditedRuleSucceeded: false
            });
          }
        );
      } else {
        // If rule object doesn't exist then create a new rule
        this.subscription = TelemetryService.createRule(
          toEditRuleRequestModel(requestProps)
        ).subscribe(
          createdRule => {
            insertRules([createdRule]);
            this.setState({
              isPending: false,
              changesApplied: true,
              isNewRuleSucceeded: true
            });
          },
          error =>
            this.setState({
              error,
              isPending: false,
              changesApplied: true,
              isNewRuleSucceeded: false
            })
        );
      }
    }
  };

  onGroupIdChange = ({
    target: {
      value: { value = {} }
    }
  }) => {
    this.setState(prevState => ({
      sensorMeasuresForCurrentFarm: [],
      fieldQueryPending: true,
      isPending: true,
      formData: { ...prevState.formData, conditions: [newCondition()] }
    }));
    this.getDeviceCountAndFields(value);
    this.formControlChange();
  };

  onAddEmail = link => e => {
    if (e.key === "Enter") {
      e.preventDefault();
      if (!this.newEmailLink.error && this.newEmailLink.value !== "") {
        const newState = update(this.state, {
          // Saves user entered email value into action object's list of emails
          ...link.setter({
            $set: [...link.value, this.newEmailLink.value]
          }),
          // Clears new email input textfield of user's entered email value
          ...this.newEmailLink.setter({ $set: "" })
        });
        this.setState(newState);
      }
    }
    this.formControlChange();
  };

  deleteEmail = index => link => () =>
    link.set(link.value.filter((_, idx) => index !== idx));

  getDeviceCountAndFields(farmId) {
    // Filter devices by farmId
    const devices = this.props.devices.filter(
      device => device.farmId === farmId
    );
    // Filter sensors by devices on the farm
    const deviceIds = devices.map(device => device.id);
    const sensors = this.props.sensors.filter(sensor =>
      deviceIds.includes(sensor.deviceId)
    );
    // Filter sensorModelIds on the farm
    const sensorModelIds = sensors.map(sensor => sensor.sensorModelId);
    // Get sensorModels by filtered SensorModelIds
    const sensorModels = this.props.sensorModels.filter(sensorModel =>
      sensorModelIds.includes(sensorModel.id)
    );
    // Get list of unique sensor measures from the sensorModels
    const measureNamesAlreadyAdded = new Set();
    const uniqueSensorMeasures = sensorModels
      .flatMap(sensorModel => sensorModel.sensorMeasures)
      .map(measure => {
        if (!measureNamesAlreadyAdded.has(measure.name)) {
          measureNamesAlreadyAdded.add(measure.name);
          return measure;
        }
        return undefined;
      })
      .filter(measure => measure !== undefined);

    // Set the state
    this.setState({
      sensorMeasuresForCurrentFarm: uniqueSensorMeasures,
      fieldQueryPending: false,
      isPending: false
    });
    this.formControlChange();
  }

  getConditionFields(devices, modules) {
    const layoutList = [];

    devices
      .filter(device => device.farmId !== undefined && device.farmId !== null)
      .forEach(device => {
        const Matches = this.props.sensor.filter(
          sensor => sensor.hardwareId === device.id
        );

        const sensorMeasure = this.props.sensortype;
        if (sensorMeasure !== "undefined") {
          Matches.forEach(function(match) {
            sensorMeasure.forEach(function(measure) {
              if (match.sensorTypeId === measure.id) {
                if (
                  measure.sensorMeasures !== undefined ||
                  measure.sensorMeasures !== null
                )
                  for (
                    let index = 0;
                    index < measure.sensorMeasures.length;
                    index++
                  ) {
                    layoutList.push(measure.sensorMeasures[index].name);
                  }
              }
            });
          });
        }
      });
    const conditions = new Set(); // Using a set to avoid searching the array multiple times in the every
    layoutList.forEach(layoutId => {
      conditions.add(layoutId);
    });
    modules.forEach(({ moduleFields = {} }) => {
      Object.keys(moduleFields).forEach(field => {
        conditions.add(field);
      });
    });
    return [...conditions.values()].map(field => ({
      label: field,
      value: field
    }));
  }

  // todo toggle button didn't support link
  onStatusToggle = ({ target: { value } }) => {
    this.setState({
      formData: { ...this.state.formData, status: value ? enabled : disabled }
    });
    this.formControlChange();
  };

  onCalculationChange = ({
    target: {
      value: { value = {} }
    }
  }) => {
    this.formControlChange();
  };

  onFieldChange = (
    index,
    {
      target: {
        value: { value = {} }
      }
    }
  ) => {
    // var eventProperties = { 'FieldChosen': value, 'ConditionNumber': index };
    this.formControlChange();
  };
  onOperatorChange = (
    index,
    {
      target: {
        value: { value = {} }
      }
    }
  ) => {
    // var eventProperties = { 'OperatorChosen': value, 'ConditionNumber': index };
    this.formControlChange();
  };

  onSeverityChange = ({ target: { value } }) => {
    this.formControlChange();
  };
  onCloseClick = () => {
    const { onClose } = this.props;
    // const rule = { ...this.state.formData };
    onClose();
  };

  formControlChange = () => {
    if (this.state.changesApplied) {
      this.setState({ changesApplied: false });
    }
  };

  deletePill = link => index => () => {
    link.set(link.value.filter((_, idx) => index !== idx));
    this.formControlChange();
  };

  onActionToggle = ({ target: { value } }) => {
    this.setState({
      formData: { ...this.state.formData, actionEnabled: value }
    });
    this.formControlChange();
  };

  render() {
    const { t, rule } = this.props;
    const {
      changesApplied,
      error,
      fieldQueryPending,
      formData,
      isPending,
      isNewRuleSucceeded,
      isEditedRuleSucceeded
    } = this.state;
    const healthMonitorId = (rule || {}).id;
    isHealthRule = healthMonitorId === healthMonitorRuleId;
    /* const calculationOptions = ruleCalculations.map(value => ({
      label: t(`rules.flyouts.ruleEditor.calculationOptions.${value.toLowerCase()}`),
      value
    }));
    */
    const affectedDevicesCount = this.affectedDevicesCount(
      formData.farmId,
      formData.conditions
    );
    //       )
    const listSKU = this.props.farms.map(this.toSelectOption);

    const filteredSensors = this.state.sensorMeasuresForCurrentFarm.map(
      this.filteredModelMeasures
    );

    const requiredValidator = new Validator().check(
      Validator.notEmpty,
      t("rules.flyouts.ruleEditor.validation.required")
    );
    // State links
    this.formDataLink = this.linkTo("formData");

    this.ruleNameLink = this.formDataLink
      .forkTo("name")
      .withValidator(requiredValidator)
      .reject(nonAlphaNumeric)
      .check(Validator.notEmpty, () =>
        this.props.t("rules.flyouts.ruleEditor.errorMsg.fieldCantBeEmpty")
      )
      .check(Validator.noSpaces, () =>
        this.props.t("rules.flyouts.ruleEditor.errorMsg.spaceNotAllowed")
      )
      .check(Validator.charlength, () =>
        this.props.t("rules.flyouts.ruleEditor.errorMsg.maxlength")
      )
      .check(Validator.notEmpty, () => t("farms.errorMsg.required"))
      .check(
        value => (this.props ? Validator.maxlength(value, 1000) : true),
        () => t("farms.errorMsg.maxLengthAddress")
      )
      .check(Validator.minlength, () => t("farms.errorMsg.minLength"));

    this.descriptionLink = this.formDataLink.forkTo("description");
    this.deviceGroupLink = this.formDataLink
      .forkTo("farmId")
      .map(({ value }) => value)
      .withValidator(requiredValidator);
    /* this.calculationLink = this.formDataLink.forkTo('calculation').map(({ value }) => value).withValidator(requiredValidator);

    this.timePeriodLink = this.formDataLink.forkTo('timePeriod')
      .map(({ value }) => value)
      .check(
        timePeriod => this.calculationLink.value === ruleCalculations[0] ? Validator.notEmpty(timePeriod) : true,
        this.props.t('rules.flyouts.ruleEditor.validation.required')
      );
    */
    this.conditionsLink = this.formDataLink
      .forkTo("conditions")
      .withValidator(requiredValidator);
    this.actionsLink = this.formDataLink
      .forkTo("actions")
      .withValidator(requiredValidator);
    this.severityLink = this.formDataLink.forkTo("severityLevel");
    // todo toggle button didn't support link
    this.enabledLink = this.formDataLink.forkTo("enabled");
    // Create the state link for the dynamic form elements
    const conditionLinks = this.conditionsLink.getLinkedChildren(
      conditionLink => {
        const fieldLink = conditionLink
          .forkTo("field")
          .map(({ value }) => value)
          .withValidator(requiredValidator);
        const operatorLink = conditionLink
          .forkTo("operator")
          .map(({ value }) => value)
          .withValidator(requiredValidator);
        const valueLink = conditionLink
          .forkTo("value")
          .check(Validator.notEmpty, () =>
            this.props.t("rules.flyouts.ruleEditor.validation.required")
          )
          .check(
            val => !isNaN(val),
            t("rules.flyouts.ruleEditor.validation.nan")
          );
        const error = fieldLink.error || operatorLink.error || valueLink.error;

        return { fieldLink, operatorLink, valueLink, error };
      }
    );

    const actionLinks = this.actionsLink.getLinkedChildren(actionLink => {
      const parametersLink = actionLink.forkTo("parameters");
      // recipients is valid if email input box is empty and at least one email has been
      // entered, or if email input box has a valid email
      const recipientsLink = parametersLink
        .forkTo("recipients")
        .check(
          val =>
            (this.state.newEmail !== "" && isValidEmail(this.state.newEmail)) ||
            (val.length > 0 && isValidEmail(this.state.newEmail)),
          this.props.t("rules.flyouts.ruleEditor.validation.required")
        );
      const notesLink = parametersLink.forkTo("notes");
      const subjectLink = parametersLink.forkTo("subject");
      const error = formData.actionEnabled ? recipientsLink.error : false;
      return { recipientsLink, notesLink, subjectLink, error };
    });

    const conditionsHaveErrors = conditionLinks.some(({ error }) => error);
    const actionsHaveErrors = actionLinks.some(({ error }) => error);

    const operatorOptions = ruleOperators.map(value => ({
      label: t(`rules.flyouts.ruleEditor.condition.operatorOptions.${value}`),
      value
    }));
    return (
      <form onSubmit={this.apply} className="new-rule-flyout-container">
        <Section.Container className="rule-property-container">
          <Section.Content>
            <FormGroup>
              <FormLabel isRequired="true">
                {t("rules.flyouts.ruleEditor.ruleName")}
              </FormLabel>
              <FormControl
                type="text"
                className="long"
                maxLength={26}
                placeholder={t("rules.flyouts.ruleEditor.namePlaceholder")}
                onChange={this.formControlChange}
                link={this.ruleNameLink}
                disabled={isHealthRule}
              />
            </FormGroup>
            <FormGroup>
              <FormLabel>{t("rules.flyouts.ruleEditor.description")}</FormLabel>
              <FormControl
                type="textarea"
                placeholder={t(
                  "rules.flyouts.ruleEditor.descriptionPlaceholder"
                )}
                onChange={this.formControlChange}
                link={this.descriptionLink}
                disabled={isHealthRule}
              />
            </FormGroup>
            <FormGroup className="farm-dropdown">
              <FormLabel isRequired="true">
                {t("rules.flyouts.ruleEditor.deviceGroup")}
              </FormLabel>
              <FormControl
                type="select"
                className="long"
                options={listSKU}
                onChange={this.onGroupIdChange}
                clearable={false}
                searchable={true}
                matchProp={"label"}
                placeholder={t(
                  "rules.flyouts.ruleEditor.deviceGroupPlaceholder"
                )}
                link={this.deviceGroupLink}
                disabled={isHealthRule}
              />
            </FormGroup>
          </Section.Content>
        </Section.Container>
        {!fieldQueryPending && (
          <div>
            {!isHealthRule &&
              conditionLinks.map((condition, idx) => (
                <Section.Container key={formData.conditions[idx].key}>
                  <Section.Header>
                    {t("rules.flyouts.ruleEditor.condition.condition")}{" "}
                    {idx + 1}
                  </Section.Header>
                  <Section.Content>
                    <FormGroup className="farm-dropdown">
                      <FormLabel isRequired="true">
                        {t("rules.flyouts.ruleEditor.condition.field")}
                      </FormLabel>
                      <FormControl
                        type="select"
                        className="long"
                        placeholder={t(
                          "rules.flyouts.ruleEditor.condition.fieldPlaceholder"
                        )}
                        onChange={target => this.onFieldChange(idx + 1, target)}
                        link={condition.fieldLink}
                        options={filteredSensors}
                        clearable={false}
                        searchable={true}
                        matchProp={"label"}
                      />
                    </FormGroup>

                    <FormGroup className="farm-dropdown">
                      <FormLabel isRequired="true">
                        {t("rules.flyouts.ruleEditor.condition.operator")}
                      </FormLabel>
                      <FormControl
                        type="select"
                        className="long"
                        placeholder={t(
                          "rules.flyouts.ruleEditor.condition.operatorPlaceholder"
                        )}
                        onChange={target =>
                          this.onOperatorChange(idx + 1, target)
                        }
                        link={condition.operatorLink}
                        options={operatorOptions}
                        clearable={false}
                        searchable={true}
                        matchProp={"label"}
                      />
                    </FormGroup>
                    <FormGroup>
                      <FormLabel isRequired="true">
                        {t("rules.flyouts.ruleEditor.condition.value")}
                      </FormLabel>
                      <FormControl
                        type="text"
                        placeholder={t(
                          "rules.flyouts.ruleEditor.condition.valuePlaceholder"
                        )}
                        onChange={this.formControlChange}
                        link={condition.valueLink}
                      />
                    </FormGroup>
                    {conditionLinks.length > 1 && (
                      <Btn
                        className="padded-top"
                        svg={svgs.trash}
                        onClick={this.deleteCondition(idx)}
                      >
                        {t("rules.flyouts.ruleEditor.delete")}
                      </Btn>
                    )}
                  </Section.Content>
                </Section.Container>
              ))}
            {!isHealthRule && (
              <Section.Container collapsable={false}>
                <Section.Header>
                  <Btn
                    svg={svgs.plus}
                    className="add-condition"
                    onClick={this.addCondition}
                  >
                    {t("rules.flyouts.ruleEditor.addCondition")}
                  </Btn>
                </Section.Header>
              </Section.Container>
            )}
            <Section.Container collapsable={false}>
              <Section.Header>
                {t("rules.flyouts.ruleEditor.severityLevel")}
              </Section.Header>
              <Section.Content>
                <FormGroup>
                  <Radio
                    onChange={this.onSeverityChange}
                    link={this.severityLink}
                    value={Config.ruleSeverity.critical}
                  >
                    <FormLabel>
                      <SeverityRenderer
                        value={Config.ruleSeverity.critical}
                        context={{ t }}
                      />
                    </FormLabel>
                  </Radio>
                  <Radio
                    onChange={this.onSeverityChange}
                    link={this.severityLink}
                    value={Config.ruleSeverity.warning}
                  >
                    <FormLabel>
                      <SeverityRenderer
                        value={Config.ruleSeverity.warning}
                        context={{ t }}
                      />
                    </FormLabel>
                  </Radio>
                  <Radio
                    onChange={this.onSeverityChange}
                    link={this.severityLink}
                    value={Config.ruleSeverity.info}
                  >
                    <FormLabel>
                      <SeverityRenderer
                        value={Config.ruleSeverity.info}
                        context={{ t }}
                      />
                    </FormLabel>
                  </Radio>
                </FormGroup>
              </Section.Content>
              <Section.Container collapsable={false}>
                <Section.Header>
                  {t("rules.flyouts.ruleEditor.actions.action")}
                </Section.Header>
                <div className="email-toggle-container">
                  <ToggleBtn
                    value={formData.actionEnabled}
                    onChange={this.onActionToggle}
                  >
                    {formData.actionEnabled
                      ? t("rules.flyouts.ruleEditor.actions.on")
                      : t("rules.flyouts.ruleEditor.actions.off")}
                  </ToggleBtn>
                </div>
                {formData.actionEnabled && <ActionEmailSetupContainer />}
                <Section.Content>
                  {formData.actionEnabled &&
                    actionLinks.map((action, idx) => (
                      <Section.Content key={formData.actions[idx].key}>
                        <p className="padded-top">
                          {t("rules.flyouts.ruleEditor.actions.emailAddresses")}
                        </p>
                        <FormGroup>
                          <FormControl
                            type="text"
                            className="long"
                            onKeyPress={this.onAddEmail(action.recipientsLink)}
                            link={this.newEmailLink}
                            placeholder={t(
                              "rules.flyouts.ruleEditor.actions.enterEmail"
                            )}
                          />
                        </FormGroup>
                        <PillGroup
                          pills={action.recipientsLink.value}
                          svg={svgs.cancelX}
                          onSvgClick={this.deletePill(action.recipientsLink)}
                          altSvgText={t(
                            "rules.flyouts.ruleEditor.actions.deleteEmail"
                          )}
                        />
                        <p className="padded-top">
                          {t("rules.flyouts.ruleEditor.actions.emailSubject")}
                        </p>
                        <FormGroup>
                          <FormControl
                            type="textarea"
                            link={action.subjectLink}
                            placeholder={t(
                              "rules.flyouts.ruleEditor.actions.enterEmailSubject"
                            )}
                            onChange={this.formControlChange}
                          />
                        </FormGroup>
                        <p className="padded-top">
                          {t("rules.flyouts.ruleEditor.actions.emailComments")}
                        </p>
                        <FormGroup>
                          <FormControl
                            type="textarea"
                            link={action.notesLink}
                            placeholder={t(
                              "rules.flyouts.ruleEditor.actions.enterEmailComments"
                            )}
                            onChange={this.formControlChange}
                          />
                        </FormGroup>
                      </Section.Content>
                    ))}
                </Section.Content>
              </Section.Container>
              <Section.Container>
                <Section.Header>
                  {t("rules.flyouts.ruleEditor.ruleStatus")}
                </Section.Header>
                <Section.Content>
                  <FormGroup>
                    <ToggleBtn
                      value={formData.status === enabled}
                      onChange={this.onStatusToggle}
                    >
                      {formData.status === enabled
                        ? t("rules.flyouts.ruleEditor.ruleEnabled")
                        : t("rules.flyouts.ruleEditor.ruleDisabled")}
                    </ToggleBtn>
                  </FormGroup>
                </Section.Content>
              </Section.Container>
            </Section.Container>
          </div>
        )}

        <RuleSummary devicesAffected={affectedDevicesCount} t={t} />
        {isNewRuleSucceeded === true && (
          <SuccessMessage
            className="rule-editor-success"
            t={t}
            successMsg={t("rules.flyouts.ruleEditor.newRuleSuccess")}
          />
        )}
        {isEditedRuleSucceeded === true && (
          <SuccessMessage
            className="rule-editor-success"
            t={t}
            successMsg={t("rules.flyouts.ruleEditor.editRuleSucceess")}
          />
        )}

        {error && (
          <AjaxError className="rule-editor-error" t={t} error={error} />
        )}
        {
          <BtnToolbar>
            {!changesApplied && (
              <Btn
                primary={true}
                disabled={
                  isPending ||
                  !this.formIsValid() ||
                  conditionsHaveErrors ||
                  actionsHaveErrors
                }
                type="submit"
                onClick={() => {
                  appInsights.trackEvent(
                    {
                      name:
                        "UiEvent:Click: " +
                        (this.props.rule ? "Edit" : "New") +
                        " Rule Btn"
                    },
                    {
                      context: {
                        conditions: this.state.formData.conditions,
                        actionsLength: this.state.formData.actions.length
                      }
                    }
                  );
                }}
                id={
                  isPending ||
                  !this.formIsValid() ||
                  conditionsHaveErrors ||
                  actionsHaveErrors
                    ? "applyRuleBtnDisabled"
                    : "applyRuleBtn"
                }
              >
                {t("rules.flyouts.ruleEditor.apply")}
              </Btn>
            )}
            {/* <Btn svg={svgs.cancelX} onClick={this.onCloseClick}>
                     {t("rules.flyouts.ruleEditor.cancel")}
                   </Btn> */}
          </BtnToolbar>
        }
      </form>
    );
  }
}
