import PropTypes from "prop-types";
import { RuleSummaryAuto } from "./ruleSummaryAuto";
import { withNamespaces } from "react-i18next";
import { connect } from "react-redux";
import { getDevices } from "store/reducers/devicesReducer";
import { getSensors, getSensorModels } from "store/reducers/sensorsReducer";
const mapStateToProps = state => ({
  devices: getDevices(state),
  sensors: getSensors(state),
  sensorModels: getSensorModels(state)
});

export const RuleSummaryAutoContainer = withNamespaces()(
  connect(mapStateToProps)(RuleSummaryAuto)
);

RuleSummaryAutoContainer.propTypes = {
  rule: PropTypes.object,
  farmId: PropTypes.string
};
