import React from "react";
import {
  Flyout,
  FlyoutHeader,
  FlyoutTitle,
  FlyoutCloseBtn,
  FlyoutContent
} from "components/shared/flyout";
import { Observable } from "rxjs";
import moment from "moment";
import { dateFormat } from "utilities";
import { LinkedComponent } from "utilities";
import { ManualAssociate } from "./manualAssociate";
import {
  Btn,
  // ContextMenuAlign,
  AjaxError,
  SuccessMessage,
  BtnToolbar,
  Indicator
} from "components/shared";
import { deviceService } from "services";
import { toNewDeviceRequestModel } from "services/models/deviceModels";

import "./associateDevices.scss";
import { isArray } from "util";
import { appInsights } from "applicationInsights";

const MANUAL = "manualAssociate";
// const RECOMMENDED = "recommended";

const initialState = {
  selectedTab: MANUAL,
  associateDeviceError: undefined,
  changesApplied: undefined,
  isAssociateDevicePending: undefined,
  deviceFarmMapping: undefined,
  isAssociateDevicesSuccessful: undefined
};

export class AssociateDevices extends LinkedComponent {
  constructor(props) {
    super(props);

    this.state = initialState;

    const {
      deviceLastUpdated,
      deviceError,
      fetchDevices,

      deviceModelLastUpdated,
      deviceModelError,
      fetchDeviceModel,

      farmLastUpdated,
      farmsError,
      fetchFarms
    } = this.props;

    if (!deviceLastUpdated && !deviceError) fetchDevices();
    if (!farmLastUpdated && !farmsError) fetchFarms();
    if (!deviceModelLastUpdated && !deviceModelError) fetchDeviceModel();

    this.onClickTabChange = this.onClickTabChange.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.rowData !== nextProps.rowData) {
      this.setState({
        isAssociateDevicesSuccessful: undefined,
        isAssociateDevicePending: undefined,
        associateDeviceError: undefined,
        changesApplied: undefined
      });
    }
  }

  componentWillUnmount() {}

  apply = event => {
    appInsights.trackEvent({ name: "UiEvent:Click: Associate Devices Btn" });
    event.preventDefault();
    const { modifyDevices, devices } = this.props;
    const { deviceFarmMapping } = this.state;
    this.setState({
      isAssociateDevicePending: true,
      associateDeviceError: null
    });
    Observable.from(deviceFarmMapping)
      .mergeMap(selectedDevice => {
        const editableDevice = devices.find(
          device => device.id === selectedDevice.deviceId
        );
        editableDevice.farmId = selectedDevice.farmId;
        const requestProps = JSON.parse(JSON.stringify(editableDevice));
        return deviceService.updateDevice(
          editableDevice.id,
          toNewDeviceRequestModel(requestProps)
        );
      })
      .mergeMap(device => {
        return deviceService.getDeviceById(device.id);
      })
      .subscribe(
        updatedDevice => {
          const { farmsMap, devices, deviceModelsMap } = this.props;
          updatedDevice.deviceModelType = deviceModelsMap.get(
            updatedDevice.deviceModelId
          );
          updatedDevice.deviceModelName = deviceModelsMap.get(
            updatedDevice.deviceModelId
          );
          const deviceMapWithParent = devices.find(
            deviceParent => deviceParent.id === updatedDevice.parentDeviceId
          );
          updatedDevice.parentDevice =
            deviceMapWithParent === undefined
              ? undefined
              : deviceMapWithParent.name;
          updatedDevice.lastModifiedAt = moment(
            updatedDevice.lastModifiedAt
          ).format(dateFormat);
          updatedDevice.farmName = updatedDevice.farmId
            ? farmsMap.get(updatedDevice.farmId)
            : undefined;

          modifyDevices([{ ...updatedDevice }]);
          this.setState({
            isAssociateDevicePending: false,
            changesApplied: true,
            isAssociateDevicesSuccessful: true
          });
        },
        error =>
          this.setState({
            associateDeviceError: error,
            isAssociateDevicePending: false,
            changesApplied: true
          }),
        () =>
          this.setState({
            isAssociateDevicePending: false,
            changesApplied: true,
            confirmStatus: false
          })
      );
  };

  onClickTabChange(selectedTab) {
    this.setState({
      selectedTab: selectedTab
    });
  }

  onDeviceFarmMappingChange = newDeviceMapping => {
    const deviceFarmMapping = [];
    for (const [deviceId, farmId] of newDeviceMapping) {
      deviceFarmMapping.push({
        deviceId,
        farmId
      });
    }
    this.setState({ ...this.state, deviceFarmMapping });
  };

  areDevicesAssigned = () => {
    const { deviceFarmMapping } = this.state;
    let validity = false;
    if (isArray(deviceFarmMapping) && deviceFarmMapping.length !== 0) {
      validity = true;
      deviceFarmMapping.forEach(device => {
        if (!device.farmId) validity = false;
      });
    }
    return validity;
  };

  render() {
    const { onClose, t, farms, rowData } = this.props;
    const {
      selectedTab,
      associateDeviceError,
      changesApplied,
      isAssociateDevicesSuccessful,
      isAssociateDevicePending
    } = this.state;
    const generalProps = {
      t,
      rowData,
      farms,
      onDeviceFarmMappingChange: this.onDeviceFarmMappingChange
    };

    return (
      <div className="associate-device-container">
        <Flyout>
          <FlyoutHeader>
            <FlyoutTitle>
              {t("devices.flyouts.associateDevices.title")}
            </FlyoutTitle>
            <FlyoutCloseBtn onClick={onClose} />
          </FlyoutHeader>
          <FlyoutContent className="associate-device-content ">
            {/*
            TODO change when both tabs are created
            <ContextMenuAlign left={true} className="tab-container">
              <Btn
                className={selectedTab === RECOMMENDED ? "tab active" : "tab"}
                onClick={() => this.onClickTabChange(RECOMMENDED)}
              >
                {t("farms.flyouts.addDevices.recommended")}
              </Btn>
              <Btn
                className={selectedTab === MANUAL ? "tab active" : "tab"}
                onClick={() => this.onClickTabChange(MANUAL)}
              >
                {t("devices.flyouts.associateDevices.manualSelect.header")}
              </Btn>
            </ContextMenuAlign> */}
            {selectedTab === MANUAL && <ManualAssociate {...generalProps} />}
            {/* TODO Add RecommendedDevices component */}
            {/* {selectedTab === RECOMMENDED && <RecommendedDevices {...generalProps} />} */}

            {/** Displays an error message if one occurs while applying changes. */}
            {associateDeviceError && (
              <AjaxError
                className="associate-devices-error"
                t={t}
                error={associateDeviceError}
              />
            )}
            {changesApplied === true &&
              isAssociateDevicesSuccessful === true && (
                <SuccessMessage
                  className="associate-devices-success"
                  t={t}
                  successMsg={t("devices.flyouts.associateDevices.success")}
                />
              )}
            {/** If changes are not yet applied, show the buttons for applying changes and closing the flyout. */
            !changesApplied && !isAssociateDevicePending && (
              <BtnToolbar className="button-box">
                <Btn onClick={onClose} id="cancelBtn">
                  {t("devices.flyouts.associateDevices.cancel")}
                </Btn>
                <Btn
                  primary={true}
                  disabled={!this.areDevicesAssigned()}
                  type="submit"
                  onClick={this.apply}
                  id={
                    !this.areDevicesAssigned() ? "applyBtnDisabled" : "applyBtn"
                  }
                >
                  {t("devices.flyouts.associateDevices.assign")}
                </Btn>
              </BtnToolbar>
            )}
            {isAssociateDevicePending && (
              <BtnToolbar className="button-box">
                <Indicator />
              </BtnToolbar>
            )}
            {/**
             * If changes are applied, show only the close button.
             * Other text or component might be included here as well.
             * For example, you might provide a link to the detail page for a newly submitted job.
             * */
            !!changesApplied && (
              <BtnToolbar>
                {/* <Btn svg={svgs.cancelX} onClick={onClose}>
                  {t("devices.flyouts.associateDevices.close")}
                </Btn> */}
              </BtnToolbar>
            )}
          </FlyoutContent>
        </Flyout>
      </div>
    );
  }
}
