import React, { Component } from "react";
import { permissions /* toDiagnosticsModel*/ } from "services/models";
import { Protected, ProtectedError } from "components/shared";
import { RuleEditorContainer } from "./ruleEditor";
import Flyout from "components/shared/flyout";
import { withAITracking } from "@microsoft/applicationinsights-react-js";
import { reactPlugin, appInsights } from "applicationInsights";

class EditRuleFlyout extends Component {
  onTopXClose = () => {
    const { onClose } = this.props;
    onClose();
  };

  componentDidMount() {
    appInsights.trackEvent({ name: "UiEvent:FlyOpen: Rule Edit" });
  }
  componentWillUnmount() {
    appInsights.trackEvent({ name: "UiEvent:FlyClose: Rule Edit" });
  }
  componentDidUpdate(prevProps) {
    if (prevProps.ruleId !== this.props.ruleId) {
      appInsights.trackEvent({ name: "UiEvent:FlyChange: Rule Edit" });
    }
  }

  render() {
    const { onClose, t, ruleId } = this.props;
    return (
      <Flyout.Container>
        <Flyout.Header>
          <Flyout.Title>{t("rules.flyouts.editRule")}</Flyout.Title>
          <Flyout.CloseBtn onClick={this.onTopXClose} />
        </Flyout.Header>
        <Flyout.Content>
          <Protected permissions={permissions.updateRules}>
            {(hasPermission, permission) =>
              hasPermission ? (
                <RuleEditorContainer onClose={onClose} ruleId={ruleId} />
              ) : (
                <div>
                  <ProtectedError t={t} permissions={permission} />
                  <p>
                    A read-only view will be added soon as part of another PBI.
                  </p>
                </div>
              )
            }
          </Protected>
        </Flyout.Content>
      </Flyout.Container>
    );
  }
}

export default withAITracking(reactPlugin, EditRuleFlyout, "Edit Rule Flyout");
