import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";
import {
  redux as appRedux,
  getAzureMapsKey,
  getTheme,
  getTimeInterval,
  getTimeSeriesExplorerUrl
} from "store/reducers/appReducer";

import {
  epics as jobsEpics,
  getJobTypes,
  getJobTypesError,
  getJobTypesPendingStatus,
  getJobTypesLastUpdated
} from "store/reducers/jobsReducer";

import {
  epics as rulesAlertsEpics,
  getEntities as getRuleEntities,
  getRulesPendingStatus,
  getRulesError
} from "store/reducers/rulesAlertsReducer";
import {
  epics as farmEpics,
  redux as farmRedux,
  getFarms,
  getFarmsError,
  getFarmsLastUpdated,
  getFarmsPendingStatus
} from "store/reducers/farmsReducer";
import {
  epics as deviceEpics,
  getDevices,
  getDevicesError,
  getDevicesLastUpdated,
  getDevicesPendingStatus,
  getDeviceModelError,
  getDeviceModelLastUpdated,
  getDeviceModelsPendingStatus
} from "store/reducers/devicesReducer";

import FarmDetails from "./farmDetails";
import {
  epics as sensorEpics,
  getSensors,
  getSensorError,
  getSensorPendingStatus,
  getSensorLastUpdated
} from "store/reducers/sensorsReducer";

import {
  epics as mapsEpics,
  getMaps,
  getMapsError,
  getMapsLastUpdated,
  getMapsPendingStatus
} from "store/reducers/mapsReducer";

const mapStateToProps = (state, props) => ({
  farms: getFarms(state),
  // farmID:
  // props &&
  // props.location &&
  // props.location.state &&
  // props.location.state.farmName
  //   ? getMaps(state).filter(
  //       map => map.farmName === props.location.state.farmName
  //     )
  //   : getMaps(state),
  farmsError: getFarmsError(state),
  farmsLastUpdated: getFarmsLastUpdated(state),
  isFarmsPending: getFarmsPendingStatus(state),

  devices: getDevices(state),
  devicesError: getDevicesError(state),
  devicesLastUpdated: getDevicesLastUpdated(state),
  isDevicesPending: getDevicesPendingStatus(state),

  deviceModelsError: getDeviceModelError(state),
  isDeviceModelsPending: getDeviceModelsPendingStatus(state),
  deviceModelsLastUpdated: getDeviceModelLastUpdated(state),

  maps: getMaps(state),

  sensors: getSensors(state),
  sensorsError: getSensorError(state),
  sensorsLastUpdated: getSensorLastUpdated(state),
  isSensorsPending: getSensorPendingStatus(state),

  azureMapsKey: getAzureMapsKey(state),
  rules: getRuleEntities(state),
  // rulesById: getRulesByFarmId(state, props),
  // ruleIdsByFarmId : getRulesIDsByFarmId(state, props),
  rulesError: getRulesError(state),
  isRulesPending: getRulesPendingStatus(state),
  // alerts: getAlerts(state, props),

  theme: getTheme(state),
  timeInterval: getTimeInterval(state),
  timeSeriesExplorerUrl: getTimeSeriesExplorerUrl(state),

  mapsError: getMapsError(state),
  isMapsPending: getMapsPendingStatus(state),
  mapsLastUpdated: getMapsLastUpdated(state),

  jobTypes: getJobTypes(state),
  jobTypesError: getJobTypesError(state),
  isJobTypesPending: getJobTypesPendingStatus(state),
  jobTypeslastUpdated: getJobTypesLastUpdated(state)
});

// Wrap the dispatch method
const mapDispatchToProps = dispatch => ({
  fetchJobTypes: () => dispatch(jobsEpics.actions.fetchJobTypes()),
  fetchDeviceModel: () => dispatch(deviceEpics.actions.fetchDeviceModel()),
  fetchFarms: () => dispatch(farmEpics.actions.fetchFarms()),
  fetchDevices: () => dispatch(deviceEpics.actions.fetchDevices()),
  fetchSensors: () => dispatch(sensorEpics.actions.fetchSensors()),
  fetchMaps: () => dispatch(mapsEpics.actions.fetchMaps()),
  fetchRules: () => dispatch(rulesAlertsEpics.actions.fetchRules()),
  updateTimeInterval: timeInterval =>
    dispatch(appRedux.actions.updateTimeInterval(timeInterval)),
  deleteFarm: farmId => dispatch(farmRedux.actions.deleteFarm(farmId))
});

export const FarmDetailsContainer = withNamespaces()(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(FarmDetails)
);
