import { combineReducers } from "redux";

// Reducers
import { reducer as appReducer } from "./reducers/appReducer";
import { reducer as devicesReducer } from "./reducers/devicesReducer";
import { reducer as rulesAlertsReducer } from "./reducers/rulesAlertsReducer";
import { reducer as farmReducer } from "./reducers/farmsReducer";
import { reducer as mapReducer } from "./reducers/mapsReducer";
import { reducer as jobsReducer } from "./reducers/jobsReducer";
import { reducer as sensorsReducer } from "./reducers/sensorsReducer";
import { reducer as userReducer } from "./reducers/userReducer";
import { reducer as roleReducer } from "./reducers/rolesReducer";

const rootReducer = combineReducers({
  ...appReducer,
  ...devicesReducer,
  ...rulesAlertsReducer,
  ...farmReducer,
  ...mapReducer,
  ...jobsReducer,
  ...sensorsReducer,
  ...userReducer,
  ...roleReducer
});

export default rootReducer;
