import React from "react";

export function getDisplayValueFromSuggestion(suggestion, query) {
  if ((suggestion.mail || "").startsWith(query)) {
    return suggestion.mail;
  } else if (suggestion.userPrincipalName.startsWith(query)) {
    return suggestion.userPrincipalName;
  } else {
    return suggestion.mail || suggestion.userPrincipalName;
  }
}

export function suggestionRenderer(suggestion, { query }) {
  const matchingValueFromSuggestion = getDisplayValueFromSuggestion(
    suggestion,
    query
  );
  return (
    <div>
      <div>{suggestion.displayName}</div>
      <div>{matchingValueFromSuggestion}</div>
    </div>
  );
}
