import React, { Component } from "react";
import { isFunc, translateColumnDefs } from "utilities";
import { PcsGrid } from "components/shared";
import {
  associateDeviceColumnDefs,
  defaultAssociateDeviceGridProps
} from "./associateDeviceGridConfig";
import "./associateDeviceGrid.scss";
const initialState = {
  softSelectedId: undefined,
  selectedDevices: undefined
};

export class AssociateDeviceGrid extends Component {
  constructor(props) {
    super(props);

    this.state = initialState;

    this.columnDefs = [
      associateDeviceColumnDefs.deviceName,
      associateDeviceColumnDefs.currentFarm,
      associateDeviceColumnDefs.assignToFarm
    ];

    this.defaulGridProps = { ...defaultAssociateDeviceGridProps };
  }

  onGridReady = gridReadyEvent => {
    this.associateDeviceGridApi = gridReadyEvent.api;
    // Call the onReady props if it exists
    if (isFunc(this.props.onGridReady)) {
      this.props.onGridReady(gridReadyEvent);
    }
  };

  onFarmSelectChange = (data, farmId) => {
    const { onFarmSelectChange } = this.props;
    if (isFunc(onFarmSelectChange)) {
      onFarmSelectChange(data, farmId);
    }
  };

  render() {
    const { t, farms } = this.props;
    const { softSelectedId } = this.state;
    const gridProps = {
      ...this.defaulGridProps,
      columnDefs: translateColumnDefs(t, this.columnDefs),
      sizeColumnsToFit: true,
      softSelectId: softSelectedId,
      ...this.props,
      deltaRowDataMode: true,
      enableSorting: true,
      getRowNodeId: ({ id }) => id,
      unSortIcon: true,
      context: {
        t: t,
        farms: farms,
        onFarmSelectChange: this.onFarmSelectChange
      },
      /* Grid Events */
      onGridReady: this.onGridReady
    };

    return (
      <div className="associate-device-grid">
        <PcsGrid {...gridProps} />
      </div>
    );
  }
}
