import React from "react";
import Flyout from "components/shared/flyout";
import Config from "app.config";
import { LinkedComponent } from "utilities";
import { AccessControlContainer } from "../accessControl";
import { ComponentArray, Protected } from "components/shared";
import { permissions } from "services/models";
import "./settings.scss";

const Section = Flyout.Section;

export class Settings extends LinkedComponent {
  constructor(props) {
    super(props);
    this.state = {
      desiredSimulationState: this.props.isSimulationEnabled,
      diagnosticsOptIn: this.props.diagnosticsOptIn,
      logoFile: undefined,
      applicationName: "",
      loading: false,
      toggledSimulation: false,
      madeLogoUpdate: false,
      username: undefined,
      openFlyout: ""
    };

    this.applicationName = this.linkTo("applicationName").map(value =>
      value.length === 0 ? undefined : value
    );
  }

  openManageUsers = () => this.setState({ openFlyout: "manageUsers" });

  closeFlyout = () => this.setState({ openFlyout: "" });

  componentWillReceiveProps({
    setLogoPending,
    setLogoError,
    diagnosticsOptIn
  }) {
    const { madeLogoUpdate } = this.state;
    this.setState({ diagnosticsOptIn: diagnosticsOptIn });
    if (madeLogoUpdate && !setLogoPending && !setLogoError) {
      this.props.onClose();
    }
  }

  onChange = ({ target }) => {
    const { name, value } = target;
    this.setState({ [name]: value });
  };

  onFlyoutClose = eventName => {
    return this.props.onClose();
  };

  toggleDiagnostics = () => {
    const { diagnosticsOptIn } = this.state;
    this.setState({ diagnosticsOptIn: !diagnosticsOptIn }, () => {
      this.props.updateDiagnosticsOptIn(!diagnosticsOptIn);
    });
  };

  render() {
    const { t } = this.props;
    const { openFlyout } = this.state;

    return (
      <ComponentArray>
        <Flyout.Container>
          <Flyout.Header>
            <Flyout.Title>{t("settingsFlyout.title")}</Flyout.Title>
            <Flyout.CloseBtn
              onClick={this.onFlyoutClose.bind(
                this,
                "Settings_TopXClose_Click"
              )}
            />
          </Flyout.Header>
          <Flyout.Content className="settings-workflow-container">
            <Protected key="edit" permissions={permissions.readRoleAssignments}>
              <Section.Container collapsable={false}>
                <Section.Header>
                  <div
                    onClick={this.openManageUsers}
                    className="cursor-pointer"
                  >
                    {this.props.t("users.manageUsersFlyout.title")}
                  </div>
                </Section.Header>
              </Section.Container>
            </Protected>
            <Section.Container collapsable={false}>
              <Section.Header>
                {t("settingsFlyout.sendDiagnosticsHeader")}
              </Section.Header>
              <Section.Content className="diagnostics-content">
                {this.props.t("settingsFlyout.sendDiagnosticsText")}
                <a
                  href={Config.serviceUrls.privacy}
                  className="privacy-link"
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  {this.props.t(
                    "settingsFlyout.sendDiagnosticsMicrosoftPrivacyUrl"
                  )}
                </a>
              </Section.Content>
            </Section.Container>
            {/* <ApplicationSettingsContainer
              onUpload={this.onUpload}
              applicationNameLink={this.applicationName}
              {...this.props} />
            {
              (madeLogoUpdate && setLogoError) &&
              <div className="set-logo-error">
                {t('settingsFlyout.setLogoError')}
              </div>
            } */}
            {
              // <div className="btn-container">
              // {
              //   !loading && hasChanged &&
              //   <Btn type="submit" /*onClick={this.onFlyoutClose.bind(this, 'Settings_Apply_Click')}*/ className="apply-button">{t('settingsFlyout.apply')}</Btn>
              // }
              // {/* <Btn svg={svgs.x} onClick={this.onFlyoutClose.bind(this, 'Settings_Close_Click')} className="close-button">
              //   {hasChanged ? t('settingsFlyout.cancel') : t('settingsFlyout.close')}</Btn> */}
              // {loading && <Indicator size='small' />}
              // </div>
            }
            <span className="version">
              {t("settingsFlyout.version")}:
              {" v " + global.DeploymentConfig.farmbeatsVersion}
            </span>
          </Flyout.Content>
        </Flyout.Container>

        {openFlyout === "manageUsers" && (
          <AccessControlContainer onClose={this.closeFlyout} />
        )}
      </ComponentArray>
    );
  }
}
