import React from "react";

import { LinkedComponent } from "utilities";
import { FormControl } from "components/shared";
import "../cellRenderer.scss";
import "./farmSelectRenderer.scss";

export class FarmSelectRenderer extends LinkedComponent {
  constructor(props) {
    super(props);
    this.state = {
      selectedFarm: undefined
    };
  }

  componentDidMount() {
    const { value } = this.props;
    if (value) {
      this.setState({ selectedFarm: value });
    }
  }

  refresh() {
    return false;
  }
  onFarmSelect = event => {
    const { context, data } = this.props;
    const selectedFarm = event.target.value.value;
    if (context.onFarmSelectChange) {
      context.onFarmSelectChange(data, selectedFarm);
    }
  };

  toSelectOptions = ({ id, name }) => {
    return {
      label: name,
      value: id
    };
  };

  render() {
    const { context } = this.props;
    const { farms, t } = context;
    const farmOptions = farms.map(this.toSelectOptions);

    this.selectFarmLink = this.linkTo("selectedFarm").map(({ value }) => value);
    return (
      <FormControl
        type="select"
        options={farmOptions}
        className="farm-select-renderer"
        onChange={this.onFarmSelect}
        clearable={false}
        searchable={true}
        placeholder={t("devices.flyouts.associateDevices.selectFarm")}
        link={this.selectFarmLink}
      />
    );
  }
}
