import "rxjs";
import { Observable } from "rxjs";

import moment from "moment";
import { schema, normalize } from "normalizr";
import update from "immutability-helper";

import { createSelector } from "reselect";
import { roleService } from "services";

import {
  createReducerScenario,
  createEpicScenario,
  resetPendingAndErrorReducer,
  errorPendingInitialState,
  pendingReducer,
  errorReducer,
  resetErrorReducer,
  getPending,
  setPending,
  toActionCreator,
  getError
} from "store/utilities";

import { toNewRoleAssignmentRequestModel } from "services/models";

// ========================= Epics - START
const handleError = fromAction => error =>
  Observable.of(
    redux.actions.registerError(fromAction.type, { error, fromAction })
  );

export const epics = createEpicScenario({
  fetchRoleAssignments: {
    type: "ROLEASSIGNMENTS_FETCH",
    epic: fromAction =>
      roleService
        .getRoleAssignments()
        .map(toActionCreator(redux.actions.updateRoleAssignments, fromAction))
        .catch(handleError(fromAction))
  },

  fetchRoleDefinitions: {
    type: "ROLEDEFINITIONS_FETCH",
    epic: fromAction =>
      roleService
        .getRoleDefinitions()
        .map(toActionCreator(redux.actions.updateRoleDefinitions, fromAction))
        .catch(handleError(fromAction))
  },

  deleteRoleAssignmentById: {
    type: "ROLEASSIGNMENT_DELETE",
    epic: fromAction =>
      Observable.of(fromAction.payload.id)
        .flatMap(id => roleService.deleteRoleAssignment(id).map(() => id))
        .map(
          toActionCreator(redux.actions.deleteRoleAssignmentById, fromAction)
        )
        .do(() => fromAction.payload.callbacks.success())
        .catch(error => {
          fromAction.payload.callbacks.error();
          return handleError(fromAction)(error);
        })
  },

  addRoleAssignment: {
    type: "ROLEASSIGNMENT_ADD",
    epic: fromAction => {
      return Observable.of(fromAction.payload)
        .flatMap(payload =>
          roleService.addRoleAssignment(
            toNewRoleAssignmentRequestModel(payload)
          )
        )
        .map(toActionCreator(redux.actions.mergeRoleAssignment, fromAction))
        .catch(handleError(fromAction));
    }
  }
});
const roleAssignmentSchema = new schema.Entity("roleAssignments");
const roleAssignmentListSchema = new schema.Array(roleAssignmentSchema);

const roleDefinitionSchema = new schema.Entity("roleDefinitions");
const roleDefinitionListSchema = new schema.Array(roleDefinitionSchema);

// ========================= Schemas - END

// ========================= Reducers - START
const initialState = {
  ...errorPendingInitialState,

  roleAssignmentEntities: {},
  roleAssignmentItems: [],
  roleAssignmentLastUpdated: "",

  roleDefinitionEntities: [],
  roleDefinitionItems: [],
  roleDefinitionLastUpdated: ""
};

const updateRoleAssignmentReducer = (state, { payload, fromAction }) => {
  const {
    entities: { roleAssignments },
    result
  } = normalize(payload, roleAssignmentListSchema);
  return update(state, {
    roleAssignmentEntities: { $set: roleAssignments },
    roleAssignmentItems: { $set: result },
    roleAssignmentLastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

const mergeRoleAssignmentReducer = (state, { payload, fromAction }) => {
  const {
    entities: { roleAssignments },
    result
  } = normalize(payload, roleAssignmentSchema);
  return update(state, {
    roleAssignmentEntities: { $merge: roleAssignments },
    roleAssignmentItems: { $push: [result] },
    ...setPending(fromAction.type, false)
  });
};

const deleteRoleAssignmentByIdReducer = (state, { payload, fromAction }) => {
  return update(state, {
    roleAssignmentEntities: { $unset: [payload] },
    roleAssignmentItems: items => items.filter(item => item !== payload),
    ...setPending(fromAction.type, false)
  });
};

const updateRoleDefinitionReducer = (state, { payload, fromAction }) => {
  const {
    entities: { roleDefinitions },
    result
  } = normalize(payload, roleDefinitionListSchema);
  return update(state, {
    roleDefinitionEntities: { $set: roleDefinitions },
    roleDefinitionItems: { $set: result },
    roleDefinitionLastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

/* Action types that cause a pending flag */
const fetchableTypes = [
  epics.actionTypes.fetchRoleAssignments,
  epics.actionTypes.fetchRoleDefinitions,
  epics.actionTypes.addRoleAssignment,
  epics.actionTypes.deleteRoleAssignmentById
];

export const redux = createReducerScenario({
  updateRoleAssignments: {
    type: "ROLEASSIGNMENT_UPDATE",
    reducer: updateRoleAssignmentReducer
  },
  updateRoleDefinitions: {
    type: "ROLEDEFINITION_UPDATE",
    reducer: updateRoleDefinitionReducer
  },
  deleteRoleAssignmentById: {
    type: "ROLEASSIGNMENT_DELETE_BY_ID",
    reducer: deleteRoleAssignmentByIdReducer
  },
  mergeRoleAssignment: {
    type: "ROLEASSIGNMENT_ADD",
    reducer: mergeRoleAssignmentReducer
  },
  registerError: { type: "ROLES_REDUCER_ERROR", reducer: errorReducer },
  clearError: { type: "ROLES_RESET_ALL_ERRORS", reducer: resetErrorReducer },
  isFetching: { multiType: fetchableTypes, reducer: pendingReducer },
  resetPendingAndError: {
    type: "ROLE_REDUCER_RESET_ERROR_PENDING",
    reducer: resetPendingAndErrorReducer
  }
});

export const reducer = {
  role: redux.getReducer(initialState)
};

// ========================= Reducers - END

// ========================= Selectors - START
export const getRolesReducer = state => state.role;

export const getRoleAssignmentEntities = state =>
  getRolesReducer(state).roleAssignmentEntities || {};
export const getRoleAssignmentItems = state =>
  getRolesReducer(state).roleAssignmentItems || [];
export const getRoleAssignmentLastUpdated = state =>
  getRolesReducer(state).roleAssignmentLastUpdated;

export function getFetchRoleAssignmentError(state) {
  return getError(
    getRolesReducer(state),
    epics.actionTypes.fetchRoleAssignments
  );
}

export function getAddRoleAssignmentError(state) {
  return getError(getRolesReducer(state), epics.actionTypes.addRoleAssignment);
}

export function getDeleteRoleAssignmentError(state) {
  return getError(
    getRolesReducer(state),
    epics.actionTypes.deleteRoleAssignmentById
  );
}

export function getFetchRoleAssignmentPendingStatus(state) {
  return getPending(
    getRolesReducer(state),
    epics.actionTypes.fetchRoleAssignments
  );
}

export function getAddRoleAssignmentPendingStatus(state) {
  return getPending(
    getRolesReducer(state),
    epics.actionTypes.addRoleAssignment
  );
}

export function getDeleteRoleAssignmentPendingStatus(state) {
  return getPending(
    getRolesReducer(state),
    epics.actionTypes.deleteRoleAssignmentById
  );
}

export const getRoleDefinitionEntities = state =>
  getRolesReducer(state).roleDefinitionEntities || {};
export const getRoleDefinitionItems = state =>
  getRolesReducer(state).roleDefinitionItems || [];
export const getRoleDefinitionLastUpdated = state =>
  getRolesReducer(state).roleDefinitionLastUpdated;
export const getRoleDefinitionError = state =>
  getError(getRolesReducer(state), epics.actionTypes.fetchRoleDefinitions);
export const getRoleDefinitionPendingStatus = state =>
  getPending(getRolesReducer(state), epics.actionTypes.fetchRoleDefinitions);

export const getRoleAssignments = createSelector(
  getRoleAssignmentEntities,
  getRoleAssignmentItems,
  (roleAssignmentEntities, roleAssignmentItems) =>
    roleAssignmentItems.map(id => roleAssignmentEntities[id])
);

export const getRoleDefinitions = createSelector(
  getRoleDefinitionEntities,
  getRoleDefinitionItems,
  (roleDefinitionEntities, roleDefinitionItems) =>
    roleDefinitionItems.map(id => roleDefinitionEntities[id])
);
