import React from "react";
import PropTypes from "prop-types";
import { displayable } from "utilities";

export const TableRenderer = ({ data, colonAvailable }) => (
  <table>
    <tbody>
      {data &&
        Object.entries(data).map(([itemKey, itemValue], key) => (
          <tr key={key}>
            <td className="label">{displayable(itemKey)}</td>
            <td>{colonAvailable ? ": " : ""}</td>
            <td>{displayable(itemValue)}</td>
          </tr>
        ))}
    </tbody>
  </table>
);

TableRenderer.propTypes = {
  data: PropTypes.object,
  colonAvailable: PropTypes.bool
};
