import React, { Component } from "react";
import { MapService } from "services";
import { Panel, PanelError, AjaxError } from "components/shared";
import "./sensorPlacementMap.scss";
import { MapPanelContainer as MapPanel } from "./mapPanel.container";
import { latestMapIndex, dateFormat } from "utilities";
import moment from "moment";
import { svgs } from "utilities";

const noMapImage = svgs.NoMapImage;
export class SensorPlacementMap extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isPending: true,
      sensorPlacementLatLong: undefined,
      isModalOpen: false,
      mapName: undefined,
      sensorPlacementUrl: undefined,
      mapFound: undefined
    };
    this.onSensorPlacementClick = this.onSensorPlacementClick.bind(this);
    this.onModalclose = this.onModalclose.bind(this);
    this.generateSensorPlacementUrl = this.generateSensorPlacementUrl.bind(
      this
    );
    this.generateSensorPlacementUrl();
  }

  onSensorPlacementClick() {
    const { t } = this.props;
    this.setState({
      isModalOpen: true,
      mapName: t("farms.panel.sensorPlacement")
    });
  }

  onModalclose() {
    this.setState({ isModalOpen: false, modalName: undefined });
  }

  generateSensorPlacementUrl() {
    let LatestMapScene;
    MapService.getLatestSensorPlacementMapByFarmId(this.props.farmId).subscribe(
      maps => {
        if (maps.length !== 0) {
          LatestMapScene = maps[latestMapIndex];
          this.setState({
            sensorPlacementLatLong: LatestMapScene.properties.sensor_placements,
            latestMapDate: moment(LatestMapScene.date).format(dateFormat)
          });
        } else {
          this.setState({ noMapUrl: noMapImage });
        }
      },
      sceneError => this.setState({ sceneError })
    );
  }

  render() {
    const {
      t,
      azureMapsKey,
      farmId,
      farm,
      mapKeyIsPending,
      isPending,
      farmsError
    } = this.props;
    const {
      sensorPlacementLatLong,
      latestMapDate,
      sceneError,
      noMapUrl
    } = this.state;
    const error = sceneError || farmsError;

    return (
      <Panel className="map-panel-container">
        {!error && (
          <MapPanel
            azureMapsKey={azureMapsKey}
            farmId={farmId}
            farm={farm}
            sensorPlacementLatLong={sensorPlacementLatLong}
            mapKeyIsPending={mapKeyIsPending}
            isPending={isPending}
            t={t}
            noMapUrl={noMapUrl}
            latestMapDate={latestMapDate}
          />
        )}
        {error && (
          <PanelError>
            <AjaxError t={t} error={error} />
          </PanelError>
        )}
      </Panel>
    );
  }
}
