import { HttpClient } from "utilities/httpClient";
import Config from "app.config";

import { toFarmsModel, toFarmModel } from "./models";

const ENDPOINT = Config.serviceUrls.farms;
export class FarmService {
  static createFarm(farm) {
    return HttpClient.post(`${ENDPOINT}`, farm).map(toFarmModel);
  }

  static getFarms() {
    return HttpClient.get(
      `${ENDPOINT}?includes=Geometry&includes=Properties&maxItems=${Config.apiGetAllMaxItems}`
    ).map(toFarmsModel);
  }

  static getFarmById(id) {
    return HttpClient.get(`${ENDPOINT}/${id}`).map(toFarmModel);
  }

  static deleteFarm(id) {
    return HttpClient.delete(`${ENDPOINT}/${id}`);
  }

  static updateFarm(id, farm) {
    return HttpClient.put(`${ENDPOINT}/${id}`, farm).map(() => ({ id }));
  }
}
