import React, { Component } from "react";

import {
  AjaxError,
  ComponentArray,
  ContextMenu,
  ContextMenuAlign,
  PageContent,
  PageTitle,
  // RefreshBarContainer as RefreshBar,
  SearchInput
} from "components/shared";
import { SensorsGrid } from "./sensorsGrid";
import "./sensors.scss";
import { sensorService } from "services";
import { isDef } from "utilities";
import { reactPlugin } from "applicationInsights";
import { withAITracking } from "@microsoft/applicationinsights-react-js";

const closedDeletePopupState = { openDeleteDevicePopup: undefined };

class Sensors extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sensorsList: [],
      ...closedDeletePopupState,
      deleteModalSuccess: false,
      deleteModalError: undefined,
      deleteSensorPending: false
    };
  }

  fetchApiData = () => {
    const {
      fetchSensors,
      fetchSensorModels,
      fetchDevices,
      sensorsLastUpdated,
      isSensorsPending,
      sensorModelsLastUpdated,
      isSensorModelsPending,
      devicesLastUpdated,
      isDevicesPending
    } = this.props;
    if (!sensorsLastUpdated && !isSensorsPending) fetchSensors();
    if (!sensorModelsLastUpdated && !isSensorModelsPending) fetchSensorModels();
    if (!isDevicesPending && !devicesLastUpdated) fetchDevices();
  };
  locFunc = (lat, lang) => {
    let locationCordinate = undefined;
    if (isDef(lat && lang)) {
      locationCordinate = lat + " , " + lang;
    }

    return locationCordinate;
  };

  refreshSensorData = () => {
    const { fetchSensors, fetchSensorModels, fetchDevices } = this.props;
    fetchSensors();
    fetchSensorModels();
    fetchDevices();
  };

  componentDidMount() {
    if (!this.props.sensorsLastUpdated && !this.props.isSensorsPending)
      this.fetchApiData();
    else this.sensorDataMappingFromProp();
  }

  onContextMenuChange = contextBtns =>
    this.setState({
      contextBtns,
      openFlyoutName: undefined
    });
  onGridReady = gridReadyEvent => (this.sensorGridApi = gridReadyEvent.api);
  searchOnChange = ({ target: { value } }) => {
    if (this.sensorGridApi) this.sensorGridApi.setQuickFilter(value);
  };

  sensorDataMappingFromProp = () => {
    const { sensors, sensorModels, devices } = this.props;
    let deviceMapWithParent;
    let sensorModelList;
    const Sensorslist = sensors.map(item => {
      return {
        ...item
      };
    });
    Sensorslist.forEach(sensor => {
      sensorModelList = sensorModels.find(
        sensorModel => sensorModel.id === sensor.sensorModelId
      );
      sensor.modelType =
        sensorModelList === undefined ? undefined : sensorModelList.type;
      sensor.modelName =
        sensorModelList === undefined ? undefined : sensorModelList.name;
      deviceMapWithParent = devices.find(
        devicePrarent => devicePrarent.id === sensor.deviceId
      );
      sensor.parentDevice =
        deviceMapWithParent === undefined
          ? undefined
          : deviceMapWithParent.name;
      sensor.SensorMeasuresList =
        sensorModelList === undefined
          ? undefined
          : sensorModelList.sensorMeasures;
      sensor.farmName =
        deviceMapWithParent === undefined
          ? undefined
          : deviceMapWithParent.farmName;
      sensor.farmId =
        deviceMapWithParent === undefined
          ? undefined
          : deviceMapWithParent.farmId;
      sensor.deviceModelType =
        deviceMapWithParent === undefined
          ? undefined
          : deviceMapWithParent.deviceModelType;
      sensor.locationCordinate =
        sensor.location === undefined
          ? undefined
          : this.locFunc(sensor.location.latitude, sensor.location.longitude);
    });
    this.setState({ sensorsList: Sensorslist });
  };

  componentDidUpdate(prevProps, prevState) {
    // TO DO: Map this code on sensor selecter with one epic calling multiple epics for preformance inprovement.
    if (
      prevProps.sensors !== this.props.sensors ||
      prevProps.sensorModels !== this.props.sensorModels ||
      prevProps.devices !== this.props.devices
    ) {
      this.sensorDataMappingFromProp();
    }
  }
  sensorListUpdate = Ids => {
    this.setState({
      deleteModalSuccess: false,
      deleteModalError: undefined,
      deleteSensorPending: true
    });
    this.subscription = sensorService.deleteSensor(Ids.id).subscribe(
      () => {
        this.props.deleteSensorsById([Ids.id]);
        this.setState({
          deleteModalError: undefined,
          deleteModalSuccess: true,
          deleteSensorPending: false
        });
      },
      error =>
        this.setState({
          deleteModalError: error,
          deleteModalSuccess: false,
          isPending: false,
          changesApplied: true,
          deleteSensorPending: false
        }),
      () =>
        this.setState({
          isPending: false,
          changesApplied: true,
          ruleDeleted: true
        })
    );
  };
  closeDeletePopup = () => this.setState(closedDeletePopupState);

  render() {
    const {
      t,
      isSensorsPending,
      // sensorsLastUpdated,
      sensorsError
    } = this.props;
    const {
      deleteModalSuccess,
      deleteModalError,
      deleteSensorPending
    } = this.state;
    const gridProps = {
      sensorListUpdate: this.sensorListUpdate,
      sensorList: this.state.sensorsList,
      onGridReady: this.onGridReady,
      rowData: isSensorsPending ? undefined : this.state.sensorsList || [],
      onContextMenuChange: this.onContextMenuChange,
      t: this.props.t,
      deleteModalSuccess: deleteModalSuccess,
      deleteModalError: deleteModalError,
      deleteSensorPending: deleteSensorPending
    };

    return (
      <ComponentArray>
        <ContextMenu>
          <ContextMenuAlign left={true}>
            <SearchInput
              onChange={this.searchOnChange}
              placeholder={t("sensors.searchPlaceholder")}
            />
            {this.state.contextBtns}
          </ContextMenuAlign>
        </ContextMenu>
        <PageContent className="sensors-container">
          {/* <RefreshBar
            refresh={this.refreshSensorData}
            time={sensorsLastUpdated}
            isPending={isSensorsPending}
            t={t}
          /> */}
          <PageTitle
            titleValue={t("sensors.title").concat(
              "(",
              String(this.state.sensorsList.length),
              ")"
            )}
          />
          {!!sensorsError && <AjaxError t={t} error={sensorsError} />}
          {!sensorsError && <SensorsGrid {...gridProps} />}
        </PageContent>
      </ComponentArray>
    );
  }
}

export default withAITracking(reactPlugin, Sensors, "Sensors Grid");
