import React, { Component } from "react";
import {
  Panel,
  PanelHeader,
  PanelHeaderLabel,
  PanelContent,
  PanelError,
  AjaxError,
  ContextMenuAlign
} from "components/shared";
import "./soilMoistureMap.scss";
import { svgs, getSceneData, getPNGFilefromSceneFiles } from "utilities";
import { Modal } from "components/shared/mapsModal/modal/modal";
import { mapModalType, farmNameLoading } from "utilities";
import { appInsights } from "applicationInsights";

const sceneMapType = "soil-moisture";
let mapFileDownloadSelectObject = [];
const noMapImage = svgs.NoMapImage;
export class SoilMoistureMap extends Component {
  constructor(props) {
    super(props);
    const farmId = this.props.farmId;
    const mapsData = this.props.maps;
    this.state = {
      isPending: true,
      isModalOpen: false,
      mapName: undefined,
      url: undefined,
      mapFound: undefined,
      farmName: farmNameLoading
    };
    this.onSoilMoistureClick = this.onSoilMoistureClick.bind(this);
    this.onModalclose = this.onModalclose.bind(this);
    this.getSoilMoistureSceneData = this.getSoilMoistureSceneData.bind(this);
    this.getSoilMoistureSceneData(sceneMapType, farmId, mapsData);
  }
  onSoilMoistureClick() {
    const { t } = this.props;
    this.setState({
      isModalOpen: true,
      mapName: t("farms.panel.soilMoisture")
    });
    appInsights.trackEvent({ name: "UiEvent:ModalOpen: Soil Moisture" });
  }

  onModalclose() {
    appInsights.trackEvent({ name: "UiEvent:ModalClose: Soil Moisture" });
    this.setState({ isModalOpen: false, modalName: undefined });
  }

  componentDidUpdate(prevProps) {
    if (
      !this.props.farmNameLoading &&
      prevProps.farmName !== this.props.farmName
    )
      this.setState({ farmName: this.props.farmName });
  }
  getSoilMoistureSceneData(sceneMapType, farmId, mapsData) {
    getSceneData(sceneMapType, farmId, mapsData, this.sceneDataCallback);
  }
  sceneDataCallback = (selectedObject, mapData, sceneError) => {
    if (sceneError) {
      this.setState({ sceneError });
    } else if (mapData.mapFound) {
      const pngUrl = getPNGFilefromSceneFiles(selectedObject);
      this.setState({
        url: pngUrl,
        mapFound: mapData.mapFound,
        latestMapDate: mapData.latestMapDate,
        latestCreatedAt: mapData.latestCreatedAt
      });
      mapFileDownloadSelectObject = selectedObject.mapFileDownloadSelectObject;
    } else {
      this.setState({
        url: mapData.url,
        mapFound: mapData.mapFound
      });
    }
  };

  onImgError = (name, value) => {
    this.setState({ [name]: value });
  };

  render() {
    const { t, farmsError } = this.props;
    const {
      sceneError,
      isModalOpen,
      mapName,
      url,
      mapFound,
      latestMapDate,
      latestCreatedAt,
      farmName,
      isSoilMoistureImageBroken
    } = this.state;
    const error = sceneError || farmsError;

    return (
      <Panel>
        <PanelHeader>
          <PanelHeaderLabel>{t("farms.panel.soilMoisture")}</PanelHeaderLabel>
          <ContextMenuAlign right={true}>
            <div id="date">{latestMapDate}</div>
          </ContextMenuAlign>
        </PanelHeader>

        <PanelContent>
          {mapFound === undefined ? (
            <div className="shimmer-soil-panel"> </div>
          ) : (
            <div className="no-maping">
              <img
                className={mapFound ? "map-img" : "no-map-img"}
                src={url}
                onClick={mapFound && this.onSoilMoistureClick}
                alt={
                  isSoilMoistureImageBroken
                    ? t("dashboard.panels.map.corruptImageError")
                    : t("farms.panel.noSoilMoisture")
                }
                onError={() =>
                  this.onImgError("isSoilMoistureImageBroken", true)
                }
              />
              {mapFound === false && (
                <div className="soil-col-one">{t("maps.modal.noMapYet")}</div>
              )}
            </div>
          )}
          {isModalOpen && (
            <Modal
              type={mapModalType}
              name={mapName}
              farmName={farmName}
              modalClose={this.onModalclose}
              mapDate={latestMapDate}
              latestCreatedAt={latestCreatedAt}
              blobUrl={url}
              noMapImage={noMapImage}
              isMapFound={mapFound}
              mapFileDownloadSelectObject={mapFileDownloadSelectObject}
              t={t}
            />
          )}
        </PanelContent>
        {error && (
          <PanelError>
            <AjaxError t={t} error={error} />
          </PanelError>
        )}
      </Panel>
    );
  }
}
