import React from "react";
import PropTypes from "prop-types";
import "./propertiesTable.scss";
import { TableRenderer } from "./tableRenderer";

const PropertiesTable = ({ leftTableData, rightTableData, isTableColon }) => (
  <div className="properties-container">
    <TableRenderer data={leftTableData} colonAvailable={isTableColon} />
    <TableRenderer data={rightTableData} colonAvailable={isTableColon} />
  </div>
);

PropertiesTable.propTypes = {
  leftTableData: PropTypes.object,
  rightTableData: PropTypes.object,
  isTableColon: PropTypes.bool
};

PropertiesTable.defaultProps = {
  isTableColon: true
};

export default PropertiesTable;
