import React from "react";
import { Observable } from "rxjs";
import Autosuggest from "react-autosuggest";
import { permissions } from "services/models";
import { UserService } from "services/userService";
import {
  Indicator,
  FormGroup,
  FormControl,
  Btn,
  Protected,
  BtnToolbar,
  ThemedSvgContainer,
  Tooltip
} from "components/shared";
import { themedPaths } from "utilities";
import Flyout from "components/shared/flyout";

import { getDisplayValueFromSuggestion, suggestionRenderer } from "./utils";
import { appInsights } from "applicationInsights";

export class AddUser extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      email: "",
      roleDefinitionOption: null,
      suggestions: []
    };
    this.suggestionsFetchSubscription = null;

    this.roleChangeHandler = this.roleChangeHandler.bind(this);
    this.inputChangeHandler = this.inputChangeHandler.bind(this);
    this.addUser = this.addUser.bind(this);
    this.onSuggestionsFetchRequested = this.onSuggestionsFetchRequested.bind(
      this
    );
    this.onSuggestionsClearRequested = this.onSuggestionsClearRequested.bind(
      this
    );
    this.getSuggestionValue = this.getSuggestionValue.bind(this);
  }

  inputChangeHandler(event, { newValue }) {
    this.setState({
      email: newValue,
      error: null
    });
  }

  getSuggestionValue(suggestion) {
    return getDisplayValueFromSuggestion(suggestion, this.state.email);
  }

  roleChangeHandler(event) {
    this.setState({
      roleDefinitionOption: event.target.value
    });
  }

  onSuggestionsFetchRequested({ value }) {
    if (this.suggestionsFetchSubscription)
      this.suggestionsFetchSubscription.unsubscribe();
    this.suggestionsFetchSubscription = Observable.of(value)
      .map(value => UserService.getUserSuggestions(value))
      .mergeAll()
      .subscribe(next => this.setState({ suggestions: next }));
  }

  onSuggestionsClearRequested() {
    this.setState({
      suggestions: []
    });
  }

  addUser() {
    const { email, roleDefinitionOption } = this.state;
    const { successCallback, errorCallback, t, clearError } = this.props;
    clearError();
    errorCallback(null);
    Observable.of(email)
      .map(
        emailId => UserService.getUserByEmailOrUpn(emailId) // Get user for emailId or upn
      )
      .mergeAll()
      .subscribe(
        next => {
          if (next.length > 1) {
            errorCallback({
              message: t(
                "users.manageUsersFlyout.addRoleSection.roleAddError.multipleUsersFound"
              )
            });
          } else if (next.length === 0) {
            errorCallback({
              message: t(
                "users.manageUsersFlyout.addRoleSection.roleAddError.noUserFound"
              )
            });
          } else {
            const user = next[0];
            const objectId = user.id;
            Observable.of(objectId)
              .map(objectId =>
                this.props.addRoleAssignment({
                  objectId,
                  roleDefinitionId: roleDefinitionOption.value,
                  objectIdType: "UserId",
                  tenantId: global.DeploymentConfig.aad.tenant
                })
              )
              .subscribe(
                () => {},
                error => {
                  errorCallback(error);
                },
                () => {
                  successCallback(
                    t("users.manageUsersFlyout.addRoleSection.roleAddSuccess")
                  );
                }
              );
          }
        },
        error => {
          errorCallback(error);
        }
      );
  }
  toSelectOption = ({ id, name }) => {
    return {
      label: name,
      value: id
    };
  };

  formValid = () => {
    return (
      this.state.roleDefinitionOption &&
      !this.props.roleAssignmentAddPending &&
      this.state.email !== ""
    );
  };
  render() {
    const { roleDefinitionOption, email, suggestions } = this.state;
    const { t, roleDefinitions } = this.props;
    const pending = this.props.roleAssignmentAddPending;
    const roleSelectOptions = roleDefinitions.map(this.toSelectOption);

    const autoSuggestInputProps = {
      placeholder: t("users.manageUsersFlyout.addRoleSection.enterEmail"),
      value: email,
      onChange: this.inputChangeHandler
    };
    return (
      <Protected permissions={permissions.addUser}>
        <Flyout.Section.Container collapsable={false}>
          <Flyout.Section.Header>
            <div>
              {t("users.manageUsersFlyout.addRoleSection.header")}
              <Tooltip
                content={t(
                  "users.manageUsersFlyout.addRoleSection.tooltipText"
                )}
              >
                <ThemedSvgContainer paths={themedPaths.questionBubble} />
              </Tooltip>
            </div>
          </Flyout.Section.Header>
          <Flyout.Section.Content>
            <div className="form-control-inputs">
              <div className="add-role-inputs">
                <Autosuggest
                  suggestions={suggestions}
                  onSuggestionsFetchRequested={this.onSuggestionsFetchRequested}
                  onSuggestionsClearRequested={this.onSuggestionsClearRequested}
                  getSuggestionValue={this.getSuggestionValue}
                  renderSuggestion={suggestionRenderer}
                  inputProps={autoSuggestInputProps}
                  renderInputComponent={props => (
                    <input {...props} spellCheck={false} />
                  )} // Agnivesh: This is to keep spellcheck off, I couldn't find any prop on autosuggest that could do it, and there's no CSS fix
                  // shouldRenderSuggestions={value => value.trim().length > 0} // Use this to set a min char limit to fetch suggestions
                />

                <FormGroup className="form-control-group form-role">
                  <FormControl
                    type="select"
                    className="role-editor-input form-select-role"
                    onChange={this.roleChangeHandler}
                    disabled={pending}
                    options={roleSelectOptions}
                    placeholder={t(
                      "users.manageUsersFlyout.addRoleSection.selectRole"
                    )}
                    value={roleDefinitionOption}
                  ></FormControl>
                </FormGroup>
              </div>
              <BtnToolbar>
                <Btn
                  primary={true}
                  onClick={() => {
                    this.addUser();
                    appInsights.trackEvent({
                      name: "UiEvent:Click: Add User Button"
                    });
                  }}
                  disabled={!this.formValid()}
                  type="submit"
                  id={this.formValid() ? "applyRole" : "disableRole"}
                >
                  {t("users.manageUsersFlyout.addRoleSection.addRole")}
                </Btn>
              </BtnToolbar>
              {pending && (
                <div className="role-loader">
                  <Indicator />
                </div>
              )}
            </div>
          </Flyout.Section.Content>
        </Flyout.Section.Container>
      </Protected>
    );
  }
}
