import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";
import { RuleViewer } from "./ruleViewer";
import { getRuleById } from "store/reducers/rulesAlertsReducer";
import { getDevices } from "store/reducers/devicesReducer";
import { getFarmById } from "store/reducers/farmsReducer";

import {
  epics as sensorEpics,
  getSensors,
  getSensorModels,
  getSensorLastUpdated,
  getSensorPendingStatus,
  getSensorModelPendingStatus,
  getSensorModelLastUpdated
} from "store/reducers/sensorsReducer";
// Pass the devices status
const mapStateToProps = (state, props) => ({
  rule: getRuleById(state, props.ruleId),
  devices: getDevices(state),
  sensors: getSensors(state),
  sensorModels: getSensorModels(state),
  sensorsLastUpdated: getSensorLastUpdated(state),
  isSensorsPending: getSensorPendingStatus(state),
  sensorModelsLastUpdated: getSensorModelLastUpdated(state),
  sensorModelsIsPending: getSensorModelPendingStatus(state),
  farm: getFarmById(state, getRuleById(state, props.ruleId).farmId)
});
const mapDispatchToProps = dispatch => ({
  fetchSensors: () => dispatch(sensorEpics.actions.fetchSensors()),
  fetchSensorModels: () => dispatch(sensorEpics.actions.fetchSensorModels())
});

export const RuleViewerContainer = withNamespaces()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
    null
  )(RuleViewer)
);
