import React, { Component } from "react";

import { AjaxError, Indicator } from "components/shared";
import { Svg } from "components/shared/svg/svg";
import { svgs, renderUndefined, isDef } from "utilities";
import {
  Panel,
  PanelHeader,
  PanelHeaderLabel,
  PanelContent,
  PanelOverlay,
  PanelError
} from "components/shared";

import "./overviewPanel.scss";

export class OverviewPanel extends Component {
  constructor(props) {
    super(props);

    this.state = { isPending: true };
  }

  render() {
    const {
      t,
      error,
      isPending,
      onlineDeviceCount,
      offlineDeviceCount,
      nodesCount,
      gatewaysCount
    } = this.props;

    const deviceDataloaded =
      isDef(onlineDeviceCount) && isDef(offlineDeviceCount);
    const showOverlay =
      isPending && (!nodesCount || !gatewaysCount || !deviceDataloaded);
    const totalDevices = deviceDataloaded
      ? nodesCount + gatewaysCount
      : undefined;
    // const deviceGroupName =
    //   activeDeviceGroup
    //     ? activeDeviceGroup.displayName
    //     : t('dashboard.panels.overview.allDevices');

    return (
      <Panel>
        <PanelHeader>
          <PanelHeaderLabel>
            {t("dashboard.panels.overview.headerDeviceCount")}
          </PanelHeaderLabel>
          {!showOverlay && isPending && <Indicator size="small" />}
        </PanelHeader>
        <PanelContent className="device-stats-container">
          <div className="stat-container">
            <div className="stat-cell col-third">
              <div className="stat-value">{renderUndefined(gatewaysCount)}</div>
              <span className="icon-margin-left spacing-text">
                <Svg path={svgs.gateway} />
              </span>
              <div className="stat-label spacing-text">
                {t("dashboard.panels.overview.gateway")}
              </div>
            </div>

            <div className="stat-cell col-third">
              <div className="stat-value">{renderUndefined(nodesCount)}</div>
              <span className="icon-margin-left spacing-text">
                <Svg path={svgs.node} />
              </span>
              <div className="stat-label spacing-text">
                {t("dashboard.panels.overview.nodes")}
              </div>
            </div>

            <div className="stat-cell col-third">
              <div className="stat-value">{renderUndefined(totalDevices)}</div>
              <div className="stat-label spacing-text">
                {t("dashboard.panels.overview.total")}
              </div>
            </div>
          </div>
        </PanelContent>

        <PanelHeader>
          <PanelHeaderLabel>
            {t("dashboard.panels.overview.headerDeviceStatus")}
          </PanelHeaderLabel>
        </PanelHeader>
        <PanelContent className="device-stats-container">
          <div className="stat-container">
            {/* <div className="stat-container-left"> */}
            <div className="stat-cell col-third">
              <div className="stat-value online-color">
                {renderUndefined(onlineDeviceCount)}
              </div>

              <span className="icon-margin-left spacing-text">
                <Svg path={svgs.onlineBig} />
              </span>
              <div className="stat-label spacing-text">
                {t("dashboard.panels.overview.online")}
              </div>
            </div>

            <div className="stat-cell col-third">
              <div className="stat-value">
                {renderUndefined(offlineDeviceCount)}
              </div>
              <span className="icon-margin-left spacing-text">
                <Svg path={svgs.offline} />
              </span>
              <div className="stat-label spacing-text">
                {t("dashboard.panels.overview.notConnected")}
              </div>
            </div>
          </div>
        </PanelContent>
        {showOverlay && (
          <PanelOverlay>
            <Indicator />
          </PanelOverlay>
        )}
        {error && (
          <PanelError>
            <AjaxError t={t} error={error} />
          </PanelError>
        )}
      </Panel>
    );
  }
}
