import React from "react";
import { MapService, JobsService } from "services";
import {
  Validator,
  LinkedComponent,
  isNull,
  soilMoistureMeatureType
} from "utilities";
import { svgs, isEmptyCheck } from "utilities";
import { DatePickerElement } from "./datePickerElement";
import { toNewJobsRequestModel } from "services/models";
import { Link } from "react-router-dom";
import {
  AjaxError,
  Btn,
  SuccessMessage,
  BtnToolbar,
  ComponentArray,
  Flyout,
  FlyoutHeader,
  FlyoutTitle,
  FlyoutCloseBtn,
  FlyoutContent,
  FormGroup,
  FormLabel,
  Indicator,
  Radio,
  Svg,
  Select
} from "components/shared";
import "./generateMapsFlyout.scss";
import moment from "moment";

import {
  soilMoistureMap,
  sensorPlacementMap,
  jobsDateFormat,
  satelliteIndicesMap
} from "utilities";
import { appInsights } from "applicationInsights";

let lastYearDate;
let jobTypeId = undefined;

const initialState = {
  itemCount: 3,
  isPending: false,
  error: undefined,
  successCount: 0,
  changesApplied: false,
  date: new Date(),
  farmName: undefined,
  isChecked: undefined,
  startDate: undefined,
  endDate: undefined,
  mapGenerated: undefined,
  farm: null,
  sensorMeasureName: "",
  flyoutName: undefined,
  farmId: undefined,
  jobError: undefined,
  selectDate: false,
  confirmStatus: undefined,
  noJobType: false,
  jobCreationError: undefined
};

export class GenerateMapsFlyout extends LinkedComponent {
  constructor(props) {
    super(props);
    this.state = { ...initialState, farm: props.farmId };
    this.onApplyEvent = this.onApplyEvent.bind(this);
    this.selectFarm = this.selectFarm.bind(this);
    this.selectSensorMeasure = this.selectSensorMeasure.bind(this);
    this.generateMaps = this.generateMaps.bind(this);
    this.getsensorMeasureSelectObject = this.getsensorMeasureSelectObject.bind(
      this
    );
    this.getFarmId = this.getFarmId.bind(this);

    if (!props.jobTypeslastUpdated && !props.jobTypesError) {
      props.fetchJobTypes();
    }
    if (!props.sensorsLastUpdated && !props.isSensorsPending) {
      props.fetchSensors();
    }
    if (!props.sensorModelsLastUpdated && !props.isSensorModelsPending) {
      props.fetchSensorModels();
    }
    if (!props.isDevicesPending && !props.devicesLastUpdated) {
      props.fetchDevices();
    }
  }

  static getDerivedStateFromProps(props, state) {
    if (props.flyoutName !== state.flyoutName) {
      if (props.flyoutName === satelliteIndicesMap) {
        return {
          ...initialState,
          flyoutName: props.flyoutName,
          startDate: moment()
            .subtract(7, "d")
            .format(jobsDateFormat),
          endDate: moment().format(jobsDateFormat)
        };
      }
      if (props.flyoutName === soilMoistureMap) {
        return {
          ...initialState,
          flyoutName: props.flyoutName,
          startDate: moment()
            .subtract(1, "d")
            .format(jobsDateFormat),
          endDate: moment()
            .subtract(1, "d")
            .format(jobsDateFormat)
        };
      }
      if (props.flyoutName === sensorPlacementMap) {
        lastYearDate = moment()
          .subtract(365, "d")
          .format(jobsDateFormat);
        return {
          ...initialState,
          flyoutName: props.flyoutName,
          startDate: lastYearDate,
          endDate: moment().format(jobsDateFormat)
        };
      }
    }
    return null;
  }

  componentWillUnmount() {
    if (this.subscription) this.subscription.unsubscribe();
    appInsights.trackEvent({
      name: "UiEvent:FlyClose: Generate " + this.props.flyoutName
    });
  }

  componentDidUpdate(prevProps) {
    if (prevProps.flyoutName !== this.props.flyoutName) {
      appInsights.trackEvent({
        name: "UiEvent:FlyChange: Generate " + this.props.flyoutName
      });
    }
  }

  componentDidMount() {
    appInsights.trackEvent({
      name: "UiEvent:FlyOpen: Generate " + this.props.flyoutName
    });
    if (this.props.flyoutName === satelliteIndicesMap) {
      this.setState({
        flyoutName: this.props.flyoutName,
        startDate: moment()
          .subtract(7, "d")
          .format(jobsDateFormat),
        endDate: moment().format(jobsDateFormat)
      });
    } else if (this.props.flyoutName === soilMoistureMap) {
      this.setState({
        flyoutName: this.props.flyoutName,
        startDate: moment()
          .subtract(1, "d")
          .format(jobsDateFormat),
        endDate: moment()
          .subtract(1, "d")
          .format(jobsDateFormat)
      });
    } else {
      lastYearDate = moment()
        .subtract(365, "d")
        .format(jobsDateFormat);
      this.setState({
        flyoutName: this.props.flyoutName,
        startDate: lastYearDate,
        endDate: moment().format(jobsDateFormat)
      });
    }
  }

  apply = event => {
    event.preventDefault();
    this.setState({ isPending: true, successCount: 0, error: null });
    this.subscription = MapService.updateMapsItems().subscribe(
      _ => {
        this.setState({
          successCount: this.state.successCount + this.state.itemCount
        });
      },
      error => this.setState({ error, isPending: false }),
      () =>
        this.setState({
          isPending: false,
          confirmStatus: false
        })
    );
  };

  onDateSelect = ({ target: { value } }) => {
    switch (value) {
      case "recommended":
        lastYearDate = moment()
          .subtract(365, "d")
          .format(jobsDateFormat);
        this.setState({
          selectDate: false,
          startDate: lastYearDate,
          endDate: moment().format(jobsDateFormat)
        });
        break;
      case "select-date":
        this.setState({ selectDate: true });
        break;
      case "this-week":
        this.setState({
          selectDate: false,
          startDate: moment()
            .subtract(7, "d")
            .format(jobsDateFormat),
          endDate: moment().format(jobsDateFormat)
        });
        break;
      case "yesterday":
        this.setState({
          selectDate: false,
          startDate: moment()
            .subtract(1, "d")
            .format(jobsDateFormat),
          endDate: moment()
            .subtract(1, "d")
            .format(jobsDateFormat)
        });

        break;
      default:
    }
  };

  onApplyEvent(event, picker) {
    const startDate = picker.startDate.format(jobsDateFormat);
    const endDate = picker.endDate.format(jobsDateFormat);

    this.setState({ startDate: startDate, endDate: endDate });
  }

  selectFarm(event) {
    this.setState({
      farm: event.target.value,
      sensorMeasureName: ""
    });
  }

  selectSensorMeasure(event) {
    this.setState({ sensorMeasureName: event.target.value.value });
  }

  getFarmId() {
    return this.props.farmId
      ? this.props.farmId
      : this.state.farm === null || this.state.farm === undefined
        ? ""
        : this.state.farm.value;
  }

  generateMaps() {
    const { flyoutName, insertJob } = this.props;
    const { startDate, endDate, sensorMeasureName } = this.state;
    const jobQuery = {
      startDate: startDate,
      endDate: endDate,
      farmId: this.getFarmId(),
      sensorMeasureName: sensorMeasureName,
      mapType: flyoutName,
      jobTypeId: jobTypeId
    };
    if (jobTypeId === undefined) {
      this.setState({
        noJobType: true,
        mapGenerated: false,
        changesApplied: true
      });
    } else {
      const requestProps = JSON.parse(JSON.stringify(jobQuery));
      JobsService.submitJob(toNewJobsRequestModel(requestProps)).subscribe(
        job => {
          insertJob({ job });
          this.setState({
            isPending: false,
            changesApplied: true,
            mapGenerated: true
          });
        },
        error => {
          this.setState({
            jobCreationError: error,
            isPending: false,
            changesApplied: true,
            mapGenerated: false
          });
        }
      );
    }
  }

  formIsValid() {
    const x = [this.farmNameLink].every(link => !link.error);
    const y = [this.sensorMeasureNameLink].every(link => !link.error);
    const { flyoutName, farmId } = this.props;
    const { farm, sensorMeasureName } = this.state;

    const validFarm = farmId !== undefined || (x && !isNull(farm));
    const validsensorMeasure =
      flyoutName !== soilMoistureMap || (y && !isEmptyCheck(sensorMeasureName));

    return validFarm && validsensorMeasure;
  }

  getsensorMeasureSelectObject() {
    const { devices, sensors, sensorModels } = this.props;
    const farmId = this.getFarmId();

    const sensorMeasureNames = devices
      .filter(device => device.farmId === farmId)
      .reduce(
        (acc, device) =>
          acc.concat(sensors.filter(sensor => sensor.deviceId === device.id)),
        []
      )
      .reduce(
        (acc, sensor) =>
          acc.concat(
            sensorModels.filter(
              sensorModel => sensorModel.id === sensor.sensorModelId
            )
          ),
        []
      )
      .reduce((acc, sensorModel) => acc.concat(sensorModel.sensorMeasures), [])
      .filter(sensorMeasure => sensorMeasure.type === soilMoistureMeatureType)
      .map(sensorMeasure => sensorMeasure.name);

    return Array.from(new Set(sensorMeasureNames)).map(sensorMeasureName =>
      Object({
        label: sensorMeasureName,
        value: sensorMeasureName
      })
    );
  }

  render() {
    const {
      t,
      onClose,
      flyoutName,
      farmsSelectObject,
      farmId,
      isJobTypesPending,
      isFarmsPending,
      farmsError,
      jobTypeName,
      jobTypes,
      jobTypesError
    } = this.props;
    const {
      itemCount,
      isPending,
      jobCreationError,
      selectDate,
      changesApplied,
      farm,
      sensorMeasureName,
      startDate,
      endDate,
      mapGenerated,
      noJobType
    } = this.state;
    const isApiPending = isFarmsPending || isJobTypesPending;
    const error = farmsError || jobTypesError;

    jobTypeId = jobTypes
      ? (jobTypes.find(jobType => jobType.name === jobTypeName) || {}).id
      : undefined;

    this.farmNameLink = this.linkTo("farm").check(Validator.notEmpty, () =>
      t("templates.flyouts.errorMsg.fieldCantBeEmpty")
    );
    this.sensorMeasureNameLink = this.linkTo("sensorMeasureName").check(
      Validator.notEmpty,
      () => t("templates.flyouts.errorMsg.fieldCantBeEmpty")
    );
    const weekendDate = moment()
      .subtract(7, "d")
      .format(jobsDateFormat);
    const today = moment().format(jobsDateFormat);

    return (
      <div className="mapsflyout">
        <Flyout>
          <FlyoutHeader>
            <FlyoutTitle>
              <div className="bold"> {flyoutName}</div>
            </FlyoutTitle>
            <FlyoutCloseBtn onClick={onClose} />
          </FlyoutHeader>

          <FlyoutContent>
            {!!error && (
              <AjaxError className="map-generation-error" t={t} error={error} />
            )}
            {!error && isApiPending && (
              <BtnToolbar className="map-flyout-indicator">
                <Indicator />
              </BtnToolbar>
            )}
            {!error && !isApiPending && (
              <form className="maps-flyout-container" onSubmit={this.apply}>
                <div className="maps-flyout-header">
                  {flyoutName === satelliteIndicesMap &&
                    t("maps.flyouts.satteliteIndicesMapDescription")}

                  {flyoutName === soilMoistureMap &&
                    t("maps.flyouts.soilMoistureMapDescription")}

                  {flyoutName === sensorPlacementMap &&
                    t("maps.flyouts.sensorPlacementMapDescription")}
                </div>
                {farmId !== undefined && (
                  <div className="maps-flyout-descr">
                    {
                      <Link
                        to={{
                          pathname: "/maps",
                          state: {
                            farmId: farmId
                          }
                        }}
                      >
                        {t("maps.flyouts.allMapsLink")}
                      </Link>
                    }
                  </div>
                )}

                {farmId === undefined && (
                  <div className="bold">{t("maps.flyouts.selectFarms")}</div>
                )}

                {farmId === undefined && (
                  <Select
                    className="flyouts-select-list"
                    options={farmsSelectObject}
                    matchProp={"label"}
                    value={farm}
                    clearable={false}
                    searchable={true}
                    onChange={this.selectFarm}
                    icon={svgs.plus}
                    placeholder={t("maps.flyouts.selectFarmPlaceholder")}
                    link={this.farmNameLink}
                  />
                )}

                {flyoutName === soilMoistureMap && (
                  <div className="bold soil-info">
                    {t("maps.flyouts.selectSensorMeasures")}
                    <div className="soil-tooltip">
                      <Svg
                        className="summary-icon soil-tooltip"
                        path={svgs.infoBubble}
                      />
                      <span className="soil-tooltip soil-tooltip-text">
                        {t("maps.flyouts.soilMoistureTooltip")}
                      </span>
                    </div>
                  </div>
                )}

                {flyoutName === soilMoistureMap && (
                  <Select
                    className="flyouts-select-list soil-moisutre-measure"
                    options={this.getsensorMeasureSelectObject()}
                    matchProp={"label"}
                    value={sensorMeasureName}
                    clearable={false}
                    searchable={true}
                    onChange={this.selectSensorMeasure}
                    icon={svgs.plus}
                    placeholder={t(
                      "maps.flyouts.selectSensorMeasurePlaceholder"
                    )}
                    link={this.sensorMeasureNameLink}
                  />
                )}

                <ComponentArray>
                  <FormGroup className="radio">
                    <FormLabel>
                      <div className="bold">
                        {t("maps.flyouts.selectDates")}
                      </div>
                    </FormLabel>
                    {flyoutName === soilMoistureMap && (
                      <Radio
                        name="date"
                        value="yesterday"
                        onClick={this.onDateSelect}
                        defaultChecked
                      >
                        {t("maps.flyouts.yesterday")}
                      </Radio>
                    )}
                    <div>
                      <div>
                        {flyoutName === soilMoistureMap && (
                          <Radio
                            name="date"
                            value="this-week"
                            onClick={this.onDateSelect}
                          >
                            {t("maps.flyouts.past7Days").concat(
                              "  (",
                              String(weekendDate),
                              " to ",
                              String(today),
                              ")"
                            )}
                          </Radio>
                        )}
                      </div>
                      {flyoutName === satelliteIndicesMap && (
                        <Radio
                          name="date"
                          value="this-week"
                          onClick={this.onDateSelect}
                          defaultChecked={flyoutName === satelliteIndicesMap}
                        >
                          {t("maps.flyouts.thisWeek").concat(
                            "  (",
                            String(weekendDate),
                            " to ",
                            String(today),
                            ")"
                          )}
                        </Radio>
                      )}
                    </div>
                    {flyoutName === sensorPlacementMap && (
                      <Radio
                        name="date"
                        value="recommended"
                        defaultChecked
                        onClick={this.onDateSelect}
                      >
                        {t("maps.flyouts.recommended")}
                      </Radio>
                    )}
                    <div className="select-date-range">
                      {
                        <Radio
                          name="date"
                          value="select-date"
                          onClick={this.onDateSelect}
                        >
                          {t("maps.flyouts.selectDateRange")}
                        </Radio>
                      }
                      {selectDate && (
                        <DatePickerElement
                          inputValue={startDate + " to " + endDate}
                          onApplyEvent={this.onApplyEvent}
                        />
                      )}
                    </div>
                  </FormGroup>
                </ComponentArray>

                {mapGenerated === true && (
                  <SuccessMessage
                    className="map-generation-success"
                    t={t}
                    successMsg={t("maps.flyouts.jobSuccess")}
                  />
                )}
                {noJobType === true && (
                  <AjaxError
                    className="map-generation-error"
                    t={t}
                    error={{ message: t("maps.flyouts.noJobType") }}
                  />
                )}

                {jobCreationError && (
                  <AjaxError
                    className="map-generation-error"
                    t={t}
                    error={jobCreationError}
                  />
                )}
                {!changesApplied && !isPending && (
                  <BtnToolbar>
                    {/* <Btn primary={true} onClick={onClose} className="cancel-btn">
                      {t("maps.flyouts.cancelButton")}
                    </Btn> */}
                    {
                      <Btn
                        primary={true}
                        disabled={
                          isPending || itemCount === 0 || !this.formIsValid()
                        }
                        type="submit"
                        onClick={() => {
                          this.generateMaps();
                          appInsights.trackEvent({
                            name:
                              "UiEvent:Click: Generate " +
                              flyoutName +
                              " Maps Btn"
                          });
                        }}
                        className="generate-map-btn"
                        id={
                          !this.formIsValid()
                            ? "applyMapBtnDisabled"
                            : "applyMapBtn"
                        }
                      >
                        {t("maps.flyouts.generateMapsButton")}
                      </Btn>
                    }
                  </BtnToolbar>
                )}
                {isPending && (
                  <BtnToolbar className="button-box">
                    <Indicator />
                  </BtnToolbar>
                )}
                {!!changesApplied && (
                  <BtnToolbar>
                    {/* <Btn primary={true} onClick={onClose} className="cancel-btn">
                      {t("maps.flyouts.cancelButton")}
                    </Btn> */}
                  </BtnToolbar>
                )}
              </form>
            )}
          </FlyoutContent>
        </Flyout>
      </div>
    );
  }
}
