import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";
import {
  epics as rulesAlertsEpics,
  getAlerts,
  getAlertsError,
  getAlertsPendingStatus,
  getAlertsLastUpdated,
  getAlertsRuleBased,
  getRules,
  getRulesError,
  getRulesLastUpdated
} from "store/reducers/rulesAlertsReducer";
import Alerts from "./alerts";

// Pass the data
const mapStateToProps = state => ({
  rules: getRules(state),
  rulesError: getRulesError(state),
  rulesLastUpdated: getRulesLastUpdated(state),
  alerts: getAlerts(state),
  alertsByRule: getAlertsRuleBased(state),
  alertsError: getAlertsError(state),
  isAlertsPending: getAlertsPendingStatus(state),
  alertsLastUpdated: getAlertsLastUpdated(state)
});

// Wrap the dispatch method
const mapDispatchToProps = dispatch => ({
  fetchAlerts: () => dispatch(rulesAlertsEpics.actions.fetchAlerts()),
  fetchRules: () => dispatch(rulesAlertsEpics.actions.fetchRules())
});

export const AlertsContainer = withNamespaces()(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Alerts)
);
