import React, { Component } from "react";
import "util";
import {
  Btn,
  AjaxError,
  ComponentArray,
  ContextMenu,
  ContextMenuAlign,
  PageContent,
  PageTitle,
  // RefreshBarContainer as RefreshBar,
  SearchInput
} from "components/shared";
import { JobsGrid } from "./jobsGrid";
import { svgs, isDef, getJobSelectObjectData } from "utilities";
import "./jobs.scss";
import FilterControl from "../../shared/filter/filterControl";
import { FormElementType } from "../../../utilities/constants";
import { reactPlugin } from "applicationInsights";
import { withAITracking } from "@microsoft/applicationinsights-react-js";

let farmsNameObject = [];
let jobTypesObject = [];
let filteredJobs = [];
class Jobs extends Component {
  constructor(props) {
    super(props);
    this.state = {
      contextBtns: null,
      farmsNameObject: [],
      isCalculating: true,
      openFlyout: false,
      filterState: {
        selectedStatus: undefined,
        selectedFarmName: undefined,
        selectedJobType: undefined,
        isFilterOn: false,
        filtersApplied: 0
      }
    };
    const {
      fetchJobs,
      fetchFarms,
      jobsLastUpdated,
      jobsError,
      farmsLastUpdated,
      farmsError,
      fetchJobTypes,
      jobTypeslastUpdated,
      jobTypesError
    } = this.props;

    if (!farmsLastUpdated && !farmsError) {
      fetchFarms();
    }
    if (!jobsLastUpdated && !jobsError) {
      fetchJobs();
    }
    if (!jobTypeslastUpdated && !jobTypesError) {
      fetchJobTypes();
    }

    this.filterJobsFlyout = this.filterJobsFlyout.bind(this);
    this.filterJobsGrid = this.filterJobsGrid.bind(this);
    this.onCloseFlyout = this.onCloseFlyout.bind(this);
  }

  onContextMenuChange = contextBtns => this.setState({ contextBtns });

  onGridReady = gridReadyEvent => (this.jobGridApi = gridReadyEvent.api);

  searchOnChange = ({ target: { value } }) => {
    if (this.jobGridApi) this.jobGridApi.setQuickFilter(value);
  };

  componentDidMount() {
    filteredJobs = this.props.jobs;
  }

  filterJobsFlyout() {
    this.setState({ openFlyout: true });
  }

  filterJobsGrid(FilterControlstate) {
    const Filterobj = {
      filterState: {
        selectedStatus: FilterControlstate.status
          ? FilterControlstate.status
          : undefined,
        selectedFarmName: FilterControlstate.farm_name
          ? FilterControlstate.farm_name
          : undefined,
        selectedJobType: FilterControlstate.job_type
          ? FilterControlstate.job_type
          : undefined,
        isFilterOn: FilterControlstate.isFilterOn,
        filtersApplied: FilterControlstate.appliedFilters
      }
    };
    this.setState(Filterobj);
    filteredJobs = this.props.jobs.filter(
      job =>
        (isDef(FilterControlstate.farm_name)
          ? job.arguments && job.arguments.farm_id
            ? job.arguments.farm_id.includes(FilterControlstate.farm_name.value)
            : job.arguments && job.arguments.farm_ids
            ? job.arguments.farm_ids.includes(
                FilterControlstate.farm_name.value
              )
            : null
          : {}) &&
        (isDef(FilterControlstate.status)
          ? job.currentState === FilterControlstate.status.value
          : {}) &&
        (isDef(FilterControlstate.job_type)
          ? job.typeId === FilterControlstate.job_type.value
          : {})
    );
  }

  toSelectOption = ({ name, id }) => ({ label: name, value: id });

  onCloseFlyout = () => this.setState({ openFlyout: false });

  render() {
    const {
      t,
      jobs,
      jobsError,
      isJobsPending,
      farms,
      isFarmsPending,
      // jobsLastUpdated,
      fetchJobs,
      abortJob,
      jobTypes
    } = this.props;

    const { openFlyout, filterState } = this.state;

    farmsNameObject = farms.map(this.toSelectOption);
    jobTypesObject = jobTypes
      .filter(jobType => jobType.name !== undefined)
      .map(this.toSelectOption);

    const gridProps = {
      onGridReady: this.onGridReady,
      rowData: isJobsPending
        ? undefined
        : filterState && filterState.isFilterOn
        ? filteredJobs
        : jobs || [],
      onContextMenuChange: this.onContextMenuChange,
      t: this.props.t,
      refresh: fetchJobs,
      abortJob: abortJob,
      onRowClicked: ({ node }) => node.setSelected(!node.isSelected())
    };

    const filterConfig = {
      FilterList: [
        {
          List: farmsNameObject,
          type: FormElementType.select,
          typeProps: {
            selectedValue: filterState.selectedFarmName
              ? filterState.selectedFarmName
              : undefined,
            placeholder: t("jobs.flyouts.selectFarms")
          },
          label: t("jobs.flyouts.selectFarms"),
          filterKey: "farm_name",
          className: ""
        },
        {
          List: jobTypesObject,
          type: FormElementType.select,
          typeProps: {
            selectedValue: filterState.selectedJobType
              ? filterState.selectedJobType
              : undefined,
            placeholder: t("jobs.flyouts.jobSelect")
          },
          label: t("jobs.flyouts.selectJobs"),
          filterKey: "job_type",
          className: ""
        },
        {
          List: getJobSelectObjectData(),
          type: FormElementType.select,
          typeProps: {
            selectedValue: filterState.selectedStatus
              ? filterState.selectedStatus
              : undefined,
            placeholder: t("jobs.flyouts.statusSelect")
          },
          label: t("jobs.flyouts.status"),
          filterKey: "status",
          className: ""
        }
      ],

      applyCallback: this.filterJobsGrid,
      resetCallback: this.onResetClick,
      header: {
        className: "",
        content: t("jobs.flyouts.filter")
      }
    };

    return (
      <ComponentArray>
        <ContextMenu>
          <ContextMenuAlign left={true}>
            <SearchInput
              onChange={this.searchOnChange}
              placeholder={t("jobs.searchPlaceholder")}
            />
            {this.state.contextBtns}
            <ContextMenuAlign right={true}>
              <Btn
                svg={
                  filterState.filtersApplied > 0
                    ? svgs.appliedFilter
                    : svgs.filter
                }
                className="filter-job"
                onClick={this.filterJobsFlyout}
              >
                {this.props.t("jobs.filter")}{" "}
                {filterState.filtersApplied > 0 && (
                  <span>{`(${filterState.filtersApplied})`}</span>
                )}
              </Btn>
            </ContextMenuAlign>
          </ContextMenuAlign>
        </ContextMenu>

        <PageContent className="jobs-container" key="page-content">
          {/* <RefreshBar
            refresh={fetchJobs}
            time={jobsLastUpdated}
            isPending={isJobsPending}
            t={t}
          /> */}
          <PageTitle titleValue={t("jobs.title")} />
          {!!jobsError && <AjaxError t={t} error={jobsError} />}
          {!jobsError && <JobsGrid {...gridProps} />}
          {openFlyout && (
            <FilterControl
              t={t}
              onCloseFlyout={this.onCloseFlyout}
              isPending={isFarmsPending}
              filterConfig={filterConfig}
            />
          )}
        </PageContent>
      </ComponentArray>
    );
  }
}

export default withAITracking(reactPlugin, Jobs, "Jobs Grid");
