import React from "react";
import {
  Validator,
  svgs,
  LinkedComponent,
  isEmptyCheck,
  isJSON,
  nonAlphaNumeric,
  isObject
} from "utilities";
import {
  AjaxError,
  Btn,
  BtnToolbar,
  FormLabel,
  FormControl,
  Indicator,
  FormGroup,
  Svg,
  SuccessMessage,
  Grid,
  Cell
} from "components/shared";
import { FarmService } from "services";
import { toNewFarmRequestModel } from "services/models";
import {
  MapPanelContainer as MapPanel,
  DescriptionPanel,
  GeoJSON
} from "components/pages/farms/panels";
import { cropSuggestions } from "utilities";

import "./farmEditor.scss";
import { appInsights } from "applicationInsights";
import geoJSONValidation from "geojson-validation";

const AzureMaps = window.atlas;

// A state object for a new Farm
const newFarm = () => ({
  name: "",
  address: "",
  geometry: "",
  crops: ""
});

export class FarmEditor extends LinkedComponent {
  constructor(props) {
    super(props);
    this.state = {
      error: undefined,
      isPending: false,
      changesApplied: undefined,
      formData: newFarm(),
      farms: this.props.farms,
      geoSelect: "GeoJSON",
      isGeoJSONInvalid: false,
      editableFarm: undefined,
      isCreateFarmSuccess: undefined,
      isEditFarmSuccess: undefined
    };

    this.toggleGeoJSONInput = this.toggleGeoJSONInput.bind(this);
    this.getGeoJSON = this.getGeoJSON.bind(this);
  }

  isFormValid() {
    const validity = [
      this.farmNameLink,
      this.farmAddressLink,
      this.farmCropLink
    ].every(link => !link.error);
    return validity;
  }

  setFormState = newFarm => {
    this.setState({
      ...this.state,
      editableFarm: newFarm,
      formData: {
        name: newFarm.name,
        geometry: newFarm.geometry,
        address: newFarm.properties.address,
        crops: cropSuggestions.find(
          crop => crop.value === newFarm.properties.crops
        )
      }
    });
  };

  componentDidMount() {
    const { farm } = this.props;
    if (farm) {
      this.setFormState(farm);
    }
    appInsights.trackEvent({
      name: "UiEvent:FlyOpen: " + (farm ? "Edit" : "New") + " Farm"
    });
  }

  componentWillReceiveProps(nextProps) {
    const { farm } = nextProps;
    if (farm) {
      this.setFormState(farm);
    }
  }

  componentWillUnmount() {
    if (this.subscription) this.subscription.unsubscribe();
    appInsights.trackEvent({
      name: "UiEvent:FlyClose: Edit/New Farm"
    });
  }

  apply = event => {
    event.preventDefault();
    const { insertFarm, modifyFarm, farm } = this.props;
    this.setState({
      isPending: true,
      error: null
    });
    const { formData } = this.state;
    const { geometry } = formData;
    const isGeoJSONFilled =
      isObject(geometry) &&
      isJSON(JSON.stringify(geometry)) &&
      (geoJSONValidation.isPolygon(geometry) ||
        geoJSONValidation.isMultiPolygon(geometry));
    if (!isGeoJSONFilled) formData.geometry = undefined;
    const requestProps = JSON.parse(JSON.stringify(formData));
    if (farm) {
      this.subscription = FarmService.updateFarm(
        farm.id,
        toNewFarmRequestModel(requestProps)
      )
        .mergeMap(farm => {
          return FarmService.getFarmById(farm.id);
        })
        .subscribe(
          updatedFarm => {
            modifyFarm([{ ...updatedFarm }]);
            this.setState({
              isPending: false,
              changesApplied: true,
              isEditFarmSuccess: true
            });
            this.props.history.push(`/farms/${farm.id}`, {
              id: farm.id,
              farmName: formData.name
            });
          },
          error =>
            this.setState({ error, isPending: false, changesApplied: true }),
          () =>
            this.setState({
              isPending: false,
              changesApplied: true,
              confirmStatus: false
            })
        );
    } else {
      this.subscription = FarmService.createFarm(
        toNewFarmRequestModel(requestProps)
      ).subscribe(
        farm => {
          insertFarm({ farm });
          this.setState({
            isPending: false,
            changesApplied: true,
            isCreateFarmSuccess: true
          });
        },
        error =>
          this.setState({ error, isPending: false, changesApplied: true }),
        () =>
          this.setState({
            isPending: false,
            changesApplied: true,
            confirmStatus: false
          })
      );
    }
  };

  formControlChange = () => {
    if (this.state.changesApplied) {
      this.setState({ changesApplied: false });
    }
  };

  getGeoJSON(geoString) {
    let geoJSON;
    if (
      !isEmptyCheck(geoString) &&
      isJSON(geoString) &&
      (geoJSONValidation.isPolygon(JSON.parse(geoString)) ||
        geoJSONValidation.isMultiPolygon(JSON.parse(geoString)))
    ) {
      geoJSON = JSON.parse(geoString);
    } else {
      geoJSON = geoString;
    }
    const newForm = {
      ...this.state.formData,
      geometry: geoJSON
    };

    this.setState({ ...this.state, formData: newForm, changesApplied: false });
  }

  toggleGeoJSONInput(value) {
    const newState = { ...this.state, geoSelect: value };
    this.setState(newState);
  }

  setGeoJSONInvalidStatus = isInvalid => {
    this.setState({
      isGeoJSONInvalid: isInvalid
    });
  };

  render() {
    const {
      t,
      // onClose,
      azureMapsKey,
      azureMapsKeyError,
      farm
    } = this.props;

    const {
      isPending,
      error,
      changesApplied,
      geoSelect,
      formData,
      isGeoJSONInvalid,
      isCreateFarmSuccess,
      isEditFarmSuccess
    } = this.state;

    const requiredValidator = new Validator().check(
      Validator.notEmpty,
      t("farms.errorMsg.required")
    );
    this.formDataLink = this.linkTo("formData");

    const AzureMapError = AzureMaps ? false : true;

    this.farmNameLink = this.formDataLink
      .forkTo("name")
      .withValidator(requiredValidator)
      .reject(nonAlphaNumeric)
      .check(Validator.noSpaces, () => t("farms.errorMsg.spaceNotAllowed"))
      .check(
        value => (this.props ? Validator.maxlength(value, 25) : true),
        () => t("farms.errorMsg.maxLengthName")
      )
      .check(Validator.minlength, () => t("farms.errorMsg.minLength"));

    this.farmAddressLink = this.formDataLink
      .forkTo("address")
      .check(
        value => (this.props ? Validator.maxlength(value, 200) : true),
        () => t("farms.errorMsg.maxLengthAddress")
      )
      .check(
        value => (value ? Validator.minlength(value) : true),
        () => t("farms.errorMsg.minLength")
      );

    this.farmCropLink = this.formDataLink
      .forkTo("crops")
      .map(({ value }) => value);

    const isGeometryEmpty =
      formData.geometry === "" || formData.geometry === undefined
        ? true
        : false;

    const submitButtonDisabled =
      !this.isFormValid() ||
      isPending ||
      isGeoJSONInvalid ||
      isGeometryEmpty ||
      isCreateFarmSuccess;

    return (
      <form onSubmit={e => e.preventDefault()} className="farms-editor-form">
        <div className="form-control-inputs">
          {/* Name Label */}
          <FormGroup className="form-control-group">
            <FormLabel className="input-label">
              {t("farms.flyouts.editor.name")}
            </FormLabel>
            <Svg className="svg-mandatory" path={svgs.error} />
            <div className="tooltip">
              <Svg className="summary-icon tooltip" path={svgs.infoBubble} />
              <span className="tooltip tooltiptext">
                {t("farms.flyouts.editor.toolTips.farmName")}
              </span>
            </div>
            <FormControl
              className="farms-editor-input"
              type="text"
              maxLength={26}
              name="name"
              placeholder={t("farms.flyouts.editor.farmName")}
              onChange={this.formControlChange.bind(this)}
              link={this.farmNameLink}
              value={formData.name}
            />
          </FormGroup>
          {/* Crop Label */}
          <FormGroup className="form-control-group">
            <FormLabel className="input-label">
              {t("farms.flyouts.editor.crops")}
            </FormLabel>
            <div className="tooltip">
              <Svg className="summary-icon tooltip" path={svgs.infoBubble} />
              <span className="tooltip tooltiptext">
                {t("farms.flyouts.editor.toolTips.crops")}
              </span>
            </div>
            <FormControl
              type="select"
              className="farms-editor-input"
              options={cropSuggestions}
              link={this.farmCropLink}
              clearable={false}
              searchable={true}
              matchProp={"label"}
              onChange={this.formControlChange.bind(this)}
              name="crops"
              value={formData.crops}
              placeholder={t("farms.flyouts.editor.selectCrops")}
            />
          </FormGroup>
          {/* Address Label */}
          <FormGroup className="form-control-group">
            <FormLabel className="input-label">
              {t("farms.flyouts.editor.address")}
            </FormLabel>

            <div className="tooltip">
              <Svg className="summary-icon tooltip" path={svgs.infoBubble} />
              <span className="tooltip tooltiptext">
                {t("farms.flyouts.editor.toolTips.farmAddress")}
              </span>
            </div>
            <FormControl
              className="farms-editor-input"
              type="text"
              maxLength={201}
              onChange={this.formControlChange.bind(this)}
              name="address"
              placeholder={t("farms.flyouts.editor.farmAddress")}
              link={this.farmAddressLink}
              value={formData.address}
            />
          </FormGroup>
        </div>
        <div className="form-placeholder">
          <Grid>
            <Cell className="col-2">
              <DescriptionPanel
                t={t}
                toggleGeoJSONInput={this.toggleGeoJSONInput}
                geoSelect={geoSelect}
                isGeometryEmpty={isGeometryEmpty}
                disableMarkOnMap={geoJSONValidation.isMultiPolygon(
                  formData.geometry
                )}
              />
            </Cell>
            <Cell className="col-8">
              {geoSelect === "Azure-Maps" && (
                <MapPanel
                  azureMapsKey={!AzureMapError && azureMapsKey}
                  mapKeyIsPending={false}
                  setGeoJSONInvalidStatus={this.setGeoJSONInvalidStatus}
                  error={azureMapsKeyError}
                  t={t}
                  getGeoJSON={this.getGeoJSON}
                  address={formData.address}
                  geometry={
                    isObject(formData.geometry)
                      ? JSON.stringify(formData.geometry)
                      : formData.geometry
                  }
                />
              )}
              {geoSelect === "GeoJSON" && (
                <GeoJSON
                  t={t}
                  getGeoJSON={this.getGeoJSON}
                  setGeoJSONInvalidStatus={this.setGeoJSONInvalidStatus}
                  GeoJSON={
                    isObject(formData.geometry)
                      ? JSON.stringify(formData.geometry)
                      : formData.geometry
                  }
                />
              )}
            </Cell>
          </Grid>
        </div>

        {isCreateFarmSuccess === true && (
          <SuccessMessage
            className="farms-editor-success"
            t={t}
            successMsg={t("farms.flyouts.editor.createFarmSuccess")}
          />
        )}
        {isEditFarmSuccess === true && (
          <SuccessMessage
            className="farms-editor-success"
            t={t}
            successMsg={t("farms.flyouts.editor.editFarmSuccess")}
          />
        )}
        {/** Displays an error message if one occurs while applying changes. */}
        {error && (
          <AjaxError className="farms-editor-error" t={t} error={error} />
        )}
        {/** If changes are not yet applied, show the buttons for applying changes and closing the flyout. */
        !changesApplied && !isPending && !isCreateFarmSuccess && (
          <BtnToolbar className="button-box">
            {/* <Btn svg={svgs.cancelX} onClick={onClose} className="close-editor">
              {t("farms.flyouts.editor.cancel")}
            </Btn>
            */}
            <Btn
              primary={true}
              disabled={submitButtonDisabled}
              type="button"
              id={submitButtonDisabled ? "applyBtnDisabled" : "applyBtn"}
              name="apply"
              onClick={event => {
                appInsights.trackEvent(
                  {
                    name:
                      "UiEvent:Click: " +
                      (farm ? "Edit" : "Create") +
                      " Farm Btn"
                  },
                  {
                    context: {
                      createFarmMode: {
                        geoSelect // Azure maps mode or GeoJSON mode
                      }
                    }
                  }
                );
                this.apply(event);
              }}
            >
              {t("farms.flyouts.editor.apply")}
            </Btn>
          </BtnToolbar>
        )}
        {isPending && (
          <BtnToolbar className="button-box">
            <Indicator />
          </BtnToolbar>
        )}
        {/**
         * If changes are applied, show only the close button.
         * Other text or component might be included here as well.
         * For example, you might provide a link to the detail page for a newly submitted job.
         * */
        !!changesApplied && (
          <BtnToolbar className="button-box">
            {/* <Btn svg={svgs.cancelX} onClick={onClose} className="close-editor">
              {t("farms.flyouts.editor.close")}
            </Btn> */}
          </BtnToolbar>
        )}
      </form>
    );
  }
}
