import React from "react";
import { Link } from "react-router-dom";
import { isDef, dateFormat } from "utilities";
import moment from "moment";

import { PropertiesTable } from "components/shared";
export class SensorProperties extends React.Component {
  render() {
    const { sensorData, t } = this.props;

    const SensorMeasuresArray = [];
    let sensorName = undefined;
    if (sensorData.SensorMeasuresList !== undefined) {
      sensorData.SensorMeasuresList.map(sensorMeasure =>
        SensorMeasuresArray.push(sensorMeasure.name)
      );
      sensorName = SensorMeasuresArray.join(" , ");
    }

    const leftTableData = {
      [t("sensors.flyouts.details.property.sensorName")]: sensorData.name,
      [t("sensors.flyouts.details.property.hardwareID")]: sensorData.hardwareId,
      [t("sensors.flyouts.details.property.sensorType")]: sensorData.modelName,
      [t(
        "sensors.flyouts.details.property.location"
      )]: sensorData.locationCordinate,
      [t(
        "sensors.flyouts.details.property.parentDevice"
      )]: sensorData.parentDevice,
      [t("sensors.flyouts.details.property.portType")]: (sensorData.port || {})
        .type
    };

    const rightTableData = {
      [t("sensors.flyouts.details.property.sensorId")]: (
        <span className="sensorid">{sensorData.id}</span>
      ),
      [t("sensors.flyouts.details.property.portName")]: (sensorData.port || {})
        .name,
      [t("sensors.flyouts.details.property.addedOn")]: moment(
        sensorData.createdAt
      ).format(dateFormat),
      [t("sensors.flyouts.details.property.lastUpdated")]: moment(
        sensorData.lastModifiedAt
      ).format(dateFormat),
      [t("sensors.flyouts.details.property.farm")]: isDef(
        sensorData.farmName
      ) ? (
        <Link
          to={{
            pathname: `/farms/${sensorData.farmId}`,
            state: {
              id: sensorData.farmId,
              farmName: sensorData.farmName
            }
          }}
        >
          {sensorData.farmName}
        </Link>
      ) : null,
      [t("sensors.flyouts.details.property.measures")]: sensorName
    };

    return (
      <PropertiesTable
        leftTableData={leftTableData}
        rightTableData={rightTableData}
      />
    );
  }
}
