import React, { Component } from "react";
import { permissions } from "services/models";
import { Btn, Indicator, Protected } from "components/shared";
import { svgs } from "utilities";
import { appInsights } from "applicationInsights";

export class DeleteRoleAssignmentRenderer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      deletePending: false
    };
  }

  render() {
    const t = this.props.context.t;
    const {
      deleteRoleAssignmentsById,
      successCallback,
      clearRolesError
    } = this.props;
    const deletePending = this.state.deletePending;
    return (
      <div>
        {!deletePending && (
          <Protected permissions={permissions.deleteUser}>
            <Btn
              svg={svgs.trash}
              className="delete-user"
              onClick={event => {
                appInsights.trackEvent({
                  name: "UiEvent:Click: Delete User Button"
                });
                clearRolesError();
                this.setState({
                  deletePending: true
                });
                deleteRoleAssignmentsById(
                  this.props.node.data.roleAssignment.id,
                  {
                    success: () =>
                      successCallback(
                        t(
                          "users.manageUsersFlyout.rolesGridSection.roleDeleteSuccess"
                        )
                      ),
                    error: () => {
                      this.setState({ deletePending: false });
                    }
                  }
                );
              }}
            />
          </Protected>
        )}
        {!!deletePending && (
          <div className={"delete-pending-indicator"}>
            <center>
              <Indicator size={"small"} />
            </center>
          </div>
        )}
      </div>
    );
  }
}
