import React, { Component } from "react";
import { Observable } from "rxjs";
import {
  AjaxError,
  SuccessMessage,
  Btn,
  BtnToolbar,
  Protected,
  ToggleBtn,
  Indicator
} from "components/shared";
import { enabled, disabled } from "utilities";
import { TelemetryService } from "services";
import { permissions, toEditRuleRequestModel } from "services/models";
import Flyout from "components/shared/flyout";

import "./ruleStatus.scss";
import { RuleSummaryAutoContainer as RuleSummaryAuto } from "../shared";

export class RuleStatus extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isPending: false,
      error: undefined,
      changesApplied: undefined,
      isRuleStatusChanged: undefined,
      ruleStatusError: undefined
    };
  }

  componentDidMount() {
    const { rules } = this.props;
    this.setState({
      rules,
      status: rules.length === 1 ? rules[0].status !== enabled : false
    });
  }

  componentWillUnmount() {
    if (this.subscription) this.subscription.unsubscribe();
  }

  onToggle = ({ target: { value } }) => {
    this.setState({ status: value, changesApplied: false });
  };

  changeRuleStatus = event => {
    event.preventDefault();
    const { rules, status } = this.state;
    this.setState({ isPending: true, error: null });
    if (rules !== undefined) {
      const requestPropList = rules.map(rule => ({
        ...rule,
        status: status ? enabled : disabled
      }));

      this.subscription = Observable.from(requestPropList)
        .flatMap(rule => {
          return TelemetryService.updateRule(
            rule.id,
            toEditRuleRequestModel(rule)
          ).map(updatedRule => ({ ...rule, eTag: updatedRule.eTag }));
        })
        .subscribe(
          // Next
          updatedRule => {
            this.props.modifyRules([updatedRule]);
          },
          // Error
          error =>
            this.setState({
              isPending: false,
              changesApplied: true,
              isRuleStatusChanged: false,
              ruleStatusError: error
            }),
          // Complete
          () =>
            this.setState({
              isPending: false,
              changesApplied: true,
              isRuleStatusChanged: true
            })
        );
    }
  };

  render() {
    const { onClose, t, rules } = this.props;
    const {
      isPending,
      status,
      changesApplied,
      isRuleStatusChanged,
      ruleStatusError
    } = this.state;
    return (
      <Flyout.Container>
        <Flyout.Header>
          <Flyout.Title>{t("rules.flyouts.statusTitle")}</Flyout.Title>
          <Flyout.CloseBtn onClick={onClose} />
        </Flyout.Header>
        <Flyout.Content>
          <Protected permissions={permissions.updateRules}>
            <form
              onSubmit={this.changeRuleStatus}
              className="disable-rule-flyout-container"
            >
              <div className="padded-top-bottom">
                <ToggleBtn value={status} onChange={this.onToggle}>
                  {!status
                    ? t("rules.flyouts.disable")
                    : t("rules.flyouts.enable")}
                </ToggleBtn>
              </div>
              {rules &&
                rules.map((rule, idx) => (
                  <RuleSummaryAuto key={rule.id} rule={rule} />
                ))}
              {isPending && <Indicator />}
              {isRuleStatusChanged === true && (
                <SuccessMessage
                  className="rule-status-success"
                  t={t}
                  successMsg={
                    rules.length > 1
                      ? t("rules.flyouts.ruleStatusSuccess.plural")
                      : t("rules.flyouts.ruleStatusSuccess.singular")
                  }
                />
              )}

              {ruleStatusError && (
                <AjaxError
                  className="rule-status-error"
                  t={t}
                  error={ruleStatusError}
                />
              )}
              {
                <BtnToolbar>
                  {!changesApplied && !isPending && (
                    <Btn primary={true} type="submit" id={"applyRuleBtn"}>
                      {t("rules.flyouts.ruleEditor.apply")}
                    </Btn>
                  )}
                  {/* <Btn svg={svgs.cancelX} onClick={onClose}>
                    {t("rules.flyouts.ruleEditor.cancel")}
                  </Btn> */}
                </BtnToolbar>
              }
            </form>
          </Protected>
        </Flyout.Content>
      </Flyout.Container>
    );
  }
}
