import React, { Component } from "react";
import {
  ComponentArray,
  PageContent,
  ContextMenuAlign,
  SearchInput
} from "components/shared";
import { AddDeviceGrid } from "./addDeviceGrid";
import "./addDevices.scss";
const initialState = {
  selectedDevices: undefined
};

export class SelectDevices extends Component {
  constructor(props) {
    super(props);

    this.state = initialState;
  }

  onGridReady = gridReadyEvent => (this.addDeviceGridApi = gridReadyEvent.api);

  searchOnChange = ({ target: { value } }) => {
    if (this.addDeviceGridApi) this.addDeviceGridApi.setQuickFilter(value);
  };

  mapDevicesToFarm = () => {
    const { farms, devices } = this.props;
    const deviceList = devices.map((device, id) => {
      const farm = farms.find(
        farm => device.farmId && farm.id === device.farmId
      );
      return {
        farmId: farm ? farm.id : null,
        id: id,
        deviceId: device.id,
        farmName: farm ? farm.name : undefined,
        deviceName: device.name
      };
    });

    return deviceList;
  };

  render() {
    const rowData = this.mapDevicesToFarm();
    const { t, onHardSelectChange } = this.props;
    const gridProps = {
      t,
      onGridReady: this.onGridReady,
      rowData,
      onHardSelectChange: onHardSelectChange
    };
    return (
      <ComponentArray>
        <ContextMenuAlign left={true}>
          <SearchInput
            className="search-Devices"
            onChange={this.searchOnChange}
            placeholder={t("farms.flyouts.addDevices.searchPlaceholder")}
          />
        </ContextMenuAlign>
        <PageContent>
          <AddDeviceGrid {...gridProps} />
        </PageContent>
      </ComponentArray>
    );
  }
}
