import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";
import { AccessControl } from "./accessControl";

import {
  epics as userEpics,
  getUsers,
  getUserError,
  getUserLastUpdated,
  getUserPendingStatus
} from "store/reducers/userReducer";
import {
  epics as roleEpics,
  redux as rolesRedux,
  getRoleAssignments,
  getFetchRoleAssignmentError,
  getAddRoleAssignmentError,
  getDeleteRoleAssignmentError,
  getRoleAssignmentLastUpdated,
  getFetchRoleAssignmentPendingStatus,
  getRoleDefinitions,
  getRoleDefinitionError,
  getRoleDefinitionLastUpdated,
  getRoleDefinitionPendingStatus,
  getAddRoleAssignmentPendingStatus,
  getDeleteRoleAssignmentPendingStatus
} from "store/reducers/rolesReducer";

const mapStateToProps = state => ({
  users: getUsers(state),
  userError: getUserError(state),
  userLastUpdated: getUserLastUpdated(state),
  userPending: getUserPendingStatus(state),

  roleAssignments: getRoleAssignments(state),
  roleAssignmentFetchError: getFetchRoleAssignmentError(state),
  roleAssignmentAddError: getAddRoleAssignmentError(state),
  roleAssignmentDeleteError: getDeleteRoleAssignmentError(state),
  roleAssignmentLastUpdated: getRoleAssignmentLastUpdated(state),
  roleAssignmentFetchPending: getFetchRoleAssignmentPendingStatus(state),
  roleAssignmentAddPending: getAddRoleAssignmentPendingStatus(state),
  roleAssignmentDeletePending: getDeleteRoleAssignmentPendingStatus(state),

  roleDefinitions: getRoleDefinitions(state),
  roleDefinitionError: getRoleDefinitionError(state),
  roleDefinitionLastUpdated: getRoleDefinitionLastUpdated(state),
  roleDefinitionPending: getRoleDefinitionPendingStatus(state)
});

const mapDispatchToProps = dispatch => ({
  fetchUsers: userIds => dispatch(userEpics.actions.fetchUsers(userIds)),
  fetchRoleAssignments: () =>
    dispatch(roleEpics.actions.fetchRoleAssignments()),
  updateRoleAssignment: (id, payload) =>
    dispatch(roleEpics.actions.updateRoleAssignment({ id, payload })),
  deleteRoleAssignmentsById: (id, callbacks) =>
    dispatch(roleEpics.actions.deleteRoleAssignmentById({ id, callbacks })),
  fetchRoleDefinitions: () =>
    dispatch(roleEpics.actions.fetchRoleDefinitions()),
  clearRolesError: () => dispatch(rolesRedux.actions.clearError())
});

export const AccessControlContainer = withNamespaces()(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(AccessControl)
);
