import React, { Component } from "react";
import moment from "moment";
import { Btn } from "components/shared";
// import { toDiagnosticsModel } from 'services/models';
import { svgs, dateFormat } from "utilities";

import "./refreshBar.scss";

export class RefreshBar extends Component {
  refresh = () => {
    return !this.props.isPending && this.props.refresh();
  };

  render() {
    const { t, isPending, time } = this.props;
    return (
      <div className="last-updated-container">
        {isPending || time ? (
          <span className="time">
            <span className="refresh-text">
              {isPending
                ? t("refreshBar.refreshing")
                : t("refreshBar.lastRefreshed")}{" "}
            </span>
            {!isPending ? moment(time).format(dateFormat) : ""}
          </span>
        ) : null}
        <Btn
          svg={svgs.refresh}
          className={`refresh-btn ${isPending ? "refreshing" : ""}`}
          onClick={this.refresh}
        />
      </div>
    );
  }
}
