import React, { Component } from "react";
import { Trans } from "react-i18next";

import { permissions /* toDiagnosticsModel*/ } from "services/models";
import { Btn, ComponentArray, PcsGrid, Protected } from "components/shared";
import { rulesColumnDefs, defaultRulesGridProps } from "./rulesGridConfig";
import { checkboxColumn } from "components/shared/pcsGrid/pcsGridConfig";
import { translateColumnDefs, svgs } from "utilities";
import {
  EditRuleFlyout,
  RuleDetailsFlyout,
  RuleStatusContainer,
  DeleteRuleContainer,
  NewRuleFlyout
} from "../flyouts";

import "./rulesGrid.scss";
import PropTypes from "prop-types";
import { appInsights } from "applicationInsights";

const closedFlyoutState = {
  openFlyoutName: undefined,
  softSelectedRuleId: undefined
};

export class RulesGrid extends Component {
  constructor(props) {
    super(props);

    // Set the initial state
    this.state = {
      ...closedFlyoutState,
      selectedRules: undefined
    };
    this.columnDefs = [
      checkboxColumn,
      rulesColumnDefs.ruleName,
      rulesColumnDefs.description,
      rulesColumnDefs.severity,
      rulesColumnDefs.filter,
      rulesColumnDefs.trigger,
      rulesColumnDefs.status
      // rulesColumnDefs.count,
      // rulesColumnDefs.lastTrigger
    ];

    this.contextBtns = {
      // disable: (
      //   <Protected key="disable" permissions={permissions.updateRules}>
      //     <Btn
      //       className="rule-status-btn"
      //       svg={svgs.disableToggle}
      //       onClick={this.openStatusFlyout}
      //     >
      //       <Trans i18nKey="rules.flyouts.disable">Disable</Trans>
      //     </Btn>
      //   </Protected>
      // ),
      // enable: (
      //   <Protected key="enable" permissions={permissions.updateRules}>
      //     <Btn
      //       className="rule-status-btn enabled"
      //       svg={svgs.enableToggle}
      //       onClick={this.openStatusFlyout}
      //     >
      //       <Trans i18nKey="rules.flyouts.enable">Enable</Trans>
      //     </Btn>
      //   </Protected>
      // ),
      changeStatus: (
        <Protected key="changeStatus" permissions={permissions.updateRules}>
          <Btn
            className="rule-status-btn"
            svg={svgs.changeStatus}
            onClick={() => {
              this.openStatusFlyout();
              appInsights.trackEvent({
                name: "UiEvent:Click: Chg Rule Status Fly Open Btn"
              });
            }}
          >
            <Trans i18nKey="rules.flyouts.changeStatus">Change status</Trans>
          </Btn>
        </Protected>
      ),
      edit: (
        <Protected key="edit" permissions={permissions.updateRules}>
          <Btn
            svg={svgs.edit}
            onClick={() => {
              this.openEditRuleFlyout();
              appInsights.trackEvent({
                name: "UiEvent:Click: Edit Rule Fly Open Btn"
              });
            }}
            className="edit-rule"
          >
            {props.t("rules.flyouts.edit")}
          </Btn>
        </Protected>
      ),
      delete: (
        <Protected key="delete" permissions={permissions.deleteRules}>
          <Btn
            svg={svgs.trash}
            onClick={() => {
              this.openDeleteFlyout();
              appInsights.trackEvent({ name: "UiEvent:Click: Del Rule Btn" });
            }}
          >
            <Trans i18nKey="rules.flyouts.delete">Delete</Trans>
          </Btn>
        </Protected>
      ),
      addNewRule: (
        <Protected key="newRule" permissions={permissions.createRules}>
          <Btn
            svg={svgs.plus}
            onClick={() => {
              this.openNewRuleFlyout();
              appInsights.trackEvent({
                name: "UiEvent:Click: New Rule Fly Open Btn"
              });
            }}
          >
            {props.t("rules.flyouts.newRule")}
          </Btn>
        </Protected>
      )
    };
    this.props.onContextMenuChange([this.contextBtns.addNewRule]);
  }

  // componentWillReceiveProps({ rowData }) {
  //   const { selectedRules = [], softSelectedRuleId } = this.state;
  //   if (rowData && (selectedRules.length || softSelectedRuleId)) {
  //     let updatedSoftSelectedRule = undefined;
  //     const selectedIds = new Set(selectedRules.map(({ id }) => id));
  //     const updatedSelectedRules = rowData.reduce((acc, rule) => {
  //       if (selectedIds.has(rule.id)) acc.push(rule);
  //       if (softSelectedRuleId && rule.id === softSelectedRuleId) {
  //         updatedSoftSelectedRule = rule;
  //       }
  //       return acc;
  //     }, []);
  //     this.setState({
  //       selectedRules: updatedSelectedRules,
  //       softSelectedRuleId: (updatedSoftSelectedRule || {}).id
  //     });
  //   }
  // }

  openEditRuleFlyout = () => {
    this.setState({ openFlyoutName: "edit" });
  };

  openStatusFlyout = () => this.setState({ openFlyoutName: "status" });

  openDeleteFlyout = () => this.setState({ openFlyoutName: "delete" });

  openNewRuleFlyout = () => this.setState({ openFlyoutName: "newRule" });

  setSelectedRules = selectedRules => this.setState({ selectedRules });

  getOpenFlyout = () => {
    switch (this.state.openFlyoutName) {
      case "view":
        return (
          <RuleDetailsFlyout
            onClose={this.closeFlyout}
            className="rulesview-flyout"
            t={this.props.t}
            ruleId={
              this.state.softSelectedRuleId || this.state.selectedRules[0].id
            }
            key="view-rule-flyout"
          />
        );
      case "edit":
        return (
          <EditRuleFlyout
            onClose={this.closeFlyout}
            t={this.props.t}
            ruleId={
              this.state.softSelectedRuleId ||
              (this.state.selectedRules[0] && this.state.selectedRules[0].id)
            }
            key="edit-rule-flyout"
          />
        );
      case "status":
        return (
          <RuleStatusContainer
            onClose={this.closeFlyout}
            t={this.props.t}
            rules={this.state.selectedRules}
            key="edit-rule-flyout"
          />
        );
      case "delete":
        return (
          <DeleteRuleContainer
            onClose={this.closeFlyout}
            t={this.props.t}
            rule={this.state.selectedRules[0]}
            key="delete-rule-flyout"
            refresh={this.props.refresh}
          />
        );
      case "newRule":
        return <NewRuleFlyout t={this.props.t} onClose={this.closeFlyout} />;
      default:
        return null;
    }
  };

  /**
   * Handles context filter changes and calls any hard select props method
   *
   * @param {Array} selectedRules A list of currently selected rules
   */
  onHardSelectChange = selectedRules => {
    this.setState({ selectedRules });
    const { onContextMenuChange } = this.props;

    if (selectedRules.length === 1) {
      onContextMenuChange([
        this.contextBtns.delete,
        this.contextBtns.changeStatus,
        this.contextBtns.edit
      ]);
    } else {
      // No rule selected
      onContextMenuChange([this.contextBtns.addNewRule]);
    }
  };

  onSoftSelectChange = ruleId => {
    if (ruleId) {
      this.setState({
        openFlyoutName: "view",
        softSelectedRuleId: ruleId
      });
    }
  };

  getSoftSelectId = ({ id } = "") => id;

  closeFlyout = () => this.setState(closedFlyoutState);

  render() {
    const gridProps = {
      /* Grid Properties */
      ...defaultRulesGridProps,
      columnDefs: translateColumnDefs(this.props.t, this.columnDefs),
      sizeColumnsToFit: true,
      getSoftSelectId: this.getSoftSelectId,
      softSelectId: this.state.softSelectedRuleId || {},
      deltaRowDataMode: true,
      ...this.props, // Allow default property overrides
      getRowNodeId: ({ id }) => id,
      enableSorting: true,
      unSortIcon: true,
      context: {
        t: this.props.t
      },

      /* Grid Events */
      onRowClicked: ({ node }) => node.setSelected(!node.isSelected()),
      onHardSelectChange: this.onHardSelectChange,
      onSoftSelectChange: this.onSoftSelectChange
    };

    return (
      <ComponentArray>
        <PcsGrid {...gridProps} />
        {this.getOpenFlyout()}
      </ComponentArray>
    );
  }
}

RulesGrid.propTypes = {
  onContextMenuChange: PropTypes.func.isRequired,
  t: PropTypes.func
};
