import React from "react";

import Config from "app.config";
import { Svg } from "components/shared/svg/svg";
import { svgs } from "utilities";

import "../cellRenderer.scss";
import "./deviceStatusRenderer.scss";

const getSvg = value => {
  if (value === Config.deviceModel.online) return svgs.online;
  if (value === Config.deviceModel.offline) return svgs.offline;
};

export const deviceStatusRenderer = ({ value, context: { t }, iconOnly }) => {
  const cleanValue = (value || "").toLowerCase();
  const cellClasses = `pcs-renderer-cell status ${cleanValue || ""} ${
    cleanValue ? "highlight" : ""
  }`;
  return (
    <div className={cellClasses}>
      <Svg path={getSvg(cleanValue)} className="pcs-renderer-icon" />
      {<div className="pcs-renderer-text">{t(value)}</div>}
    </div>
  );
};

export default deviceStatusRenderer;
