import React, { Component } from "react";
import { DevicesGrid } from "./devicesGrid";
import {
  AjaxError,
  ComponentArray,
  ContextMenu,
  ContextMenuAlign,
  PageContent,
  PageTitle,
  // RefreshBarContainer as RefreshBar,
  SearchInput
} from "components/shared";
import { deviceService } from "services";
import "./devices.scss";
import { withAITracking } from "@microsoft/applicationinsights-react-js";
import { reactPlugin } from "applicationInsights";

const closedFlyoutState = { openFlyoutName: undefined };
const closedDeletePopupState = { openDeleteDevicePopup: undefined };

class Devices extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ...closedFlyoutState,
      deviceList: undefined,
      contextBtns: null,
      ...closedDeletePopupState,
      deleteModalSuccess: false,
      deleteModalError: undefined,
      deleteDevicePending: false
    };

    const {
      deviceLastUpdated,
      deviceError,
      fetchDevices,
      farmLastUpdated,
      farmError,
      fetchFarms
    } = this.props;

    if (!deviceLastUpdated && !deviceError) fetchDevices();
    if (!farmLastUpdated && !farmError) fetchFarms();
  }

  closeFlyout = () => this.setState(closedFlyoutState);
  closeDeletePopup = () => this.setState(closedDeletePopupState);

  onContextMenuChange = contextBtns =>
    this.setState({
      contextBtns,
      openFlyoutName: undefined
    });

  onGridReady = gridReadyEvent => (this.deviceGridApi = gridReadyEvent.api);

  searchOnChange = ({ target: { value } }) => {
    if (this.deviceGridApi) this.deviceGridApi.setQuickFilter(value);
  };

  // TODO Should be an epic
  deleteDevicesById = devices => {
    this.setState({
      deleteModalSuccess: false,
      deleteModalError: undefined,
      deleteDevicePending: true
    });
    this.subscription = deviceService.deleteDevice(devices[0].id).subscribe(
      () => {
        this.props.deleteDevicesById([devices[0].id]);
        this.setState({
          deleteModalError: undefined,
          deleteModalSuccess: true,
          deleteDevicePending: false
        });
      },
      error => {
        this.setState({
          deleteModalError: error,
          deleteModalSuccess: false,
          isPending: false,
          changesApplied: true,
          deleteDevicePending: false
        });
      },
      () =>
        this.setState({
          isPending: false,
          changesApplied: true,
          ruleDeleted: true
        })
    );
  };
  render() {
    const {
      t,
      deviceError,
      isDevicesPending,
      devices,
      isFarmsPending
    } = this.props;
    const {
      deleteModalSuccess,
      deleteModalError,
      deleteDevicePending
    } = this.state;
    const gridProps = {
      deleteDevicesById: this.deleteDevicesById,
      onGridReady: this.onGridReady,
      rowData: isDevicesPending || isFarmsPending ? undefined : devices || [],
      onContextMenuChange: this.onContextMenuChange,
      t: t,
      deleteModalSuccess: deleteModalSuccess,
      deleteModalError: deleteModalError,
      deleteDevicePending: deleteDevicePending
    };

    const error = deviceError;

    return (
      <ComponentArray>
        <ContextMenu>
          <ContextMenuAlign left={true}>
            <SearchInput
              onChange={this.searchOnChange}
              placeholder={t("devices.searchPlaceholder")}
            />
            {this.state.contextBtns}
          </ContextMenuAlign>
        </ContextMenu>
        <PageContent className="devices-container">
          <PageTitle
            titleValue={t("devices.title").concat(
              "(",
              String(devices.length),
              ")"
            )}
          />
          {!!error && <AjaxError t={t} error={error} />}
          {!error && <DevicesGrid {...gridProps} />}
        </PageContent>
      </ComponentArray>
    );
  }
}

export default withAITracking(reactPlugin, Devices, "Device Grid");
