import Config from "app.config";
import { HttpClient } from "utilities/httpClient";
import {
  RoleAssignmentsModel,
  RoleDefinitionsModel,
  RoleDefinitionModel
} from "./models";

/** Contains methods for calling the RoleAssignment and RoleDefinition APIs */
export class roleService {
  static getRoleAssignments() {
    return HttpClient.get(
      `${Config.serviceUrls.roleAssignment}?maxItems=${Config.apiGetAllMaxItems}`,
      {},
      true
    ).map(RoleAssignmentsModel);
    // return HttpClient.get(Config.serviceUrls.roleAssignment, {}, true);
  }

  static getRoleAssignmentsByUserObjId(userObjId) {
    return HttpClient.get(
      `${Config.serviceUrls.roleAssignment}?maxItems=${Config.apiGetAllMaxItems}&objectIds=${userObjId}`,
      {},
      true
    ).map(RoleAssignmentsModel);
  }

  static getRoleDefinitions() {
    return HttpClient.get(
      `${Config.serviceUrls.roleDefinition}?maxItems=${Config.apiGetAllMaxItems}`,
      {},
      true
    ).map(RoleDefinitionsModel);
  }

  static getRoleDefinitionById(roleDefinitionId) {
    return HttpClient.get(
      `${Config.serviceUrls.roleDefinition}/${roleDefinitionId}`,
      {},
      true
    ).map(RoleDefinitionModel);
  }

  static deleteRoleAssignment(id) {
    return HttpClient.delete(
      `${Config.serviceUrls.roleAssignment}/${id}`,
      {},
      true
    ).map(() => ({
      id: id
    }));
  }

  static updateRoleAssignment(id, payload) {
    return HttpClient.put(
      `${Config.serviceUrls.roleAssignment}/${id}`,
      payload
    ).map(() => id);
  }

  static addRoleAssignment(payload) {
    return HttpClient.post(`${Config.serviceUrls.roleAssignment}`, payload);
  }
}
