import React, { Component } from "react";
import { RulesGrid } from "./rulesGrid";
import {
  AjaxError,
  ComponentArray,
  ContextMenu,
  ContextMenuAlign,
  PageContent,
  PageTitle,
  SearchInput
} from "components/shared";
import { reactPlugin } from "applicationInsights";
import { withAITracking } from "@microsoft/applicationinsights-react-js";

import "./rules.scss";

class Rules extends Component {
  constructor(props) {
    super(props);

    this.state = {
      contextBtns: null
    };

    if (!this.props.lastUpdated && !this.props.error) {
      this.props.fetchRules();
    }
    if (!this.props.farmsLastUpdated && !this.props.farmsError) {
      this.props.fetchFarms();
    }
    if (!this.props.devicesLastUpdated && !this.props.devicesError) {
      this.props.fetchDevices();
    }
    if (!this.props.sensorsLastUpdated && !this.props.sensorsError) {
      this.props.fetchSensors();
    }

    if (
      !this.props.sensorModelsLastUpdated &&
      !this.props.isSensorModelsPending
    ) {
      this.props.fetchSensorModels();
    }
  }

  getRulesList(farms, rules) {
    let farmList;
    const ruleslist = rules.map(item => {
      return {
        ...item
      };
    });
    ruleslist.forEach(rules => {
      farmList = farms.find(farm => farm.id === rules.farmId);
      rules.farmName = farmList === undefined ? undefined : farmList.name;
    });

    return ruleslist;
  }

  onGridReady = gridReadyEvent => (this.rulesGridApi = gridReadyEvent.api);

  searchOnChange = ({ target: { value } }) => {
    if (this.rulesGridApi) this.rulesGridApi.setQuickFilter(value);
  };

  onContextMenuChange = contextBtns => this.setState({ contextBtns });

  render() {
    const rulesList = this.getRulesList(this.props.farms, this.props.rules);
    const { t, error, isRulesPending, isFarmsPending } = this.props;
    const isPending = isRulesPending || isFarmsPending;
    const gridProps = {
      onGridReady: this.onGridReady,
      rowData: isPending ? undefined : rulesList || [],
      onContextMenuChange: this.onContextMenuChange,
      t: this.props.t,
      refresh: this.refreshRulesData
    };

    return (
      <ComponentArray>
        <ContextMenu>
          <ContextMenuAlign left={true} className="rule-menus">
            <SearchInput
              onChange={this.searchOnChange}
              placeholder={t("rules.searchPlaceholder")}
            />
            {this.state.contextBtns}
          </ContextMenuAlign>
        </ContextMenu>
        <PageContent className="rules-container">
          <PageTitle
            titleValue={t("rules.title").concat(
              "(",
              String(rulesList.length),
              ")"
            )}
          />
          {!!error && <AjaxError t={t} error={error} />}
          {!error && <RulesGrid {...gridProps} />}
        </PageContent>
      </ComponentArray>
    );
  }
}

export default withAITracking(reactPlugin, Rules, "Rules Grid");
