import React, { Component } from "react";
import moment from "moment";
import "bootstrap-daterangepicker/daterangepicker.css";
import { svgs } from "utilities";
import DateRangePicker from "react-bootstrap-daterangepicker";
import { Btn } from "components/shared";
export class DatePickerElement extends Component {
  render() {
    const {
      inputValue,
      onApplyEvent,
      containerType,
      containerPlaceHolder,
      containerValue,
      containerIcon,
      className,
      IconClass,
      t,
      minDate,
      startDate,
      endDate
    } = this.props;
    return (
      <DateRangePicker
        singleDatePicker={this.props.singleDatePicker}
        showDropdowns
        maxDate={moment()}
        minDate={minDate}
        startDate={startDate}
        endDate={endDate}
        opens="left"
        onApply={onApplyEvent}
      >
        {containerType ? (
          <React.Fragment>
            <input
              className={className}
              type="text"
              placeholder={containerPlaceHolder}
              defaultValue={containerValue}
              onChange={this.props.onChange}
            />
            {containerIcon && (
              <span className={IconClass}>
                <img
                  src={containerIcon}
                  alt={t("maps.filterFlyout.datePickerIcon")}
                />
              </span>
            )}
          </React.Fragment>
        ) : (
          <Btn primary={true} svg={svgs.calendar} className="date-picker-btn">
            {inputValue}
          </Btn>
        )}
      </DateRangePicker>
    );
  }
}
