import React, { Component } from "react";
import { PcsGrid } from "components/shared";
import { alertsColumnDefs, defaultAlertsGridProps } from "./alertsGridConfig";
import { isFunc, translateColumnDefs } from "utilities";
const initialState = {
  softSelectedId: undefined,
  softSelectedAlert: undefined
};

export class AlertsGrid extends Component {
  constructor(props) {
    super(props);

    // Set the initial state
    this.state = initialState;

    // Default device grid columns
    this.columnDefs = [
      alertsColumnDefs.ruleName,
      alertsColumnDefs.description,
      alertsColumnDefs.severity,
      alertsColumnDefs.totalCount,
      alertsColumnDefs.open,
      alertsColumnDefs.acknowledged,
      alertsColumnDefs.closed,
      alertsColumnDefs.lastOccurrence
    ];
  }

  onGridReady = gridReadyEvent => {
    this.alertsGridApi = gridReadyEvent.api;
    // Call the onReady props if it exists
    if (isFunc(this.props.onGridReady)) {
      this.props.onGridReady(gridReadyEvent);
    }
  };

  onSoftSelectChange = (rowId, rowData) => {
    // Note: only the Id is reliable, rowData may be out of date
    const { onSoftSelectChange } = this.props;

    if (rowId) {
      this.setState({
        softSelectedId: rowId,
        softSelectedAlert: rowData
      });
    }

    if (isFunc(onSoftSelectChange)) {
      onSoftSelectChange(rowId, rowData);
    }
  };

  onHardSelectChange = selectedAlerts => {
    const { onContextMenuChange, onHardSelectChange } = this.props;
    // Show the context buttons when there are rows checked.
    if (isFunc(onContextMenuChange)) {
      onContextMenuChange(selectedAlerts.length > 0 ? this.contextBtns : null);
    }
    if (isFunc(onHardSelectChange)) {
      onHardSelectChange(selectedAlerts);
    }
  };

  getSoftSelectId = ({ id } = "") => id;

  render() {
    const { softSelectedAlert } = this.state;
    const { t } = this.props;

    const gridProps = {
      /* Grid Properties */
      ...defaultAlertsGridProps,
      columnDefs: translateColumnDefs(t, this.columnDefs),
      sizeColumnsToFit: true,
      getSoftSelectId: this.getSoftSelectId,
      softSelectId: (softSelectedAlert || {}).id,
      ...this.props, // Allow default property overrides
      deltaRowDataMode: true,
      enableSorting: true,
      unSortIcon: true,
      getRowNodeId: ({ id }) => id,
      context: {
        t: t
      },
      onGridReady: this.onGridReady,
      onSoftSelectChange: this.onSoftSelectChange,
      onHardSelectChange: this.onHardSelectChange
    };

    return <PcsGrid key="alert-grid-key" {...gridProps} />;
  }
}
