import Config from "app.config";
import {
  /* EMPTY_FIELD_VAL,*/ gridValueFormatters
} from "components/shared/pcsGrid/pcsGridConfig";
import {
  SoftSelectLinkRenderer,
  JobStatusRenderer,
  TimeRenderer
} from "components/shared/cellRenderers";
import { caseInsensitiveStringComparator } from "utilities";

const { checkForEmpty } = gridValueFormatters;

export const jobsColumnDefs = {
  id: {
    headerName: "jobs.grid.id",
    field: "id"
  },
  name: {
    headerName: "jobs.grid.name",
    field: "name",
    sort: "asc",
    cellRendererFramework: SoftSelectLinkRenderer,
    comparator: caseInsensitiveStringComparator
  },
  typeOfJob: {
    headerName: "jobs.grid.typeOfJob",
    field: "jobTypeName"
  },
  farmName: {
    headerName: "jobs.grid.farmName",
    field: "farmName",
    valueFormatter: ({ value }) => checkForEmpty(value),
    comparator: caseInsensitiveStringComparator
  },
  status: {
    headerName: "jobs.grid.status",
    field: "currentState",
    cellRendererFramework: JobStatusRenderer,
    cellStyle: {
      overflow: "visible",
      opacity: 1
    }
  },
  startTime: {
    headerName: "jobs.grid.startDate",
    field: "startDate",
    valueFormatter: ({ value }) => checkForEmpty(value)
  },
  endTime: {
    headerName: "jobs.grid.endDate",
    field: "endDate",
    valueFormatter: ({ value }) => checkForEmpty(value)
  },
  createdAt: {
    headerName: "jobs.grid.createdAt",
    field: "createdAt",
    cellRendererFramework: TimeRenderer
  }
};

export const getSoftSelectId = ({ id }) => id;

/** Shared jobs grid AgGrid properties */
export const defaultjobsGridProps = {
  enableColResize: true,
  multiSelect: true,
  pagination: true,
  paginationPageSize: Config.paginationPageSize,
  rowSelection: "single"
};
