import React from "react";
import {
  Flyout,
  FlyoutHeader,
  FlyoutTitle,
  FlyoutCloseBtn,
  FlyoutContent
} from "components/shared/flyout";
import { Observable } from "rxjs";
import moment from "moment";
import { LinkedComponent, svgs, dateFormat } from "utilities";
import { SelectDevices } from "./selectDevices";
import "./addDevices.scss";
import {
  Btn,
  // ContextMenuAlign,
  AjaxError,
  BtnToolbar,
  SectionDesc,
  SuccessMessage,
  SummaryBody,
  SummaryCount,
  Indicator,
  SummarySection
} from "components/shared";
import { deviceService } from "services";
import { toNewDeviceRequestModel } from "services/models/deviceModels";
import { appInsights } from "applicationInsights";

const SELECTDEVICES = "selectDevices";
// const RECOMMENDED = "recommended";

const initialState = {
  selectedTab: SELECTDEVICES,
  addDeviceError: undefined,
  changesApplied: undefined,
  isAddDevicePending: undefined,
  selectedDevices: undefined,
  isAddDeviceSuccessful: undefined
};

export class AddDevices extends LinkedComponent {
  constructor(props) {
    super(props);

    this.state = initialState;

    const {
      deviceLastUpdated,
      deviceError,
      fetchDevices,

      deviceModelLastUpdated,
      deviceModelError,
      fetchDeviceModel,

      farmLastUpdated,
      farmsError,
      fetchFarms
    } = this.props;

    if (!deviceLastUpdated && !deviceError) fetchDevices();
    if (!farmLastUpdated && !farmsError) fetchFarms();
    if (!deviceModelLastUpdated && !deviceModelError) fetchDeviceModel();

    this.onClickTabChange = this.onClickTabChange.bind(this);
  }

  componentWillReceiveProps(nextProps) {
    if (this.props.rowData !== nextProps.rowData) {
      this.setState({ ...initialState });
    }
  }

  componentWillUnmount() {
    if (this.subscription) this.subscription.unsubscribe();
  }

  apply = event => {
    const { modifyDevices, devices, farmId } = this.props;
    const { selectedDevices } = this.state;
    event.preventDefault();
    appInsights.trackEvent(
      { name: "UiEvent:Click: Add Devices Btn" },
      {
        context: {
          deviceCount:
            selectedDevices && selectedDevices.length
              ? selectedDevices.length
              : 0
        }
      }
    );
    this.setState({
      isAddDevicePending: true,
      addDeviceError: null
    });

    Observable.from(selectedDevices)
      .mergeMap(selectedDevice => {
        const editableDevice = devices.find(
          device => device.id === selectedDevice.deviceId
        );
        editableDevice.farmId = farmId;
        const requestProps = JSON.parse(JSON.stringify(editableDevice));
        return deviceService.updateDevice(
          editableDevice.id,
          toNewDeviceRequestModel(requestProps)
        );
      })
      .mergeMap(device => {
        return deviceService.getDeviceById(device.id);
      })
      .subscribe(
        updatedDevice => {
          const { farmsMap, devices, deviceModelsMap } = this.props;
          updatedDevice.deviceModelType = deviceModelsMap.get(
            updatedDevice.deviceModelId
          );
          updatedDevice.deviceModelName = deviceModelsMap.get(
            updatedDevice.deviceModelId
          );
          const deviceMapWithParent = devices.find(
            deviceParent => deviceParent.id === updatedDevice.parentDeviceId
          );
          updatedDevice.parentDevice =
            deviceMapWithParent === undefined
              ? undefined
              : deviceMapWithParent.name;
          updatedDevice.lastModifiedAt = moment(
            updatedDevice.lastModifiedAt
          ).format(dateFormat);
          updatedDevice.farmName = updatedDevice.farmId
            ? farmsMap.get(updatedDevice.farmId)
            : undefined;
          modifyDevices([{ ...updatedDevice }]);
          this.setState({
            isAddDevicePending: false,
            changesApplied: true,
            isAddDeviceSuccessful: true
          });
        },
        error =>
          this.setState({
            addDeviceError: error,
            isAddDevicePending: false,
            changesApplied: true
          }),
        () =>
          this.setState({
            isAddDevicePending: false,
            changesApplied: true,
            confirmStatus: false
          })
      );
  };

  onClickTabChange(selectedTab) {
    this.setState({
      selectedTab: selectedTab
    });
  }

  onHardSelectChange = selectedDevices => {
    this.setState({ selectedDevices });
  };

  render() {
    const { onClose, t, farms, devices } = this.props;
    const {
      selectedTab,
      addDeviceError,
      changesApplied,
      isAddDeviceSuccessful,
      selectedDevices,
      isAddDevicePending
    } = this.state;
    const generalProps = {
      t,
      farms,
      devices,
      onHardSelectChange: this.onHardSelectChange
    };

    return (
      <div className="add-device-container">
        <Flyout>
          <FlyoutHeader>
            <FlyoutTitle>{t("farms.flyouts.addDevices.header")}</FlyoutTitle>
            <FlyoutCloseBtn onClick={onClose} />
          </FlyoutHeader>
          <FlyoutContent className="add-device-content ">
            {/* <ContextMenuAlign left={true} className="tab-container">
              <Btn
                className={selectedTab === RECOMMENDED ? "tab active" : "tab"}
                onClick={() => this.onClickTabChange(RECOMMENDED)}
              >
                {t("farms.flyouts.addDevices.recommended")}
              </Btn>
              <Btn
                className={selectedTab === SELECTDEVICES ? "tab active" : "tab"}
                onClick={() => this.onClickTabChange(SELECTDEVICES)}
              >
                {t("farms.flyouts.addDevices.selectDevices")}
              </Btn>
            </ContextMenuAlign> */}
            {selectedTab === SELECTDEVICES && (
              <SelectDevices {...generalProps} />
            )}
            {/* TODO Add RecommendedDevices component */}
            {/* {selectedTab === RECOMMENDED && <RecommendedDevices {...generalProps} />} */}

            <SummarySection>
              <SummaryBody>
                <SummaryCount>
                  {selectedDevices ? selectedDevices.length : 0}
                </SummaryCount>
                <SectionDesc>
                  {t("farms.flyouts.addDevices.selectedDevices")}
                </SectionDesc>
              </SummaryBody>
            </SummarySection>

            {/** Displays an error message if one occurs while applying changes. */}
            {addDeviceError && (
              <AjaxError
                className="add-devices-error"
                t={t}
                error={addDeviceError}
              />
            )}
            {changesApplied === true && isAddDeviceSuccessful === true && (
              <SuccessMessage
                className="add-devices-success"
                t={t}
                successMsg={t("farms.flyouts.addDevices.success")}
              />
            )}
            {/** If changes are not yet applied, show the buttons for applying changes and closing the flyout. */
            !changesApplied && !isAddDevicePending && (
              <BtnToolbar className="button-box">
                <Btn id="cancelBtn" svg={svgs.cancelX} onClick={onClose}>
                  {t("farms.flyouts.addDevices.cancel")}
                </Btn>
                <Btn
                  primary={true}
                  disabled={!selectedDevices || selectedDevices.length === 0}
                  type="submit"
                  onClick={this.apply}
                  id={
                    !selectedDevices || selectedDevices.length === 0
                      ? "applyBtnDisabled"
                      : "applyBtn"
                  }
                >
                  {t("farms.flyouts.addDevices.apply")}
                </Btn>
              </BtnToolbar>
            )}
            {isAddDevicePending && (
              <BtnToolbar className="button-box">
                <Indicator />
              </BtnToolbar>
            )}
            {/**
             * If changes are applied, show only the close button.
             * Other text or component might be included here as well.
             * For example, you might provide a link to the detail page for a newly submitted job.
             * */
            !!changesApplied && (
              <BtnToolbar>
                {/* <Btn svg={svgs.cancelX} onClick={onClose}>
                  {t("farms.flyouts.addDevice.close")}
                </Btn> */}
              </BtnToolbar>
            )}
          </FlyoutContent>
        </Flyout>
      </div>
    );
  }
}
