import { camelCaseReshape, getItems } from "utilities";

export const RoleDefinitionsModel = (response = []) =>
  getItems(response).map(RoleDefinitionModel);

export const RoleDefinitionModel = (response = {}) =>
  camelCaseReshape(response, {
    id: "id",
    createdAt: "createdAt",
    lastModifiedAt: "lastModifiedAt",
    permissions: "permissions",
    name: "name",
    description: "description"
  });

export const RoleAssignmentsModel = (response = []) =>
  getItems(response).map(RoleAssignmentModel);

export const RoleAssignmentModel = (response = {}) =>
  camelCaseReshape(response, {
    id: "id",
    createdAt: "createdAt",
    lastModifiedAt: "lastModifiedAt",
    roleDefinitionId: "roleDefinitionId",
    objectId: "objectId",
    objectIdType: "objectIdType",
    tenantId: "tenantId"
  });

export const toNewRoleAssignmentRequestModel = roleAssignment => roleAssignment;
