import { Component } from "react";
import { isFunc } from "utilities";

export class ProtectedImpl extends Component {
  userHasPermission() {
    let { permissions, userPermissions } = this.props;
    userPermissions = new Set(userPermissions);
    return permissions.reduce(
      (acc, permission) => acc && userPermissions.has(permission),
      true
    );
  }

  render() {
    const { children, permissions } = this.props;
    const hasPermission = this.userHasPermission();
    if (isFunc(children)) {
      return children(hasPermission, permissions);
    }
    return hasPermission ? children : null;
  }
}
