import { combineEpics } from "redux-observable";

// Epics
import { epics as appEpics } from "./reducers/appReducer";
import { epics as devicesEpics } from "./reducers/devicesReducer";
import { epics as farmEpics } from "./reducers/farmsReducer";
import { epics as mapEpics } from "./reducers/mapsReducer";
import { epics as jobsEpics } from "./reducers/jobsReducer";
import { epics as sensorsEpic } from "./reducers/sensorsReducer";
import { epics as rulesAlertsEpics } from "./reducers/rulesAlertsReducer";
import { epics as userEpics } from "./reducers/userReducer";
import { epics as roleEpics } from "./reducers/rolesReducer";

// import { template } from 'lodash-es';

// Extract the epic function from each property object
const epics = [
  ...appEpics.getEpics(),
  ...devicesEpics.getEpics(),
  ...farmEpics.getEpics(),
  ...mapEpics.getEpics(),
  ...jobsEpics.getEpics(),
  ...sensorsEpic.getEpics(),
  ...rulesAlertsEpics.getEpics(),
  ...userEpics.getEpics(),
  ...roleEpics.getEpics()
];

const rootEpic = combineEpics(...epics);

export default rootEpic;
