import React from "react";
import { Link } from "react-router-dom";
import { PropertiesTable } from "components/shared";
import { isDef, dateFormat } from "utilities";
import moment from "moment";

export class DeviceProperties extends React.Component {
  render() {
    const { device, t } = this.props;
    const leftTableData = {
      [t("devices.grid.description")]: device.description,
      [t("devices.grid.deviceModelName")]: device.deviceModelName,
      [t("devices.grid.location")]:
        isDef(device.latitude) && isDef(device.longitude)
          ? device.latitude + ", " + device.longitude
          : null,
      [t("devices.flyouts.details.deviceProperties.reportingInterval")]: [
        device.reportingInterval,
        "(",
        t("devices.reportingIntervalType"),
        ")"
      ].join(" "),
      [t("devices.grid.status")]: device.status,
      [t("devices.grid.parentDevice")]: device.parentDevice
    };
    const rightTableData = {
      [t("devices.grid.deviceId")]: device.id,
      [t("devices.grid.addedOn")]: moment(new Date(device.createdAt)).format(
        dateFormat
      ),
      [t("devices.grid.lastUpdated")]: moment(
        new Date(device.lastModifiedAt)
      ).format(dateFormat),
      [t("devices.grid.farm")]: isDef(device.farmName) ? (
        <Link
          to={{
            pathname: `/farms/${device.farmId}`,
            state: {
              id: device.farmId,
              farmName: device.farmName
            }
          }}
        >
          {device.farmName}
        </Link>
      ) : null
    };

    return (
      <PropertiesTable
        leftTableData={leftTableData}
        rightTableData={rightTableData}
      />
    );
  }
}
