import Config from "app.config";
import { HttpClient } from "utilities/httpClient";
import {
  deviceModelsModel,
  toDevicesModel,
  toDeviceModel
} from "./models/deviceModels";

const ENDPOINT = Config.serviceUrls;
export class deviceService {
  /** Returns a list of Devices */
  static getDevices() {
    return HttpClient.get(
      `${ENDPOINT.device}?includes=Properties&maxItems=${Config.apiGetAllMaxItems}`
    ).map(toDevicesModel);
  }

  /** Returns a list of DeviceModel */
  static getDeviceModels() {
    return HttpClient.get(
      `${ENDPOINT.deviceModel}?includes=Ports&includes=Properties&maxItems=${Config.apiGetAllMaxItems}`
    ).map(deviceModelsModel);
  }

  static updateDevice(id, device) {
    return HttpClient.put(`${ENDPOINT.device}/${id}`, device).map(() => ({
      id
    }));
  }
  static getDeviceById(id) {
    return HttpClient.get(`${ENDPOINT.device}/${id}`).map(toDeviceModel);
  }

  /** Deletes a device */
  static deleteDevice(id) {
    return HttpClient.delete(`${ENDPOINT.device}/${id}`).map(() => ({
      deletedDeviceId: id
    }));
  }
}
