import Config from "app.config";
import { HttpClient } from "utilities/httpClient";
import { SensorsModel, SensorModelsModel } from "./models";

/** Contains methods for calling the Device service */
export class sensorService {
  static getSensors = (conditions = []) =>
    HttpClient.get(
      `${Config.serviceUrls.sensors}?includes=Properties&maxItems=${Config.apiGetAllMaxItems}`
    ).map(SensorsModel);

  static getSensorModels = (conditions = []) =>
    HttpClient.get(
      `${Config.serviceUrls.sensorModel}?includes=Properties&includes=SensorMeasures&maxItems=${Config.apiGetAllMaxItems}`
    ).map(SensorModelsModel);

  static deleteSensor(id) {
    return HttpClient.delete(`${Config.serviceUrls.sensors}/${id}`).map(() => ({
      id: id
    }));
  }
}
