import React, { Component } from "react";
import PropTypes from "prop-types";

import {
  AjaxError,
  Indicator,
  TimeSeriesInsightsLinkContainer
} from "components/shared";
import {
  Panel,
  PanelContent,
  PanelError,
  PanelHeader,
  PanelHeaderLabel,
  PanelMsg,
  PanelOverlay
} from "components/shared";
import { DatePickerElement } from "./datePickerElement";
import { TelemetryChartContainer as TelemetryChart } from "./telemetryChart.container";
import { transformTelemetryResponse } from "./utils";
import { chartColorObjects } from "./chartColorObjects";
import { Observable, Subject } from "rxjs";
import { joinClasses } from "utilities";
import moment from "moment";
import { TelemetryService } from "services";
import { ContextMenuAlign } from "components/shared";

import "./telemetryPanel.scss";
import { appInsights } from "applicationInsights";

const initialState = {
  telemetryLive: {},
  isTelemetryPending: true,
  telemetryError: null,
  isRealTimeTelemetry: true,
  telemetryCustom: {},
  telemetryCustomDateRange: { start: null, end: null },
  buttonActive: "live", // live, custom
  measureNameTypeMap: new Map(),
  selectedMeasureType: "",
  selectedMeasureName: ""
};

const refreshEvent = (devices = [], sensors = [], sensorModels = []) => ({
  devices,
  sensors,
  sensorModels
});

export default class TelemetryPanel extends Component {
  constructor(props) {
    super(props);

    this.state = {
      ...initialState,
      ...this.getSensorMeasureData(props.sensors, props.sensorModels)
    };
    this.subscriptions = [];
    this.telemetryPanelRefresh$ = new Subject(); // Restarts all streams

    this.filterSensorIds = this.filterSensorIds.bind(this);
    this.onApplyEvent = this.onApplyEvent.bind(this);
    this.onLiveButtonClick = this.onLiveButtonClick.bind(this);
    this.getSensorMeasureNameUnitMap = this.getSensorMeasureNameUnitMap.bind(
      this
    );
    this.getCustomTelemetryStream = this.getCustomTelemetryStream.bind(this);

    const {
      fetchDevices,
      devicesLastUpdated,
      devicesError,

      fetchSensors,
      sensorsLastUpdated,
      sensorsError,

      fetchSensorModels,
      sensorModelsLastUpdated,
      sensorModelsError
    } = this.props;

    if (!devicesLastUpdated && !devicesError) fetchDevices();
    if (!sensorsLastUpdated && !sensorsError) fetchSensors();
    if (!sensorModelsLastUpdated && !sensorModelsError) fetchSensorModels();
  }

  componentDidMount() {
    // Telemetry stream - START
    const getTelemetryStream = ({
      devices = [],
      sensors = [],
      sensorModels = {}
    }) => {
      return Observable.from(this.filterSensorIds(sensorModels, sensors))
        .mergeMap(sensorId =>
          TelemetryService.getTelemetryBySensorIdP1D(sensorId)
        )
        .toArray()
        .flatMap(transformTelemetryResponse(() => this.state.telemetryLive))
        .map(telemetryLive => {
          this.getTelemetryByDisplayName(telemetryLive, devices, sensors);
          return {
            telemetryLive
          };
        });
    };
    // Telemetry stream - END

    this.subscriptions.push(
      this.telemetryPanelRefresh$.subscribe(() =>
        this.setState({
          telemetryLive: {},
          isTelemetryPending: true
        })
      )
    );

    this.subscriptions.push(
      this.telemetryPanelRefresh$.switchMap(getTelemetryStream).subscribe(
        telemetryState => {
          this.setState({
            ...telemetryState,
            lastRefreshed: moment(),
            isTelemetryPending: false
          });
        },
        telemetryError => {
          this.setState({ telemetryError, isTelemetryPending: false });
        }
      )
    );

    // Start polling all panels
    if (
      this.props.devicesLastUpdated &&
      this.props.sensorsLastUpdated &&
      this.props.sensorModelsLastUpdated
    ) {
      this.telemetryPanelRefresh$.next(
        refreshEvent(
          this.props.devices,
          this.props.sensors,
          this.props.sensorModels
        )
      );
    }
  }

  componentWillUnmount() {
    this.subscriptions.forEach(sub => sub.unsubscribe());
  }

  componentDidUpdate(prevProps) {
    if (
      this.props.devicesLastUpdated &&
      this.props.sensorsLastUpdated &&
      this.props.sensorModelsLastUpdated && // If now I have everything and ...
      !(
        prevProps.devicesLastUpdated &&
        prevProps.sensorsLastUpdated &&
        prevProps.sensorModelsLastUpdated
      ) // ... in the previous update, I was missing something, then update telemetry
    ) {
      this.setState(
        {
          ...this.getSensorMeasureData(
            this.props.sensors,
            this.props.sensorModels
          )
        },
        () => {
          if (this.state.isRealTimeTelemetry) {
            this.telemetryPanelRefresh$.next(
              refreshEvent(
                this.props.devices,
                this.props.sensors,
                this.props.sensorModels
              )
            );
          } else {
            this.getCustomTelemetryStream();
          }
        }
      );
    }
  }

  getSensorMeasureData(sensors, sensorModels) {
    const { sensorIds } = this.props;

    const validSensorModelIds = sensors
      .filter(sensor => sensorIds.includes(sensor.id))
      .map(sensor => sensor.sensorModelId);

    const measureNameMeasureTypeList = sensorModels
      .filter(sensorModel => validSensorModelIds.includes(sensorModel.id))
      .reduce((acc, sensorModel) => acc.concat(sensorModel.sensorMeasures), [])
      .map(sensorMeasure => [sensorMeasure.name, sensorMeasure.type]);

    return {
      measureNameTypeMap: new Map(measureNameMeasureTypeList),
      selectedMeasureType:
        measureNameMeasureTypeList.length > 0
          ? measureNameMeasureTypeList[0][1]
          : "",
      selectedMeasureName:
        measureNameMeasureTypeList.length > 0
          ? measureNameMeasureTypeList[0][0]
          : ""
    };
  }

  filterSensorIds(sensorModels, sensors) {
    const { selectedMeasureName } = this.state;
    const { sensorIds } = this.props;

    const sensorModelIds = sensorModels
      .filter(sensorModel =>
        sensorModel.sensorMeasures
          .map(sensorMeasure => sensorMeasure.name)
          .includes(selectedMeasureName)
      )
      .map(sensorModel => sensorModel.id);

    return sensors
      .filter(sensor => sensorModelIds.includes(sensor.sensorModelId))
      .map(sensor => sensor.id)
      .filter(sensorId => sensorIds.includes(sensorId));
  }

  getTelemetryByDisplayName(telemetry, devices, sensors) {
    const deviceIdNameList = devices.map(device => [device.id, device.name]);
    const deviceIdNameMap = new Map(deviceIdNameList);

    const sensorIdDisplayNameList = devices
      .reduce(
        (acc, device) =>
          acc.concat(sensors.filter(sensor => sensor.deviceId === device.id)),
        []
      )
      .map(sensor => [
        sensor.id,
        [deviceIdNameMap.get(sensor.deviceId), sensor.name, sensor.id].join("_")
      ]);

    const sensorIdDisplayNameMap = new Map(sensorIdDisplayNameList);

    // replace sensor id with name
    for (const sensorMeasure in telemetry) {
      if (Object.prototype.hasOwnProperty.call(telemetry, sensorMeasure)) {
        for (const sensorId in telemetry[sensorMeasure]) {
          if (
            Object.prototype.hasOwnProperty.call(
              telemetry[sensorMeasure],
              sensorId
            )
          ) {
            telemetry[sensorMeasure][sensorIdDisplayNameMap.get(sensorId)] =
              telemetry[sensorMeasure][sensorId];
            delete telemetry[sensorMeasure][sensorId];
          }
        }
      }
    }
  }

  getSensorMeasureNameUnitMap() {
    const { devices, sensors, sensorModels } = this.props;

    const sensorMeasureNameUnitList = devices
      .reduce(
        (acc, device) =>
          acc.concat(sensors.filter(sensor => sensor.deviceId === device.id)),
        []
      )
      .reduce(
        (acc, sensor) =>
          acc.concat(
            sensorModels.filter(
              sensorModel => sensorModel.id === sensor.sensorModelId
            )
          ),
        []
      )
      .reduce((acc, sensorModel) => acc.concat(sensorModel.sensorMeasures), [])
      .map(sensorMeasure => [sensorMeasure.name, sensorMeasure.unit]);

    return new Map(sensorMeasureNameUnitList);
  }

  // onApply event For Date Picker
  onApplyEvent(event, picker) {
    const start = moment(picker.startDate).toISOString();
    const end = moment(picker.endDate).toISOString();

    this.setState({
      telemetryCustom: {},
      telemetryCustomDateRange: { start: start, end: end },
      buttonActive: "custom",
      isTelemetryPending: true
    });

    this.getCustomTelemetryStream();
  }

  getCustomTelemetryStream() {
    const { telemetryCustomDateRange } = this.state;
    const { devices, sensors, sensorModels } = this.props;
    const telemetryCurrent = {};

    Observable.from(this.filterSensorIds(sensorModels, sensors))
      .mergeMap(sensorId =>
        TelemetryService.getTelemetryBySensorIdForDateRange(
          sensorId,
          telemetryCustomDateRange.start,
          telemetryCustomDateRange.end
        )
      )
      .toArray()
      .flatMap(transformTelemetryResponse(() => telemetryCurrent))
      .map(telemetryCustom => {
        this.getTelemetryByDisplayName(telemetryCustom, devices, sensors);
        return {
          telemetryCustom: telemetryCustom,
          isTelemetryPending: false
        };
      })
      .subscribe(
        telemetryState =>
          this.setState({
            ...telemetryState,
            lastRefreshed: moment(),
            isRealTimeTelemetry: false
          }),
        telemetryError =>
          this.setState({
            telemetryError,
            isTelemetryPending: false
          })
      );
  }

  refreshTelemetryData() {
    const { buttonActive } = this.state;
    const { devices, sensors, sensorModels } = this.props;

    if (buttonActive === "live") {
      this.setState(
        {
          telemetryLive: {},
          isTelemetryPending: true
        },
        () => {
          this.telemetryPanelRefresh$.next(
            refreshEvent(devices, sensors, sensorModels)
          );
        }
      );
    } else if (buttonActive === "custom") {
      this.setState(
        {
          telemetryCustom: {},
          isTelemetryPending: true
        },
        () => {
          this.getCustomTelemetryStream();
        }
      );
    }
  }

  onLiveButtonClick() {
    this.setState({
      istelemetryPending: false,
      isRealTimeTelemetry: true,
      buttonActive: "live"
    });
  }

  render() {
    const {
      t,
      theme,

      isDevicesPending,
      isSensorsPending,
      isSensorModelsPending,
      devicesError,
      sensorsError,
      sensorModelsError
    } = this.props;

    const {
      buttonActive,
      isTelemetryPending,
      telemetryError,
      isRealTimeTelemetry,
      telemetryLive,
      telemetryCustom,
      measureNameTypeMap,
      selectedMeasureType,
      selectedMeasureName
    } = this.state;

    const timeSeriesExplorerUrl = global.DeploymentConfig.tsiEndpoint;
    const isPending =
      isDevicesPending ||
      isSensorsPending ||
      isTelemetryPending ||
      isSensorModelsPending;
    const error =
      devicesError || sensorsError || telemetryError || sensorModelsError;
    const telemetry = isRealTimeTelemetry ? telemetryLive : telemetryCustom;
    const sensorMeasureNameUnitMap = this.getSensorMeasureNameUnitMap();

    const classNameLive =
      buttonActive === "live"
        ? "live-button live-button-active"
        : "live-button";
    const classNameDatePicker =
      buttonActive === "custom"
        ? "date-picker-button date-picker-button-active"
        : "date-picker-button";

    return (
      <Panel className="telemetry-panel">
        <PanelHeader>
          {this.props.showHeader && (
            <PanelHeaderLabel>
              {t("dashboard.panels.telemetry.header")}
            </PanelHeaderLabel>
          )}
          <ContextMenuAlign>
            <DatePickerElement
              className={classNameDatePicker}
              onApplyEvent={this.onApplyEvent}
              t={t}
            />
            <button className={classNameLive} onClick={this.onLiveButtonClick}>
              {t("dashboard.liveButton")}
            </button>
          </ContextMenuAlign>
        </PanelHeader>
        <PanelContent className="telemetry-panel-container">
          <div className="options-container bold-measure-type">
            {Array.from(new Set(measureNameTypeMap.values())).map(
              (measureType, index) => (
                <button
                  key={index}
                  onClick={() =>
                    this.setState({ selectedMeasureType: measureType })
                  }
                  className={joinClasses(
                    "telemetry-option",
                    selectedMeasureType === measureType ? "active" : ""
                  )}
                >
                  {measureType}
                </button>
              )
            )}
          </div>
          <div className="options-container">
            {Array.from(measureNameTypeMap.keys())
              .filter(
                tKey => measureNameTypeMap.get(tKey) === selectedMeasureType
              )
              .map((key, index) => {
                return (
                  <button
                    key={index}
                    onClick={() =>
                      this.setState({ selectedMeasureName: key }, () => {
                        this.refreshTelemetryData();
                      })
                    }
                    className={joinClasses(
                      "telemetry-option",
                      selectedMeasureName === key ? "active" : ""
                    )}
                  >
                    {key}
                  </button>
                );
              })}
          </div>

          <TelemetryChart
            telemetry={telemetry}
            telemetryKey={selectedMeasureName}
            theme={theme}
            colors={chartColorObjects}
          />
          {Object.keys(telemetry).length !== 0 && (
            <div className="measure-unit-container">
              {"Measure Unit: " +
                sensorMeasureNameUnitMap.get(selectedMeasureName)}
            </div>
          )}
          {!isPending && Object.keys(telemetry).length === 0 && (
            <PanelMsg className="no-data">{t("dashboard.noData")}</PanelMsg>
          )}
          {isPending === true && <PanelMsg>{t("dashboard.wait")}</PanelMsg>}
          {timeSeriesExplorerUrl && (
            <TimeSeriesInsightsLinkContainer
              href={timeSeriesExplorerUrl}
              onClick={() => {
                appInsights.trackEvent(
                  {
                    name: "UiEvent:Click: Explore in TSI Link"
                  },
                  { type: "External Link Click", toUrl: timeSeriesExplorerUrl }
                );
              }}
            />
          )}
        </PanelContent>
        {isPending && (
          <PanelOverlay>
            <Indicator />
          </PanelOverlay>
        )}
        {error && (
          <PanelError>
            <AjaxError t={t} error={error} />
          </PanelError>
        )}
      </Panel>
    );
  }
}

TelemetryPanel.propTypes = {
  t: PropTypes.func.isRequired,
  theme: PropTypes.oneOf(["light", "dark"]).isRequired,
  showHeader: PropTypes.bool.isRequired
};
