import React, { Component } from "react";
import { svgs } from "utilities";
import { TelemetryService } from "services";
import { DeleteModal } from "components/shared/deleteModal/deleteModal";

import "./deleteRule.scss";

export class DeleteRule extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isPending: false,
      isRuleDeleted: undefined,
      ruleDeletionError: undefined
    };
  }

  componentWillUnmount() {
    if (this.subscription) this.subscription.unsubscribe();
  }

  // TODO make this epic
  deleteRule = () => {
    const { rule } = this.props;
    this.setState({ isPending: true, error: null });
    this.subscription = TelemetryService.deleteRule(rule.id).subscribe(
      () => {
        this.props.deleteRule([rule.id]);
        this.setState({
          isPending: false,
          isRuleDeleted: true
        });
      },
      error =>
        this.setState({
          ruleDeletionError: error,
          isPending: false
        }),
      () =>
        this.setState({
          isPending: false,
          isRuleDeleted: true
        })
    );
  };

  render() {
    const { onClose, t } = this.props;

    return (
      <DeleteModal
        onClose={onClose}
        deleteItem={this.deleteRule}
        title={t("rules.flyouts.deleteRule.title")}
        deleteConfirmation={t("rules.flyouts.deleteRule.preDeleteText")}
        deleteBtn={t("modal.delete")}
        icon={svgs.trash}
        t={t}
        isPending={this.state.isPending}
        deleteModalError={this.state.ruleDeletionError}
        deleteModalSuccess={this.state.isRuleDeleted}
        successMsg={t("rules.flyouts.deleteRule.deleteConfirmation")}
      />
    );
  }
}
