import React, { Component } from "react";
import { PcsGrid } from "components/shared";
import { farmColumnDefs, defaultFarmGridProps } from "./farmsGridConfig";
import { isFunc, translateColumnDefs } from "utilities";

const initialState = {
  softSelectedId: undefined,
  eviBlobUrl: undefined,
  eviBlobUrlError: undefined
};

export class FarmGrid extends Component {
  constructor(props) {
    super(props);
    // Set the initial state
    this.state = initialState;
    // Default device grid columns
    this.columnDefs = [
      farmColumnDefs.name,
      farmColumnDefs.address,
      farmColumnDefs.crop,
      farmColumnDefs.devices,
      farmColumnDefs.createdOn,
      farmColumnDefs.lastUpdated
    ];

    this.onRowClicked = this.onRowClicked.bind(this);
  }

  onGridReady = gridReadyEvent => {
    this.farmGridApi = gridReadyEvent.api;
    // Call the onReady props if it exists
    if (isFunc(this.props.onGridReady)) {
      this.props.onGridReady(gridReadyEvent);
    }
  };

  onSoftSelectChange = (rowId, rowData) => {
    // Note: only the Id is reliable, rowData may be out of date
    const { onSoftSelectChange, history } = this.props;
    const name = rowData.name;
    history.push(`/farms/${rowId}`, { id: rowId, farmName: name });
    if (rowId) {
      this.setState({ softSelectedId: rowId });
    }
    if (isFunc(onSoftSelectChange)) {
      onSoftSelectChange(rowId, rowData);
    }
  };

  onHardSelectChange = selectedObjs => {
    const { onContextMenuChange, onHardSelectChange } = this.props;
    if (isFunc(onContextMenuChange)) {
      onContextMenuChange(selectedObjs.length > 0 ? this.contextBtns : null);
    }
    if (isFunc(onHardSelectChange)) {
      onHardSelectChange(selectedObjs);
    }
  };

  getSoftSelectId = ({ id } = "") => id;

  onRowClicked = () => {
    //
  };
  render() {
    const gridProps = {
      ...defaultFarmGridProps,
      columnDefs: translateColumnDefs(this.props.t, this.columnDefs),
      sizeColumnsToFit: true,
      deviceCount: this.props.deviceCount,
      getSoftSelectId: this.getSoftSelectId,
      softSelectId: (this.state.softSelectedDevice || {}).id,
      ...this.props,
      deltaRowDataMode: true,
      enableSorting: true,
      unSortIcon: true,
      getRowNodeId: ({ id }) => id,
      context: {
        t: this.props.t
      },
      /* Grid Events */
      onRowClicked: this.onRowClicked,
      onGridReady: this.onGridReady,
      onSoftSelectChange: this.onSoftSelectChange,
      onHardSelectChange: this.onHardSelectChange
    };

    return <PcsGrid key="farm-grid-key" {...gridProps} />;
  }
}
