import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";
import Devices from "./devices";
import {
  epics as devicesEpics,
  redux as devicesRedux,
  getDevices,
  getDevicesError,
  getDevicesLastUpdated,
  getDevicesPendingStatus,
  getDeviceModels
} from "store/reducers/devicesReducer";
import {
  epics as farmEpics,
  getFarms,
  getFarmsError,
  getFarmsLastUpdated,
  getFarmsPendingStatus
} from "store/reducers/farmsReducer";

// Pass the devices status
const mapStateToProps = state => ({
  farms: getFarms(state),
  farmError: getFarmsError(state),
  farmLastUpdated: getFarmsLastUpdated(state),
  isFarmsPending: getFarmsPendingStatus(state),
  devices: getDevices(state),
  deviceModels: getDeviceModels(state),
  deviceError: getDevicesError(state),
  isDevicesPending: getDevicesPendingStatus(state),
  deviceLastUpdated: getDevicesLastUpdated(state)
});

// Wrap the dispatch method
const mapDispatchToProps = dispatch => ({
  fetchDevices: () => dispatch(devicesEpics.actions.fetchDevices()),
  fetchFarms: () => dispatch(farmEpics.actions.fetchFarms()),
  fetchDeviceModel: () => dispatch(devicesEpics.actions.fetchDeviceModel()),
  deleteDevicesById: ids => dispatch(devicesRedux.actions.deleteDevices(ids))
});

export const DevicesContainer = withNamespaces()(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(Devices)
);
