import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";
import { RuleEditor } from "./ruleEditor";
import {
  rulesRedux,
  getRuleById,
  getRules
} from "store/reducers/rulesAlertsReducer";
import {
  epics as sensorEpics,
  getSensors,
  getSensorModels,
  getSensorLastUpdated,
  getSensorPendingStatus,
  getSensorModelPendingStatus,
  getSensorModelLastUpdated,
  getSensorModelsError
} from "store/reducers/sensorsReducer";
import {
  epics as deviceEpics,
  getDevices,
  getDevicesLastUpdated,
  getDevicesPendingStatus
} from "store/reducers/devicesReducer";
import {
  epics as farmEpics,
  getFarms,
  getFarmsError,
  getFarmsLastUpdated,
  getFarmsPendingStatus
} from "store/reducers/farmsReducer";

// Pass device groups
const mapStateToProps = (state, props) => ({
  rules: getRules(state),
  rule: getRuleById(state, props.ruleId),
  sensorError: getSensorModelsError(state),
  farms: getFarms(state),
  farmsError: getFarmsError(state),
  isFarmsPending: getFarmsPendingStatus(state),
  farmsLastUpdated: getFarmsLastUpdated(state),
  devices: getDevices(state),
  sensors: getSensors(state),
  sensorModels: getSensorModels(state),
  sensorsLastUpdated: getSensorLastUpdated(state),
  isSensorsPending: getSensorPendingStatus(state),
  sensorModelsLastUpdated: getSensorModelLastUpdated(state),
  sensorModelsIsPending: getSensorModelPendingStatus(state),
  devicesLastUpdated: getDevicesLastUpdated(state),
  isDevicesPending: getDevicesPendingStatus(state)
});

// Wrap the dispatch method
const mapDispatchToProps = dispatch => ({
  fetchFarms: () => dispatch(farmEpics.actions.fetchFarms()),
  insertRules: rules => dispatch(rulesRedux.actions.insertRules(rules)),
  modifyRules: rules => dispatch(rulesRedux.actions.modifyRules(rules)),
  fetchSensors: () => dispatch(sensorEpics.actions.fetchSensors()),
  fetchSensorModels: () => dispatch(sensorEpics.actions.fetchSensorModels()),
  fetchDevices: () => dispatch(deviceEpics.actions.fetchDevices())
});

export const RuleEditorContainer = withNamespaces()(
  connect(
    mapStateToProps,
    mapDispatchToProps
  )(RuleEditor)
);
