import React, { Component } from "react";
import update from "immutability-helper";

import Config from "app.config";
import { AjaxError, Indicator } from "components/shared";
import {
  Panel,
  PanelHeader,
  PanelHeaderLabel,
  PanelContent,
  PanelOverlay,
  PanelError
} from "components/shared";
import { DeviceDetailsContainer } from "components/pages/devices/flyouts/deviceDetails";
// import { toDiagnosticsModel } from 'services/models';
import { AzureMap } from "components/shared/azureMapPanel/azureMap";
import { zoomToGeometry } from "utilities";

import "./mapPanel.scss";

const AzureMaps = window.atlas;
const nominalDeviceLayer = "devices-nominal-layer";
const warningDevicesLayer = "devices-warning-layer";
const criticalDevicesLayer = "devices-critical-layer";

const deviceToMapPin = ({ farmName, latitude, longitude, name }) =>
  new AzureMaps.data.Feature(new AzureMaps.data.Point([longitude, latitude]), {
    farmName,
    name
  });

export class MapPanel extends Component {
  constructor(props) {
    super(props);

    this.state = { selectedDeviceId: undefined }; // agadhika Possible cleanup
  }

  componentDidUpdate() {
    this.calculatePins(this.props);
  }

  onMapReady = map => {
    // Create the map popup
    this.popup = new AzureMaps.Popup();

    // Create the device pin layers and bind click events
    [
      [nominalDeviceLayer, "nominal", "pin-blue"],
      [warningDevicesLayer, "warning", "pin-darkblue"],
      [criticalDevicesLayer, "alert", "pin-red"]
    ].forEach(([name, classname, icon]) => {
      map.addPins([], { name, icon, cluster: true });
      map.addEventListener("click", name, event => {
        const [pin] = event.features;
        this.popup.setPopupOptions({
          position: pin.geometry.coordinates,
          content: this.buildDevicePopup(pin.properties, classname) // agadhika Possible cleanup
        });
        this.popup.open(map);
      });
    });

    // Save this.map here to prevent calculatePins
    // from being called before the pins are added
    this.map = map;
    this.calculatePins(this.props, true);
  };

  buildDevicePopup = (properties, classname) => {
    // agadhika Possible cleanup
    const popupContentBox = document.createElement("div");
    popupContentBox.classList.add("popup-content-box");
    popupContentBox.classList.add(classname);

    const type = document.createElement("div");
    type.classList.add("popup-type");
    type.innerText = properties.farmName;

    const name = document.createElement("div");
    name.classList.add("popup-device-name");
    name.innerText = properties.name;

    popupContentBox.appendChild(type);
    popupContentBox.appendChild(name);

    popupContentBox.onclick = () => {
      // Check this to void any potential attempts to reference the component after unmount
      if (this) this.openDeviceDetails(properties.id);
    };

    return popupContentBox;
  };

  calculatePins(props, mounting = false) {
    if (this.map) {
      if (this.props.farm && this.props.farm.geometry) {
        const geoJson = this.props.farm.geometry;

        // Calculating boundary pins to zoom to bounding box
        zoomToGeometry(this.map, geoJson);

        // Actuallly drawing the Polygon/MultiPolygon
        let polygon;
        if (geoJson.type === "Polygon") {
          polygon = new AzureMaps.data.Polygon(geoJson.coordinates);
        } else if (geoJson.type === "MultiPolygon") {
          polygon = new AzureMaps.data.MultiPolygon(geoJson.coordinates);
        }
        // !NOTE: addPolygons doesn't exist in any documentation.
        // In this commit, support for MultiPolygon was added. The part of the
        // code that actually draws the polygon was not touched.
        // At the time of this commit, the docs (https://docs.microsoft.com/en-us/azure/azure-maps/map-add-shape)
        // suggest this snippet of code:

        // const dataSource = new atlas.source.DataSource();
        // map.sources.add(dataSource);

        // // Create a rectangular polygon.
        // dataSource.add(
        //   new atlas.data.Feature(
        //     new atlas.data.Polygon([                                                         // or MultiPolygon
        //       [
        //         [-73.98235, 40.76799],
        //         [-73.95785, 40.80044],
        //         [-73.94928, 40.7968],
        //         [-73.97317, 40.76437],
        //         [-73.98235, 40.76799]
        //       ]
        //     ])
        //   )
        // );

        // // Create and add a polygon layer to render the polygon to the map.
        // map.layers.add(
        //   new atlas.layer.PolygonLayer(dataSource, null, {                                  // NOT MultiPolygonLayer
        //     fillColor: "red",
        //     opacaty: 0.5
        //   })
        // );

        this.map.addPolygons([polygon], {
          name: "farm-azure-map",
          color: "#1e90ff",
          opacity: 0.5,
          overwrite: true
        });
      }
      // if (props.analyticsVersion !== this.props.analyticsVersion || boundZoomToDevices) {
      const geoLocatedDevices = this.extractGeoLocatedDevices(
        props.devices || [],
        props.farmId
      );
      // if (Object.keys(geoLocatedDevices).length > 0 && props.devicesInAlert) {
      {
        const { normal, warning, critical } = this.devicesToPins(
          geoLocatedDevices
        );

        this.map.addPins(normal, {
          name: nominalDeviceLayer,
          fillColor: "blue",
          overwrite: true
        });
        this.map.addPins(warning, {
          name: warningDevicesLayer,
          overwrite: true
        });
        this.map.addPins(critical, {
          name: criticalDevicesLayer,
          overwrite: true
        });

        // if (boundZoomToDevices) {
        // this.zoomToPins([].concat.apply([], [ normal, warning, critical ]));
        // }
      }
      // }
    }
  }

  extractGeoLocatedDevices(devices, farmId) {
    return Object.keys(devices)
      .map(key => devices[key])
      .filter(
        device =>
          device.latitude && device.longitude && device.farmId === farmId
      );
  }

  devicesToPins(devices) {
    return devices.reduce(
      (acc, device) => {
        const devicePin = deviceToMapPin(device);
        const category = "normal";
        if (
          category === "normal" ||
          category === Config.ruleSeverity.warning ||
          category === Config.ruleSeverity.critical
        ) {
          return update(acc, {
            [category]: { $push: [devicePin] }
          });
        }
        return acc;
      },
      { normal: [], warning: [], critical: [] }
    );
  }

  zoomToPins(pins) {
    const lons = [];
    const lats = [];
    pins.forEach(({ geometry: { coordinates: [longitude, latitude] } }) => {
      lons.push(longitude);
      lats.push(latitude);
    });

    const swLon = Math.min.apply(null, lons);
    const swLat = Math.min.apply(null, lats);
    const neLon = Math.max.apply(null, lons);
    const neLat = Math.max.apply(null, lats);

    // Zoom the map to the bounding box of the devices
    this.map.setCameraBounds({
      bounds: [swLon, swLat, neLon, neLat],
      padding: 50
    });
  }

  openDeviceDetails = selectedDeviceId => this.setState({ selectedDeviceId }); // agadhika Possible cleanup

  closeDeviceDetails = () => this.setState({ selectedDeviceId: undefined }); // agadhika Possible cleanup

  render() {
    const { t, isPending, mapKeyIsPending, azureMapsKey, error } = this.props;
    const showOverlay = !error && isPending && mapKeyIsPending;
    return (
      <Panel className="map-panel-container">
        <PanelHeader className="mappanel-header">
          <PanelHeaderLabel>
            {t("dashboard.panels.map.header")}
          </PanelHeaderLabel>
          {/* { !showOverlay && isPending && <Indicator size="small" /> } */}
        </PanelHeader>
        <PanelContent className="map-panel-container">
          {!error && (
            <AzureMap
              azureMapsKey={azureMapsKey}
              onMapReady={this.onMapReady}
              mapDivId={"farm-boundary-map"}
            />
          )}
        </PanelContent>
        {showOverlay && (
          <PanelOverlay>
            <Indicator />
          </PanelOverlay>
        )}
        {!mapKeyIsPending && !this.props.azureMapsKey && (
          <PanelError>{t("dashboard.panels.map.notSupportedError")}</PanelError>
        )}
        {error && (
          <PanelError>
            <AjaxError t={t} error={error} />
          </PanelError>
        )}
        {this.state.selectedDeviceId && ( // agadhika Possible cleanup
          <DeviceDetailsContainer
            onClose={this.closeDeviceDetails}
            deviceId={this.state.selectedDeviceId}
          />
        )}
      </Panel>
    );
  }
}
