import React, { Component } from "react";
import {
  Panel,
  PanelHeader,
  PanelHeaderLabel,
  PanelContent,
  PanelError,
  ContextMenuAlign,
  AjaxError
} from "components/shared";
import moment from "moment";
import { Modal } from "components/shared/mapsModal/modal/modal";
import {
  jobsDateFormat,
  farmNameLoading,
  svgs,
  getSceneData,
  getPNGFilefromSceneFiles
} from "utilities";
import "./satelliteIndicesMap.scss";
import { appInsights } from "applicationInsights";

const eviMapType = "evi";
const ndwiMapType = "ndwi";
const noMapImage = svgs.NoMapImage;
export class SatelliteIndicesMap extends Component {
  constructor(props) {
    super(props);
    const farmId = this.props.farmId;
    const mapsData = this.props.maps;
    this.state = {
      isPending: true,
      isModalopen: false,
      modalName: undefined,
      mapName: undefined,
      eviUrl: undefined,
      ndwiUrl: undefined,
      isEviMapFound: undefined,
      isNdwiMapFound: undefined,
      eviMapFileDownloadSelectObject: [],
      ndwiMapFileDownloadSelectObject: [],
      isEviMapBroken: undefined,
      isNdwiMapBroken: undefined,
      farmName: props.farmName ? props.farmName : farmNameLoading
    };
    this.onMapImageClick = this.onMapImageClick.bind(this);
    this.onModalclose = this.onModalclose.bind(this);
    this.getEviSceneData = this.getEviSceneData.bind(this);
    this.getNdwiSceneData = this.getNdwiSceneData.bind(this);
    this.getEviSceneData(eviMapType, farmId, mapsData);
    this.getNdwiSceneData(ndwiMapType, farmId, mapsData);
  }

  onMapImageClick(mapName, modalMapName) {
    this.setState({
      isModalopen: true,
      mapName: mapName,
      modalName: modalMapName
    });
    appInsights.trackEvent({
      name: "UiEvent:ModalOpen: Satellite Indices " + modalMapName
    });
  }

  onModalclose() {
    appInsights.trackEvent({
      name: "UiEvent:ModalClose: Satellite Indices " + this.state.modalMapName
    });
    this.setState({ ...this.state, isModalopen: false, modalName: undefined });
  }

  getEviSceneData(eviMapType, farmId, mapsData) {
    getSceneData(eviMapType, farmId, mapsData, this.eviSceneDataCallback);
  }

  eviSceneDataCallback = (eviSelectedObject, eviMapData, sceneError) => {
    if (sceneError) {
      this.setState({ sceneError });
    } else if (eviMapData.mapFound) {
      const pngUrl = getPNGFilefromSceneFiles(eviSelectedObject);
      this.setState({
        eviUrl: pngUrl,
        mapFound: eviMapData.mapFound,
        latestMapDate: eviMapData.latestMapDate,
        latestCreatedAt: eviMapData.latestCreatedAt,
        isEviMapFound: eviMapData.mapFound,
        eviMapFileDownloadSelectObject:
          eviSelectedObject.mapFileDownloadSelectObject
      });
    } else {
      this.setState({
        eviUrl: eviMapData.url,
        mapFound: eviMapData.mapFound,
        isEviMapFound: eviMapData.mapFound
      });
    }
  };
  getNdwiSceneData(ndwiMapType, farmId, mapsData) {
    getSceneData(ndwiMapType, farmId, mapsData, this.ndwiSceneDataCallback);
  }

  ndwiSceneDataCallback = (ndwiSelectedObject, ndwiMapData, sceneError) => {
    if (sceneError) {
      this.setState({ sceneError });
    } else if (ndwiMapData.mapFound) {
      const pngUrl = getPNGFilefromSceneFiles(ndwiSelectedObject);
      this.setState({
        ndwiUrl: pngUrl,
        mapFound: ndwiMapData.mapFound,
        latestMapDate: ndwiMapData.latestMapDate,
        latestCreatedAt: ndwiMapData.latestCreatedAt,
        isNdwiMapFound: ndwiMapData.mapFound,
        ndwiMapFileDownloadSelectObject:
          ndwiSelectedObject.mapFileDownloadSelectObject
      });
    } else {
      this.setState({
        ndwiUrl: ndwiMapData.url,
        mapFound: ndwiMapData.mapFound,
        isNdwiMapFound: ndwiMapData.mapFound
      });
    }
  };

  onError = (name, value) => {
    this.setState({ [name]: value });
  };
  render() {
    const { t, farmsError } = this.props;
    const {
      sceneError,
      mapName,
      eviUrl,
      ndwiUrl,
      latestMapDate,
      isEviMapFound,
      isNdwiMapFound,
      eviMapFileDownloadSelectObject,
      ndwiMapFileDownloadSelectObject,
      modalName,
      latestCreatedAt,
      farmName,
      isEviMapBroken,
      isNdwiMapBroken
    } = this.state;
    const error = sceneError || farmsError;

    return (
      <Panel className>
        <PanelHeader className="ph">
          <PanelHeaderLabel>
            {t("farms.panel.satelliteindices")}
          </PanelHeaderLabel>
          <ContextMenuAlign right={true}>{latestMapDate}</ContextMenuAlign>
        </PanelHeader>

        <PanelContent>
          {isEviMapFound === undefined ? (
            <div className="shimmer-panel"> </div>
          ) : (
            <img
              className={
                isEviMapFound && !isEviMapBroken ? "image-map" : "image"
              }
              src={eviUrl}
              onClick={
                isEviMapFound &&
                (() =>
                  this.onMapImageClick(t("farms.panel.eviMaps"), eviMapType))
              }
              alt={
                isEviMapBroken
                  ? t("dashboard.panels.map.corruptImageError")
                  : t("farms.panel.noEvi")
              }
              onError={() => {
                this.onError("isEviMapBroken", true);
              }}
            />
          )}
          {isNdwiMapFound === undefined ? (
            <div className="shimmer-panel"> </div>
          ) : (
            <img
              className={
                isNdwiMapFound && !isNdwiMapBroken ? "image-map" : "image"
              }
              src={ndwiUrl}
              onClick={
                isNdwiMapFound &&
                (() =>
                  this.onMapImageClick(t("farms.panel.ndwiMaps"), ndwiMapType))
              }
              alt={
                isNdwiMapBroken
                  ? t("dashboard.panels.map.corruptImageError")
                  : t("farms.panel.noNdwi")
              }
              onError={() => {
                this.onError("isNdwiMapBroken", true);
              }}
            />
          )}
          {isEviMapFound === false ? (
            <div className="col-one">{t("maps.modal.noMapYet")}</div>
          ) : (
            ""
          )}
          {isNdwiMapFound === false ? (
            <div className="col-one">{t("maps.modal.noMapYet")}</div>
          ) : (
            ""
          )}
          <div className="col-one">{t("farms.panel.evi")}</div>
          <div className="col-one">{t("farms.panel.ndwi")}</div>
          {modalName === eviMapType && (
            <Modal
              type="MAP-MODAL"
              name={mapName}
              farmName={farmName}
              mapName={
                eviMapType +
                "_" +
                farmName +
                "_" +
                moment(latestMapDate).format(jobsDateFormat)
              }
              modalClose={this.onModalclose}
              mapFileDownloadSelectObject={eviMapFileDownloadSelectObject}
              mapDate={latestMapDate}
              latestCreatedAt={latestCreatedAt}
              blobUrl={eviUrl}
              noMapImage={noMapImage}
              isMapFound={isEviMapFound}
              t={t}
            />
          )}
          {modalName === ndwiMapType && (
            <Modal
              type="MAP-MODAL"
              name={mapName}
              farmName={farmName}
              mapName={
                ndwiMapType +
                "_" +
                farmName +
                "_" +
                moment(latestMapDate).format(jobsDateFormat)
              }
              modalClose={this.onModalclose}
              mapFileDownloadSelectObject={ndwiMapFileDownloadSelectObject}
              mapDate={latestMapDate}
              latestCreatedAt={latestCreatedAt}
              blobUrl={ndwiUrl}
              noMapImage={noMapImage}
              isMapFound={isNdwiMapFound}
              t={t}
            />
          )}
        </PanelContent>
        {error && (
          <PanelError>
            <AjaxError t={t} error={error} />
          </PanelError>
        )}
      </Panel>
    );
  }
}
