import "rxjs";
import { Observable } from "rxjs";
import moment from "moment";
import { schema, normalize } from "normalizr";
import update from "immutability-helper";
import { createSelector } from "reselect";
import { MapService } from "services";
import {
  createReducerScenario,
  createEpicScenario,
  errorPendingInitialState,
  pendingReducer,
  errorReducer,
  setPending,
  getPending,
  getError,
  toActionCreator
} from "store/utilities";
import { jobsDateFormat } from "utilities";
import { getFarmsMap } from "store/reducers/farmsReducer";
// ========================= Epics - START
const handleError = fromAction => error =>
  Observable.of(
    redux.actions.registerError(fromAction.type, { error, fromAction })
  );

const cellResponse = (response, error) => ({ response, error });
export const epics = createEpicScenario({
  fetchMapFiles: {
    type: "MAPS_FILES_FETCH",
    epic: (fromAction, action$) =>
      MapService.getMapFileByIdAndType(fromAction.payload.id)
        .map(mapFiles =>
          redux.actions.updateMapsFiles({
            id: fromAction.payload.id,
            mapFiles: cellResponse(mapFiles)
          })
        )
        .catch(error =>
          Observable.of(
            redux.actions.updateMapsFiles({
              id: fromAction.payload.id,
              mapFiles: cellResponse(undefined, error)
            })
          )
        )
  },
  fetchMaps: {
    type: "MAPS_FARMS_DETAILS_FETCH",
    epic: fromAction =>
      MapService.getMapsItems()
        .map(toActionCreator(redux.actions.updateMaps, fromAction))
        .catch(handleError(fromAction))
  }
});
// ========================= Epics - END

// ========================= Schemas - START
const itemSchema = new schema.Entity("maps");
const itemListSchema = new schema.Array(itemSchema);
// ========================= Schemas - END

// ========================= Reducers - START
const initialState = {
  ...errorPendingInitialState,
  mapEntities: {},
  mapItems: [],
  lastUpdated: "",
  isTooltipVisible: true
};

const updateMapsReducer = (state, { payload, fromAction }) => {
  const {
    entities: { maps },
    result
  } = normalize(payload, itemListSchema);
  return update(state, {
    mapEntities: { $set: maps },
    mapItems: { $set: result },
    lastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

const updateTooltipReducer = state => {
  console.log("update tool tip reducer"); // eslint-disable-line no-console
  return update(state, {
    isTooltipVisible: { $set: false }
  });
};

const updateFileMapsReducer = (state, { payload: { id, mapFiles } }) => {
  const imageUrlMaps = new Map();
  let imageType;
  let imageUrl;
  let imageTypeStartIndex;
  let imageTypeLastIndex;
  const contentTypeInitials = "image/";
  if (mapFiles.response !== undefined) {
    for (let i = 0; i < mapFiles.response.length; i++) {
      if (mapFiles.response[i].contentType !== undefined) {
        imageTypeStartIndex =
          mapFiles.response[i].contentType.indexOf(contentTypeInitials) +
          contentTypeInitials.length;
        imageTypeLastIndex = mapFiles.response[i].contentType.length;
        imageType = mapFiles.response[i].contentType
          .substring(imageTypeStartIndex, imageTypeLastIndex)
          .toUpperCase();
        imageUrl = mapFiles.response[i].downloadSASUrl;
        imageUrlMaps.set(imageType, imageUrl);
      }
    }
  }
  return update(state, {
    mapEntities: { [id]: { imageUrlMaps: { $set: imageUrlMaps } } }
  });
};

/* Action types that cause a pending flag */
const fetchableTypes = [
  epics.actionTypes.fetchMaps,
  epics.actionTypes.fetchMapFiles
];

export const redux = createReducerScenario({
  updateMaps: { type: "MAPS_UPDATE", reducer: updateMapsReducer },
  updateTooltip: { type: "TOOLTIP_UPDATE", reducer: updateTooltipReducer },
  updateMapsFiles: {
    type: "MAPS_FILES_UPDATE",
    reducer: updateFileMapsReducer
  },
  registerError: { type: "MAPS_REDUCER_ERROR", reducer: errorReducer },
  isFetching: { multiType: fetchableTypes, reducer: pendingReducer }
});

export const reducer = { maps: redux.getReducer(initialState) };
// ========================= Reducers - END

// ========================= Selectors - START
export const getMapsReducer = state => state.maps;
export const getMapEntities = state => getMapsReducer(state).mapEntities || {};
export const getTooltipPendingStatus = state =>
  getMapsReducer(state).isTooltipVisible;
export const getMapItems = state => getMapsReducer(state).mapItems || [];
export const getMapsLastUpdated = state => getMapsReducer(state).lastUpdated;
export const getMapsError = state =>
  getError(getMapsReducer(state), epics.actionTypes.fetchMaps);
export const getMapsPendingStatus = state =>
  getPending(getMapsReducer(state), epics.actionTypes.fetchMaps);
export const getMaps = createSelector(
  getMapEntities,
  getMapItems,
  getFarmsMap,
  (mapEntities, mapItems, farmMap) => {
    return mapItems.map(id => {
      return {
        ...mapEntities[id],
        farmName: farmMap.get(mapEntities[id].farmId),
        mapName:
          String(mapEntities[id].type) +
          "_" +
          farmMap.get(mapEntities[id].farmId) +
          "_" +
          String(moment(mapEntities[id].date).format(jobsDateFormat))
      };
    });
  }
);

export const getMapTypes = createSelector(
  getMapEntities,
  getMapItems,
  (mapTypeEntities, mapTypeItems) => {
    return [
      ...new Set(
        Object.keys(mapTypeEntities).map(item => mapTypeEntities[item].type)
      )
    ];
  }
);

// ========================= Selectors - END
