import { ApplicationInsights } from "@microsoft/applicationinsights-web";
import { ReactPlugin } from "@microsoft/applicationinsights-react-js";
import uuidv4 from "uuid";
export let appInsights = null;
export const reactPlugin = new ReactPlugin();
const globallyUniqueSessionId = uuidv4();

export const initialize = (instrumentationKey, historyObject) => {
  if (!historyObject) {
    throw new Error("History object missing");
  }
  if (!instrumentationKey) {
    throw new Error("Instrumentation key missing");
  }

  appInsights = new ApplicationInsights({
    config: {
      instrumentationKey: instrumentationKey,
      maxBatchInterval: 0,
      disableFetchTracking: false,
      extensions: [reactPlugin],
      extensionConfig: {
        [reactPlugin.identifier]: {
          history: historyObject
        }
      }
    }
  });

  appInsights.loadAppInsights();

  const globallyUniqueSessionIdTelemetryInitializer = telemetryItem => {
    telemetryItem.ext.app.sesId = globallyUniqueSessionId;
    return true;
  };

  appInsights.addTelemetryInitializer(
    globallyUniqueSessionIdTelemetryInitializer
  );
};
