import React, { Component } from "react";
import update from "immutability-helper";

import { AjaxError, Indicator } from "components/shared";
import {
  Panel,
  PanelHeader,
  PanelHeaderLabel,
  PanelContent,
  PanelOverlay,
  PanelError,
  ContextMenuAlign,
  NoMapImage
} from "components/shared";
// import { toDiagnosticsModel } from 'services/models';
import { AzureMap } from "components/shared/azureMapPanel/azureMap";
import { zoomToGeometry } from "utilities";
import "./mapPanel.scss";

const AzureMaps = window.atlas;

const deviceToMapPin = latLong =>
  new AzureMaps.data.Feature(new AzureMaps.data.Point(latLong));

export class MapPanel extends Component {
  constructor(props) {
    super(props);

    this.state = { selectedDeviceId: undefined };
  }

  componentDidUpdate() {
    this.calculatePins(this.props);
  }

  onMapReady = map => {
    // Create the map popup
    this.popup = new AzureMaps.Popup();
    this.map = map;
    this.calculatePins(this.props, true);
  };

  calculatePins(props, mounting = false) {
    if (this.map) {
      if (this.props.farm && this.props.farm.geometry) {
        const geoJson = this.props.farm.geometry;

        zoomToGeometry(this.map, geoJson);

        let polygon;
        if (geoJson.type === "Polygon") {
          polygon = new AzureMaps.data.Polygon(geoJson.coordinates);
        } else if (geoJson.type === "MultiPolygon") {
          polygon = new AzureMaps.data.MultiPolygon(geoJson.coordinates);
        }

        this.map.addPolygons([polygon], {
          name: "farm-boundary",
          color: "#1e90ff",
          opacity: 0.5,
          overwrite: true
        });
      }
      if (props.sensorPlacementLatLong) {
        const { normal } = this.devicesToPins(props.sensorPlacementLatLong);
        this.map.addPins(normal, {
          name: "sensor-placement",
          fillColor: "blue",
          overwrite: true
        });
      }
    }
  }

  extractGeoLocatedDevices(devices, farmId) {
    return Object.keys(devices)
      .map(key => devices[key])
      .filter(
        device =>
          device.latitude && device.longitude && device.farmId === farmId
      );
  }

  devicesToPins(devices) {
    return devices.reduce(
      (acc, device) => {
        const devicePin = deviceToMapPin(device);
        const category = "normal";
        if (category === "normal") {
          return update(acc, {
            [category]: { $push: [devicePin] }
          });
        }
        return acc;
      },
      { normal: [] }
    );
  }

  zoomToPins(pins) {
    const lons = [];
    const lats = [];
    pins.forEach(({ geometry: { coordinates: [longitude, latitude] } }) => {
      lons.push(longitude);
      lats.push(latitude);
    });

    const swLon = Math.min.apply(null, lons);
    const swLat = Math.min.apply(null, lats);
    const neLon = Math.max.apply(null, lons);
    const neLat = Math.max.apply(null, lats);

    // Zoom the map to the bounding box of the devices
    this.map.setCameraBounds({
      bounds: [swLon, swLat, neLon, neLat],
      padding: 50
    });
  }

  openDeviceDetails = selectedDeviceId => this.setState({ selectedDeviceId });

  closeDeviceDetails = () => this.setState({ selectedDeviceId: undefined });

  render() {
    const {
      t,
      isPending,
      mapKeyIsPending,
      azureMapsKey,
      error,
      latestMapDate,
      sensorPlacementLatLong,
      page
    } = this.props;
    const showOverlay = !error && isPending && mapKeyIsPending;
    return page === "Maps" ? (
      <div className={"modal-sensor-placement"}>
        <div>
          {sensorPlacementLatLong ? (
            <React.Fragment>
              <AzureMap
                azureMapsKey={azureMapsKey}
                onMapReady={this.onMapReady}
                mapDivId={"sensor-placement-map"}
              />
            </React.Fragment>
          ) : null}
        </div>
      </div>
    ) : (
      <Panel className="sensor-map-panel-container">
        <PanelHeader>
          <PanelHeaderLabel>
            {t("farms.panel.sensorPlacement")}{" "}
          </PanelHeaderLabel>
          <ContextMenuAlign right={true}>{latestMapDate}</ContextMenuAlign>
          {!showOverlay && isPending && <Indicator size="small" />}
        </PanelHeader>
        <PanelContent>
          <div className="map-panel-container">
            {sensorPlacementLatLong ? (
              <React.Fragment>
                <AzureMap
                  azureMapsKey={azureMapsKey}
                  onMapReady={this.onMapReady}
                  mapDivId={"sensor-placement-map"}
                />
              </React.Fragment>
            ) : (
              <NoMapImage
                msg={t("maps.modal.noMapYet")}
                className={"no-map"}
                t={t}
              />
            )}
          </div>
        </PanelContent>
        {showOverlay && (
          <PanelOverlay>
            <Indicator />
          </PanelOverlay>
        )}
        {!mapKeyIsPending && !this.props.azureMapsKey && (
          <PanelError>{t("dashboard.panels.map.notSupportedError")}</PanelError>
        )}
        {error && (
          <PanelError>
            <AjaxError t={t} error={error} />
          </PanelError>
        )}
      </Panel>
    );
  }
}
