import React, { Component } from "react";
import {
  Panel,
  PanelContent,
  FormControl,
  PanelParagraph
} from "components/shared";
import {
  isJSON,
  isEmptyCheck,
  isNull,
  geoJSONValidation,
  sampleGeoJSON
} from "utilities";
import "./geoJSON.scss";

// var GeoJSONErrors = [];
let isInvalid = undefined;

export class GeoJSON extends Component {
  constructor(props) {
    super(props);

    isInvalid = false;
    this.state = {
      geoString:
        !isNull(this.props.GeoJSON) &&
        !isEmptyCheck(this.props.GeoJSON) &&
        isJSON(this.props.GeoJSON) &&
        (geoJSONValidation.isPolygon(JSON.parse(this.props.GeoJSON)) ||
          geoJSONValidation.isMultiPolygon(JSON.parse(this.props.GeoJSON)))
          ? JSON.stringify(JSON.parse(this.props.GeoJSON), null, 4)
          : this.props.GeoJSON
    };

    this.handleGeoJSONInput = this.handleGeoJSONInput.bind(this);
    // this.callback = this.callback.bind(this);
  }
  /**
   * TODO For Error Display
  callback(valid,errors){
    GeoJSONErrors = [...errors];
  }
  */
  componentDidMount() {
    const { GeoJSON } = this.props;

    if (GeoJSON) {
      const geoString =
        !isNull(GeoJSON) &&
        !isEmptyCheck(GeoJSON) &&
        isJSON(GeoJSON) &&
        (geoJSONValidation.isPolygon(JSON.parse(GeoJSON)) ||
          geoJSONValidation.isMultiPolygon(JSON.parse(GeoJSON)))
          ? JSON.stringify(JSON.parse(GeoJSON), null, 4)
          : GeoJSON;
      this.setState({
        geoString
      });
    }
  }

  componentDidUpdate(prevProps) {
    const { GeoJSON } = this.props;
    if (GeoJSON && GeoJSON !== prevProps.GeoJSON) {
      this.setState({
        geoString:
          !isNull(GeoJSON) &&
          !isEmptyCheck(GeoJSON) &&
          isJSON(GeoJSON) &&
          (geoJSONValidation.isPolygon(JSON.parse(GeoJSON)) ||
            geoJSONValidation.isMultiPolygon(JSON.parse(GeoJSON)))
            ? JSON.stringify(JSON.parse(GeoJSON), null, 4)
            : GeoJSON
      });
    }
  }

  handleGeoJSONInput(event) {
    const geoJSONInput = event.target;
    let geoJSONValue = geoJSONInput.value;
    let invalid = false;
    if (isJSON(geoJSONValue)) {
      if (geoJSONValue.length === 0) {
        invalid = false;
        geoJSONValue = "";
      } else {
        const geoJSONObject = JSON.parse(geoJSONValue);
        if (
          geoJSONValidation.valid(geoJSONObject, this.callback) &&
          (geoJSONValidation.isPolygon(geoJSONObject, this.callback) ||
            geoJSONValidation.isMultiPolygon(geoJSONObject, this.callback)) &&
          geoJSONValue.length > 0
        ) {
          invalid = false;
        } else {
          invalid = true;
        }
      }
    } else {
      invalid = true;
    }
    isInvalid = invalid;
    this.props.getGeoJSON(geoJSONValue);
    this.props.setGeoJSONInvalidStatus(invalid);
    this.setState({ ...this.state, geoString: geoJSONValue });
  }

  render() {
    const { t } = this.props;
    const { geoString } = this.state;
    return (
      <Panel className="no-box-shadow">
        <PanelContent className="geo-json">
          <FormControl
            type="textarea"
            name="geoJSON"
            value={geoString || ""}
            onChange={this.handleGeoJSONInput}
            id={isInvalid ? "invalid-input" : "geo-string"}
            placeholder={JSON.stringify(sampleGeoJSON, null, 4)}
          />
          {/* TODO {isInvalid && GeoJSONErrors.map(error => {return <PanelParagraph className="warning-text" key={error}>{error}</PanelParagraph>})} */}
          {isInvalid && (
            <PanelParagraph className="warning-text">
              {t("farms.panels.geoJSON.geoJSONError")}
            </PanelParagraph>
          )}
        </PanelContent>
      </Panel>
    );
  }
}
