import React from "react";

import { SuccessMsg } from "../forms";
import { joinClasses } from "utilities";

import "./successMessage.scss";

export const SuccessMessage = ({ successMsg, t, className }) => (
  <div className={joinClasses("success-message-container", className)}>
    <SuccessMsg>
      {t("successMessage", {
        message: t(successMsg, { message: successMsg })
      })}
    </SuccessMsg>
  </div>
);
