import React, { Component } from "react";
import { isFunc, translateColumnDefs } from "utilities";
import { PcsGrid } from "components/shared";
import { checkboxColumn } from "components/shared/pcsGrid/pcsGridConfig";
import {
  addDeviceColumnDefs,
  defaultAddDeviceGridProps
} from "./addDeviceGridConfig";
import "./addDeviceGrid.scss";

const initialState = {
  softSelectedId: undefined,
  selectedDevices: undefined
};

export class AddDeviceGrid extends Component {
  constructor(props) {
    super(props);

    this.state = initialState;

    this.columnDefs = [
      checkboxColumn,
      addDeviceColumnDefs.deviceName,
      addDeviceColumnDefs.farmName
    ];

    this.defaulGridProps = { ...defaultAddDeviceGridProps };
  }

  getSoftSelectId = ({ id } = "") => id;

  onGridReady = gridReadyEvent => {
    this.addDeviceGridApi = gridReadyEvent.api;
    // Call the onReady props if it exists
    if (isFunc(this.props.onGridReady)) {
      this.props.onGridReady(gridReadyEvent);
    }
  };

  onHardSelectChange = selectedDevices => {
    const { onHardSelectChange } = this.props;
    if (isFunc(onHardSelectChange)) {
      onHardSelectChange(selectedDevices);
      this.setState({ selectedDevices });
    }
  };

  onSoftSelectChange = (rowId, rowData) => {
    // Note: only the Id is reliable, rowData may be out of date
    const { onSoftSelectChange } = this.props;
    if (rowId) {
      this.setState({ softSelectedId: rowId });
    }
    if (isFunc(onSoftSelectChange)) {
      onSoftSelectChange(rowId, rowData);
    }
  };

  render() {
    const { t } = this.props;
    const { softSelectedId } = this.state;
    const gridProps = {
      ...this.defaulGridProps,
      columnDefs: translateColumnDefs(t, this.columnDefs),
      sizeColumnsToFit: true,
      getSoftSelectId: this.getSoftSelectId,
      softSelectId: softSelectedId,
      ...this.props,
      deltaRowDataMode: true,
      enableSorting: true,
      unSortIcon: true,
      getRowNodeId: ({ id }) => id,
      context: {
        t: t
      },
      /* Grid Events */
      onGridReady: this.onGridReady,
      onRowClicked: ({ node }) => node.setSelected(!node.isSelected()),
      onHardSelectChange: this.onHardSelectChange,
      onSoftSelectChange: this.onSoftSelectChange
    };

    return (
      <div className="add-device-grid">
        <PcsGrid {...gridProps} />
      </div>
    );
  }
}
