import "rxjs";
import { Observable } from "rxjs";
import moment from "moment";
import { schema, normalize } from "normalizr";
import update from "immutability-helper";
import { createSelector } from "reselect";
import { TelemetryService, AlertService } from "services";
import {
  createReducerScenario,
  createEpicScenario,
  errorPendingInitialState,
  pendingReducer,
  errorReducer,
  setPending,
  getPending,
  getError,
  toActionCreator
} from "store/utilities";
import { formatConditions } from "utilities";

// ========================= Epics - START
const alertsHandleError = fromAction => error =>
  Observable.of(
    alertsRedux.actions.alertsRegisterError(fromAction.type, {
      error,
      fromAction
    })
  );

const rulesHandleError = fromAction => error =>
  Observable.of(
    rulesRedux.actions.rulesRegisterError(fromAction.type, {
      error,
      fromAction
    })
  );

export const epics = createEpicScenario({
  /** Loads the alerts alertsItems */
  fetchAlerts: {
    type: "ALERTS_FETCH",
    epic: fromAction =>
      AlertService.getAlerts()
        .map(toActionCreator(alertsRedux.actions.updateAlerts, fromAction))
        .catch(alertsHandleError(fromAction))
  },
  /** Loads the rules, including deleted rules */
  fetchRules: {
    type: "RULES_FETCH",
    epic: fromAction =>
      TelemetryService.getRules()
        .map(toActionCreator(rulesRedux.actions.updateRules, fromAction))
        .catch(rulesHandleError(fromAction))
  },

  deleteRule: {
    type: "RULES_DELETE",
    epic: fromAction =>
      TelemetryService.deleteRule(fromAction.payload)
        .map(toActionCreator(rulesRedux.actions.deleteRule, fromAction))
        .catch(rulesHandleError(fromAction))
  }
});
// ========================= Epics - END

// ========================= Schemas - START
const alertSchema = new schema.Entity("alerts");
const alertListSchema = new schema.Array(alertSchema);

const ruleSchema = new schema.Entity("rules");
const ruleListSchema = new schema.Array(ruleSchema);
// ========================= Schemas - END

// ========================= Reducers - START
const alertsInitialState = {
  ...errorPendingInitialState,
  alertEntities: undefined,
  alertItems: [],
  alertLastUpdated: undefined
};

const rulesInitialState = {
  ...errorPendingInitialState,
  ruleEntities: {},
  ruleItems: [],
  ruleLastUpdated: undefined
};

const updateAlertsReducer = (state, { payload, fromAction }) => {
  const {
    entities: { alerts },
    result
  } = normalize(payload, alertListSchema);
  return update(state, {
    alertEntities: { $set: alerts },
    alertItems: { $set: result },
    lastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

const insertRulesReducer = (state, { payload }) => {
  const inserted = payload.map(rule => ({ ...rule, isNew: true }));
  const {
    entities: { rules },
    result
  } = normalize(inserted, ruleListSchema);
  if (state.ruleEntities) {
    return update(state, {
      ruleEntities: { $merge: rules },
      ruleItems: { $push: result }
    });
  }
  return update(state, {
    ruleEntities: { $set: rules },
    ruleItems: { $set: [result] }
  });
};

const modifyRulesReducer = (state, { payload }) => {
  const {
    entities: { rules = {} }
  } = normalize(payload, ruleListSchema);
  return update(state, {
    ruleEntities: { $merge: rules }
  });
};

const updateRulesReducer = (state, { payload, fromAction }) => {
  const {
    entities: { rules = {} },
    result
  } = normalize(payload, ruleListSchema);
  return update(state, {
    ruleEntities: { $set: rules },
    ruleItems: { $set: result },
    ruleLastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

const updateRulesWithFarmReducer = (state, { payload, fromAction }) => {
  const {
    entities: { rules = {} },
    result
  } = normalize(payload, ruleListSchema);
  return update(state, {
    ruleEntities: { $set: rules },
    ruleItems: { $set: result },
    ruleLastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

const deleteRuleReducer = (state, { payload, fromAction }) => {
  const spliceArr = payload.reduce((idxAcc, payloadItem) => {
    const idx = state.ruleItems.indexOf(payloadItem);
    if (idx !== -1) {
      idxAcc.push([idx, 1]);
    }
    return idxAcc;
  }, []);
  return update(state, {
    ruleEntities: { $unset: payload },
    ruleItems: { $splice: spliceArr }
  });
};

const updateCountReducer = (state, { payload: { id, count } }) =>
  update(state, {
    ruleEntities: { [id]: { count: { $set: count } } }
  });

const updateLastTriggerReducer = (state, { payload: { id, lastTrigger } }) =>
  update(state, {
    ruleEntities: { [id]: { lastTrigger: { $set: lastTrigger } } }
  });

const alertsFetchableTypes = [epics.actionTypes.fetchAlerts];
const rulesFetchableTypes = [epics.actionTypes.fetchRules];

export const alertsRedux = createReducerScenario({
  // Reducers defined in this file
  updateAlerts: { type: "ALERTS_UPDATE", reducer: updateAlertsReducer },

  // Common Reducers (from store/utilities.js)
  alertsRegisterError: { type: "ALERTS_REDUCER_ERROR", reducer: errorReducer },
  alertsIsFetching: { multiType: alertsFetchableTypes, reducer: pendingReducer }
});

export const rulesRedux = createReducerScenario({
  // Reducers defined in this file
  insertRules: { type: "RULE_INSERT", reducer: insertRulesReducer },
  modifyRules: { type: "RULES_MODIFY", reducer: modifyRulesReducer },
  deleteRule: { type: "RULES_DELETE", reducer: deleteRuleReducer },
  updateRules: { type: "RULES_UPDATE", reducer: updateRulesReducer },
  updateRuleCount: { type: "RULES_COUNT_UPDATE", reducer: updateCountReducer },
  updateRuleLastTrigger: {
    type: "RULES_LAST_TRIGGER_UPDATE",
    reducer: updateLastTriggerReducer
  },
  updateRuleWithFarmId: {
    type: "RULES_UPDATE_FARM_ID",
    reducer: updateRulesWithFarmReducer
  },

  // Common Reducers (from store/utilities.js)
  rulesRegisterError: { type: "RULES_REDUCER_ERROR", reducer: errorReducer },
  rulesIsFetching: { multiType: rulesFetchableTypes, reducer: pendingReducer }
});

export const reducer = {
  alerts: alertsRedux.getReducer(alertsInitialState),
  rules: rulesRedux.getReducer(rulesInitialState)
};
// ========================= Reducers - END

// ========================= Selectors - START
// Alerts Selectors
export const getAlertsReducer = state => state.alerts; // Conflict
export const getAlertEntities = state =>
  getAlertsReducer(state).alertEntities || {};
export const getAlertItems = state => getAlertsReducer(state).alertItems || [];
export const getAlertsLastUpdated = state =>
  getAlertsReducer(state).lastUpdated;
export const getAlertsError = state =>
  getError(getAlertsReducer(state), epics.actionTypes.fetchAlerts);
export const getAlertsPendingStatus = state =>
  getPending(getAlertsReducer(state), epics.actionTypes.fetchAlerts);
export const getDeleteAlertError = state =>
  getError(getAlertsReducer(state), epics.actionTypes.deleteAlert);
export const getDeleteAlertPendingStatus = state =>
  getPending(getAlertsReducer(state), epics.actionTypes.deleteAlert);
export const getAlerts = createSelector(
  // Conflict
  getAlertEntities,
  getAlertItems,
  (alertEntities, alertItems) => alertItems.map(id => alertEntities[id])
);

export const getAlertsRuleBased = state => {
  const alerts = getAlerts(state);
  const rules = getRules(state);
  const ruleAdded = [];
  const alertsByRule = [];
  alerts.forEach(alert => {
    if (!ruleAdded[alert.ruleId]) {
      ruleAdded[alert.ruleId] = true;
      const rule = rules.find(rule => rule.id === alert.ruleId);
      alertsByRule.push({
        ...alert,
        ruleName: rule ? rule.name : undefined,
        counts: getAlertCountsByRuleId(state, alert.ruleId),
        lastOccurrence: getAlertLatestOccurrenceByRuleId(state, alert.ruleId)
      });
    }
  });
  return alertsByRule;
};

export const getAlertCountsByRuleId = (state, ruleId) => {
  const alertEntities = getAlerts(state).filter(
    alert => alert.ruleId === ruleId
  );
  return {
    total: alertEntities.length,
    open: alertEntities.filter(alert => alert.status === "open").length,
    acknowledged: alertEntities.filter(alert => alert.status === "acknowledged")
      .length,
    closed: alertEntities.filter(alert => alert.status === "closed").length
  };
};
export const getAlertLatestOccurrenceByRuleId = (state, ruleId) => {
  const alertEntities = getAlerts(state)
    .filter(alert => alert.ruleId === ruleId)
    .map(alert => alert.createdAt);

  return alertEntities.reduce((latestOccurrence, currentOccurrence) => {
    if (moment(currentOccurrence).isAfter(latestOccurrence))
      latestOccurrence = currentOccurrence;
    return latestOccurrence;
  });
};
export const getAlertById = (state, id) =>
  id === undefined ? null : getAlertEntities(state)[id];

export const getAlertsByFarmId = (state, id) => {
  // Add code to update the alerts reducer
};

// Rules Selectors
export const getRulesReducer = state => state.rules; // Conflict
export const getEntities = state => getRulesReducer(state).ruleEntities || {};
export const getRulesWithFarmId = state => getRulesReducer(state) || {};
const getItems = state => getRulesReducer(state).ruleItems || [];
export const getRulesLastUpdated = state =>
  getRulesReducer(state).ruleLastUpdated;
export const getRulesError = state =>
  getError(getRulesReducer(state), epics.actionTypes.fetchRules);
export const getRulesPendingStatus = state =>
  getPending(getRulesReducer(state), epics.actionTypes.fetchRules);
export const getRules = createSelector(
  // Conflict
  getEntities,
  getItems,
  (entities, items, includeDeleted = false) =>
    items.reduce((acc, id) => {
      const rule = {
        ...entities[id],
        sortableConditions: formatConditions(entities[id])
      };
      return !rule.deleted || includeDeleted ? [...acc, rule] : acc;
    }, [])
);

export const getRuleById = (state, id) =>
  id === undefined ? null : getEntities(state)[id];
// ========================= Selectors - END
