import React from "react";
import { Svg } from "components/shared";
import {
  DevicesGrid,
  childDeviceColumnDefs,
  defaultChildDeviceGridProps
} from "components/pages/devices/devicesGrid";
import { svgs, translateColumnDefs } from "utilities";
import Flyout from "components/shared/flyout";
import { TelemetryPanelContainer as TelemetryPanel } from "components/shared/telemetryPanel";
import { DeviceProperties } from "components/pages/devices/deviceProperties/deviceProperties";
import memoizeOne from "memoize-one";
import { appInsights, reactPlugin } from "applicationInsights";
import { withAITracking } from "@microsoft/applicationinsights-react-js";

const Section = Flyout.Section;

const filterByParentId = memoizeOne((list, parentId) =>
  list.filter(object => object.parentDeviceId === parentId)
);

const columnDefs = [
  childDeviceColumnDefs.name,
  childDeviceColumnDefs.deviceModelType,
  childDeviceColumnDefs.status
];

class DeviceDetails extends React.Component {
  componentDidMount() {
    appInsights.trackEvent({ name: "UiEvent:FlyOpen: Device Details" });
  }
  componentDidUpdate(prevProps) {
    if (prevProps.deviceId !== this.props.deviceId) {
      appInsights.trackEvent({ name: "UiEvent:FlyChange: Device Details" });
    }
  }
  componentWillUnmount() {
    appInsights.trackEvent({ name: "UiEvent:FlyClose: Device Details" });
  }
  render() {
    const {
      fetchSensors,
      sensorLastUpdated,
      sensorsError,
      sensors,
      devices,
      deviceId,
      device,
      t,
      onClose,
      theme
    } = this.props;

    if (!sensorLastUpdated && !sensorsError) fetchSensors();

    const devicesGridProps = {
      t,
      rowData: filterByParentId(devices, deviceId),
      domLayout: "autoHeight",
      ...defaultChildDeviceGridProps,
      columnDefs: translateColumnDefs(t, columnDefs),
      suppressFlyouts: true
    };

    const filteredSensorIds = sensors
      .filter(sensor => sensor.deviceId === deviceId)
      .map(sensor => sensor.id);

    return (
      <Flyout.Container>
        <Flyout.Header>
          <Flyout.Title />
          <Flyout.CloseBtn onClick={onClose} />
        </Flyout.Header>
        <Flyout.Content className="device-details-container">
          {!!device && (
            <div className="device-details-container">
              <div>
                <table className="header-table">
                  <tbody>
                    <tr>
                      <td className="deviceType">
                        <Svg
                          path={
                            device.deviceModelType === "Node"
                              ? svgs.nodeBig
                              : svgs.gatewayBig
                          }
                        />
                      </td>
                      <td className="deviceName">
                        <div className="device-title"> {device.name}</div>
                      </td>
                      <td className="deviceStatus">
                        <Svg
                          path={
                            device.status === "Offline"
                              ? svgs.offline
                              : svgs.onlineBig
                          }
                        />
                      </td>
                      <td className="callspan-td" />
                    </tr>
                  </tbody>
                </table>
              </div>
              <Section.Container className="device-property-container">
                <Section.Header>
                  {t("devices.flyouts.details.deviceProperties.title")}
                </Section.Header>
                <Section.Content>
                  <div>
                    <DeviceProperties device={device} t={t} />
                  </div>
                </Section.Content>
              </Section.Container>
              {/**
              <Section.Container>
                <Section.Header>
                  {t("devices.flyouts.details.activeAlerts.title")}
                </Section.Header>
                <Section.Content />
              </Section.Container>
              */}
              <Section.Container
                className="device-telemetry-panel"
                collapsable={false}
              >
                <Section.Header>
                  {t("devices.flyouts.details.telemetry.title")}
                </Section.Header>
                <Section.Content>
                  <TelemetryPanel
                    theme={theme}
                    t={t}
                    sensorIds={filteredSensorIds}
                    showHeader={false}
                  />
                </Section.Content>
              </Section.Container>
              <Section.Container>
                <Section.Header>
                  {t("devices.flyouts.details.childDevices.title").concat(
                    " (",
                    String(devicesGridProps.rowData.length),
                    ")"
                  )}
                </Section.Header>
                <Section.Content>
                  {devicesGridProps.rowData.length > 0 ? (
                    <DevicesGrid {...devicesGridProps} />
                  ) : (
                    <p>
                      {" "}
                      {t("devices.flyouts.details.childDevices.noDevices")}{" "}
                    </p>
                  )}
                </Section.Content>
              </Section.Container>
            </div>
          )}
        </Flyout.Content>
      </Flyout.Container>
    );
  }
}

export default withAITracking(reactPlugin, DeviceDetails, "Device Details");
