import "rxjs";
import { Observable } from "rxjs";
import moment from "moment";
import { schema, normalize } from "normalizr";
import update from "immutability-helper";
import { createSelector } from "reselect";
import { redux as appRedux } from "./appReducer";
import { deviceService, FarmService } from "services";
import {
  createReducerScenario,
  createEpicScenario,
  resetPendingAndErrorReducer,
  errorPendingInitialState,
  pendingReducer,
  errorReducer,
  setPending,
  toActionCreator,
  getPending,
  getError
} from "store/utilities";

// ========================= Epics - START
const handleError = fromAction => error =>
  Observable.of(
    redux.actions.registerError(fromAction.type, { error, fromAction })
  );

export const epics = createEpicScenario({
  /** Loads the devices */
  fetchDevices: {
    type: "DEVICES_FETCH",
    epic: fromAction =>
      Observable.forkJoin(
        deviceService.getDevices(),
        deviceService.getDeviceModels(),
        FarmService.getFarms()
      )
        .map(([devices, devicetypes, farms]) => {
          const devicetypemap = new Map();
          const deviceTypeNamemap = new Map();
          const farmMap = new Map();
          farms.forEach(farm => {
            farmMap.set(farm.id, farm.name);
          });
          devicetypes.forEach(type => {
            devicetypemap.set(type.id, type.type);
          });
          devicetypes.forEach(type => {
            deviceTypeNamemap.set(type.id, type.type);
          });

          devices.forEach(device => {
            device.deviceModelType = devicetypemap.get(device.deviceModelId);
            device.deviceModelName = deviceTypeNamemap.get(
              device.deviceModelId
            );
            const deviceMapWithParent = devices.find(
              devicePrarent => devicePrarent.id === device.parentDeviceId
            );
            device.parentDevice =
              deviceMapWithParent === undefined
                ? undefined
                : deviceMapWithParent.name;
            device.farmName =
              device.farmId === null || undefined
                ? undefined
                : farmMap.get(device.farmId);
          });
          return { ...devices };
        })
        .map(toActionCreator(redux.actions.updateDevices, fromAction))
        .catch(handleError(fromAction))
  },

  fetchDeviceModel: {
    type: "DEVICESMODEL_FETCH",
    epic: fromAction => {
      return deviceService
        .getDeviceModels()
        .map(toActionCreator(redux.actions.updateDevicesModel, fromAction))
        .catch(handleError(fromAction));
    }
  },

  /* Update the devices if the selected device group changes */
  refreshDevices: {
    type: "DEVICES_REFRESH",
    rawEpic: $actions =>
      $actions
        .ofType(appRedux.actionTypes.updateActiveDeviceGroup)
        .map(({ payload }) => payload)
        .distinctUntilChanged()
        .map(_ => epics.actions.fetchDevices())
  },

  deleteDevicesById: {
    type: "DELETE_DEVICES_BY_ID",
    epic: fromAction => {
      return Observable.from(fromAction.payload)
        .flatMap(({ id }) => {
          return deviceService.deleteDevice(id).map(() => id);
        })
        .map(toActionCreator(redux.actions.deleteDevices, fromAction));
    }
  }
});
// ========================= Epics - END

// ========================= Schemas - START
const deviceSchema = new schema.Entity("devices");
const deviceListSchema = new schema.Array(deviceSchema);

const deviceModelSchema = new schema.Entity("deviceModels");
const deviceModelListSchema = new schema.Array(deviceModelSchema);
// ========================= Schemas - END

// ========================= Reducers - START
const initialState = {
  ...errorPendingInitialState,
  entities: {},
  items: [],
  deviceModelEntities: {},
  deviceModelItems: [],
  lastUpdated: undefined,
  deviceModelLastUpdated: undefined
};

const updateDevicesReducer = (state, { payload, fromAction }) => {
  const {
    entities: { devices },
    result
  } = normalize(payload, deviceListSchema);
  return update(state, {
    entities: { $set: devices },
    items: { $set: result },
    lastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

const updateDeviceModelReducer = (state, { payload, fromAction }) => {
  const {
    entities: { deviceModels },
    result
  } = normalize(payload, deviceModelListSchema);
  return update(state, {
    deviceModelEntities: { $set: deviceModels },
    deviceModelItems: { $set: result },
    deviceModelLastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

const updateDevicesByConditionReducer = (state, { payload, fromAction }) => {
  const {
    entities: { devices }
  } = normalize(payload, deviceListSchema);
  return update(state, {
    devicesByCondition: { $set: devices },
    ...setPending(fromAction.type, false)
  });
};

const deleteDevicesReducer = (state, { payload, fromAction }) => {
  const spliceArr = payload.reduce((idxAcc, payloadItem) => {
    const idx = state.items.indexOf(payloadItem);
    if (idx !== -1) {
      idxAcc.push([idx, 1]);
    }
    return idxAcc;
  }, []);
  return update(state, {
    entities: { $unset: payload },
    items: { $splice: spliceArr }
  });
};

const insertDevicesReducer = (state, { payload }) => {
  const inserted = payload.map(device => ({ ...device, isNew: true }));
  const {
    entities: { devices },
    result
  } = normalize(inserted, deviceListSchema);
  if (state.entities) {
    return update(state, {
      entities: { $merge: devices },
      items: { $splice: [[0, 0, ...result]] }
    });
  }
  return update(state, {
    entities: { $set: devices },
    items: { $set: result }
  });
};

const updateTagsReducer = (state, { payload }) => {
  const updatedTagData = {};
  payload.updatedTags.forEach(
    ({ name, value }) => (updatedTagData[name] = value)
  );

  const updatedDevices = payload.deviceIds.map(id =>
    update(state.entities[id], {
      tags: {
        $merge: updatedTagData,
        $unset: payload.deletedTags
      }
    })
  );

  const {
    entities: { devices }
  } = normalize(updatedDevices, deviceListSchema);
  return update(state, {
    entities: { $merge: devices }
  });
};

const updateModuleStatusReducer = (state, { payload, fromAction }) => {
  const updateAction = payload
    ? { deviceModuleStatus: { $set: payload } }
    : { $unset: ["deviceModuleStatus"] };

  return update(state, {
    ...updateAction,
    ...setPending(fromAction.type, false)
  });
};

const updatePropertiesReducer = (state, { payload }) => {
  const updatedPropertyData = {};
  payload.updatedProperties.forEach(
    ({ name, value }) => (updatedPropertyData[name] = value)
  );

  const updatedDevices = payload.deviceIds.map(id =>
    update(state.entities[id], {
      desiredProperties: {
        $merge: updatedPropertyData,
        $unset: payload.deletedProperties
      }
    })
  );

  const {
    entities: { devices }
  } = normalize(updatedDevices, deviceListSchema);
  return update(state, {
    entities: { $merge: devices }
  });
};

const modifyDeviceReducer = (state, { payload }) => {
  const {
    entities: { devices = {} }
  } = normalize(payload, deviceListSchema);
  return update(state, {
    entities: { $merge: devices },
    lastUpdated: { $set: moment() }
  });
};

/* Action types that cause a pending flag */
const fetchableTypes = [
  epics.actionTypes.fetchDevices,
  epics.actionTypes.fetchDeviceModel,
  epics.actionTypes.fetchDevicesByCondition,
  epics.actionTypes.fetchEdgeAgent
];

export const redux = createReducerScenario({
  updateDevices: { type: "DEVICES_UPDATE", reducer: updateDevicesReducer },
  updateDevicesModel: {
    type: "DEVICEMODEL_UPDATE",
    reducer: updateDeviceModelReducer
  },
  updateDevicesByCondition: {
    type: "DEVICES_UPDATE_BY_CONDITION",
    reducer: updateDevicesByConditionReducer
  },
  registerError: { type: "DEVICES_REDUCER_ERROR", reducer: errorReducer },
  isFetching: { multiType: fetchableTypes, reducer: pendingReducer },
  deleteDevices: { type: "DEVICE_DELETE", reducer: deleteDevicesReducer },
  insertDevices: { type: "DEVICE_INSERT", reducer: insertDevicesReducer },
  modifyDevices: { type: "DEVICE_MODIFY", reducer: modifyDeviceReducer },
  updateTags: { type: "DEVICE_UPDATE_TAGS", reducer: updateTagsReducer },
  updateProperties: {
    type: "DEVICE_UPDATE_PROPERTIES",
    reducer: updatePropertiesReducer
  },
  updateModuleStatus: {
    type: "DEVICE_MODULE_STATUS",
    reducer: updateModuleStatusReducer
  },
  resetPendingAndError: {
    type: "DEVICE_REDUCER_RESET_ERROR_PENDING",
    reducer: resetPendingAndErrorReducer
  }
});

export const reducer = { devices: redux.getReducer(initialState) };
// ========================= Reducers - END

// ========================= Selectors - START
export const getDevicesReducer = state => state.devices;
export const getEntities = state => getDevicesReducer(state).entities || {};
export const getItems = state => getDevicesReducer(state).items || [];
export const getDevicesLastUpdated = state =>
  getDevicesReducer(state).lastUpdated;
export const getDevicesError = state =>
  getError(getDevicesReducer(state), epics.actionTypes.fetchDevices);
export const getDevicesPendingStatus = state =>
  getPending(getDevicesReducer(state), epics.actionTypes.fetchDevices);
export const getDevicesByCondition = state =>
  getDevicesReducer(state).devicesByCondition || {};
export const getDevicesByConditionError = state =>
  getError(getDevicesReducer(state), epics.actionTypes.fetchDevicesByCondition);
export const getDevicesByConditionPendingStatus = state =>
  getPending(
    getDevicesReducer(state),
    epics.actionTypes.fetchDevicesByCondition
  );

export const getDeviceModelEntities = state =>
  getDevicesReducer(state).deviceModelEntities;
export const getDeviceModelItems = state =>
  getDevicesReducer(state).deviceModelItems;
export const getDeviceModelError = state =>
  getError(getDevicesReducer(state), epics.actionTypes.fetchDeviceModel);
export const getDeviceModelLastUpdated = state =>
  getDevicesReducer(state).deviceModelLastUpdated;
export const getDeviceModelsPendingStatus = state =>
  getPending(getDevicesReducer(state), epics.actionTypes.fetchDeviceModel);

export const getDevices = createSelector(
  getEntities,
  getItems,
  (entities, items) => items.map(id => entities[id])
);

export const getDeviceById = (state, id) => getEntities(state)[id];
export const getDeviceModuleStatus = state => {
  // agadhika Possible cleanup
  const deviceModuleStatus = getDevicesReducer(state).deviceModuleStatus;
  return deviceModuleStatus
    ? {
        code: deviceModuleStatus.code,
        description: deviceModuleStatus.description
      }
    : undefined;
};
export const getDeviceModuleStatusPendingStatus = (
  state // agadhika Possible cleanup
) => getPending(getDevicesReducer(state), epics.actionTypes.fetchEdgeAgent);
export const getDeviceModuleStatusError = (
  state // agadhika Possible cleanup
) => getError(getDevicesReducer(state), epics.actionTypes.fetchEdgeAgent);

export const getDeviceModels = createSelector(
  getDeviceModelEntities,
  getDeviceModelItems,
  (deviceModelEntities, deviceModelItems) =>
    deviceModelItems.map(id => deviceModelEntities[id])
);

export const getDevicesMap = createSelector(
  getEntities,
  getItems,
  (deviceEntities, deviceItems) =>
    deviceItems.reduce((devicesMap, id) => {
      return devicesMap.set(id, deviceEntities[id].name);
    }, new Map())
);
export const getDeviceModelsMap = createSelector(
  getDeviceModelEntities,
  getDeviceModelItems,
  (deviceModelEntities, deviceModelItems) =>
    deviceModelItems.reduce((deviceModelsMap, id) => {
      return deviceModelsMap.set(id, deviceModelEntities[id].type);
    }, new Map())
);
// ========================= Selectors - END
