import React from "react";
import "./toolTip.scss";
import { Svg } from "components/shared";
import { svgs } from "utilities";

export const ToolTip = props => {
  const { closeTooltip, title, content } = props;
  return (
    <div className="arrow_box">
      <span>
        {" "}
        <Svg onClick={closeTooltip} path={svgs.cancelX} />
      </span>
      <p className="title">
        <b>{title} </b>{" "}
      </p>
      <p>{content}</p>
    </div>
  );
};
