import React, { Component } from "react";

import { ModalContent, ModalFadeBox, ModalFooter, ModalHeader } from "..";
import Content from "../content/content";
import { joinClasses } from "utilities";

import "./modal.scss";

export class Modal extends Component {
  constructor(props) {
    super(props);

    this.state = {
      disagree: false
    };
  }

  componentDidMount() {
    if (this.props.onClose) {
      window.addEventListener("keydown", this.listenKeyboard, true);
    }
  }

  componentWillUnmount() {
    if (this.props.onClose) {
      window.removeEventListener("keydown", this.listenKeyboard, true);
    }
  }

  listenKeyboard = event => {
    if (event.key === "Escape" || event.keyCode === 27) {
      this.props.onClose();
    }
  };

  onOverlayClick = event => {
    event.preventDefault();
  };

  onClickDisagree = () => {
    this.setState({
      disagree: true
    });
  };

  render() {
    if (this.props.type === "EULA") {
      return (
        <div className={joinClasses("modal", "open", this.props.className)}>
          <article className="content-wrapper">
            <ModalHeader>Microsoft Software License Terms</ModalHeader>{" "}
            {/* {this.props.header} />*/}
            <Content>{this.props.content}</Content>
            {this.state.disagree && (
              <div id="disagree">
                {" "}
                To start using the application please read and agree to the
                terms by clicking on the <b> I Agree </b> button{" "}
              </div>
            )}
            <ModalFooter
              onClickAgree={this.props.onEulaAgree}
              onClickDisagree={this.onClickDisagree}
            />
          </article>
        </div>
      );
    }

    return (
      <div className={joinClasses("modal-container", this.props.className)}>
        <ModalFadeBox onClick={this.onOverlayClick} />
        <ModalContent>{this.props.children}</ModalContent>
      </div>
    );
  }
}
