import React, { Component } from "react";
import {
  Panel,
  PanelHeader,
  PanelHeaderLabel,
  PanelContent,
  PanelError,
  AjaxError,
  ContextMenuAlign
} from "components/shared";
import { svgs, getSceneData } from "utilities";
import { Modal } from "components/shared/mapsModal/modal/modal";
import {
  mapModalType,
  farmNameLoading,
  getPNGFilefromSceneFiles
} from "utilities";
import "./droneMap.scss";
import { appInsights } from "applicationInsights";
const sceneMapType = "drone";
let mapFileDownloadSelectObject = [];
const noMapImage = svgs.NoMapImage;
export class Drone extends Component {
  constructor(props) {
    super(props);
    const farmId = this.props.farmId;
    const mapsData = this.props.maps;
    this.state = {
      isPending: true,
      isModalOpen: false,
      mapName: undefined,
      url: undefined,
      mapFound: undefined,
      farmName: farmNameLoading
    };
    this.onDroneClick = this.onDroneClick.bind(this);
    this.onModalclose = this.onModalclose.bind(this);
    this.getDroneSceneData = this.getDroneSceneData.bind(this);
    this.getDroneSceneData(sceneMapType, farmId, mapsData);
    // generateUrl(sceneMapType, farmId, mapsData);
  }

  onDroneClick() {
    const { t } = this.props;
    this.setState({
      isModalOpen: true,
      mapName: t("farms.panel.Orthomosaic")
    });
    appInsights.trackEvent({ name: "UiEvent:ModalOpen: Drone Imagery" });
  }

  onModalclose() {
    this.setState({ isModalOpen: false, modalName: undefined });
    appInsights.trackEvent({ name: "UiEvent:ModalClose: Drone Imagery" });
  }

  getDroneSceneData(sceneMapType, farmId, mapsData) {
    getSceneData(sceneMapType, farmId, mapsData, this.sceneDataCallback);
  }

  sceneDataCallback = (selectedObject, mapData, sceneError) => {
    if (sceneError) {
      this.setState({ sceneError });
    } else if (mapData.mapFound) {
      const pngUrl = getPNGFilefromSceneFiles(selectedObject);
      this.setState({
        url: pngUrl,
        mapFound: mapData.mapFound,
        latestMapDate: mapData.latestMapDate,
        latestCreatedAt: mapData.latestCreatedAt
      });
      mapFileDownloadSelectObject = selectedObject.mapFileDownloadSelectObject;
    } else {
      this.setState({
        url: mapData.url,
        mapFound: mapData.mapFound
      });
    }
  };

  onImgError = (name, value) => {
    this.setState({ [name]: value });
  };

  render() {
    const { t, farmsError } = this.props;
    const {
      sceneError,
      isModalOpen,
      mapName,
      url,
      mapFound,
      latestMapDate,
      latestCreatedAt,
      isDroneImageBroken
    } = this.state;
    const error = sceneError || farmsError;
    const { farmName } = this.props;

    return (
      <Panel>
        <PanelHeader>
          <PanelHeaderLabel>{t("farms.panel.Drone")}</PanelHeaderLabel>
          <ContextMenuAlign right={true}>
            <div id="date">{latestMapDate}</div>
          </ContextMenuAlign>
        </PanelHeader>

        <PanelContent>
          {mapFound === undefined ? (
            <div className="shimmer-soil-panel"> </div>
          ) : (
            <div
              className={url !== noMapImage ? "drone-image" : "nodrone-image"}
            >
              <center>
                <img
                  src={url}
                  onClick={mapFound && this.onDroneClick}
                  alt={
                    isDroneImageBroken
                      ? t("dashboard.panels.map.corruptImageError")
                      : t("farms.panel.nodrone-image")
                  }
                  onError={() => this.onImgError("isDroneImageBroken", true)}
                />
                {url !== noMapImage ? (
                  <p>{t("farms.panel.Orthomosaic")}</p>
                ) : (
                  <p>{t("maps.modal.noMapYet")}</p>
                )}
              </center>
            </div>
          )}
          {isModalOpen && (
            <Modal
              type={mapModalType}
              name={mapName}
              farmName={farmName}
              modalClose={this.onModalclose}
              mapDate={latestMapDate}
              latestCreatedAt={latestCreatedAt}
              blobUrl={url}
              noMapImage={noMapImage}
              mapFileDownloadSelectObject={mapFileDownloadSelectObject}
              t={t}
            />
          )}
        </PanelContent>
        {error && (
          <PanelError>
            <AjaxError t={t} error={error} />
          </PanelError>
        )}
      </Panel>
    );
  }
}
