import { HttpClient } from "utilities/httpClient";
import Config from "app.config";

import { toAlertsModel } from "./models";

const ENDPOINT = Config.serviceUrls.alerts;
export class AlertService {
  static getAlerts() {
    return HttpClient.get(
      `${ENDPOINT}?includes=RuleActions&maxItems=${Config.apiGetAllMaxItems}`
    ).map(toAlertsModel);
  }
}
