/* A helper method to copy text to the clipbaord */
export const copyToClipboard = data => {
  const textField = document.createElement("textarea");
  textField.innerText = data;
  document.body.appendChild(textField);
  textField.select();
  document.execCommand("copy");
  textField.remove();
};

export const isValidExtension = file => {
  if (!file) return false;
  const fileExt = file.name
    .split(".")
    .pop()
    .toLowerCase();
  return Config.validExtensions.indexOf("." + fileExt) > -1;
};

// Helper construct from and to parameters for time intervals
export const getIntervalParams = timeInterval => {
  switch (timeInterval) {
    case "P1D":
      return [
        { from: "NOW-P1D", to: "NOW" },
        { from: "NOW-P2D", to: "NOW-P1D" }
      ];

    case "P7D":
      return [
        { from: "NOW-P7D", to: "NOW" },
        { from: "NOW-P14D", to: "NOW-P7D" }
      ];

    case "P1M":
      return [
        { from: "NOW-P1M", to: "NOW" },
        { from: "NOW-P2M", to: "NOW-P1M" }
      ];

    default:
      // Use PT1H as the default case
      return [
        { from: "NOW-PT1H", to: "NOW" },
        { from: "NOW-PT2H", to: "NOW-PT1H" }
      ];
  }
};

// Helper to format conditions for display
export const formatConditions = rule => {
  if (rule && Array.isArray(rule.conditions) && rule.conditions.length) {
    return rule.conditions
      .map(trigger => trigger["field"] || Config.emptyFieldValue)
      .join(" & ");
  }
  return Config.emptyFieldValue;
};

// Helper to get Centroid of a polygon
export const getCentroid = geometry => {
  const coordinates = geometry[0];
  let itr1;
  let itr2;
  let pointA;
  let pointB;
  let factor1 = 0;
  let factor2 = 0;
  let xCoordinate = 0;
  let yCoordinate = 0;

  for (
    itr1 = 0, itr2 = coordinates.length - 1;
    itr1 < coordinates.length;
    itr2 = itr1, itr1 += 1
  ) {
    pointA = coordinates[itr1];
    pointB = coordinates[itr2];
    factor1 += pointA[0] * pointB[1];
    factor1 -= pointA[1] * pointB[0];
  }
  factor1 *= 3;

  for (
    itr1 = 0, itr2 = coordinates.length - 1;
    itr1 < coordinates.length;
    itr2 = itr1, itr1 += 1
  ) {
    pointA = coordinates[itr1];
    pointB = coordinates[itr2];
    factor2 = pointA[0] * pointB[1] - pointA[1] * pointB[0];
    xCoordinate += (pointA[0] + pointB[0]) * factor2;
    yCoordinate += (pointA[1] + pointB[1]) * factor2;
  }

  return [xCoordinate / factor1, yCoordinate / factor1];
};

export const geoJSONValidation = require("geojson-validation");

geoJSONValidation.define("Position", function(position) {
  // the postion must be valid point on the earth, x between -180 and 180
  const errors = [];
  if (position.length > 2) {
    errors.push(i18n.t("farms.panels.geoJSON.invalidCoordinates"));
  }
  if (position[0] < -180 || position[0] > 180) {
    errors.push(i18n.t("farms.panels.geoJSON.invalidXCoordinate"));
  }
  if (position[1] < -90 || position[1] > 90) {
    errors.push(i18n.t("farms.panels.geoJSON.invalidYCoordinate"));
  }
  return errors;
});

export const getFormattedTelemtery = (
  acc,
  { metric, deviceId, time, data }
) => {
  return !data
    ? acc
    : update(acc, {
        [metric]: {
          $auto: {
            [deviceId]: {
              $auto: {
                "": {
                  $auto: {
                    [time]: {
                      $auto: {
                        val: { $set: data }
                      }
                    }
                  }
                }
              }
            }
          }
        }
      });
};

export const handleKeyPressInput = (event, func, keyCode) => {
  if (event.which === keyCode) {
    func();
  }
};

export const getSceneAndSceneFile = (
  sceneMapType,
  farmId,
  mapsData,
  callback
) => {
  let LatestMapSceneId;
  const noMapImage = svgs.NoMapImage;
  MapService.getLatestFarmMapFileById(sceneMapType, farmId).subscribe(
    maps => {
      if (maps.length !== 0) {
        maps.sort((a, b) => new Date(b.date) - new Date(a.date));
        LatestMapSceneId = maps[latestMapIndex][id];
        const mapById = mapsData.filter(map => map.id === LatestMapSceneId);

        if (mapById !== undefined) {
          getSceneFile(LatestMapSceneId, fileType, callback, maps);
        }
      } else {
        const mapData = {
          mapFound: false,
          url: noMapImage
        };
        callback({}, mapData, null);
      }
    },
    sceneError => callback({}, {}, sceneError)
  );
};

export const getSceneFile = (LatestMapSceneId, fileType, callback, maps) => {
  const imageUrlSelectObj = [];
  let blobUrl = undefined;
  let noMaps = false;
  let noPngMaps = true;
  MapService.getMapFileByIdAndType(LatestMapSceneId, fileType).subscribe(
    imageUrlMaps => {
      if (imageUrlMaps !== undefined) {
        // var imageTypeStartIndex;
        // var imageTypeLastIndex;
        blobUrl = undefined;
        noMaps = false;
        noPngMaps = true;
        if (imageUrlMaps.length === 0) {
          noMaps = true;
        }

        for (let i = 0; i < imageUrlMaps.length; i++) {
          if (
            contentTypeLabelsMap.get(imageUrlMaps[i].contentType) ===
            pngImageType
          ) {
            blobUrl = imageUrlMaps[i].downloadSASUrl;
            noPngMaps = false;
          }
          imageUrlSelectObj.push({
            label:
              contentTypeLabelsMap.get(imageUrlMaps[i].contentType) ||
              imageUrlMaps[i].contentType,
            value: imageUrlMaps[i].downloadSASUrl,
            contentType: imageUrlMaps[i].contentType
          });
        }
      }
      const selectedObject = {
        mapFileDownloadSelectObject: imageUrlSelectObj
      };
      const mapData = {
        url:
          imageUrlMaps && imageUrlMaps[latestMapIndex]
            ? imageUrlMaps[latestMapIndex][downloadSASUrl]
            : undefined,
        latestMapDate: maps
          ? moment(maps[latestMapIndex][date]).format(dateFormat)
          : undefined,
        latestCreatedAt: maps
          ? moment(maps[latestMapIndex][createdAt]).format(dateFormat)
          : undefined,
        mapFound: true,
        noMaps: noMaps,
        noPngMaps: noPngMaps,
        isMapFilesPending: false,
        blobUrl: blobUrl
      };
      callback(selectedObject, mapData, null);
    },
    sceneError => callback({}, {}, sceneError)
  );
};

export const getSceneData = (sceneMapType, farmId, mapsData, callback) => {
  getSceneAndSceneFile(sceneMapType, farmId, mapsData, callback);
};

