export function caseInsensitiveStringComparator(a, b) {
  return (a || "").toLowerCase().localeCompare((b || "").toLowerCase());
}

export function getAffectedDevicesCount(farmId, conditions) {
  const sensorMeasures = conditions.map(condition => condition.field);
  const filteredMeasures = [...new Set(sensorMeasures)];
  const filteredSensorModels = this.props.sensorModels.filter(sensorModel =>
    sensorModel.sensorMeasures.some(sensorMeasure =>
      filteredMeasures.includes(sensorMeasure.name)
    )
  );
  const sensorModelIds = filteredSensorModels.map(
    sensorMeasure => sensorMeasure.id
  );
  const filteredSensors = this.props.sensors.filter(sensor =>
    sensorModelIds.includes(sensor.sensorModelId)
  );
  const deviceIds = filteredSensors.map(sensor => sensor.deviceId);
  const devicesList = this.props.devices.filter(device =>
    deviceIds.includes(device.id)
  );
  const affectedDevices = devicesList.filter(
    device => device.farmId === farmId
  );
  return affectedDevices.length;
}

export function isTwoSetsSame(setA, setB) {
  if (setA.size === setB.size) {
    for (const elem of setB) {
      if (!setA.has(elem)) {
        return false;
      }
    }
    return true;
  }
  return false;
}
