import { connect } from "react-redux";
import { withNamespaces } from "react-i18next";
import { AddDevices } from "./addDevices";
import {
  getFarms,
  getFarmsError,
  getFarmsMap,
  getFarmsPendingStatus,
  getFarmsLastUpdated,
  epics as farmsEpics
} from "store/reducers/farmsReducer";

import {
  redux as deviceRedux,
  getDevices,
  getDevicesError,
  getDevicesLastUpdated,
  getDevicesPendingStatus,
  getDeviceModelError,
  getDeviceModelsMap,
  getDeviceModelLastUpdated,
  getDeviceModuleStatusPendingStatus,
  epics as devicesEpics
} from "store/reducers/devicesReducer";

const mapStatetoProps = state => ({
  farms: getFarms(state),
  farmsMap: getFarmsMap(state),
  farmsError: getFarmsError(state),
  isFarmPending: getFarmsPendingStatus(state),
  farmLastUpdated: getFarmsLastUpdated(state),

  devices: getDevices(state),
  deviceError: getDevicesError(state),
  isDevicePending: getDevicesPendingStatus(state),
  deviceLastUpdated: getDevicesLastUpdated(state),

  deviceModelsMap: getDeviceModelsMap(state),
  deviceModelError: getDeviceModelError(state),
  isDeviceModelPending: getDeviceModuleStatusPendingStatus(state),
  deviceModelLastUpdated: getDeviceModelLastUpdated(state)
});

const mapDispatchToProps = dispatch => ({
  fetchDevices: () => dispatch(devicesEpics.actions.fetchDevices()),
  fetchFarms: () => dispatch(farmsEpics.actions.fetchFarms()),
  fetchDeviceModel: () => dispatch(devicesEpics.actions.fetchDeviceModel()),
  modifyDevices: devices => dispatch(deviceRedux.actions.modifyDevices(devices))
});

export const AddDevicesContainer = withNamespaces()(
  connect(
    mapStatetoProps,
    mapDispatchToProps
  )(AddDevices)
);
