import React from "react";
import PropTypes from "prop-types";

import {
  SectionDesc,
  SummaryBody,
  SummaryCount,
  SummarySection
} from "components/shared";
import { displayable } from "utilities";
import "./ruleSummary.scss";

export class RuleSummary extends React.Component {
  render() {
    const { devicesAffected, t } = this.props;
    return (
      <SummarySection>
        {/* <SectionHeader>{ruleName? ruleName : t("rules.flyouts.ruleEditor.summaryHeader")}</SectionHeader> */}

        <SummaryBody>
          <SummaryCount>{displayable(devicesAffected)}</SummaryCount>
          <SectionDesc>
            {t("rules.flyouts.ruleEditor.devicesAffected")}
          </SectionDesc>
        </SummaryBody>
      </SummarySection>
    );
  }
}

RuleSummary.propTypes = {
  ruleName: PropTypes.string,
  ruleDescription: PropTypes.string,
  devicesAffected: PropTypes.number,
  t: PropTypes.func.isRequired
};
