import "rxjs";
import { Observable } from "rxjs";
import moment from "moment";
import { schema, normalize } from "normalizr";
import update from "immutability-helper";
import { createSelector } from "reselect";
import { FarmService } from "services";
import {
  createReducerScenario,
  createEpicScenario,
  errorPendingInitialState,
  pendingReducer,
  errorReducer,
  setPending,
  toActionCreator,
  getPending,
  getError
} from "store/utilities";

import { name } from "utilities";
import { getDevices } from "./devicesReducer";
// ========================= Epics - START
const handleError = fromAction => error =>
  Observable.of(
    redux.actions.registerError(fromAction.type, { error, fromAction })
  );

export const epics = createEpicScenario({
  /** Loads the farm farmItems */
  fetchFarms: {
    type: "FARMS_FETCH",
    epic: fromAction =>
      FarmService.getFarms()
        .map(toActionCreator(redux.actions.updateFarms, fromAction))
        .catch(handleError(fromAction))
  },
  /** Delete Farm */
  deleteFarm: {
    type: "FARMS_DELETE",
    epic: fromAction =>
      FarmService.deleteFarm(fromAction.payload)
        .map(toActionCreator(redux.actions.deleteFarm, fromAction))
        .catch(handleError(fromAction))
  }
});
// ========================= Epics - END

// ========================= Schemas - START
const farmSchema = new schema.Entity("farms");
const farmListSchema = new schema.Array(farmSchema);
// ========================= Schemas - END

// ========================= Reducers - START
const initialState = {
  ...errorPendingInitialState,
  farmEntities: undefined,
  farmItems: []
};

const insertFarmReducer = (state, { payload }) => {
  const {
    entities: { farms },
    result
  } = normalize(payload, farmListSchema);
  if (state.farmEntities) {
    return update(state, {
      farmEntities: { $merge: farms },
      farmItems: { $splice: [[0, 0, ...result]] }
    });
  }
  return update(state, {
    farmEntities: { $set: farms },
    farmItems: { $set: [result] }
  });
};

const updateFarmsReducer = (state, { payload, fromAction }) => {
  const {
    entities: { farms },
    result
  } = normalize(payload, farmListSchema);
  return update(state, {
    farmEntities: { $set: farms },
    farmItems: { $set: result },
    lastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

const deleteFarmReducer = (state, { payload, fromAction }) => {
  const spliceArr = payload.reduce((idxAcc, payloadItem) => {
    const idx = state.farmItems.indexOf(payloadItem);
    if (idx !== -1) {
      idxAcc.push([idx, 1]);
    }
    return idxAcc;
  }, []);
  return update(state, {
    farmEntities: { $unset: payload },
    farmItems: { $splice: spliceArr }
  });
};

const modifyFarmReducer = (state, { payload }) => {
  const {
    entities: { farms }
  } = normalize(payload, farmListSchema);
  return update(state, {
    farmEntities: { $merge: farms }
  });
};

/* Action types that cause a pending flag */
const fetchableTypes = [
  epics.actionTypes.fetchFarms,
  epics.actionTypes.deleteFarm
];

export const redux = createReducerScenario({
  updateFarms: { type: "FARMS_UPDATE", reducer: updateFarmsReducer },
  registerError: { type: "RULES_REDUCER_ERROR", reducer: errorReducer },
  insertFarm: { type: "FARMS_INSERT", reducer: insertFarmReducer },
  deleteFarm: { type: "FARMS_DELETE", reducer: deleteFarmReducer },
  modifyFarm: { type: "FARM_MODIFY", reducer: modifyFarmReducer },
  isFetching: { multiType: fetchableTypes, reducer: pendingReducer }
});

export const reducer = { farms: redux.getReducer(initialState) };
// ========================= Reducers - END

// ========================= Selectors - START
export const getFarmsReducer = state => state.farms;
export const getFarmEntities = state =>
  getFarmsReducer(state).farmEntities || {};
export const getFarmItems = state => getFarmsReducer(state).farmItems || [];
export const getFarmsLastUpdated = state => getFarmsReducer(state).lastUpdated;
export const getFarmsError = state =>
  getError(getFarmsReducer(state), epics.actionTypes.fetchFarms);
export const getFarmsPendingStatus = state =>
  getPending(getFarmsReducer(state), epics.actionTypes.fetchFarms);
export const getDeleteFarmError = state =>
  getError(getFarmsReducer(state), epics.actionTypes.deleteFarm);
export const getDeleteFarmPendingStatus = state =>
  getPending(getFarmsReducer(state), epics.actionTypes.deleteFarm);

export const getFarms = createSelector(
  getFarmEntities,
  getFarmItems,
  getDevices,
  (farmEntities, farmItems, devices) => {
    return farmItems.map(id => {
      return {
        ...farmEntities[id],
        count: devices.filter(device => device.farmId === id).length
      };
    });
  }
);
export const getFarmById = (state, id) =>
  id === undefined ? null : getFarmEntities(state)[id];

export const getFarmsMap = createSelector(
  getFarmEntities,
  getFarmItems,
  (FarmEntities, FarmItems) =>
    FarmItems.reduce((FarmMap, id) => {
      return FarmMap.set(id, FarmEntities[id][name]);
    }, new Map())
);

// ========================= Selectors - END
