import React from "react";
import PropTypes from "prop-types";

import { RuleSummary } from "./ruleSummary";
import { getAffectedDevicesCount } from "utilities";

/** *
 * Component that renders a <tt>RuleSummary</tt> component
 * and passes it the <tt>devicesAffected</tt> prop
 * after automatically calculating it from either
 * a <tt>rule</tt> prop or a <tt>farmId</tt> prop
 * passed to it
 */
export class RuleSummaryAuto extends React.Component {
  render() {
    return (
      <RuleSummary
        devicesAffected={getAffectedDevicesCount.call(
          this,
          this.props.rule.farmId,
          this.props.rule.conditions
        )}
        t={this.props.t}
      />
    );
  }
}

RuleSummaryAuto.propTypes = {
  // From mapStateToProps
  devices: PropTypes.array.isRequired,

  // Passed to HOC and passed down
  rule: PropTypes.object,
  farmId: PropTypes.string,
  ruleName: PropTypes.string,
  ruleDescription: PropTypes.string
};
