/*
  This file exists to show the rule name in the alerts grid.
  When a rule is deleted, the alerts grid is to show "Rule Deleted",
  just showing '---' doesn't work.

  Later, this component will have to change, so as to allow Rule Name as
  a clickable link. Look at how that is being done in rulesGrid.

  Hint:  see rulesGridConfig

*/

import React from "react";

import "../cellRenderer.scss";

export const ruleNameRenderer = ({ value, context: { t } }) => (
  <div
    className={`pcs-renderer-cell ${value === "Enabled" ? "highlight" : ""}`}
  >
    {value ? (
      <div className="pcs-renderer-text">{value}</div>
    ) : (
      <div className="pcs-renderer-text" style={{ color: "red" }}>
        {t("alerts.grid.ruleDeleted")}
      </div>
    )}
  </div>
);
