import React from "react";

import Config from "app.config";
import { Svg } from "components/shared/svg/svg";
import { svgs } from "utilities";

import "../cellRenderer.scss";
import "./jobStatusRenderer.scss";
import { appInsights } from "applicationInsights";

const getSvg = value => {
  switch (value) {
    case Config.jobModel.submitted:
      return svgs.waiting;
    case Config.jobModel.accepted:
      return svgs.waiting;
    case Config.jobModel.running:
      return svgs.waiting;
    case Config.jobModel.pendingRetry:
      return svgs.waiting;
    case Config.jobModel.failed:
      return svgs.rejected;
    case Config.jobModel.succeeded:
      return svgs.online;
    case Config.jobModel.stopped:
      return svgs.rejected;
    case Config.jobModel.stopRequested:
      return svgs.rejected;
    default:
  }
};

export const JobStatusRenderer = ({
  data: { error },
  value,
  context: { t }
}) => {
  const cleanValue = (value || "").toLowerCase();
  const cellClasses = `pcs-renderer-cell status ${cleanValue || ""} ${
    cleanValue ? "highlight" : ""
  }`;
  return (
    <div className={cellClasses}>
      <Svg path={getSvg(cleanValue)} className="pcs-renderer-icon" />
      {
        <div className="pcs-renderer-text">
          {t(`jobs.status.${cleanValue}`)}
        </div>
      }
      {t(cleanValue) === "failed" && (
        <div>
          <div
            className="tooltip"
            onMouseEnter={() =>
              appInsights.trackEvent({
                name: "UiEvent:Hover: Job Failure Tooltip"
              })
            }
          >
            <Svg className="fail-info tooltip" path={svgs.infoBubble} />
            <div className="tooltip tooltiptext">
              <span className="error-text">{error}</span>
            </div>
          </div>
        </div>
      )}
    </div>
  );
};

export default JobStatusRenderer;
