import React, { Component } from "react";
import "util";
import { ToolTip } from "../../shared/toolTip/toolTip";
import {
  AjaxError,
  Select,
  ComponentArray,
  ContextMenu,
  ContextMenuAlign,
  PageContent,
  PageTitle,
  Btn,
  Protected,
  // RefreshBarContainer as RefreshBar,
  SearchInput,
  GenerateMapsFlyoutContainer
} from "components/shared";
import { MapsGrid } from "./mapsGrid";
import { svgs, isDef, FormElementType } from "utilities";
import "./maps.scss";
import {
  satelliteIndicesMap,
  soilMoistureMap,
  sensorPlacementMap,
  satelliteIndicesJobTypeName,
  sensorPlacementJobTypeName,
  soilMoistureJobTypeName,
  jobsDateFormat
} from "utilities";
import { permissions } from "services/models";
import moment from "moment";
import FilterControl from "components/shared/filter/filterControl";
import { reactPlugin } from "applicationInsights";
import { withAITracking } from "@microsoft/applicationinsights-react-js";
import { appInsights } from "applicationInsights";

let farmsNameObject = [];
let mapsTypesObject = [];
let filteredMaps = [];

class Maps extends Component {
  constructor(props) {
    super(props);
    this.state = {
      contextBtns: null,
      modalIsOpen: false,
      farmsNameObject: [],
      openFilterFlyout: false,
      filterState: {
        isFilterOn: false,
        filterMapType: undefined,
        selectedMapType: undefined,
        selectedDate: undefined,
        selectedFarmName: undefined,
        appliedFilters: 0
      }
    };
    this.createMaps = this.createMaps.bind(this);
    this.modalclose = this.modalclose.bind(this);
    this.modalopen = this.modalopen.bind(this);

    const {
      mapsLastUpdated,
      fetchMaps,
      mapsError,
      fetchFarms,
      farmsLastUpdated,
      farmsError
    } = this.props;

    if (!mapsLastUpdated && !mapsError) {
      fetchMaps();
    }
    if (!farmsLastUpdated && !farmsError) {
      fetchFarms();
    }
  }

  openFlyout = name => () => {
    this.setState({ openFlyoutName: name });
  };

  componentDidMount() {
    filteredMaps = this.props.maps;
  }

  closeFlyout = () => this.setState({ openFlyoutName: undefined });

  onContextMenuChange = contextBtns => this.setState({ contextBtns });

  onGridReady = gridReadyEvent => (this.mapGridApi = gridReadyEvent.api);
  searchOnChange = ({ target: { value } }) => {
    if (this.mapGridApi) this.mapGridApi.setQuickFilter(value);
  };

  modalopen() {
    this.setState({ openModalName: soilMoistureMap });
  }
  modalclose() {
    this.isModalopentrue = false;
  }

  createMaps(event) {
    const typeOfSelectedMap = event.target.value.value;

    switch (typeOfSelectedMap) {
      case satelliteIndicesMap:
        this.setState({
          openFlyoutName: satelliteIndicesMap,
          jobTypeName: satelliteIndicesJobTypeName
        });
        break;
      case soilMoistureMap:
        this.setState({
          openFlyoutName: soilMoistureMap,
          jobTypeName: soilMoistureJobTypeName
        });
        break;
      case sensorPlacementMap:
        this.setState({
          openFlyoutName: sensorPlacementMap,
          jobTypeName: sensorPlacementJobTypeName
        });
        break;
      default:
    }
  }

  toSelectOptionFromArray = item => ({ label: item, value: item });

  toSelectOption = ({ name, id }) => ({ label: name, value: id });

  closeTooltip = () => {
    const { updateToolTipPendingStatus } = this.props;
    updateToolTipPendingStatus();
  };

  filterMapsFlyout = () => {
    this.setState({ openFilterFlyout: true });
  };

  onCloseFlyout = () => this.setState({ openFilterFlyout: false });

  handleChangeMapType = event => {
    this.setState({ selectedMapType: event.target.value.label });
  };

  handleChangeFarmName = event => {
    this.setState({ selectedFarmName: event.target.value.label });
  };

  onFilterClick = FilterControlstate => {
    appInsights.trackEvent({
      name: "UiEvent:Click: Maps Filter Flyout Apply Btn"
    });
    const obj = {
      filterState: {
        filterMapType: FilterControlstate.map_type
          ? FilterControlstate.map_type
          : undefined,
        selectedMapType: FilterControlstate.map_type
          ? FilterControlstate.map_type
          : undefined,
        selectedDate: FilterControlstate.date
          ? FilterControlstate.date
          : undefined,
        selectedFarmName: FilterControlstate.farm_name
          ? FilterControlstate.farm_name
          : undefined,
        isFilterOn: FilterControlstate.isFilterOn,
        appliedFilters: FilterControlstate.appliedFilters
      }
    };

    this.setState(obj);

    filteredMaps =
      isDef(FilterControlstate.map_type) ||
      isDef(FilterControlstate.farm_name) ||
      (isDef(FilterControlstate.date) &&
        (isDef(FilterControlstate.date.from) ||
          isDef(FilterControlstate.date.to)))
        ? this.props.maps.filter(
            map =>
              (isDef(FilterControlstate.map_type)
                ? map.type === FilterControlstate.map_type.label
                : {}) &&
              (isDef(FilterControlstate.farm_name)
                ? map.farmId === FilterControlstate.farm_name.value
                : {}) &&
              (isDef(FilterControlstate.date) &&
              isDef(FilterControlstate.date.from)
                ? isDef(FilterControlstate.date) &&
                  isDef(FilterControlstate.date.to)
                  ? moment(moment(map.date).format(jobsDateFormat)).isBetween(
                      FilterControlstate.date.from,
                      FilterControlstate.date.to,
                      null,
                      []
                    )
                  : moment(map.date).format(jobsDateFormat) ===
                    FilterControlstate.date.from
                : {})
          )
        : this.props.maps;
  };

  render() {
    const {
      openFlyoutName,
      jobTypeName,
      openFilterFlyout,
      filterState
    } = this.state;
    const {
      t,
      maps,
      mapsError,
      isMapsPending,
      // mapsLastUpdated,
      fetchMaps,
      isFarmsPending,
      farms,
      farmsError,
      isTooltipPending,
      mapTypes
    } = this.props;

    farmsNameObject = farms.map(this.toSelectOption);

    mapsTypesObject = mapTypes
      .filter(mapType => isDef(mapType))
      .map(this.toSelectOptionFromArray);

    const isFlyoutOpen =
      openFlyoutName === satelliteIndicesMap ||
      openFlyoutName === soilMoistureMap ||
      openFlyoutName === sensorPlacementMap;
    const gridProps = {
      onGridReady: this.onGridReady,
      rowData:
        isMapsPending || isFarmsPending
          ? undefined
          : filterState && filterState.isFilterOn
          ? filteredMaps
          : maps || [],
      onContextMenuChange: this.onContextMenuChange,
      t: t,
      refresh: fetchMaps,
      farms: farms
    };

    const selectObject = [
      { label: t("maps.satelliteIndices"), value: satelliteIndicesMap },
      { label: t("maps.soilMoisture"), value: soilMoistureMap },
      { label: t("maps.sensorPlacement"), value: sensorPlacementMap }
    ];

    const classNameDatePicker = "datepicker-textbox";

    /**
     * FilterList specifies the object of a particular filter.
     *
     * List contains all the values for the select options.
     *
     * FormElementType is Type of filter Element you want.
     *
     * Label is text before the filter element.
     *
     * filterKey --> ***Important (Returns the state value based on this key).
     *
     * filterKey for DatePickers ==> it comes under Object as from and to values.
     *
     * typeProps contains the props needs to be passed on to nested component
     *
     *
     */

    //  console.log("SelectedOption filter state", filterState.selectedFarmName)
    const filterConfig = {
      FilterList: [
        {
          List: mapsTypesObject,
          type: FormElementType.select,
          typeProps: {
            selectedValue: filterState.selectedMapType,
            placeholder: t("maps.filterFlyout.selectMapType")
          },
          label: "Type",
          filterKey: "map_type",
          className: ""
        },
        {
          type: FormElementType.datePicker,
          label: "Date",
          filterKey: "date",
          typeProps: {
            className: classNameDatePicker,
            singleDatePicker: false,
            inputValue: filterState.selectedDate,
            IconClass: "datepicker-icon",
            containerType: "input",
            containerIcon: svgs.datePicker,
            containerPlaceHolder: "Enter Date Range",
            containerValue: filterState.selectedDate,
            selectedValue: filterState.selectedDate
          }
        },
        {
          List: farmsNameObject,
          type: FormElementType.select,
          typeProps: {
            selectedValue: filterState.selectedFarmName
              ? filterState.selectedFarmName
              : undefined,
            placeholder: t("maps.filterFlyout.selectFarm")
          },
          label: "Name",
          filterKey: "farm_name",
          className: ""
        }
      ],

      applyCallback: this.onFilterClick,
      resetCallback: this.onResetClick,
      header: {
        className: "",
        content: t("jobs.flyouts.filter")
      }
    };

    return (
      <ComponentArray>
        <ContextMenu>
          <ContextMenuAlign left={true}>
            {/* Search input Bar*/}
            <SearchInput
              onChange={this.searchOnChange}
              placeholder={t("maps.searchPlaceholder")}
            />

            {isTooltipPending === true && (
              <ToolTip
                t={t}
                closeTooltip={this.closeTooltip}
                title={t("maps.searchMap")}
                content={t("maps.searchMapName")}
              />
            )}
            <Protected permissions={permissions.createMaps}>
              <Select
                className="maps-group-dropdown"
                placeholder="Create Maps"
                options={selectObject}
                clearable={false}
                searchable={false}
                onChange={event => {
                  this.createMaps(event);
                  appInsights.trackEvent(
                    {
                      name:
                        "UiEvent:Click: Generate " +
                        event.target.value.value +
                        " Fly Open"
                    },
                    {
                      codeSource: {
                        component: "Maps Page"
                      }
                    }
                  );
                }}
                icon={svgs.plus}
              />
            </Protected>
            <ContextMenuAlign right={true}>
              <Btn
                svg={
                  filterState.appliedFilters > 0
                    ? svgs.appliedFilter
                    : svgs.filter
                }
                className="filter-maps"
                onClick={event => {
                  this.filterMapsFlyout(event);
                  appInsights.trackEvent({
                    name: "UiEvent:Click: Maps Filter Flyout Open Btn"
                  });
                }}
              >
                {t("jobs.filter")}{" "}
                {filterState.appliedFilters > 0 && (
                  <span>{`(${filterState.appliedFilters})`}</span>
                )}
              </Btn>
            </ContextMenuAlign>
          </ContextMenuAlign>
        </ContextMenu>

        <PageContent className="maps-container" key="page-content">
          {/* <RefreshBar
            refresh={fetchMaps}
            time={mapsLastUpdated}
            isPending={isMapsPending}
            t={t}
          /> */}
          <PageTitle titleValue={t("maps.title")} />
          {isFlyoutOpen && (
            <GenerateMapsFlyoutContainer
              farmsSelectObject={farmsNameObject}
              t={t}
              flyoutName={this.state.openFlyoutName}
              onClose={this.closeFlyout}
              jobTypeName={jobTypeName}
              isFarmsPending={isFarmsPending}
              farmsError={farmsError}
            />
          )}
          {!!mapsError && <AjaxError t={t} error={mapsError} />}
          {!mapsError && <MapsGrid {...gridProps} />}
          {openFilterFlyout && (
            <FilterControl
              t={t}
              onCloseFlyout={this.onCloseFlyout}
              isPending={isFarmsPending}
              filterConfig={filterConfig}
            />
          )}
        </PageContent>
      </ComponentArray>
    );
  }
}

export default withAITracking(reactPlugin, Maps, "Maps Grid");
