import "rxjs";
import { Observable } from "rxjs";

import moment from "moment";
import { schema, normalize } from "normalizr";
import update from "immutability-helper";

import { createSelector } from "reselect";
import { UserService } from "services";

import {
  createReducerScenario,
  createEpicScenario,
  resetPendingAndErrorReducer,
  errorPendingInitialState,
  pendingReducer,
  errorReducer,
  getPending,
  setPending,
  toActionCreator,
  getError
} from "store/utilities";

import Config from "app.config";

// ========================= Epics - START
const handleError = fromAction => error =>
  Observable.of(
    redux.actions.registerError(fromAction.type, { error, fromAction })
  );

export const epics = createEpicScenario({
  /** Fetch Users list from backend */
  fetchUsers: {
    type: "USER_FETCH",
    epic: fromAction =>
      Observable.from(fromAction.payload)
        .bufferCount(Config.graphApiUserBufferSize)
        .map(userIds => UserService.getUsersByIds(userIds))
        .mergeAll()
        .map(toActionCreator(redux.actions.mergeUsers, fromAction))
        .catch(handleError(fromAction))
  }
});

const userSchema = new schema.Entity("users");
const userListSchema = new schema.Array(userSchema);
// ========================= Schemas - END

// ========================= Reducers - START
const initialState = {
  ...errorPendingInitialState,
  userEntities: {},
  userItems: [],
  userlastUpdated: ""
};

const updateUserReducer = (state, { payload, fromAction }) => {
  const {
    entities: { users },
    result
  } = normalize(payload, userListSchema);
  return update(state, {
    userEntities: { $set: users },
    userItems: { $set: result },
    userlastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

const mergeUserReducer = (state, { payload, fromAction }) => {
  const {
    entities: { users },
    result
  } = normalize(payload, userListSchema);
  return update(state, {
    userEntities: { $merge: users },
    userItems: { $push: result },
    userlastUpdated: { $set: moment() },
    ...setPending(fromAction.type, false)
  });
};

/* Action types that cause a pending flag */
const fetchableTypes = [epics.actionTypes.fetchUsers];

export const redux = createReducerScenario({
  updateUsers: { type: "USER_UPDATE", reducer: updateUserReducer },
  mergeUsers: { type: "USER_MERGE", reducer: mergeUserReducer },
  registerError: { type: "USER_REDUCER_ERROR", reducer: errorReducer },
  isFetching: { multiType: fetchableTypes, reducer: pendingReducer },
  resetPendingAndError: {
    type: "USER_REDUCER_RESET_ERROR_PENDING",
    reducer: resetPendingAndErrorReducer
  }
});

export const reducer = {
  users: redux.getReducer(initialState)
};

// ========================= Reducers - END

// ========================= Selectors - START
export const getUsersReducer = state => state.users;

export const getUserEntities = state =>
  getUsersReducer(state).userEntities || {};

export const getUserItems = state => getUsersReducer(state).userItems || [];

export const getUserLastUpdated = state =>
  getUsersReducer(state).userlastUpdated;

export const getUserError = state =>
  getError(getUsersReducer(state), epics.actionTypes.fetchUsers);

export const getUserPendingStatus = state =>
  getPending(getUsersReducer(state), epics.actionTypes.fetchUsers);

export const getUsers = createSelector(
  getUserEntities,
  getUserItems,
  (userEntities, userItems) => userItems.map(id => userEntities[id])
);
