import React from "react";

import Config from "app.config";
import { Svg } from "components/shared/svg/svg";
import { svgs } from "utilities";

import "../cellRenderer.scss";
import "./deviceModelTypeRenderer.scss";

const getSvg = value => {
  if (value === Config.deviceModel.node) return svgs.node;
  if (value === Config.deviceModel.gateway) return svgs.gateway;
};

export const deviceModelTypeRenderer = ({
  value,
  context: { t },
  iconOnly
}) => {
  const cleanValue = (value || "").toLowerCase();
  const cellClasses = `pcs-renderer-cell deviceModelType ${cleanValue || ""} ${
    cleanValue ? "highlight" : ""
  }`;
  return (
    <div className={cellClasses}>
      <Svg path={getSvg(cleanValue)} className="pcs-renderer-icon" />
      {<div className="pcs-renderer-text">{t(value)}</div>}
    </div>
  );
};

export default deviceModelTypeRenderer;
