import Config from "app.config";
import { HttpClient } from "utilities/httpClient";

export class UserService {
  // Graph endpoints
  static getUsers() {
    const response = HttpClient.get(
      Config.serviceUrls.graphApiUsers,
      {},
      true,
      true
    );
    return response.map(response => response.value);
  }

  static getUsersByIds(ids) {
    const query =
      "?$filter=" + ids.map(id => "id eq '" + id + "'").join(" or ");

    const response = HttpClient.get(
      `${Config.serviceUrls.graphApiUsers}${query}`,
      {},
      true,
      true
    );
    return response.map(response => response.value);
  }

  // Gets user by email or UPN
  static getUserByEmailOrUpn(email) {
    const query = `?$filter=mail eq '${email}' or userPrincipalName eq '${email}'`;
    const response = HttpClient.get(
      `${Config.serviceUrls.graphApiUsers}${query}`,
      {},
      true,
      true
    );
    return response.map(response => response.value);
  }

  static getUserSuggestions(value) {
    const query = `?$filter=startswith(displayName,'${value}') or startswith(givenName,'${value}') or startswith(surname,'${value}') or startswith(mail,'${value}') or startswith(userPrincipalName,'${value}')`;
    const response = HttpClient.get(
      `${Config.serviceUrls.graphApiUsers}${query}`,
      {},
      true,
      true
    );
    return response.map(response => response.value);
  }
}
