import React, { Component } from "react";

import {
  AjaxError,
  SuccessMessage,
  Btn,
  BtnToolbar,
  Indicator,
  Modal
} from "components/shared";
import { svgs } from "utilities";

import "./deleteModal.scss";

export class DeleteModal extends Component {
  constructor(props) {
    super(props);

    this.state = {
      changesApplied: false
    };
  }

  apply = () => {
    const { deleteItem } = this.props;
    deleteItem();
    this.setState({ changesApplied: true });
  };

  genericCloseClick = eventName => {
    const { onClose } = this.props;
    onClose();
  };

  render() {
    const {
      t,
      isPending,
      deleteModalError,
      title,
      deleteConfirmation,
      deleteBtn,
      icon,
      deleteModalSuccess,
      successMsg
    } = this.props;
    const { changesApplied } = this.state;

    return (
      <Modal
        onClose={() => this.genericCloseClick("DeleteModal_ModalClose")}
        className="delete-modal-container"
      >
        <Btn
          className="delete-close-btn"
          onClick={() => this.genericCloseClick("DeleteModal_CloseClick")}
          svg={svgs.x}
        />
        <div className="delete-header-container">{title}</div>
        {!changesApplied && (
          <div className="delete-info">
            {Array.isArray(deleteConfirmation) ? (
              <div>
                {deleteConfirmation.map((par, idx) => (
                  <p key={idx}>{par}</p>
                ))}
              </div>
            ) : (
              <p> {deleteConfirmation}</p>
            )}
          </div>
        )}
        <div className="delete-summary">
          {!changesApplied && (
            <BtnToolbar>
              <Btn
                svg={icon}
                className="btn-delete"
                primary={true}
                onClick={this.apply}
              >
                {deleteBtn}
              </Btn>
            </BtnToolbar>
          )}
          {isPending && <Indicator />}
          {!isPending && changesApplied && deleteModalSuccess && (
            <SuccessMessage
              className="delete-modal-success"
              t={t}
              key="delete-modal-success"
              successMsg={successMsg}
            />
          )}
          {!isPending && changesApplied && deleteModalError !== undefined && (
            <AjaxError
              className="delete-modal-error"
              t={t}
              key="delete-modal-error"
              error={deleteModalError}
            />
          )}
        </div>
      </Modal>
    );
  }
}
